import React, { Component } from 'react';
import ProfileContainer from '../../../containers/ManageContainer/ProfileContainer/ProfileContainer';
import CarrierProfileContainer from '../../../containers/ManageContainer/CarrieProfileContainer/CarrierProfileContainer';
import ManagUserContainer from '../../../containers/ManageContainer/ManageUserContainer/ManageUserContainer';
import { ManageCapacityContainer } from '../ManageCapacityContainer';

const SectionContainer = (props: any) => {
  const renderSwitch = (section: string) => {
    switch (section) {
      case 'My Profile':
        return <ProfileContainer />;
      case 'Carrier Profile':
        return <CarrierProfileContainer />;
      case 'Manage Users':
        return <ManagUserContainer />;
      case 'Manage My Capacity':
        return <ManageCapacityContainer />;
      default:
        return <ProfileContainer />;
    }
  };
  return renderSwitch(props.section);
};

export default SectionContainer;
