import { KEYS } from '../utils/constants';

interface EndPoints {
  [index: string]: any;
  HOME: Keys;
}
interface Keys {
  [index: string]: string;
  RECOMMENDED_LOADS: string;
}

export const URL_ENDPOINTS: EndPoints = {
  HOME: {
    RECOMMENDED_LOADS: "/recommended/v1/recommended/loads"
  },
  SEARCH: {
    SEARCH: "/load/v1/loadposting"
  },
  RECENT: {
    RECENT: "/usersearch/v1/history"
  },
  FAVORITE: {
    FAVORITE_LOADS: "/usersearch/v1/history"
  },
  DRIVERS_LIST: {
    DRIVERS_LIST: "/carriermember/v1/member"
  },
  FETCH_USER_DETAILS: {
    USER_DETAILS: "/carriermember/v1/member",
    GET_USER_DETAILS: "/carriermember/v1/member"
  },
  MYLOAD: {
    CARRIER: "/v1/shipment",
    BUY_LOAD: '/load/v1/loadposting'
  },
  MANAGE: {
    MY_CAPACITY: '/laneandtruck/v1/laneandtruck',
    CARRIER_PROFILE: '/masterdata/v1/carrierdata'
  },
  LOCATION_TRACKER: {
    LOCATION_TRACKER: "/devicelocation/v1/location"
  },
  REGISTER_DEVICE: {
    REGISTER_DEVICE: "/devicemanage/v1/manage"
  },
  MORETAB: {
    MORETAB: "/contentstack/v1/contentstack"
  },
  CONFIG: {
    APP_LEVEL: "/contentstack/v1/content-stack/config?env={configEnv}"
  },
  SUBMITFEEDBACK: {
    SUBMITFEEDBACK: "/devicemanage/v1/manage"
  },
  AZURE_MAPS:{
    ACCESS_TOKEN: "/zipcode/v1/oauth2/token"
  }

};

/**
 * To build request URL with provided inputs.
 * @param {string} endPoint URL category name.
 * @param {string} urlKey endpoint key name.
 * @param {*} value query string value to append to the url.
 */

export default function getURLDetails(
  endPoint: string,
  urlKey: string,
  value?: any
) {
  return {
    get_Service_Url: function () {
      let _url: string = "";
      let keyValue = URL_ENDPOINTS[endPoint][urlKey];
      if (keyValue) {
        _url = "https://" + KEYS.API_HOST + keyValue;
        _url = value ? _url.replace("{upn}", value).replace("{configEnv}", value) : _url;
      } else {
        throw new Error(
          "key value of End point URL is not matching with provided key name."
        );
      }
      return _url;
    }
  };
}
