import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { createStructuredSelector } from "reselect";
import { isPlatform } from "@ionic/react";
import useInjectSaga from "../../../utils/injectSaga";
import useInjectReducer from "../../../utils/injectReducer";
import saga from "../../../saga/CarrierAssign/saga";
import {
  getLoading,
  getUserDetails,
  getNetworkStatus
} from "../../../selectors/selector";
import CarrierAssignedPage from "../../../pages/Execute/CarrierAssigned/CarrierAssignedPage.page";
import {
  getShowToasterValue,
  getCarrierLoads
} from "../../../selectors/carrierAssign/selector";
import reducer from "../../../reducers/carrierAssign/reducer";
import {
  upadateShowToaster,
  AssignDriver,
  fetchShipments
} from "../../../actions/carrierAssign/action";
import { DriverList } from "../../../models/home/CarrierAssign.model";
import { MyloadSectionContainer } from "../sectionContainer";
import { ExicutionLoads } from "../../../models/ExicutionLoads.model";
import { showAlert } from "../../../actions/App/action";

const key = "carrierAssigned";

class CarrierAssignedContainer extends PureComponent<any, any> {
  componentDidMount() {
    if (
      this.props.data &&
      this.props.data.length === 0 &&
      !this.props.loading
    ) {
      this.props.updateData();
    }
  }

  chnageValue = () => {
    this.props.changeToasterValue();
  };

  assignDriverButton = (loadDetails: any) => {
    const loadId = loadDetails.shipmentId;

    if (this.props.userDetails && this.props.userDetails.carrierId) {
      const modelList: DriverList = new DriverList();
      modelList.carrierId = this.props.userDetails.carrierId;
      modelList.role = 'carrierDriver';
      this.props.AssignDriver(modelList.getRequestPayload());
      this.props.history.push(`/app/MyLoad/driverAssign/${loadId}`, {
        loadDetails: JSON.stringify(loadDetails),
        pathname: this.props.location.pathname
      });
    }
  };

  getCarrierAssignLoads = (isHideLoader = true) => {
    try {
      const carrierId =
        (this.props && this.props.userDetails && this.props.userDetails.carrierId) || '';
      const carrierModel: ExicutionLoads = new ExicutionLoads();
      carrierModel.carrierId = carrierId;
      carrierModel.filter = { carrierAssigned: true };
      this.props.fetchShipments(carrierModel.getRequestPayload(), isHideLoader);
    } catch (error) {
      console.log("ERROR:", error);
    }
  };

  render() {
    const data =
      this.props.loads && this.props.loads.length > 99
        ? this.props.loads.slice(0, 99)
        : this.props.loads;
    return (
      <>
        {isPlatform("mobile") ? (
          <CarrierAssignedPage
            {...this.props}
            getLoads={this.getCarrierAssignLoads}
            chnageValue={this.chnageValue}
            AssignDriverbtn={(loadDetails: any) =>
              this.assignDriverButton(loadDetails)
            }
            module="MyLoad"
            loads={data}
            showFilter={false}
            isButtonHidden={false}
            buttonName="Assign Driver"
            showInitial={false}
            subModule={this.props.match.params[0]}
          />
        ) : (
          <MyloadSectionContainer {...this.props} />
        )}
      </>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  getToasterValue: getShowToasterValue(),
  loads: getCarrierLoads(),
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  changeToasterValue: () => dispatch(upadateShowToaster()),
  AssignDriver: (data: any) => dispatch(AssignDriver(data)),
  fetchShipments: (details: any, hideLoader: boolean) =>
    dispatch(fetchShipments(details, hideLoader)),
  showAlert: (data: any) => dispatch(showAlert(data))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(
  withSaga,
  withReducer,
  withConnect
)(CarrierAssignedContainer);
export { CarrierAssignedContainer };
