import * as moment from "moment-timezone";
import { isPlatform } from "@ionic/react";
import { EquipmentFlow } from "../../schemas/Exicution/exicution.schema";
import {
  OTMASSET,
  ExecutionStatus,
  TRAILER_PARTTEN,
} from "../../utils/constants";
import { StopActivityProps } from "../stopActivity.model";

export class StartTripModel {
  public getTimeWithTimezone(timeZone: string): string {
    return this.getTimeForIOS(moment.tz(timeZone).format());
  }

  public isIOSPlatform(): boolean {
    return isPlatform("ios") || isPlatform("ipad") || isPlatform("iphone");
  }

  public getTimeForIOS(date: string): string {
    const localZone = moment.tz.guess();
    const result = moment.tz(date.substring(0, 16), localZone).format();
    console.log("result=", result);
    return result;
  }

  /*
   * get the current year value
   */
  public yearsValue(): number {
    const _date = new Date();
    return _date.getFullYear();
  }

  /*
   * get the shipment status that staop has been confirmed or not
   * @shipment - shipment for the trip
   */
  public isConfirmStartButtonDisabled(shipment: any): boolean {
    const { startCity, startState, startDateTime } = shipment || {};
    return (startCity || startState) && startDateTime;
  }

  /*
   * get the starting location of the trip
   * @shipment - shipment for the trip
   */
  public getStartingLocation(shipment: any): string {
    const { startCity, startState } = shipment || {};
    return `${startCity}${startState ? ", " : ""}${startState}`.trim();
  }

  /*
   * get the stating date of the shipment
   * @shipment - shipment for the trip
   */
  public getStartingDateTime(shipment: any): string {
    const { startDateTime } = shipment || {};
    return startDateTime;
  }

  /*
   * get the current open stop
   * @shipment - shipment for the trip
   */
  public getCurrentOpenStop(shipment: any): number {
    let openStop = -1;
    if (this.isConfirmStartButtonDisabled(shipment)) {
      openStop = 0;
    }
    const isOtmType = this.isOTMTypeShipment(shipment);
    if (shipment && shipment.loadStop) {
      let loadStop: any[] = shipment.loadStop || [];
      loadStop = loadStop.slice().sort((load1: any, load2: any) => {
        if (load1.sequenceNo && load2.sequenceNo) {
          return load1.sequenceNo - load2.sequenceNo;
        }
        if (load1.stopSequence && load2.stopSequence) {
          return load1.stopSequence - load2.stopSequence;
        }
        return 0;
      });
      loadStop.forEach((load: any, index: number) => {
        if (isOtmType) {
          const _trailerIn = this.getTrailerIn(load, false, false, shipment);
          const _trailerIout = this.getTrailerOut(load, false, shipment);
          if (
            load.actualDepartureDateTime &&
            _trailerIn.trailerIn && _trailerIout.trailerOut
          ) {
            openStop = index + 1;
          }
        } else if (load.actualDepartureDateTime) {
          openStop = index + 1;
        }
      });
    }
    return openStop;
  }

  /*
   * is all stop completed or not
   * @shipment - shipment for the trip
   */
  public isAllStopCompleted(shipment: any): boolean {
    let allStopCompleted = false;
    if (shipment && shipment.loadStop) {
      let loadStop: any[] = shipment.loadStop || [];
      loadStop = loadStop.slice().sort((load1: any, load2: any) => {
        if (load1.sequenceNo && load2.sequenceNo) {
          return load1.sequenceNo - load2.sequenceNo;
        }
        if (load1.stopSequence && load2.stopSequence) {
          return load1.stopSequence - load2.stopSequence;
        }
        return 0;
      });
      if (loadStop.length > 0) {
        allStopCompleted = !!loadStop[loadStop.length - 1]
          .actualDepartureDateTime;
      } else {
        allStopCompleted = true;
      }
    }
    return allStopCompleted;
  }

  public isTripCompleted(shipment: any): boolean {
    return (
      (shipment && shipment.executionStatus === ExecutionStatus.COMPLETED) ||
      false
    );
  }

  /*
   * get the ETA value
   * @loadStop - loadStop of the shipment
   */
  public getETA(loadStop: any): string {
    // estimatedArrivalDateTime -- planned ETA
    const {
      estimatedArrivalDateTime,
      actualETADateTime,
      appointmentEarlyDateTime,
      plannedEtaDateTime,
    } = loadStop;
    let etaTime = "";
    if (actualETADateTime) {
      etaTime = actualETADateTime;
    } else if (plannedEtaDateTime) {
      etaTime = plannedEtaDateTime;
    } else if (appointmentEarlyDateTime) {
      etaTime = appointmentEarlyDateTime;
    } else if (estimatedArrivalDateTime) {
      etaTime = estimatedArrivalDateTime;
    }
    if (etaTime) {
      etaTime = this.getTimeForIOS(etaTime);
    }
    return etaTime || "";
  }

  /*
   * get the arrival value
   * @loadStop - loadStop of the shipment
   */
  public getArrival(loadStop: any, isNextStop: boolean): string {
    let { actualArrivalDateTime } = loadStop;

    if (actualArrivalDateTime) {
      actualArrivalDateTime = this.getTimeForIOS(actualArrivalDateTime);
    }
    return isNextStop ? "" : actualArrivalDateTime;
  }

  /*
   * set the initital value for arrival
   * @loadStop - loadStop of the shipment
   */
  public getArrivalOnClick(
    loadStop: any,
    isNextStop: boolean,
    arrival_input: string
  ): string {
    const { appointmentEarlyDateTime, actualArrivalDateTime } = loadStop;
    if (isNextStop) {
      return "";
    }
    if (arrival_input) {
      return arrival_input;
    }
    let arrivalTime = "";
    if (actualArrivalDateTime) {
      arrivalTime = actualArrivalDateTime;
    } else if (appointmentEarlyDateTime) {
      arrivalTime = appointmentEarlyDateTime; // .substring(0, 16);
    }

    return arrivalTime;
  }

  /*
   * get the arrival lable name
   * @loadStop - loadStop of the shipment
   */
  public getArrivalLabelName(loadStop: any, isNextStop: boolean): string {
    const { actualArrivalDateTime } = loadStop;

    return actualArrivalDateTime && !isNextStop ? "Arrived" : "Arrival";
  }

  /*
   * get the departure value
   * @loadStop - loadStop of the shipment
   */
  public getDeparture(loadStop: any, isNextStop: boolean): string {
    let { actualDepartureDateTime } = loadStop;
    if (actualDepartureDateTime) {
      actualDepartureDateTime = this.getTimeForIOS(actualDepartureDateTime);
    }
    return isNextStop ? "" : actualDepartureDateTime;
  }

  /*
   * set the departure value on click
   * @loadStop - loadStop of the shipment
   */
  public getDepartureOnClick(
    loadStop: any,
    isNextStop: boolean,
    departure_input: string
  ): string {
    const { appointmentLateDateTime, actualDepartureDateTime } = loadStop;
    if (isNextStop) {
      return "";
    }
    if (departure_input) {
      return departure_input;
    }
    let deaprtureTime = "";
    if (actualDepartureDateTime) {
      deaprtureTime = actualDepartureDateTime;
    } else if (appointmentLateDateTime) {
      deaprtureTime = appointmentLateDateTime; // .substring(0, 16);
    }

    return deaprtureTime;
  }

  /*
   * get the departure lable name
   * @loadStop - loadStop of the shipment
   */
  public getDepartureLabelName(loadStop: any): string {
    const { actualDepartureDateTime } = loadStop;

    return actualDepartureDateTime ? "Departed" : "Departure";
  }

  public getFormattedDate(date: string): string {
    let dateString = "";
    if (date) {
      let hr = Number(date.substring(11, 13));
      const min = Number(date.substring(14, 16));
      hr = hr % 12 || 12;
      dateString = `${date.substring(0, 11)}${`0${hr}`.slice(
        -2
      )}:${`0${min}`.slice(-2)}`;
    }
    return dateString;
  }

  /*
   * check whether ETA is readonly or not
   * @loadStop - loadStop of the shipment
   */
  public isETAReadonly(loadStop: any): boolean {
    const { actualArrivalDateTime, actualDepartureDateTime } = loadStop;
    let readonly = false;
    if (actualArrivalDateTime || actualDepartureDateTime) {
      readonly = true;
    }

    return readonly;
  }

  /*
   * check whether Arrival is readonly or not
   * @loadStop - loadStop of the shipment
   */
  public isArrivalReadonly(loadStop: any): boolean {
    const { actualDepartureDateTime, actualArrivalDateTime } = loadStop;
    let readonly = false;
    if (actualDepartureDateTime || actualArrivalDateTime) {
      readonly = true;
    }

    return readonly;
  }

  /*
   * check whether Departure is readonly or not
   * @loadStop - loadStop of the shipment
   */
  public isDepartureReadonly(loadStop: any, arrivalVal: string): boolean {
    const { actualDepartureDateTime, actualArrivalDateTime } = loadStop;
    let readonly = false;
    if (!!!actualArrivalDateTime) {
      readonly = true;
    }
    if (arrivalVal) {
      readonly = false;
    }
    if (actualDepartureDateTime) {
      readonly = true;
    }

    return readonly;
  }

  /*
   * get the trailer in value
   * @loadStop - loadStop of the shipment
   * @bobtail - bobtail status
   * activity details
   */
  public getTrailerIn(
    loadStop: any,
    isNextStop: boolean,
    isOTypeTrailer: boolean,
    shipment: any,
    updateStopInfo?: any
  ): {
    trailerIn: string;
    activity: StopActivityProps | undefined;
  } {
    let trailerIn = "";
    let activity: StopActivityProps | undefined;
    const isOtmType = this.isOTMTypeShipment(shipment);
    if (isNextStop && isOTypeTrailer) {
      trailerIn = "";
    } else if (
      loadStop &&
      loadStop.stopActivity &&
      loadStop.stopActivity.length > 0
    ) {
      activity =
        loadStop.stopActivity.find((e: StopActivityProps) => {
          return e.equipmentFlow === EquipmentFlow.TRAILER_IN;
        }) || {};

      if (activity) {
        trailerIn = `${activity.equipmentId || ""}`;
      }
      const updatedStopSno = updateStopInfo && updateStopInfo.sequenceNo;
      const prevStopSno = loadStop && loadStop.sequenceNo - 1;
      if (updatedStopSno === prevStopSno && isOtmType) {
        const loads: any[] = (shipment && shipment.loadStop) || [];
        const load = loads.filter(
          (item: any) => item.sequenceNo === loadStop.sequenceNo - 1
        );
        if (load.length > 0) {
          const _activity =
            (load[0].stopActivity &&
              load[0].stopActivity.find((e: StopActivityProps) => {
                return e.equipmentFlow === EquipmentFlow.TRAILER_OUT;
              })) ||
            {};
          trailerIn = _activity.equipmentId;
        }
      }
    }
    trailerIn = trailerIn && trailerIn.toUpperCase();
    if (isOtmType) {
      if (trailerIn && !TRAILER_PARTTEN.test(trailerIn)) {
        trailerIn = "";
      }
    } else {
      if (trailerIn && trailerIn === "UNKNOWN") {
        trailerIn = "";
      }
    }
    return { trailerIn, activity };
  }

  /*
   * get the trailer out
   * @loadStop - loadStop of the shipment
   * @bobtail - bobtail status
   * activity details
   */
  public getTrailerOut(
    loadStop: any,
    isNextStop: boolean,
    shipment: any
  ): {
    trailerOut: string;
    activity: StopActivityProps | undefined;
  } {
    let trailerOut = "";
    let activity: StopActivityProps | undefined;
    if (loadStop && loadStop.stopActivity && loadStop.stopActivity.length > 0) {
      activity =
        loadStop.stopActivity.find((e: StopActivityProps) => {
          return e.equipmentFlow === EquipmentFlow.TRAILER_OUT;
        }) || {};

      if (activity) {
        trailerOut = `${activity.equipmentId || ""}`;
      }
    }
    const isOtmType = this.isOTMTypeShipment(shipment);
    trailerOut = trailerOut && trailerOut.toUpperCase();
    if (isOtmType) {
      if (trailerOut && !TRAILER_PARTTEN.test(trailerOut)) {
        trailerOut = "";
      }
    } else {
      if (trailerOut && trailerOut === "UNKNOWN") {
        trailerOut = "";
      }
    }

    return { trailerOut, activity };
  }

  /*
   * validate whether IN activity avilable or not
   * @activity - stop activities
   */
  public isInActivityAvailable(activity: StopActivityProps[]): boolean {
    return (
      (activity &&
        activity.filter(
          (a: StopActivityProps) => a.equipmentFlow === EquipmentFlow.TRAILER_IN
        ).length > 0) ||
      false
    );
  }

  /*
   * validate whether OUT activity avilable or not
   * @activity - stop activities
   */
  public isOutActivityAvailable(activity: StopActivityProps[]): boolean {
    return (
      (activity &&
        activity.filter(
          (a: StopActivityProps) =>
            a.equipmentFlow === EquipmentFlow.TRAILER_OUT
        ).length > 0) ||
      false
    );
  }

  /*
   * Check whether stop activities are missing or not
   * @activity - stop activities
   */
  public areStopActivitiesMissing(load: any): boolean {
    const activity: StopActivityProps[] = (load && load.stopActivity) || [];
    const newActivity: StopActivityProps[] = [];
    activity.forEach((item: StopActivityProps) => {
      if ((item.tmsActivity || "").toUpperCase().trim() != "bobtail") {
        newActivity.push(item);
      }
    });
    return newActivity.length === 0 || false;
  }

  /*
   * check whether shipment is OTM or not
   * @loadStop - loadStop of the shipment
   */
  public isOTMTypeShipment(shipment: any): boolean {
    let isOTMType = false;
    if (shipment && shipment.sourceTMS === OTMASSET) {
      isOTMType = true;
    }

    return isOTMType;
  }

  public isOTypeStop(load: any): boolean {
    let oType = false;
    if (load && load.stopType === "O") {
      oType = true;
    }

    return oType;
  }

  public isShipmentTripCompleted(shipment: any): boolean {
    let status = false;
    if (shipment && shipment.executionStatus === ExecutionStatus.COMPLETED) {
      status = true;
    }

    return status;
  }

  public getCurrentLocalDateString(timeZone: string): string {
    const date = new Date();
    let dateString = "";
    if (timeZone) {
      dateString = date.toLocaleString("en-US", {
        timeZone: timeZone || "utc",
      });
    } else {
      dateString = date.toISOString();
    }

    return dateString;
  }

  public showTrailerInError(
    value: string,
    shipment: any,
    departure: string,
    isNextStop: boolean,
    loadStop: any
  ): boolean {
    if (
      this.isOTMTypeShipment(shipment) &&
      !value &&
      !isNextStop &&
      (!(loadStop && loadStop.actualDepartureDateTime) ? departure : false)
    ) {
      return true;
    }

    return false;
  }

  public showTrailerOutError(
    value: string,
    shipment: any,
    departure: string,
    isNextStop: boolean,
    loadStop: any
  ): boolean {
    if (
      this.isOTMTypeShipment(shipment) &&
      !value &&
      !isNextStop &&
      (!(loadStop && loadStop.actualDepartureDateTime) ? departure : false)
    ) {
      return true;
    }

    return false;
  }

  public getTimeZone(loadStop: any) {
    const { timeZone } = loadStop;
    return timeZone;
  }

  public getCurrentTimeInISO(): string {
    const tzoffset = new Date().getTimezoneOffset() * 60000; // offset in milliseconds
    return new Date(Date.now() - tzoffset).toISOString().slice(0, -1);
  }
}
