import React, { useState, useEffect } from 'react';
import { IonButton, IonModal, IonGrid } from '@ionic/react';
import './LogoutPopup.scss';
import { LOGOUTPOPUP } from '../../../utils/constants';

export const LogoutPopUp: React.FC<any> = (props) => {
  const [showModal, setShowModal] = useState(props.showLogoutModal);
  useEffect(() => {
    setShowModal(props.showLogoutModal);
  }, [props.showLogoutModal]);
  return (
    <IonModal isOpen={showModal} cssClass="dropdown-modal">
      <div className="logout_modalPopUp">
        <IonGrid class="popup-header"> {LOGOUTPOPUP.Logout_TEXT} </IonGrid>
        <IonGrid class="popup-body">
          <p className="modalPopUp_text">
            {props.isRegistrationPage
              ? LOGOUTPOPUP.REGISTRATION_ALERT_MESSAGE
              : LOGOUTPOPUP.ALERT_MESSAGE}
          </p>
          <div className="button-div">
            <IonButton
              className="logoutModalButtons"
              onClick={props.closeShowModal}
              data-test="logout-close"
            >
              {LOGOUTPOPUP.Cancel_TEXT}
            </IonButton>
            <IonButton
              className="logoutModalButtons"
              data-test="logout-click"
              onClick={props.logOut}
            >
              {LOGOUTPOPUP.Logout_TEXT}
            </IonButton>
          </div>
        </IonGrid>
      </div>
    </IonModal>
  );
};
