
import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { isPlatform } from '@ionic/react';
import { getLoading, getUserDetails, getNetworkStatus } from '../../../selectors/selector';
import { ManageUserProfileFormPage, ManageUserProfilePage } from '../../../pages/Manage/ManageUser';
import { showNetworkAlert } from '../../../actions/App/action';
import ManageProfileDesktopPage from '../../../pages/Manage/ManageUser/ManageProfileDesktop.page';
import { editUserDetails, removeUserDetails } from '../../../actions/manage/action';
import { handleDelink, handleUpdateSubmit } from '../ManageUserContainer/ManageUserUtil';
import { useAuthService } from 'app/services/Auth';

export const ProfileContainer: React.FC<any> = (props: any) => {
  const roles =
    (props &&
      props.userProfile &&
      props.userProfile.userApplicationDetails &&
      props.userProfile.userApplicationDetails
        .reduce((current: any, item: any) => {
          return [...current, ...item.roles];
        }, [])
        .map((role: any) => role.roleName)) ||
    [];
  const userProfile = { ...props.userProfile, roles };

  const selectedUser = {
    ...userProfile,
    defaultMobileNumber: !userProfile.defaultMobileNumber
      ? userProfile.mobileNumber
      : userProfile.defaultMobileNumber
  };

  const authService = useAuthService();

  return isPlatform('desktop') ? (
    <ManageUserProfileFormPage
      {...props}
      isSelf
      isSelfDesktop
      selectedUser={selectedUser}
      userDetails={props.userProfile}
      userProfile={userProfile}
      isEditable={false}
      module="manage"
      handleSubmit={handleUpdateSubmit(props, false, true)}
      handleDelink={handleDelink({...props, authService}, true)}
    />
  ) : (
    <ManageUserProfilePage
      {...props}
      isSelf
      selectedUser={selectedUser}
      userDetails={props.userProfile}
      userProfile={userProfile}
      isEditable={false}
      module="manage"
      handleSubmit={handleUpdateSubmit(props)}
      handleDelink={handleDelink(props, true)}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  userProfile: getUserDetails(),
  isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  updateUserAction: (payload: any) => dispatch(editUserDetails(payload)),
  delinkUserAction: (payload: any) => dispatch(removeUserDetails(payload))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps)(ProfileContainer);

export default withConnect;
