import { IonContent, IonPage, IonCard, IonGrid, IonRow, IonCol } from '@ionic/react';
import React, { Component } from 'react';
import AppHeader from '../../components/app/Bars/Bar-header';
import './ExecutePage.page.scss';
import { ToastMessage } from '../../utils/ToastMessage';
import PullToRefresh from '../../components/PullToRefresh/PullToRefresh';
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  STRING_STOP_TRACKING_TRIP,
  EVENT,
  SCREEN,
  TAB,
  OFFLINE_MODE_STATE
} from '../../utils/constants';
import { GPSWrapper } from '../../utils/common';
import { logEvent } from '../../utils/Analytics';

class ExecutePage extends Component<any, any> {
  constructor(props: any) {
    super(props);
    const shipmentID = GPSWrapper.details.getGPStrackingShipmentID(); // GPS tracking stop button implementation
    this.state = {
      isShowFinanceAlert: false,
      isShowStopButton: !!shipmentID, // GPS tracking stop button implementation
      shipmentID: shipmentID || null // GPS tracking stop button implementation
    };
  }

  componentDidMount() {
    // Analytics implementation
    logEvent(EVENT.SV_VIEW_MYLOADS, {
      screen: SCREEN.MYLOADS,
      carrierId: (this.props && this.props.userDetails && this.props.userDetails.carrierId) || ''
    });
  }

  // GPS tracking stop button implementation
  componentDidUpdate(prevProps: any, prevState: any) {
    const shipmentID = GPSWrapper.details.getGPStrackingShipmentID();
    if (prevState.shipmentID !== shipmentID) {
      if (shipmentID) {
        this.setState({ isShowStopButton: true, shipmentID });
      } else {
        this.setState({ isShowStopButton: false, shipmentID: null });
      }
    }
  }

  // My Loads Tile Click Handler
  onTileClick = (card: any, index: number, event: any) => {
    event.preventDefault();
    if (
      !this.props.isInPullToRefresh &&
      (this.props.myLoadCount[index] > 0 || this.props.isNetworkAvailable || OFFLINE_MODE_STATE)
    ) {
      card.myLoadRouting !== '' ? this.props.history.push(card.myLoadRouting) : ToastMessage.show();
    } else if (!this.props.isNetworkAvailable) {
      // call action to show Popup along with message.
      this.props.showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK
      });
    }
  };

  // Manage Financial Click Handler
  // onManageFinacialClick = (card: any) => {
  //     this.setState({ isShowFinanceAlert: true });
  // }

  // navigateToURL = async () => {
  //     const { Browser } = Plugins;
  //     if (!this.props.isInPullToRefresh)
  //         await Browser.open({ url: MANAGE_FINANCE_URL });
  //     this.setState({ isShowFinanceAlert: false });
  // }

  // onDismiss = async () => {

  //     this.setState({ isShowFinanceAlert: false });
  // }

  stopTripClicked = () => {
    this.setState({ isShowStopButton: false, shipmentID: null });
    GPSWrapper.details.stopGPS();
  };

  render() {
    const { myLoadCard, myLoadCount } = this.props;
    return (
      <IonPage className="desktop-page myLoad-landing-page">
        <AppHeader title="My Loads" />
        <IonContent className="myload-content">
          <IonContent class="myload-page-content">
            <PullToRefresh
              onPullToRefresh={this.props.getUserLoads}
              status={this.props.isInPullToRefresh}
            />

            <IonGrid class="myload-mobile-grid">
              {myLoadCard.map((card: any, index: number) => (
                <IonCard
                  class={`myload-tab-card ${
                    index % 2 === 0 && index !== 4 ? 'myload-tab-right-card' : ''
                  }`}
                  key={index}
                  onClick={(event: any) => {
                    this.onTileClick(card, index, event);
                  }}>
                  <IonGrid className={`item-dev ${card.className}`}>
                    <div className="icon-wrapper">
                      <img className="thumbnail-img" alt="assign" src={card.cradImg} />
                      <div className="itemlabel">{card.title}</div>
                    </div>
                    <div className={myLoadCount[card.count] ? 'circle-div' : ''}>
                      {myLoadCount[card.count] && myLoadCount[card.count] > 99
                        ? 99
                        : myLoadCount[card.count] || ''}
                    </div>
                  </IonGrid>
                </IonCard>
              ))}
            </IonGrid>
            <div className="myLoads_div_desktop">
              <IonRow className="myLoads_row_desktop">
                {myLoadCard.map((card: any, index: number) => (
                  <IonCol size="2.4" className={`thumbnail-item-col ${card.class}`} key={index}>
                    <IonCard
                      class="myload-tab-card"
                      onClick={(event: any) => {
                        this.onTileClick(card, index, event);
                      }}>
                      <IonGrid className={`item-dev ${card.className}`}>
                        <div className="icon-wrapper">
                          <div className={this.props.myLoadCount[card.count] ? 'circle-div' : ''}>
                            {this.props.myLoadCount[card.count] &&
                            this.props.myLoadCount[card.count] > 99
                              ? 99
                              : this.props.myLoadCount[card.count] || ''}
                          </div>
                          <img className="thumbnail-img" alt="assign" src={card.cradImg} />
                          <div className="itemlabel">{card.title}</div>
                        </div>
                      </IonGrid>
                    </IonCard>
                  </IonCol>
                ))}
              </IonRow>
              {
                // Commented  as per defect : DE3741
                /* {isEntitled("MyLoad", "manageFinancial") && <div className="manage-financial-div">
                            <IonGrid className="manage-financial">
                                <div className="manage-itemlabel" onClick={this.onManageFinacialClick}>  <img className="manage-img" alt="assign" src="assets/icon/manage2.svg"></img><p>Manage Financial</p></div>
                            </IonGrid>
                            {(
                                <Alert
                                    title={""}
                                    isOpen={this.state.isShowFinanceAlert}
                                    message={"You are being redirected to an external site"}
                                    close={this.navigateToURL}
                                    dismiss={this.onDismiss}
                                />
                            )}
                        </div>} */
              }
            </div>
          </IonContent>
          {/* GPS tracking stop button implementation */}
          {/* {this.state.isShowStopButton &&
                        <IonGrid class="loadPageFooter">
                            <IonButton expand="full" onClick={this.stopTripClicked}>{STRING_STOP_TRACKING_TRIP + this.state.shipmentID}</IonButton>
                        </IonGrid>
                    } */}
        </IonContent>
      </IonPage>
    );
  }
}

export default ExecutePage;
