import React, { useState } from 'react';
import { IonPage, IonContent, IonList, IonInput, IonItem, IonLabel, IonFooter, IonButton, IonIcon, IonText, IonImg, IonGrid, IonCol, IonRow } from '@ionic/react';
import { IRole, IAddress } from '../../../schemas/manage/manage.Schema';
import { EMAIL_ERROR } from '../../../utils/constants';
import { CustomModal } from '../../../components/CustomModal';

const BACK_PATH = '/app/manage/ManageUser';

const ManageUserProfileFormDesktopPage: React.FC<any> = (props) => {
	return (
		<IonPage className="manage-user-card-content ion-page desktop-page my-load-desktop-page">
			<IonContent class="mange-user-desktop">
				<IonGrid className="addDriver-desktop-header">{props.title}</IonGrid>
				<IonList className="manage-userlist-form">
					<IonRow>
						<IonCol>
							<IonItem className="role-info"  data-test="select-role" onClick={() => props.setShowRoleOptions(!props.showRoleOptions)}>
								<IonLabel position="floating" mode="ios">Role</IonLabel>
								<IonInput
									value={props.isUserRoleAdmin ? `Admin${props.roleTitle.length > 0 ? `, ${props.roleTitle}`: ''}` : (props.roleTitle || 'Guest')}
									readonly
									type="text"
									data-test="role-title"
									class=""
								></IonInput>
								<IonIcon src="/assets/icon/grid-down-arrow.svg"></IonIcon>
							</IonItem>
							<IonText class="role-desc">{props.roleDesc}</IonText>
							{ props.roleSelection && props.showRoleOptions ? (
								<IonList data-test="roles-list" class="roles-list-grid">
									{props.roleSelection.map((role: IRole, index: number) => (
									<IonItem
										key={`role_${index}`}
										data-test="role-list-item"
										onClick={() => props.handleRoleSelection(role)}
									>
										<i className={`role-check-icon ${role.selected?'checked': ''}`}></i>
										<span className="role-list-label">{role.label}</span>
									</IonItem>
									))}
								</IonList>
							) : null}
						</IonCol>
						<IonCol>
							<IonItem className={props.emailAddressError? 'error': ''}>
								<IonLabel position="floating" mode="ios">Email Address</IonLabel>
								<IonInput
									value={props.formFields.email}
									placeholder='Type Your Email Address'
									type="email"
									readonly={props.isUserNotExist}
									name="emailAddress"
									onIonChange={props.handleChange}
									pattern='/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/'
								></IonInput>
							</IonItem>
							{ props.emailAddressError? <IonText data-test="email-field-error" class="field-error">{EMAIL_ERROR}</IonText>: null }
						</IonCol>
					</IonRow>
				</IonList>
				{
					props.isUserNotExist || props.isEdit ? (
						<>
							<IonList className="manage-userlist-form">
								<IonRow>
									<IonCol>
										<IonItem>
											<IonLabel position="floating" mode="ios">First Name</IonLabel>
											<IonInput
												type="text"
												placeholder='Enter Your First Name'
												onIonChange={props.handleChange}
												value={props.formFields['firstName']}
												name="firstName"
											></IonInput>
										</IonItem>
										{ props.formErrors && props.formErrors['firstName']? <IonText class="field-error" data-test="firstName-field-error" >{props.formErrors['firstName']}</IonText>: null }
										<IonItem>
											<IonLabel position="floating" mode="ios">Mobile Number</IonLabel>
											<IonInput
												value={props.formFields['mobileNumber']}
												name={'mobileNumber'}
												data-test="mobile-field"
												placeholder={'Type your mobile numbers'}
												pattern="[0-9a-zA-Z]"
												type="text"
												onFocus={() => props.setFormFields({...props.formFields, mobileNumber: props.formFields.mobileNumber && props.removeSpecial(props.formFields.mobileNumber)})}
												onIonBlur={props.handleChange}
												maxlength={10}
											></IonInput>
										</IonItem>
										{ props.formErrors && props.formErrors['mobileNumber']? <IonText class="field-error"  data-test="mobile-field-error">{props.formErrors['mobileNumber']}</IonText>: null }
										{
											props.isDriverSelected && (
												<>
													<IonItem>
														<IonLabel position="floating" mode="ios">Tractor No (Optional)</IonLabel>
														<IonInput
															type="text"
															placeholder='Type Your Tractor Number'
															name={'tractorNumber'}
															data-test="tractor-field"
															onIonChange={props.handleChange}
															value={props.formFields['tractorNumber']}
														></IonInput>
													</IonItem>
													{ props.formErrors && props.formErrors['tractorNumber']? <IonText class="field-error"  data-test="tractorNumber-field-error">{props.formErrors['tractorNumber']}</IonText>: null }
												</>
											)
										}
										
									</IonCol>
									<IonCol>
										<IonItem>
											<IonLabel position="floating" mode="ios">Last Name</IonLabel>
											<IonInput
												type="text"
												name={'lastName'}
												placeholder='Enter Your Last Name'
												onIonChange={props.handleChange}
												value={props.formFields['lastName']}
											></IonInput>
										</IonItem>
										{ props.formErrors && props.formErrors['lastName']? <IonText class="field-error"  data-test="lastName-field-error">{props.formErrors['lastName']}</IonText>: null }
										{
											props.isDriverSelected && (
												<>
													<IonItem>
														<IonLabel position="floating" mode="ios">Park Location Address (Optional)</IonLabel>
														<IonInput
															type="text"
															name={'addressDTOs'}
															placeholder='Type Your Park Location Address'
															onIonChange={props.handleChange}
															data-test="address-field"
															onIonFocus={() => {
																props.setAddressEdit(true);
															}}
															value={props.isAddressEdit ? props.address: props.getAddressValue(props.formFields['addressDTOs'])}
														>
															<IonImg
																slot="end"
																alt="logo"
																src="/assets/icon/search_icon.svg"
																className="search-input-icon"
															/>
														</IonInput>
													</IonItem>
													{props.showAddressList ? (
														<IonGrid class="suggestions" data-test="address-suggestions">
															{
																props.addressList.map((address:IAddress, index: number) => (
																	<IonRow key={`address${index}`} className="suggestions_item"  data-test="suggestions_item" onClick={() => props.handleAddressSelection(address)}>
																		<IonCol class="suggestions_list">
																			{props.getAddressValue(address)}
																		</IonCol>
																	</IonRow>
																))
															}
														</IonGrid>
													): null }
												</>
											)
										}
										<IonItem>
											<IonLabel position="floating" mode="ios">Carrier ID:</IonLabel>
											<IonInput
												type="text"
												name={'carrierId'}
												value={props.formFields['carrierId']}
												readonly
											></IonInput>
											{
												props.isEdit ? (
													<img
														slot="end"
														alt="delink"
														src="/assets/icon/delink.svg"
														className="delink"
														data-test="delink-btn"
														onClick={props.handleDelinkClick}
													/>
												): null
											}
											
										</IonItem>
									</IonCol>
								</IonRow>
							</IonList>
							<IonRow className="manage-userButtons-row">
								<IonCol className="starRating-col">
									<IonButton data-kind="primary" type="submit"  data-test="back-btn"  class="manage-user_btn"  onClick={() => props.handleBackButton('/app/manage')}>Back</IonButton>
								</IonCol>
								<IonCol>
									<IonButton data-kind="primary" type="submit" data-test="submit-btn" class="manage-user_btn btn_save" onClick={() => props.handleSubmit()}>Save</IonButton>
								</IonCol>
							</IonRow>
						</>
					): (
						<IonRow className="manage-userButtons-row">
							<IonCol className="starRating-col">
								<IonButton data-kind="primary" type="submit" data-test="validate-btn" class="manage-user_btn"  onClick={() => props.validateNow()}>Validate Now</IonButton>
							</IonCol>
						</IonRow>
					)
				}
				
			</IonContent>
			{
				props.alertProps	? <CustomModal {...props.alertProps} /> : null
			}
		</IonPage>
	);
}
export default ManageUserProfileFormDesktopPage;