import React, { useState, useEffect } from "react";
import {
  IonContent,
  IonCard,
  IonGrid,
  IonCardContent,
  IonRow,
  IonCol,
  IonImg,
  IonBadge,
  IonButton
} from "@ionic/react";
import "./Recent.scss";
import RecentSearchDesktop from "./RecentSearchDesktop";
import { Isearch } from "../../../schemas/Search/Search.schema";
import PullToRefresh from "../../../components/PullToRefresh/PullToRefresh";
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  STAR,
  ANYWHERE,
  EVENT,
  SCREEN,
  TAB,
  OFFLINE_MODE_STATE
} from "../../../utils/constants";
import { logEvent } from "../../../utils/Analytics";

interface recentProps {
  recentData: any;
  updateFavorite: (fav: boolean, data: any) => void;
  favoriteFlag: boolean;
  isNetworkAvailable: boolean;
  showAlert: (data: any) => void;
}
function getMonth(month: number): string {
  switch (month) {
    case 0:
      return "Jan";
    case 1:
      return "Feb";
    case 2:
      return "Mar";
    case 3:
      return "Apr";
    case 4:
      return "May";
    case 5:
      return "Jun";
    case 6:
      return "Jul";
    case 7:
      return "Aug";
    case 8:
      return "Sep";
    case 9:
      return "Oct";
    case 10:
      return "Nov";
    case 11:
      return "Dec";
    default:
      return "";
  }
}
const RecentCard: React.FC<recentProps> = ({
  recentData,
  updateFavorite,
  favoriteFlag,
  isNetworkAvailable,
  showAlert
}) => {
  const [fav, setFav] = useState<boolean>(false);
  useEffect(() => {
    setFav(recentData.favouritesearchindicator);
  }, [recentData]);
  return (
    <IonCardContent className="card-content recent-card">
      <IonGrid className="recent_grid">
        <IonRow>
          <IonCol className="card-price-col" size="4">
            <p className="trailerType">{recentData.trailertype}</p>
          </IonCol>
          <IonCol className="card-city-col">
            <IonRow class="stop-details-row">
              <IonCol class="stop-icon-col">
                <IonImg
                  className="ellipse_icon"
                  src="assets/icon/origin-pointer.svg"
                />
                <div className="stops_border"></div>
              </IonCol>
              <IonCol class="stop-details-col">
                {recentData.originCity === "Anywhere" ? (
                  <p className="origin_city">{recentData.originCity}</p>
                ) : (
                  <p className="origin_city">
                    {recentData.originCity}, {recentData.originState}
                  </p>
                )}
                <IonBadge className="originRadius">
                  Radius : {recentData.originradius}
                </IonBadge>
                {favoriteFlag ? (
                  ""
                ) : (
                  <IonBadge className="pickupDate">
                    {recentData.pickupdate &&
                      `${getMonth(
                        new Date(recentData.pickupdate).getMonth()
                      )} ${(
                        "0" + new Date(recentData.pickupdate).getDate()
                      ).slice(-2)}`}
                  </IonBadge>
                )}
              </IonCol>
            </IonRow>
            <IonRow class="stop-details-row">
              <IonCol class="stop-icon-col">
                <IonImg
                  className="path_icon"
                  src="assets/icon/dst-pointer.svg"
                />
              </IonCol>
              <IonCol class="stop-details-col">
                {recentData.destinationCity === null ? (
                  <p className="origin_city">Anywhere</p>
                ) : (
                  <p className="origin_city">
                    {recentData.destinationCity}, {recentData.destinationState}
                  </p>
                )}
                <IonBadge className="originRadius">
                  Radius : {recentData.destinationradius}
                </IonBadge>
                {favoriteFlag || recentData.destinationCity === null ? (
                  ""
                ) : (
                  <IonBadge className="pickupDate">
                    {recentData.dropdate &&
                      `${getMonth(new Date(recentData.dropdate).getMonth())} ${(
                        "0" + new Date(recentData.dropdate).getDate()
                      ).slice(-2)}`}
                  </IonBadge>
                )}
              </IonCol>
            </IonRow>
          </IonCol>
          <IonCol size="1" className="star_icon_col">
            {
              <IonImg
                data-test="Favorite-IonImg"
                onClick={e => {
                  e.stopPropagation();
                  fav
                    ? updateFavorite(false, recentData)
                    : updateFavorite(true, recentData);
                  if (!isNetworkAvailable) {
                    showAlert({
                      isShowAlert: true,
                      alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
                      alertTitle: NO_NEWORK
                    });
                    return;
                  }
                  fav ? setFav(false) : setFav(true);
                }}
                className="star_icon"
                src={fav ? STAR.STAR_TRUE : STAR.STAR_FALSE}
              />
            }
          </IonCol>
        </IonRow>
      </IonGrid>
    </IonCardContent>
  );
};

class RecentSearch extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    // Analytics implementation
    logEvent(EVENT.SV_SEARCH_FILTER, {
      screen: SCREEN.SEARCH_FILTER,
      tab: this.props.favoriteFlag ? TAB.FAVORITE : TAB.RECENT
    });
  }

  cardClick = (detail: any) => {
    /* NETWORK CHECK CONDITION*/
    if (this.props.isNetworkAvailable) {
      const {
        originCity,
        originState,
        originradius,
        pickupdate,
        trailertype,
        dropdate,
        destinationradius,
        favouritesearchindicator,
        durationDays,
        destinationCity,
        destinationState
      } = detail;
      let origin = "";
      if (originCity) {
        origin = originCity;
      }
      if (originState) {
        if (originState.toLowerCase() !== ANYWHERE) {
          origin += ", " + originState;
        }
      }
      let destination = "";
      if (destinationCity) {
        destination = destinationCity;
      }
      if (destinationState) {
        if (destinationState.toLowerCase() !== ANYWHERE) {
          destination += ", " + destinationState;
        }
      }

      let newPickUpDate = null;
      let newDropDate = null;
      let range = Math.abs(durationDays);
      if (this.props.favoriteFlag) {
        newPickUpDate = new Date().toISOString();
        if (dropdate && range) {
          newDropDate = new Date(
            new Date().setDate(new Date().getDate() + range + 1)
          ).toISOString();
        }
      } else {
        newPickUpDate = pickupdate;
        newDropDate = dropdate;
      }

      const __searchParams: Isearch = {
        origin: origin || "Anywhere",
        originRadius: originradius,
        destination: destination || "Anywhere",
        pickUpdate: newPickUpDate,
        TrailerType: trailertype,
        dropdate: newDropDate,
        destinationRadius: destinationradius,
        favorite: favouritesearchindicator
      };
      this.props.onSearchResult(
        { ...detail, pickupdate: newPickUpDate, dropdate: newDropDate },
        __searchParams
      );
    } else {
      this.props.showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK
      });
    }
  };
  render() {
    return (
      <IonContent className="ion-padding recent_container" class="background">
        <PullToRefresh
          onPullToRefresh={
            this.props.favoriteFlag
              ? this.props.getFavoriteSearchList
              : this.props.getRecentSearchList
          }
          status={this.props.isInPullToRefresh}
        ></PullToRefresh>
        <IonGrid class="recent-list">
          {this.props.getLoads &&
            this.props.getLoads.map((detail: any, index: number) => {
              if (
                this.props.favoriteFlag &&
                this.props.id.includes(detail.id)
              ) {
                return "";
              } else if (
                !this.props.favoriteFlag &&
                this.props.flag &&
                !this.props.undo &&
                this.props.id.includes(detail.id)
              ) {
                detail = { ...detail, favouritesearchindicator: false };
                return (
                  <IonCard
                    data-test="IonCard-RecentPage"
                    onClick={() => this.cardClick(detail)}
                    className="ion-card-updated"
                    key={index}
                  >
                    <RecentCard
                      recentData={detail}
                      updateFavorite={this.props.updateFavorite}
                      favoriteFlag={this.props.favoriteFlag}
                      isNetworkAvailable={this.props.isNetworkAvailable}
                      showAlert={this.props.showAlert}
                    />
                  </IonCard>
                );
              } else if (
                !this.props.favoriteFlag &&
                this.props.undo &&
                this.props.id.includes(detail.id)
              ) {
                detail = { ...detail, favouritesearchindicator: true };
                return (
                  <IonCard
                    data-test="IonCard-RecentPage"
                    onClick={() => this.cardClick(detail)}
                    className="ion-card-updated"
                    key={index}
                  >
                    <RecentCard
                      recentData={detail}
                      updateFavorite={this.props.updateFavorite}
                      favoriteFlag={this.props.favoriteFlag}
                      isNetworkAvailable={this.props.isNetworkAvailable}
                      showAlert={this.props.showAlert}
                    />
                  </IonCard>
                );
              } else {
                return (
                  <IonCard
                    data-test="IonCard-RecentPage"
                    onClick={() => this.cardClick(detail)}
                    className="ion-card-updated"
                    key={index}
                  >
                    <RecentCard
                      recentData={detail}
                      updateFavorite={this.props.updateFavorite}
                      favoriteFlag={this.props.favoriteFlag}
                      isNetworkAvailable={this.props.isNetworkAvailable}
                      showAlert={this.props.showAlert}
                    />
                  </IonCard>
                );
              }
            })}
        </IonGrid>
        <p className="end_of_list">End of list</p>
        <div className="recentDesktop">
          <RecentSearchDesktop
            data-test="RecentSeachDesktop-RecentPage"
            onRedirectToTab={(detail: any) => this.cardClick(detail)}
            recentData={this.props.getLoads}
            updateFavorite={this.props.updateFavorite}
            favoriteFlag={this.props.favoriteFlag}
            ID={this.props.id}
          />
        </div>
      </IonContent>
    );
  }
}

export { getMonth, RecentSearch, RecentCard };
