import React, { Component, useState, useEffect } from 'react';
import DataTable from 'react-data-table-component';
import { IonBadge, IonIcon, IonImg, IonGrid } from '@ionic/react';
import './RecentSearchDesktop';
import '../../../../theme/variables.scss';
import '../../../../theme/responsive-styles.scss';
import { Anywhere, STAR } from '../../../utils/constants';

function getMonth(month: number): string {
        switch(month){
            case 0:
            return "Jan";
            case 1:
            return "Feb";
            case 2:
            return "Mar";
            case 3:
            return "Apr";
            case 4:
            return "May";
            case 5:
            return "Jun";
            case 6:
            return "Jul";
            case 7:
            return "Aug";
            case 8:
            return "Sep";
            case 9:
            return "Oct";
            case 10:
            return "Nov";
            case 11:
            return "Dec";
            default: 
            return "";
        }
      }

const RecentSearchDesktop = (props: any) => {
    // const history = useHistory();
    const showLoadDetails = (row: any) => {
        props.onRedirectToTab(row);
    }

    const [recentDataState, setrecentDataState] = useState();
   
    useEffect(() => {
        if(!props.favoriteFlag){
            setrecentDataState(props.recentData);
        }
    }, [props.recentData])

    const columns = [
        {
            name: 'Origin',
            selector: '',
            width:'91',
            style: {
                fontWeight: 800,
                fontSize: 14,
                colorName: '#000000',
            },
            cell: (row: any) => (row.originCity === "Anywhere") ? <div className="originCity">{row.originCity}</div> :<div className="originCity">{row.originCity +", "+row.originState}</div>
      
            
        },
        {
            name: 'Origin Date',
            selector: 'pickup_date',
            width: '120px'
        },
        {
            name: 'Origin Radius',
            selector: '',
            width: '65',
            cell: (row: any) => (row.originradius != null) ? <div >{row.originradius} mi</div> :<div></div>
        },
        {
            name: 'Destination',
            selector: '',
            width:'82',
            style: {
                fontWeight: 800,
                fontSize: 14,
                colorName: '#000000',
            },
            cell: (row: any) => (row.destinationCity === null) ? <div className="originCity">Anywhere</div> :<div className="originCity">{row.destinationCity +", "+ row.destinationState}</div>
        },
        {
            name: 'Destination Date',
            selector: '',
            width:'150px',
            cell: (row: any) => (row.dropdate === null || row.destinationCity === null) ? <div ></div> :<div>{row.dropdate && `${getMonth(new Date(row.dropdate).getMonth())} ${("0" + new Date(row.dropdate).getDate()).slice(-2) }`}</div>
           
        },
        {
            name: 'Destination Radius',
            selector: '',
            width: '79',
            cell: (row: any) => (row.destinationradius != null) ? <div >{row.destinationradius} mi</div> :<div>NA</div>
        },
        {
            name: 'Trailer Type',
            selector: 'trailertype',
            width: '84',
        },
        {
            name: '',
            selector: '',
            width: "50px",
            cell: (row: any) => (
                row.favouritesearchindicator ? <IonImg className="star_icon" onClick={(e) => {
                    e.stopPropagation(); 
                    props.updateFavorite(false, row);
                    if(!props.favoriteFlag){
                        let newState = recentDataState.map((load: any) => {
                            if(load.id === row.id){
                                return {...load, favouritesearchindicator: !row.favouritesearchindicator}
                            }
                            return load;
                        })
                        setrecentDataState(newState);
                    }
                 }} src="assets/icon/star-color.svg" /> 
                :<IonImg className="star_icon" onClick={(e) => {
                    e.stopPropagation(); 
                    props.updateFavorite(true, row);
                    if(!props.favoriteFlag){
                        let newState = recentDataState.map((load: any) => {
                            if(load.id === row.id){
                                return {...load, favouritesearchindicator: !row.favouritesearchindicator}
                            }
                            return load;
                        })
                        setrecentDataState(newState);
                    }
                }} src="assets/icon/star.svg"/>
            )
        },
        {
            name: '',
            selector: '',
            width: "50px",
            cell: (row: any) => <IonGrid data-test="IonGrid-RecentSearchDesktop" class="datatable-badge"><i></i></IonGrid>   }
    ];

    const columnsFav = [
        {
            name: 'Origin',
            selector: '',
            width:'91',
            style: {
                fontWeight: 800,
                fontSize: 14,
                colorName: '#000000',
            },
            cell: (row: any) => (row.originCity === "Anywhere") ? <div data-test="origin-anywhere" className="originCity">{row.originCity}</div> :<div className="originCity" data-test="origin-city">{row.originCity +", "+row.originState}</div>
      
            
        },
        {
            name: 'Origin Radius',
            selector: '',
            width: '65',
            cell: (row: any) => (row.originradius != null) ? <div data-test="origin-radius">{row.originradius} mi</div> :<div></div>
        },
        {
            name: 'Destination',
            selector: '',
            width:'82',
            style: {
                fontWeight: 800,
                fontSize: 14,
                colorName: '#000000',
            },
            cell: (row: any) => (row.destinationCity === null) ? <div className="originCity" data-test="destination-city">Anywhere</div> :<div className="originCity">{row.destinationCity +", "+ row.destinationState}</div>
        },
        {
            name: 'Destination Radius',
            selector: '',
            width: '79',
            cell: (row: any) => (row.destinationradius != null) ? <div >{row.destinationradius} mi</div> :<div>NA</div>
        },
        {
            name: 'Trailer Type',
            selector: 'trailertype',
            width: '84',
        },
        {
            name: '',
            selector: '',
            width: "50px",
            cell: (row: any) => (
                row.favouritesearchindicator ? <IonImg className="star_icon"  data-test="recent-star1" onClick={(e) => {
                    e.stopPropagation(); 
                    props.updateFavorite(false, row);
                    if(!props.favoriteFlag){
                        let newState = recentDataState.map((load: any) => {
                            if(load.id === row.id){
                                return {...load, favouritesearchindicator: !row.favouritesearchindicator}
                            }
                            return load;
                        })
                        setrecentDataState(newState);
                    }
                 }} src={STAR.STAR_TRUE} /> 
                :<IonImg className="star_icon" data-test="recent-star" onClick={(e) => {
                    e.stopPropagation(); 
                    props.updateFavorite(true, row);
                    if(!props.favoriteFlag){
                        let newState = recentDataState.map((load: any) => {
                            if(load.id === row.id){
                                return {...load, favouritesearchindicator: !row.favouritesearchindicator}
                            }
                            return load;
                        })
                        setrecentDataState(newState);
                    }
                }} src={STAR.STAR_FALSE}/>
            )
        },
        {
            name: '',
            selector: '',
            width: "50px",
            cell: (row: any) => <IonGrid data-test="IonGrid-RecentSearchDesktop" class="datatable-badge"><i></i></IonGrid>   }
    ];

    let data: [];
    if(!props.favoriteFlag){
        data = recentDataState && recentDataState.filter((load:any, index:number, array:any)=>{
            if(props.favoriteFlag && props.ID.includes(load.id)){
                return false;
            }
            return load;
        }).map((load:any, index:number, array:any) => {
            let recentDetails:any = {};
            const {originCity, originState, destinationCity, destinationState, destinationradius, trailertype, 
                originradius, favouritesearchindicator, pickupdate, searchdate, userid, id, carrierid, dropdate, originPostalCode,
                destinationPostalCode, originCountry, destinationCountry} = load;
            ({...recentDetails} = {originCity, destinationState, destinationCity, originradius, destinationradius, trailertype,
                favouritesearchindicator, pickupdate, dropdate, carrierid, searchdate, userid, id, originState, originPostalCode, destinationPostalCode,
                originCountry, destinationCountry})
            return recentDetails;
        });
    }else{
        data =  props.recentData && props.recentData.filter((load:any, index:number, array:any)=>{
            if(props.favoriteFlag && props.ID.includes(load.id)){
                return false;
            }
            return load;
        }).map((load:any, index:number, array:any) => {
            let recentDetails: any = {};
            const {originCity, originState, destinationCity, destinationState, destinationradius, trailertype, 
                originradius, favouritesearchindicator, pickupdate, searchdate, userid, id, carrierid, dropdate, originPostalCode,
                destinationPostalCode, originCountry, destinationCountry} = load;
            ({...recentDetails} = {originCity, destinationState, destinationCity, originradius, destinationradius, trailertype,
                 favouritesearchindicator, pickupdate, dropdate, carrierid, searchdate, userid, id, originState, originPostalCode, destinationPostalCode,
                originCountry, destinationCountry})
            return recentDetails;
        });
    }
    
    return (
        <>
          { data?
          <>
            <DataTable
                onRowClicked={(row) => showLoadDetails(row)}
                columns={props.favoriteFlag ? columnsFav :columns}
                data={data}
            />
            
        </>: null}
        </>
    );
}

export default RecentSearchDesktop;
export {getMonth};