import { MyLoadState } from "../../schemas/myload/myload.Schema";
import * as Constants from "../../utils/constants";
import produce from "immer";

export const myLoadInitialState: MyLoadState = {
  profileImg: [],
  myLoadCardData: [],
  carrierAssigned: [],
  driverAssigned: [],
  inTransit: [],
  myLoadCount: {
    carrierAssignedCount: 0,
    driverAssignedCount: 0,
    inTransitCount: 0,
    deliveredCount: 0,
  },
  confirmStart: {},
  confirmStartStatusMessage: {},
  updateStop: {},
  startTrip: {},
  buy: {
    status: "",
    message: "",
  },
  shipment: [],
  delivered: [],
  isGetShipment: false,
  buyLoadResponseStatus: "",
  warningDialogMessage: "",
};

export default (state = myLoadInitialState, action: any) =>
  produce(state, (draft) => {
    switch (action.type) {
      case Constants.GET_MYLOAD_CARD_DATA:
        draft.myLoadCardData = action.payload;
        break;

      case Constants.LOAD_CARRIER_DATA:
        draft.carrierAssigned = action.payload.data;
        draft.myLoadCount.carrierAssignedCount = action.payload.count;
        break;

      case Constants.DRIVER_ASSIGNED_DATA:
        draft.driverAssigned = action.payload.data;
        draft.myLoadCount.driverAssignedCount = action.payload.count;
        break;

      case Constants.LOAD_INTRANSIT_DATA:
        draft.inTransit = action.payload.data;
        draft.myLoadCount.inTransitCount = action.payload.count;
        break;

      case Constants.CarrierActionTypes.CONFIRM_START_INFORMATION_SUCCESS:
        draft.confirmStart = action.payload;
        break;

      case Constants.LOGOUT:
        draft.myLoadCardData = draft.carrierAssigned = draft.driverAssigned = draft.inTransit = draft.delivered = [];
        draft.myLoadCount.carrierAssignedCount = draft.myLoadCount.driverAssignedCount = draft.myLoadCount.inTransitCount = draft.myLoadCount.deliveredCount = 0;
        break;

      case Constants.CarrierActionTypes
        .CONFIRM_START_INFORMATION_STATUS_MESSAGE:
        draft.confirmStartStatusMessage = action.payload;
        break;

      case Constants.CarrierActionTypes.SHOW_WARNING_DIALOG:
        draft.warningDialogMessage =
          (action.payload && action.payload.message) || "";
        break;

      case Constants.CarrierActionTypes.CLEAR_WARNING_DIALOG:
        draft.warningDialogMessage = "";
        break;

      case Constants.CarrierActionTypes
        .CLEAR_CONFIRM_START_INFORMATION_STATUS_MESSAGE:
        draft.confirmStartStatusMessage = {};
        break;

      case Constants.CarrierActionTypes.UPDATE_STOP_INFORMATION_RESPONSE:
        draft.updateStop = action.payload.data;
        break;

      case Constants.CarrierActionTypes.START_TRIP_INFORMATION_RESPONSE:
        draft.startTrip = action.payload;
        break;
      case Constants.BUYLOAD_DATA_SUCCESS:
        const payload = action.payload && (action.payload as any);
        draft.buy = payload.data.buy;
        break;
      case Constants.LOAD_DELIVERED_DATA:
        draft.delivered = action.payload.data;
        draft.myLoadCount.deliveredCount = action.payload.count;
        break;
      case Constants.BUYLOAD_SHIPMENT_SUCCESS:
        const getShipmentpayload = action.payload && (action.payload as any);
        draft.shipment = (getShipmentpayload.data && getShipmentpayload.data.getShipment) || {};
        break;
      case Constants.BUYLOAD_REMOVE_STATUS:
        draft.buy = { status: "", message: "" };
        break;
      case Constants.isGetshipemntError:
        draft.isGetShipment = action.payload;
        break;
      case Constants.CLEARBUYLOAD_RESPONSE:
        draft.buyLoadResponseStatus = action.payload;
        break;
      case Constants.CarrierActionTypes.CLEAR_UPDATE_STOP_INFORMATION_RESPONSE:
        draft.updateStop = {};
        break;
    }
  });
