import { IonContent, IonPage } from '@ionic/react';
import React, { Component } from 'react';
import { Roles, EVENT, SCREEN } from '../../../utils/constants';
import { CarrierAssignedModal } from '../../../containers/ExecuteContainer/CarrierAssignedModal/CarrierAssignedModal';

import AppHeader from '../../../components/app/Bars/Bar-header';
import './AssignDriverList.scss';
import { isEntitled, IONICWrapper, R2Configuration } from '../../../utils/common';
import { DriverCard } from './DriverCard';
import { logEvent } from '../../../utils/Analytics';

import { ToastMessage } from '../../../utils/ToastMessage';

class DriverAssignList extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      showModal: false,
      selectedLoad: [],
      openAssignDriverDialog: false,
      driverDetails: {}
    };
  }

  componentDidMount() {
    this.setState({ selectedLoad: this.props.location.state.response });
    // Analytics implementation
    logEvent(EVENT.UE_ASSIGN_DRIVER_SUCCESS, {
      screen: SCREEN.MYLOADS_DRIVER_ASSIGNMENT,
      carrierId: this.state.driverDetails.carrierId
    });
  }

  render() {
    const loadId =
      this.props && this.props.match && this.props.match.params && this.props.match.params.id;
    return (
      <>
        <CarrierAssignedModal
          isOpen={this.state.openAssignDriverDialog}
          onDismiss={() => {
            this.setState({
              openAssignDriverDialog: false
            });
          }}
          loadDetails={this.props.loadDetails}
          driverDetails={this.state.driverDetails}
        />
        <IonPage className="desktop-page driverList-page">
          <AppHeader
            title={this.props.loadDetails ? loadId : ''}
            backUrl={`/app/MyLoad/CarrierAssigned/${loadId}`}
            isAddDriver={
              isEntitled('myLoad', 'addUser') &&
              IONICWrapper.details.auth.user.roles.includes(Roles.carrierDispatcher) &&
              IONICWrapper.details.auth.user.roles.includes(Roles.carrierAdmin) &&
              (() => {
                R2Configuration.details.getR2FeatureEnable() // (KEYS.RELEASE === ENV.UAT || KEYS.RELEASE === ENV.PROD)
                  ? ToastMessage.show()
                  : this.props.history.push(`/app/MyLoad/driverAssign/addDriver/${loadId}`, {
                      loadModule: `MyLoad/driverAssign/${loadId}`
                    });
              })
            }
            History={this.props.history}
          />
          <IonContent className="driverContent">
            <DriverCard
              isPopup={this.props.popupHeader}
              DriverList={this.props.driverList}
              loadDetails={this.props.loadDetails}
              module={this.props.module}
              showAssignDriverDialog={(isShow: boolean, driverDetails: any) => {
                this.setState({
                  openAssignDriverDialog: isShow,
                  driverDetails
                });
              }}
            />
          </IonContent>
        </IonPage>
      </>
    );
  }
}
export default DriverAssignList;
