import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import { IonApp, IonRouterOutlet, isPlatform } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { observer } from 'mobx-react-lite';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { AuthenticationService, AuthSingleton } from '../../services/authentication.service';
import { VaultService } from '../../services/vault.service';
import { AuthProvider } from '../../services/Auth';
import { VaultProvider } from '../../services/Vault';
import { BrowserAuthPlugin } from '../../services/browser-auth.plugin';
import { BrowserAuthService } from '../../services/browser-auth.service';
import { LoginContainer } from '../../containers/LoginContainer';
import { getSession } from '../../selectors/selector';

const PrivateRoute: React.FC<any> = ({ hasSession, component: WrappedComponent }) => {
  let browserPlugin;
  if (!isPlatform('capacitor')) {
    browserPlugin = new BrowserAuthPlugin(new BrowserAuthService());
  }
  const vaultService = new VaultService(browserPlugin);
  const authService = AuthSingleton.getInstance(vaultService);
  return (
    <VaultProvider value={vaultService}>
      <AuthProvider value={authService}>
        <AppComponent
          authService={authService}
          Wrapper={WrappedComponent}
          vaultService={vaultService}
          sessionEval={hasSession}
        />
      </AuthProvider>
    </VaultProvider>
  );
};

type AppComponentProps = { authService: AuthenticationService, Wrapper:any, vaultService: VaultService, sessionEval:any};
const AppComponent: React.FC<AppComponentProps> = observer(props => {
  const _tab = props.authService.tabName;
  const { isLoggedIn } = props.authService;

  return _tab ? (
    <IonReactRouter>
      <Route path="/" render={() => <Redirect to="/login" />} exact />
      <Route
        path="/app"
        render={() => (isLoggedIn ? <props.Wrapper {...props} /> : <Redirect to="/login" />)}
      />
      <Route
        path="/login"
        render={() => (isLoggedIn ? <Redirect to={`/app/${_tab}`} /> : <LoginContainer />)}
      />
    </IonReactRouter>
  ) : null;
});

const mapStateToProps = createStructuredSelector({
  hasSession: getSession()
});

export default connect(mapStateToProps)(PrivateRoute);
