import { IonRow, IonCol, IonGrid } from "@ionic/react";
import React from "react";
import { DestinationRadius, Dropdate, Destination } from "../../../utils/constants";
import { NewInput } from "../../../components/shared/NewUIComponents/OriginInput";
import { RadiusRange } from "../../../components/shared/NewUIComponents/RadiusRange";
import { DatePicker } from "../../../components/shared/NewUIComponents/DatePicker";
const DeliverypData = (props: any) => {
  const {
    destinationError,
    destination,
    destinationRadius,
    dropUpdateError,
    dropdate,
    defaultValue,
    isDestination,
    useHandler,
    setErrorValue,
    updateLocalCurrentTime,
    dropLocalTime,
    showDate,
  } = props;
  const upDateLocalTime = (time: string) => {
    updateLocalCurrentTime(Dropdate, time);
  };
  return (
    <IonGrid className="delivery-data">
      <IonRow>
        <IonCol>
          <NewInput
            Error={destinationError}
            locationValue={false}
            labelValue="Destination"
            InputValue={destination}
            InputName={Destination}
            handleChange={props.handleChange}
            keyboardshowHandler={props.keyboardshowHandler}
            keyboardhideHandler={props.keyboardhideHandler}
            deFaultValue={defaultValue}
            isDestination={isDestination}
            useHandler={useHandler}
            setErrorValue={setErrorValue}
            getLocalTime={upDateLocalTime}
            onFocus={props.onFocus || (() => {})}
          />
        </IonCol>
      </IonRow>
      <IonRow>
        <IonCol>
          <RadiusRange
            labelName="Destination Radius"
            rangeDisabled={props.rangeDisable}
            InputValue={destinationRadius}
            divClassName="pickRadius"
            name={DestinationRadius}
            handleChange={props.handleChange}
          />
        </IonCol>
      </IonRow>
      {showDate ? (
        <IonRow>
          <IonCol>
            <DatePicker
              Error={dropUpdateError}
              labelName="Delivery Date"
              rangeDisabled={props.rangeDisable}
              name={Dropdate}
              InputValue={dropdate}
              handleChange={props.handleChange}
              placeholder="Choose Delivery Date"
              useHandler={useHandler}
              localTime={dropLocalTime}
            />
          </IonCol>
        </IonRow>
      ) : null}
    </IonGrid>
  );
};
export default DeliverypData;
