import { isPlatform } from "@ionic/react";
import { UserDetails } from "app/models/userPforile/userProfile.modal";
import { clear, GPSWrapper } from "app/utils/common";
import { MANAGE_USER_REMOVE_SUCCESS_TEXT, MANAGE_USER_TRANSFER_EXIST, MANAGE_USER_TRANSFER_MESSAGE, MANAGE_USER_UPDATE_SUCCESS, OOPS_MESSAGE } from "app/utils/constants";
import { ToastMessage } from "app/utils/ToastMessage";

export const PATH = 'manage/ManageUser';

export const userProfileModel = new UserDetails();

export const backToPage = (props: any) => {
    props.history.push(isPlatform('desktop') ? `/app/manage` : `/app/${PATH}`);
};

export const handleTransfer = (props:any) => (payload: any, restData: Function, isCarrierSame = false) => {
    const { name, params } = payload;
    props.resetValidateUser();
    if (isCarrierSame) {
      restData();
      ToastMessage.show(MANAGE_USER_TRANSFER_EXIST);
      backToPage(props);
      return true;
    }
    const query = userProfileModel.transferMember(params);
    const callback = (success = false) => {
      restData();
      if (success) {
        ToastMessage.open({
          message: MANAGE_USER_TRANSFER_MESSAGE.replace('{{NAME}}', name),
          btnTitle: 'Ok',
          showUndo: true,
          callback: () => {}
        });
      } else {
        ToastMessage.show(OOPS_MESSAGE);
      }
      backToPage(props);
    };
    props.transferUserAction({ query, callback });
  };

async function azureLogout(props: any) {
    if (isPlatform('capacitor')) {
        GPSWrapper.details.stopGPS();
    }
    await clear();
    await props.authService.logout();
    await props.logout();
}

export const handleDelink = (props:any, isProfile = false) => (payload: any) => {
    const {
      userDetails: { userId }
    } = payload;
    const query = userProfileModel.updateMember(payload);
    const callback = async (success = false) => {
      if (success) {
        ToastMessage.show(MANAGE_USER_REMOVE_SUCCESS_TEXT);
        if(isProfile) {
            await azureLogout(props);
            return true;
        }
        props.history.push(`${isPlatform('desktop') ? '/app/manage' : `/app/${PATH}`}`);
        return true;
      }
      ToastMessage.show(OOPS_MESSAGE);
    };
    props.delinkUserAction({ query, userId, callback });
  };

export const handleUpdateSubmit = (props:any, isManageProfile = false, isSelfDesktop = false) => (payload: any, restData: Function) => {
    const query = userProfileModel.updateMember(payload);
    const callback = (success = false) => {
      if (success) {
        restData();
        ToastMessage.show(MANAGE_USER_UPDATE_SUCCESS);
        if(!isSelfDesktop) {
          if(isManageProfile) {
              props.history.push(`/app/${PATH}/Profile`);
          } else {
            props.history.push(`/app/manage/ProfileDetails`);
          }
        }
        return true;
      }
      ToastMessage.show(OOPS_MESSAGE);
    };
    props.updateUserAction({ query, isProfile: true, callback });
};
