import React, { Component } from "react";
import "./dropdown.scss";
import { IonRow, IonCol, IonModal, IonIcon } from "@ionic/react";

class Dropdown extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      selectedOption: this.props.options[0],
      clickedOption: this.props.options[0],
      asc: true,
      isDropdown: false,
      loadData: this.props.loadData,
      upArrow: "assets/icon/sort icon.svg",
      downArrow: "assets/icon/sort icon downward.svg",
      slectedValue: this.props.options[0].value,
    };
  }
  hideDropdownMenu = (option: any) => {
    this.setState({ clickedOption: option });
    if (this.state.slectedValue !== option.value) {
      this.setState({ asc: true });
    }
  };
  onConfrim = (value: any) => {
    this.setState({
      slectedValue: this.state.selectedOption.value,
      selectedOption: this.state.clickedOption,
    });
    this.sortBy(value);
  };
  dropdownClick = () => {
    this.setState({ isDropdown: true });
  };
  sortBy = (x: any) => {
    const { asc } = this.state;
    this.setState({ isDropdown: false });
    this.props.sortedData(x, asc);
  };
  render() {
    const sortByOptions = this.props.options;
    return (
      <>
        <div className="search_sortby_select">
          <div onClick={this.dropdownClick} className="search_sort_button" data-test="dropdown-click">
            <span className="text_BY">By </span>{" "}
            {this.state.selectedOption.name.length <= 16
              ? this.state.selectedOption.name
              : this.state.selectedOption.name.slice(0, 15) + "..."}
          </div>
          <i className="down"></i>
        </div>
        <div data-test="sort-arrows"
          className="sortArrows"
          onClick={() => {
            this.setState(
              {
                asc: !this.state.asc,
              },
              () => {
                this.sortBy(this.state.selectedOption.value);
              }
            );
          }}
        >
          <IonIcon
            src={
              this.state.asc === true
                ? this.state.upArrow
                : this.state.downArrow
            }
          ></IonIcon>
        </div>
        <IonModal
          show-backdrop={true}
          backdrop-dismiss={false}
          isOpen={this.state.isDropdown}
          cssClass="dropdown-modal"
          onDidDismiss={() => {
            this.setState({
              isDropdown: false,
            });
          }}
        >
          <div className="search_options">
            <IonRow class="dropdown-row dropdown-heading">
              <IonCol>
                {" "}
                <p className="sortBy_text">Sort by</p>{" "}
              </IonCol>
            </IonRow>
            {sortByOptions.map((option: any, k: any) => (
              <IonRow data-test="hide-drop"
                class="dropdown-row"
                key={k}
                onClick={() => this.hideDropdownMenu(option)}
              >
                <IonCol
                  class={
                    option.name === this.state.clickedOption.name
                      ? "checked"
                      : ""
                  }
                >
                  {option.name} <i className="check"></i>{" "}
                </IonCol>
              </IonRow>
            ))}
            <IonRow class="dropdown-handler">
              <IonCol size="1"></IonCol>
              <IonCol data-test="dropdown-cancel"
                size="5"
                className="dropdown_cancel"
                onClick={() =>
                  this.setState({
                    isDropdown: false,
                    clickedOption: this.state.selectedOption,
                  })
                }
              >
                <span>Cancel</span>
              </IonCol>
              <IonCol  data-test="dropdown_cancel"
                size="5"
                className="dropdown_cancel"
                onClick={() => this.onConfrim(this.state.clickedOption.value)}
              >
                <span>OK</span>
              </IonCol>
              <IonCol size="1"></IonCol>
            </IonRow>
          </div>
        </IonModal>
      </>
    );
  }
}

export default Dropdown;
