export function getMyloadData() {
  const CardsData = [
    ...(isEntitled('myLoad', "carrierAssigned") ? [{
      count: "carrierAssignedCount",
      cradImg: "assets/icon/carrier.svg",
      title: "Carrier Assigned",
      myLoadRouting: "/app/MyLoad/CarrierAssigned",
      className: "carrier_assigned",
      class: "carrier-assigned",
    }] : []),
    ...(isEntitled('myLoad', "driverAssigned") ? [{
      count: "driverAssignedCount",
      cradImg: IONICWrapper.details.auth.user.roles.includes(Roles.carrierDispatcher)
        ? "assets/icon/driver.svg"
        : "assets/icon/carrier.svg",
      title: IONICWrapper.details.auth.user.roles.includes(Roles.carrierDispatcher)
        ? "Driver Assigned"
        : "Loads Assigned",
      myLoadRouting: "/app/MyLoad/DriverAssigned",
      className: "driver_assinged",
      class: "driver-assigned",
    }] : []),
    ...(isEntitled('myLoad', "inTransit") ? [{
      count: "inTransitCount",
      cradImg: "assets/icon/transit.svg",
      title: "In-Transit",
      myLoadRouting: "/app/MyLoad/InTransit",
      className: "in-transit",
      class: "in_transit",
    }] : []),
    // Commented  as per defect : DE3741
    // {
    //   count: "1",
    //   cradImg: "assets/icon/delivered.svg",
    //   title: "Delivered Needs Paperwork",
    //   myLoadRouting: "",
    //   className: "delivered",
    //   class: "delivered_needs",
    // },
    ...(isEntitled('myLoad', "recentlyDeliveredLoads") ? [{
      count: "deliveredCount",
      cradImg: "assets/icon/recent.svg",
      title: "Recently Delivered Loads",
      myLoadRouting: "/app/MyLoad/Delivered",
      className: "recently_delivered",
      class: "recently-delivered",
    }] : [])
  ];

  return CardsData;
}

