import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { isPlatform } from '@ionic/react';
import useInjectSaga from '../../../utils/injectSaga';
import useInjectReducer from '../../../utils/injectReducer';
import saga from '../../../saga/CarrierAssign/saga';
import { getUserDetails, getNetworkStatus } from '../../../selectors/selector';
import CarrierAssignedPage from '../../../pages/Execute/CarrierAssigned/CarrierAssignedPage.page';
import { getDeliveredLoad } from '../../../selectors/carrierAssign/selector';
import reducer from '../../../reducers/carrierAssign/reducer';
import { fetchShipments } from '../../../actions/carrierAssign/action';
import { DriverList } from '../../../models/home/CarrierAssign.model';
import { isEntitled } from '../../../utils/common';
import { MyloadSectionContainer } from '../sectionContainer';
import { ExicutionLoads } from '../../../models/ExicutionLoads.model';
import { showAlert } from '../../../actions/App/action';
import driverNames from '../../../utils/getDriverNameList';
import { MODULES } from '../../../utils/constants';

const key = 'carrierAssigned';

class DeliveredContainer extends PureComponent<any, any> {
  __model: DriverList;

  constructor(props: any) {
    super(props);
    this.__model = new DriverList();
    this.state = {
      showFilter: isEntitled('myLoad', 'filter'),
      buttonName: isEntitled('myLoad', 'filter') ? 'Change Driver' : '',
      showInitial: isEntitled('myLoad', 'filter')
    };
  }

  chnageValue = () => {
    this.props.changeToasterValue();
  };

  getDriverLoads = (isHideLoader = true) => {
    try {
      const carrierId =
        (this.props && this.props.userDetails && this.props.userDetails.carrierId) || '';
      const userPrincipalName =
        (this.props && this.props.userDetails && this.props.userDetails.userPrincipalName) || '';
      const driverModel: ExicutionLoads = new ExicutionLoads();
      driverModel.carrierId = carrierId;
      driverModel.filter = { delivered: true };
      if (!isEntitled('myLoad', 'carrierAssigned')) {
        driverModel.driverId = userPrincipalName;
      }
      this.props.fetchShipments(driverModel.getRequestPayload(), isHideLoader); // Driver Load Assigned.
    } catch (error) {
      console.log('ERROR:', error);
    }
  };

  render() {
    const data =
      this.props.loads && this.props.loads.length > 99
        ? this.props.loads.slice(0, 99)
        : this.props.loads;
    const driverNameList: any = driverNames(data, MODULES.RECENTLY_DELIVERED_MODULE);

    return (
      <>
        {isPlatform('mobile') ? (
          <CarrierAssignedPage
            data-test="CarrierAssignedPage"
            {...this.props}
            chnageValue={this.chnageValue}
            getLoads={this.getDriverLoads}
            AssignDriverbtn={() => {}}
            module="MyLoad"
            loads={data}
            showFilter={this.state.showFilter}
            driverNameList={driverNameList}
            isButtonHidden
            buttonName={this.state.buttonName}
            showInitial={this.state.showInitial}
            subModule={this.props.match.params[0]}
            isNetworkAvailable={this.props.isNetworkAvailable}
            showAlert={this.props.showAlert}
          />
        ) : (
          <MyloadSectionContainer {...this.props} />
        )}
      </>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  loads: getDeliveredLoad(),
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus()
});

const mapDispatchToProps = (dispatch: Function) => ({
  fetchShipments: (details: any, hideLoader: boolean) =>
    dispatch(fetchShipments(details, hideLoader)),
  showAlert: (data: any) => dispatch(showAlert(data))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(DeliveredContainer);
export { DeliveredContainer };
