import { IonImg, IonGrid } from '@ionic/react';
import * as React from "react";
import getTime from "../../../utils/getTimePeriod";
import { getAppointmentTime } from '../../../utils/formatData';

const StopDetails = (props: any) => {
  const stopDetail = props.stopDetails;
  return (
    <IonGrid class="stop-data-grid">
      {props.index < props.length - 1 ? (
        <IonImg className="stop-icon" src="assets/icon/origin-pointer.svg" />
      ) : (
        <IonImg className="path_icon" src="assets/icon/dst-pointer.svg" />
      )}
      <IonGrid className="stop-address-grid">
        <p className="origin_city">
          {stopDetail.city}, {stopDetail.state}
        </p>
        <p className="origin_dateTime load_header_elements">
          {props.appointment}
        </p>
      </IonGrid>
    </IonGrid>
  );
};
class CariierAssignLoadTileHeader extends React.Component<any, any> {
  public render() {
    const loadStop = [];
    let sortedLoadStops: any[] =
      this.props.loadStop || [];
    sortedLoadStops = sortedLoadStops.slice().sort((load1: any, load2: any) => {
      if (load1.sequenceNo && load2.sequenceNo) {
        return load1.sequenceNo - load2.sequenceNo;
      } else if (load1.stopSequence && load2.stopSequence) {
        return load1.stopSequence - load2.stopSequence;
      } else {
        return 0;
      }
    });
    if (sortedLoadStops.length > 0) {
      loadStop[0] = sortedLoadStops[0];
      loadStop[1] = sortedLoadStops[sortedLoadStops.length - 1];
    }
    return (
      <IonGrid class="assign-load-details-grid">
        {loadStop
          ? loadStop.map((stop: any, index: any) => (
              <StopDetails
                key={index}
                stopDetails={stop || {}}
                appointment={(stop && getAppointmentTime(stop)) || ""}
                length={loadStop.length}
                index={index}
              />
            ))
          : null}
      </IonGrid>
    );
  }
}

export { CariierAssignLoadTileHeader };
