import { all, put, call, takeLatest, select } from "redux-saga/effects";
import {
  showLoading,
  hideLoading,
  showPullToRefresh,
  hidePullToRefresh
} from "../../actions/Login/action";
import {
  getFavorite,
  updateFavoriteDataReducer,
  updateFavoriteError
} from "../../actions/Favorite/action";
import * as constants from "../../utils/constants";
import request from "../../utils/request";
import { DomainConverter } from "../../utils/common";

// handle the API call for updating the search, and handling duplicate/List full errors
export function* updateFavoriteDataFunc(action: any) {
  const { data, isHideLoader } = action && action.payload && action.payload;
  try {
    if (!(action && action.payload && isHideLoader)) {
      yield put(showLoading());
    }
    yield put(showPullToRefresh());
    let updatefavoritemutation = DomainConverter.domainToData<any>(
      action.payload.data
    );
    console.log(updatefavoritemutation);
    const fetchFavoriteResults = yield request<any>([
      "FAVORITE",
      "FAVORITE_LOADS",
      null,
      "",
      updatefavoritemutation,
      "application/json"
    ]);
    console.log(fetchFavoriteResults);
    if (
      fetchFavoriteResults.data.updateUserSearchHistoryAsFavorite.errorMessage
    ) {
      console.log(
        fetchFavoriteResults.data.updateUserSearchHistoryAsFavorite.errorMessage
      );
      yield put(updateFavoriteError(fetchFavoriteResults));
    } else {
      yield put(updateFavoriteDataReducer(fetchFavoriteResults));
    }
    if (!(action && action.payload && isHideLoader)) {
      yield put(hideLoading());
    }
    yield put(hidePullToRefresh());
  } catch (e) {
    if (!(action && action.payload && isHideLoader)) yield put(hideLoading());
    yield put(hidePullToRefresh());
  }
}

export default function* rootSaga() {
  yield all([
    takeLatest(constants.UPDATE_FAVORITE_LOADS, updateFavoriteDataFunc)
  ]);
}
