import { MODULES } from "./constants";

export default function driverNames(data: any, module: string) {
  let driverNameList =
    (data &&
      data.map((record: any) => {
        let name = [];
        if (
          module !== MODULES.DRIVER_ASSIGNED_MODULE ||
          (module === MODULES.DRIVER_ASSIGNED_MODULE &&
            record.driverAssignmentStatus === true)
        ) {
          name =
            record.shipmentInvolvedParties &&
            record.shipmentInvolvedParties.find((data: any) => {
              return data.partyType === "PRIMARY DRIVER";
            });
        }
        if (
          module === MODULES.DRIVER_ASSIGNED_MODULE &&
          record.driverAssignmentStatus === false
        ) {
          name =
          record.assignmentTransaction &&
          record.assignmentTransaction[0];
        }
        return (name && name.partyName !== null && name.partyName) || (name && name.driverName !== null && name.driverName) || null;
      })) ||
    [];

  return [
    ...driverNameList.filter((item: any, pos: any) => {
      return driverNameList.indexOf(item) === pos && item != null;
    }),
    "All Driver",
    "Unknown"
  ];
}
