import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { isPlatform } from '@ionic/react';
import { ManageUserProfileFormPage } from '../../../pages/Manage/ManageUser';
import { getUserDetails, getNetworkStatus } from '../../../selectors/selector';
import { isUserValidated, getValidateUserDetails } from '../../../selectors/Manage/selector';
import { showNetworkAlert } from '../../../actions/App/action';
import {
  addUserDetails,
  validateUser,
  validateUserReset,
  transferUser
} from '../../../actions/manage/action';
import { UserDetails } from '../../../models/userPforile/userProfile.modal';
import { ToastMessage } from '../../../utils/ToastMessage';
import {
  OOPS_MESSAGE,
  MANAGE_USER_SUCCESS_MESSAGE,
} from '../../../utils/constants';
import { backToPage, handleTransfer, PATH, userProfileModel } from './ManageUserUtil';

export const ManageUserProfileAddContainer: React.FC<any> = (props: any) => {
  const {
    history: { push }
  } = props;

  const handleSubmit = (payload: any, restData: Function) => {
    const query = userProfileModel.addUser(payload);
    const callback = (success = false) => {
      if (success) {
        restData();
        ToastMessage.show(MANAGE_USER_SUCCESS_MESSAGE);
        backToPage(props);
        return true;
      }
      ToastMessage.show(OOPS_MESSAGE);
    };
    props.addUserAction({ query, callback });
  };

  

  return (
    <ManageUserProfileFormPage
      {...props}
      selectedUser={null}
      module={PATH}
      handleSubmit={handleSubmit}
      handleTransfer={handleTransfer(props)}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus(),
  isUserValid: isUserValidated(),
  validateUserDetails: getValidateUserDetails()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  validateUserAction: (payload: any) => dispatch(validateUser(payload)),
  addUserAction: (payload: any) => dispatch(addUserDetails(payload)),
  resetValidateUser: () => dispatch(validateUserReset()),
  transferUserAction: (payload: any) => dispatch(transferUser(payload))
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageUserProfileAddContainer);
