import React, { useEffect } from 'react';
import { IonContent, IonPage, IonList, IonItem, IonLabel, IonInput, isPlatform } from '@ionic/react';
import AppHeader from '../../../components/app/Bars/Bar-header';
import { capitalize } from '../../../utils/helper';
import moment from 'moment';
import { CallNumber } from '@ionic-native/call-number';
import { logEvent } from '../../../utils/Analytics';
import { EVENT, SCREEN, Anywhere } from '../../../utils/constants';
import ManageCapacityViewDesktop from './ManageCapacityViewDesktop.page';
import { formatStatesList } from './ManagaCapacityUtil';

export const  ManageCapacityViewPage:React.FC<any> = (props:any) => {
    const { 
        selectedCapacity: item,
        isNetworkAvailable,
        showNetworkAlert,
        history,
        userDetails
    } = props;

    useEffect(() => {
        logEvent(EVENT.SV_MANAGE_CAPACITY_ADDEDIT,
            {
                screen: SCREEN.MANAGE_CAPACITY_ADDEDIT,
                carrierId: userDetails.carrierId
            });
    }, []);

    const editData = () => {
		if(isNetworkAvailable) {
            history.push(`/app/${props.module}/Edit`);
		} else {
			showNetworkAlert();
		}
    }

    const pickupDateType = item.capacityType === 'TRUCK' ? [{
        title: 'Pick-up Start Date',
        slug: 'pickup-start-date',
        value: item &&  moment(item.pickUpFromDate).format('MMMM DD, YYYY')
    },
    {
        title: 'Pick-up End Date',
        slug: 'pickup-end-date',
        value: item && moment(item.pickUpToDate).format('MMMM DD, YYYY')
    }
    ] : [{
        title: 'Days of Week',
        slug: 'days-of-week',
        value: (item && item.preferredDays && item.preferredDays.map((value:string) => capitalize(value.substr(0,3), true)).join(', ')) || ''
    }];

    const radius = !item.destinationStates || (item.destinationStates && item.destinationStates.length === 0 && item.destination.radius) ? [{
        title: 'Destination Radius',
        slug: 'destination-radius',
        value:  item.destination.radius ? `${item.destination.radius}mi` : 'NA'
    }]: [];
    
    const listItems = [
        {
            title: 'Last Updated',
            value: moment(item.lastUpdatedDateTime).format('MMM DD, YYYY h:mm a'),
            childrens: [
                {
                    title: 'Name',
                    slug: 'last-update-name',
                    value: item && item.lastUpdatedBy || ''
                },
                {
                    title: 'Email address',
                    slug: 'last-update-email',
                    value: item && item.lastUpdatedByEmail || '',
                    callback: () => {
                        window.location.href = `mailto:${item.lastUpdatedByEmail}`
                    }
                },
                {
                    title: 'Phone',
                    slug: 'last-update-phone',
                    value: item && item.lastUpdatedByPhoneNumber  || '',
                    callback: () => {
                        CallNumber.callNumber(item.lastUpdatedByPhoneNumber, true)
                        .then((res) => {})
                        .catch((err) => {});
                    }
                }
            ]
        },
        {
            title: '',
            value: '',
            childrens: [
                {
                    title: 'Capacity Type',
                    slug: 'capacity-type',
                    value: item && capitalize(item.capacityType, true)
                },
                {
                    title: 'Trailer Type',
                    slug: 'trailer-type',
                    value: item && item.trailerType
                }
            ]
        },
        {
            title: 'Pick-up',
            value: '',
            childrens: [
                {
                    title: 'Origin',
                    slug: 'origin-name',
                    value: item && `${item.origin.city}${item.origin.state ? ', ' + item.origin.state : ''}`
                },
                {
                    title: 'Origin Radius',
                    slug: 'origin-radius',
                    value: item && `${item.origin.radius}mi`
                },
                ...pickupDateType
            ]
        },
        {
            title: 'Delivery',
            value: '',
            childrens: [
                {
                    title: item &&  item.destinationStates && item.destinationStates.length ? 'States': 'Destination',
                    slug:  item && item.destinationStates && item.destinationStates.length ? 'destination-states': 'destination-details',
                    value: item && item.destinationStates && item.destinationStates.length > 0 ? formatStatesList(item.destinationStates).join(', ') :  item.destination.city !== null ? `${item.destination.city}${item.destination.state ? ", " + item.destination.state: ''}`: Anywhere
                },
                ...radius
            ]
        }
    ]

    return isPlatform('desktop') ? (
            <ManageCapacityViewDesktop {...props} item={item} listItems={listItems}/>
        ) : (
            <IonPage className="desktop-page">
                <AppHeader title={item.capacityId} backUrl={"/app/" + props.module} History = {props.history} editData={editData} />
                <IonContent className="custom-padding">
                    {
                        listItems.map((item:any, index: number) => (
                            <div className="manage-capacity-wrapper" key={`main_${item.title}_${index}`}>
                                { item.title ? (
                                    <h3 className={item.value?'underline': ''}>
                                        {item.title}
                                        <span>{item.value}</span>
                                    </h3>
                                ): null}
                                <IonList className="manage-capcity-list">
                                    {
                                        item.childrens.map((children:any) => (
                                            <IonItem key={children.title} data-test={children.slug} className="manage-capacity-item">
                                                <IonLabel position="floating" mode="ios">{children.title}</IonLabel>
                                                <IonInput
                                                    value={children.value}
                                                    readonly
                                                    type="text"
                                                    className="manage-input-field"
                                                    onClick={children.callback ? children.callback: ()=> {}}
                                                ></IonInput>
                                            </IonItem>
                                        ))
                                    }
                                </IonList>
                            </div>
                        ))
                    }
                    
                </IonContent>
            </IonPage>
        )
}
export default ManageCapacityViewPage;