import getURLDetails from "./endPoints";
import HTTP from "./httpServices";
import { KEYS } from "../utils/constants";
import { store } from "../store";
import { session } from "../actions/Login/action";
import { showApiResponseError, showMemberApiError } from "../actions/Home/action";
import { AuthSingleton } from '../services/authentication.service';
import { IONICWrapper} from "../utils/common";
import { showBuyLoadError } from "app/actions/Search/action";

/**
 * This common service is used to make GET and POST Web API calls.
 */
var serviceRequests = {};
var hasSession = true;

class commonService<T> {

  /*
  * To get the error message based on the API status code
  * @status - API response status
  */
  private getErrorMessage(status: number) {
    let message: string = "";
    if (status >= 300 && status < 400) {
      message = "Server is not working, please try again later";
    } else if (status >= 400 && status < 500) {
      message = "Not authorized, please logging out and try again later";
    } else if (status >= 500 && status < 600) {
      message = "Internal serve error, please try again later";
    }

    return message;
  }
  /**
   * To fetch data from the Web Service API.
   * @param {string} endPoint end point value.
   * @param {string} urlKey url key value.
   * @param {*} data request data to be transfered.
   * @param {*} token token value.
   * @param {*} responseType response type.
   * @param {*} serviceProgress callback for monitoring service progress.
   * @returns {promise} result of the Service API.
   */

  get(
    endPoint: string,
    urlKey: string,
    data?: any,
    token?: string,
    responseType?: string,
    serviceProgress?: Function
  ) {
    var _endPointEntity = getURLDetails(endPoint, urlKey, data);
    var _this = this;
    return new Promise(function (resolve: any, reject: any) {
      _this.getHeaders(endPoint, responseType).then((headers: any) => {
        if (_endPointEntity) {
          new HTTP()
            .__request(
              _endPointEntity.get_Service_Url(),
              headers,
              "get",
              null,
              urlKey,
              serviceRequests,
              _this.set_TimeOut(),
              function (pgr: any) {
                serviceProgress && serviceProgress(pgr);
              }
            )
            .then((response: any) => {
              // Success
              if (response && response.status >= 200 && response.status < 300) {
                resolve(response.data);
              } else {
                const message = _this.getErrorMessage(response.status);
                reject(new Error(message));
              }
            })
            .catch((error: any) => {
              const message = _this.getErrorMessage(
                error && error.response && error.response.status
              );
              store.dispatch(showApiResponseError({ message }));
              reject(error);
            });
        } else {
          reject("No URL is provided for Service Request");
        }
      }); //testing
    });
  }

  /**
   * To post data to the Web Service API.
   * if there is no specic functional programming is designated for the post API, the above GET methods will be changed to single common metohd in future based on the excersize.
   * @param {string} endPoint end_point.
   * @param {string} urlKey url key value.
   * @param {*} data request data to be transfered.
   * @param {*} token token value.
   * @param {*} responseType response type.
   * @param {*} serviceProgress callback for monitoring service progress.
   * @returns {promise} result of the Service API.
   */

  post(
    endPoint: string,
    urlKey: string,
    data: any,
    token?: string,
    requestData?: T,
    responseType?: any,
    serviceProgress?: any
  ) {
    var _endPointEntity = getURLDetails(endPoint, urlKey, data);
    var _dataString = requestData;
    var _this = this;
    return new Promise(function (resolve: any, reject: any) {
      _this.getHeaders(endPoint, responseType).then((headers: any) => {
        if (_endPointEntity && headers) {
          new HTTP()
            .__request(
              _endPointEntity.get_Service_Url(),
              headers,
              "post",
              _dataString,
              urlKey,
              serviceRequests,
              _this.set_TimeOut(),
              function (pgr: any) {
                serviceProgress && serviceProgress(pgr);
              }
            )
            .then((response) => {
              if (response && response.status >= 200 && response.status < 300) {
                resolve(response.data);
              } else {
                const message = _this.getErrorMessage(response.status);
                reject(new Error(message));
              }
            })
            .catch((error) => {
              const message = _this.getErrorMessage(
                error && error.response && error.response.status
              );
              if (urlKey === "GET_USER_DETAILS") {
                store.dispatch(showMemberApiError(true));
              } else {
                if (message === '' && urlKey === "BUY_LOAD"){
                  let status =  error && error.response && error.response.status;
                  if(!status || status === 202 || status === "error" ||status === "UNKNOWN") {
                    store.dispatch(showBuyLoadError(true))
                  }
                }else {
                  store.dispatch(showApiResponseError({ message }));
                }
              }
              reject(error);
            });
        } else {
          reject("No URL is provided for Service Request");
        }
      });
    });
  }

  /**
   * To get Headers information for the specified servcie API.
   * @param {*} endPoint API end point.
   * @param {*} responseType response type.
   * @returns {object} consolidated header list.
   */
  async getHeaders(endPoint?: string, responseType?: string) {
    //Redirecting to the landing screen when session is expired.
    const __auth = AuthSingleton.getInstance();

    if (hasSession) {
      hasSession = false;
      const isActiveSession = await __auth.isAuthenticated();
      hasSession = true;
      if (!isActiveSession) {
        store.dispatch(session(Math.floor(Math.random() * 100)));
        return null;
      }
    }
    var __token = (await __auth.getAuthResponse()).id_token;

    var headers: any = {
      "Content-Type": "application/json",
      'Cache-Control': 'no-cache,no-store,max-age=0,must-revalidate',
      'Pragma': 'no-cache',
      'Expires': '-1',
      'X-XSS-Protection': '1; mode=block',
      'X-Frame-Options': 'SAMEORIGIN',
      'Content-Security-Policy': "script-src 'self'",
      'X-Content-Type-Options': 'nosniff',
      'Allow': 'GET, POST, HEAD',
      'Content-Encoding': 'deflate, gzip',
      'Transfer-Encoding': 'compress',
      "Ocp-Apim-Subscription-Key": KEYS.SUBSCRIPTION_KEY,
    };

    if (__token) {
      headers["Authorization"] = "Bearer " + __token;
    } else {
      return null;
    }

    return headers;
  }

  /**
   * To cancel request
   * @param {string} endPointKey response type.
   */
  cancelRequest(endPointKey: string) {
    var __currentReqest = JSON.parse(JSON.stringify(serviceRequests))[
      endPointKey
    ];
    __currentReqest.cancel(
      endPointKey + " Service Request has been cancelled by Agent"
    );
  }
  /**
   * To set timeout for request
   * @returns {number} specific time out based on end point key.
   */
  set_TimeOut() {
    const minTimeOut = 40;
    const timeOut = IONICWrapper.details.keys && parseInt(IONICWrapper.details.keys.configuration.serviceTimeout);
    return (timeOut || minTimeOut) * 1000; //Default timeout 40 seconds for any API Call.
  }
}

export default commonService;

