import { ActionType, HomeState } from "../../schemas/Home/Home.schema";
import * as Constants from "../../utils/constants";
import produce from "immer";

export const initalHomeState: HomeState = {
  watched: {
    data: [],
    totalRecords: 0,
    loaded: false,
    filter: {
      selected: [],
      direction: "asc",
    },
  },

  apiErrorMessage: "",
  showMemberAPIError: false,
  recommended: {
    totalRecords: 0,
    loaded: false,
    data: [],
    filter: {
      selected: [],
      direction: "asc",
    },
  },
};

export default (state = initalHomeState, action: ActionType) =>
  produce(state, (draft) => {
    switch (action.type) {
      case Constants.WATCHEDLOAD_DATA_REQUEST:
        draft.watched.loaded = true;
        break;
      case Constants.WATCHEDLOAD_DATA_SUCCESS:
        const payload = action.payload && (action.payload as any);
        let items =
          (payload &&
            payload.watchedResults &&
            payload.watchedResults.data &&
            payload.watchedResults.data.getUserWatchedLoads &&
            payload.watchedResults.data.getUserWatchedLoads.loadPostings) ||
          [];
        const totalRecords =
          (payload &&
            payload.watchedResults &&
            payload.watchedResults.data &&
            payload.watchedResults.data.getUserWatchedLoads &&
            payload.watchedResults.data.getUserWatchedLoads.totalRecords) ||
          0;
        items = payload.refreshStatus
          ? items
          : [...draft.watched.data, ...items];
        items = items.filter((item: any, index: number) => {
          const findIndex = items.findIndex(
            (newItem: any) => newItem.loadId === item.loadId
          );
          return index === findIndex;
        });
        draft.watched.data = items;
        draft.watched.loaded = true;
        draft.watched.totalRecords = totalRecords;
        break;
      case Constants.ADD_WATCH_LOAD_SUCCESS:
        const data = [...draft.watched.data, action.payload];
        draft.watched.data = data;
        draft.watched.totalRecords = draft.watched.totalRecords + 1;
        break;
      case Constants.REMOVE_WATCH_LOAD_SUCCESS:
        draft.watched.data = draft.watched.data.filter(
          (details) => details.loadId !== (action.payload as any).loadId
        );
        draft.watched.totalRecords = draft.watched.totalRecords - 1;
        break;
      case Constants.WATCHED_LOAD_FILTER:
        draft.watched.filter = (action.payload as any).filter;
        break;
        case Constants.RECOMMENDED_LOAD_FILTER:
        draft.recommended.filter = (action.payload as any).filter;
        break;
      case Constants.SHOW_API_RESPONSE_ERROR:
        draft.apiErrorMessage = (action.payload as any).message;
        break;
      case Constants.CLEAR_API_RESPONSE_ERROR:
        draft.apiErrorMessage = "";
        break;
      case Constants.RECOMMENDED_LOADS_SUCCESS:
        const recpayload = action.payload && (action.payload as any);

        let recitems =
          (recpayload &&
            recpayload.recommendedResults &&
            recpayload.recommendedResults.data &&
            recpayload.recommendedResults.data.recommendedLoads &&
            recpayload.recommendedResults.data.recommendedLoads.loadPostings &&
            recpayload.recommendedResults.data.recommendedLoads.loadPostings) ||
          [];
        const totalNoRecords =
          (recpayload &&
            recpayload.recommendedResults &&
            recpayload.recommendedResults.data &&
            recpayload.recommendedResults.data.recommendedLoads &&
            recpayload.recommendedResults.data.recommendedLoads.totalRecords) ||
          0;
        draft.recommended.data =  recpayload.refreshStatus ? recitems : [...draft.recommended.data, ...recitems];
        draft.recommended.totalRecords = totalNoRecords;
        draft.recommended.loaded = true;
        break;
      case Constants.REMOVE_RECOMMENDEDRESULTS:
        const removedLoad = action.payload && (action.payload as any);
        draft.recommended.data =draft.recommended.data.filter(
            (details: any) => details.loadId !== removedLoad.loadId
        );
        draft.recommended.totalRecords = draft.recommended.totalRecords - 1;
        break;
        case Constants.SHOW_MEMBER_API_ERROR:
          draft.showMemberAPIError = action.payload && (action.payload as any);
    }
  });
