import React from "react";
import { IonLabel, IonItem, IonDatetime } from "@ionic/react";

import '../../../pages/Search/New/NewPage.scss';
import { getYearValues } from "../../../utils/formatData";
interface DatePickerProps {
  labelName: any;
  InputValue: string;
  placeholder: any;
  handleChange: any;
  Error: any;
  name: any;
  useHandler?: boolean;
  rangeDisabled: boolean;
  localTime: string;
}
export const DatePicker: React.FC<DatePickerProps> = ({
  labelName,
  InputValue,
  placeholder,
  handleChange,
  Error,
  name,
  useHandler,
  rangeDisabled,
  localTime
}) => {
  const yearValues = getYearValues() || new Date().getFullYear();
  return (
    <>
      <IonItem
        mode="ios"
        className={`date-picker-item ${
          Error ? "ion-error-field-validation" : "ion-field-validation"
        }`}
        lines={name === "dropdate" && useHandler ? "none" : "full"}
      >
        <IonLabel mode="ios" className="date-picker-label">
          {labelName}
        </IonLabel>
        <IonDatetime
          displayFormat="MMM DD, YYYY"
          min={localTime && localTime.split("T")[0]}
          yearValues={yearValues}
          name={name}
          value={(InputValue && InputValue.split("T")[0]) || ""}
          disabled={rangeDisabled}
          onIonChange={handleChange}
          mode="ios"
          placeholder={placeholder}
        ></IonDatetime>
      </IonItem>
      <div className="pickupdate-error">
        <p>{Error}</p>
      </div>
    </>
  );
};
