import * as Constants from "../../utils/constants";
export const upadateShowToaster = () => {
  return {
    type: Constants.SHOW_TOASTER_VALUE,
  };
};
export const updateData = () => ({
  type: Constants.UPDATE_DATA,
});

export const loadData = (data: any) => ({
  type: Constants.LOAD_DATA,
  payload: data,
});

export const loadCarrierData = (data: any, count: number) => ({
  type: Constants.LOAD_CARRIER_DATA,
  payload: { data, count },
});
export const carrierAssignedLoads = (carierId: string) => ({
  type: Constants.FETCH_CARRIER_ASSIGNED_LOADS,
  payload: carierId,
});
export const AssignDriver = (requestDriverList: any) => ({
  type: Constants.ASSIGN_DRIVER,
  payload: requestDriverList,
});

export const SelectDriverList = (data: any) => ({
  type: Constants.ASSIGN_DRIVER_LIST,
  payload: data,
});

export const addDriverSuccess = (payload: any) => ({
  type: Constants.ADD_DRIVER_SUCCESS,
  payload,
});

export function assignDriverToShipment(payload: any) {
  return {
    type: Constants.CarrierActionTypes.ASSIGN_DRIVER_TO_SHIPMENT,
    payload,
  };
}

export function assignDriverToShipmentResponse(payload: any) {
  return {
    type: Constants.CarrierActionTypes.ASSIGN_DRIVER_TO_SHIPMENT_RESPONSE,
    payload,
  };
}

export const driverAssignedLoads = (carierId: string) => ({
  type: Constants.FETCH_DRIVER_ASSIGNED_LOADS,
  payload: carierId,
});

export const fetchMyLoads = (
  carrier: any,
  driver: any,
  hideLoader = false
) => ({
  type: Constants.FETCH_MYLOADS,
  payload: { isCarrier: carrier, isDriver: driver, hideLoader },
});

export const loadAssignedrData = (data: any, count: number) => ({
  type: Constants.DRIVER_ASSIGNED_DATA,
  payload: { data, count },
});
export function clearAssignDriverToShipmentResponse() {
  return {
    type: Constants.CarrierActionTypes.CLEAR_ASSIGN_DRIVER_TO_SHIPMENT_RESPONSE,
  };
}

export const driverAssignedLoadsFromPush = (data: any) => ({
  type: Constants.FETCH_DRIVER_ASSIGNED_LOADS_FROM_PUSH,
  payload: data,
});

export const fetchShipments = (query: any, isHideLoader = false, callBack = () => {}) => ({
  type: Constants.SHIPMENT_LOADS,
  payload: { query, isHideLoader, callBack },
});

export const inTransitData = (data: any, count: number) => ({
  type: Constants.LOAD_INTRANSIT_DATA,
  payload: { data, count },
});

export const shipmentForStartTrip = (data: any) => {
  return {
    type: Constants.CarrierActionTypes.SHIPMENT_FOR_STARTTRIP,
    payload: data,
  };
};

export const shipmentForStartTripTimestamp = (timestamp: string) => {
  return {
    type: Constants.CarrierActionTypes.SHIPMENT_FOR_STARTTRIP_TIMESTAMP,
    payload: timestamp
  };
};

export const startTrip = (data: any) => ({
  type: Constants.CarrierActionTypes.START_TRIP,
  payload: data,
});

export const startTripInformationResponse = (data: any) => ({
  type: Constants.CarrierActionTypes.START_TRIP_INFORMATION_RESPONSE,
  payload: data,
});

export const completeTripApiRequest = (data: any, shipmentId: string) => ({
  type: Constants.CarrierActionTypes.COMPLETE_TRIP_API_REQUEST,
  payload: {
    data,
    shipmentId,
  },
});

export const completeTripApiResponse = (data: any) => ({
  type: Constants.CarrierActionTypes.COMPLETE_TRIP_API_RESPONSE,
  payload: data,
});
export const deliveredData = (data: any, count: number) => ({
  type: Constants.LOAD_DELIVERED_DATA,
  payload: {
    data,
    count,
  },
});
