import React, { useState, useEffect, useCallback } from "react";
import {
  IonItem,
  IonLabel,
  IonInput,
  IonImg,
  IonRow,
  IonCol,
  IonGrid,
  isPlatform,
} from "@ionic/react";
import { CityError } from "../../../utils/constants";
import { GPSWrapper, AzureMapsWrapper } from "../../../utils/common";
import _ from "lodash";

import "../../../pages/Search/New/NewPage.scss";
export interface NewInputProps {
  disabled?: boolean;
  currentLocationOff?: boolean;
  onTimeZoneChange?: (timezone: string) => void;
  Error: any;
  labelValue: any;
  InputValue: any;
  handleChange: any;
  InputName: any;
  keyboardshowHandler?: any;
  keyboardhideHandler?: any;
  isDestination: boolean;
  deFaultValue: any;
  useHandler: boolean;
  setErrorValue: any;
  locationValue: boolean;
  getLocalTime?: any;
  placeholder?: string;
  confirmStartLocationInp?: boolean;
  onFocus?: Function;
}
export const NewInput: React.FC<NewInputProps> = ({
  disabled = false,
  currentLocationOff,
  onTimeZoneChange,
  Error,
  labelValue,
  InputValue,
  handleChange,
  InputName,
  getLocalTime,
  keyboardshowHandler,
  locationValue,
  keyboardhideHandler,
  deFaultValue,
  isDestination,
  useHandler,
  setErrorValue,
  placeholder,
  confirmStartLocationInp,
  onFocus
}) => {
  const [options, setOptions] = useState([]);
  const [selectedCity, setSelectedCity] = useState(InputValue);
  const [latLng, onChangeLatLng] = useState<{ lat: string; lng: string }>({
    lat: "",
    lng: "",
  });
  const [showSuggention, setShowSuggention] = useState(false);
  const [edited, setEdited] = useState(false);
  const [palceholder, setPlaceholder] = useState(
    placeholder ? placeholder : ""
  );
  const [currentLocation, setcurrentLocation] = useState({
    cityName: "",
    position: "",
  });
  const [showError, setShowError] = useState(false);
  const [backClicked, setBackClicked] = useState(true);
  const [country, setCountry] = useState("");
  let valueSelected = false;
  const [select, setSelect] = useState(false);
  const [focusOut, setFocusOut] = useState(false);
  const pattern = /^[a-zA-Z0-9, ]*$/;
  useEffect(() => {
    if (isPlatform("capacitor") && !currentLocationOff) {
      GPSWrapper.details.getCurrentPosition((lat: any, long: any) => {
        fetchOriginCity(`${lat},${long}`);
        onChangeLatLng({ lat, lng: long });
      });
    }
    
  }, [currentLocationOff]);

  useEffect(() => {
    if (!isDestination && !edited) {
      setSelectedCity(currentLocation.cityName);
      setPlaceholder("");
      handleChange({
        target: {
          name: InputName,
          value: currentLocation.cityName,
          backClicked,
          lat: latLng.lat,
          lng: latLng.lng,
          country,
        },
      });
    }
  }, [isDestination, edited, currentLocation]);

  useEffect(() => {
    if (!!!locationValue) {
      setSelectedCity("");
    } else {
      setSelect(true);
      setSelectedCity(currentLocation.cityName);
    }
  }, [locationValue]);

  useEffect(() => {
    if (!!!InputValue) {
      setSelectedCity("");
    } else {
      setSelectedCity(InputValue);
    }
  }, [InputValue]);

  useEffect(() => {
    setShowError(Error);
  }, [Error])

  const handleValueChange = (event: any) => {
    const preValue = selectedCity.trim();
    {
      /* Restrict user from entering special charecter*/
    }
    if (!pattern.test(event.target.value) && !select) {
      event.target.value = event.target.value.replace(/[^a-zA-Z0-9 ]*/g, "");
    }
    setSelectedCity(event.target.value);
    setEdited(true);
    if (selectedCity !== event.target.value) {
      const newValue = event.target.value.trim();
      if (preValue !== newValue) {
        callAPI(event.target.value);
      }
    }
    if (edited) {
      setPlaceholder("");
    }
    {
      /* select: flag to identify user's selection status.
       */
    }
    if (select) {
      handleChange({
        target: {
          name: InputName,
          value: event.target.value,
          backClicked,
          lat: latLng.lat,
          lng: latLng.lng,
          country,
        },
      });
    } else {
      handleChange({
        target: {
          name: InputName,
          value: event.target.value,
          backClicked,
          lat: latLng.lat,
          lng: latLng.lng,
          country: "",
        },
      });
      if (onTimeZoneChange) {
        onTimeZoneChange("");
      }
    }

    setSelect(false);
  };
  {
    /* to call azure api for city list need basis*/
  }
  const callAPI = useCallback(
    _.debounce((value: string) => fetchCityList(value), 500),
    []
  );

  const fetchOriginCity = async (value: any) => {
    await AzureMapsWrapper.details.searchCurrentAddress(
      value,
      (addresses: any) => {
        const city: any = {};
        addresses.forEach(async (data: any, count: any) => {
          if (data.address.municipality) {
            if (data.address.municipality.indexOf(",") !== -1) {
              const valueArray = data.address.municipality.split(",");
              valueArray.forEach((municipality: any, index: any) => {
                city.cityName =
                  municipality +
                  (data.address.countrySubdivision
                    ? ", " + data.address.countrySubdivision
                    : "");
              });
            } else {
              city.cityName =
                data.address.municipality +
                (data.address.countrySubdivision
                  ? ", " + data.address.countrySubdivision
                  : "");
            }
          }
          if (data.position) {
            await AzureMapsWrapper.details.searchTimezoneByCoordinates(
              data.position,
              (timeZones: any) => {
                const localTime =
                  timeZones[0] &&
                  timeZones[0].ReferenceTime &&
                  (timeZones[0].ReferenceTime.WallTime || "");
                if (onTimeZoneChange) {
                  onTimeZoneChange(
                    timeZones && timeZones.length > 0 && timeZones[0].Id
                  );
                }
                getLocalTime && getLocalTime(localTime);
              }
            );
          }
          if (data.address.countryCode) {
            setCountry(data.address.countryCode);
          }
        });
        setcurrentLocation(city);
      }
    );
  };
  // to remove dulicate values from the given array
  const removeDuplicates = (myArr: any, prop: any) => {
    if (myArr.length) {
      return myArr.filter((obj: any, pos: number, arr: any) => {
        return (
          arr.map((mapObj: any) => mapObj[prop]).indexOf(obj[prop].trim()) ===
          pos
        );
      });
    } else {
      return [];
    }
  };
  const fetchCityList = async (value: any) => {
    if (value.length > 2) {
      await AzureMapsWrapper.details.searchAddress(value, (addresses: any) => {
        const cityList: any = [];
        addresses.forEach((data: any, count: any) => {
          const city: any = {};
          if (data.address.municipality) {
            if (data.address.municipality.indexOf(",") !== -1) {
              const valueArray = data.address.municipality.split(",");
              valueArray.forEach((municipality: any, index: any) => {
                const cityObj: any = {};
                cityObj.cityName =
                  municipality +
                  (data.address.countrySubdivision
                    ? ", " + data.address.countrySubdivision
                    : "");
                cityObj.cityName = cityObj.cityName.trim();
                if (data.position) {
                  cityObj.position = data.position;
                }
                if (currentLocation.cityName !== cityObj.cityName) {
                  cityList.push(cityObj);
                }
              });
            } else {
              city.cityName =
                data.address.municipality +
                (data.address.countrySubdivision
                  ? ", " + data.address.countrySubdivision
                  : "");
              if (data.position) {
                city.position = data.position;
              }
              if (currentLocation.cityName !== city.cityName) {
                cityList.push(city);
              }
            }
            if (data.address.countryCode) {
              city.country = data.address.countryCode;
            }
          }
        });
        const uniqueValues = removeDuplicates(cityList, "cityName");
        setOptions(uniqueValues);
        setShowSuggention(true);
        if (isDestination && useHandler) {
          keyboardshowHandler();
        }
      });
    } else {
      setShowSuggention(false);
      if (isDestination && useHandler) {
        keyboardhideHandler();
      }
    }
  };
  const selectCity = async (SearchCity: any) => {
    setSelectedCity(SearchCity.cityName);
    setCountry(SearchCity.country);
    setShowSuggention(false);
    setSelect(true);
    if (SearchCity.position) {
      let latlon = "";
      const position = SearchCity.position;
      if (position) {
        latlon = `${position.lat},${position.lon}`;
      }
      onChangeLatLng({ lat: position.lat, lng: position.lon });

      await AzureMapsWrapper.details.searchTimezoneByCoordinates(
        latlon,
        (timeZones: any) => {
          if (onTimeZoneChange) {
            onTimeZoneChange(
              timeZones && timeZones.length > 0 && timeZones[0].Id
            );
          }
          const localTime =
            timeZones[0] &&
            timeZones[0].ReferenceTime &&
            (timeZones[0].ReferenceTime.WallTime || "");
          getLocalTime && getLocalTime(localTime);
        }
      );
    }
    if (isDestination && useHandler) {
      keyboardhideHandler();
    }
  };
  const onFocusOut = () => {
    setFocusOut(true);
    setShowSuggention(false);
    valueSelected = options.some((item: any) => item.cityName === selectedCity);
    if (isDestination && useHandler) {
      keyboardhideHandler();
    }
    if (!valueSelected) {
      if (
        selectedCity === currentLocation.cityName || (isDestination && selectedCity === deFaultValue)
      ) {
        setShowError(false);
        setErrorValue(false, InputName);
      } else {
        setShowError(true);
        setErrorValue(true, InputName);
      }
      if (selectedCity.length === 0) {
        setShowError(true);
        setErrorValue(true, InputName);
      }
    } else {
      setShowError(false);
      setErrorValue(false, InputName);
    }
    setBackClicked(false);
  };

  const originClick = (e: any) => {
    if (focusOut || currentLocation.cityName === selectedCity) {
      setSelectedCity("");
      setEdited(true);
      setBackClicked(true);
      setFocusOut(false);
    }
  };
  const destinationCancelClick = () => {
    setSelectedCity(deFaultValue);
  }

  const handleFocus = () => {
    onFocus && onFocus();
  }
  return (
    <>
      <IonItem
        mode="ios"
        className={`typeahead-item ${
          Error || showError
            ? "ion-error-field-validation"
            : "ion-field-validation"
          }`}
      >
        {labelValue.length > 0 ? (
          <IonLabel mode="ios" position="floating">
            {labelValue}
          </IonLabel>
        ) : null}

        <IonInput
          type="text"
          disabled={disabled}
          className={"cts-form-control"}
          name={InputName}
          onIonChange={(event) => handleValueChange(event)}
          value={selectedCity}
          placeholder={palceholder}
          onIonFocus={handleFocus}
          onIonBlur={() => onFocusOut()}
          onClick={(event: any) => originClick(event)}
        >
          <IonImg
            slot="end"
            alt="logo"
            src="/assets/icon/search_icon.svg"
            className="origin-input_icon"
          />
        </IonInput>
        {isDestination && selectedCity !== '' ? <IonImg
          slot="end"
          alt="logo"
          src="/assets/icon/cancel.svg"
          className="destination-input_icon"
          onClick = {() => destinationCancelClick()}
        /> : null}
      </IonItem>
      {
        <div className="pickupdate-error">
          <p>
            {Error} {Error ? "" : showError ? CityError : ""}
          </p>
        </div>
      }
      {showSuggention ? (
        <div
          className={` ${
            locationValue ? "suggestion-startTrip" : "suggestions_div "
            }`}
        >
          <IonRow className="suggestions_row">
            {!locationValue ? (
              <IonCol class="icon-col">
                {currentLocation.cityName ? (
                  <IonImg
                    alt="logo"
                    src="/assets/icon/position.svg"
                    className="position_icon"
                  />
                ) : null}
              </IonCol>
            ) : null}
            <IonCol class="suggestion-col">
              {currentLocation.cityName ? (
                <IonRow
                  className={` ${
                    locationValue ? "current-location2" : "current-location "
                    }`}
                  onMouseDown={() =>
                    currentLocation &&
                    selectCity({ cityName: currentLocation.cityName })
                  }
                >
                  <IonCol className="currentLocation-col">
                    {!locationValue ? (
                      <IonLabel position="floating">Current Location</IonLabel>
                    ) : null}
                    <IonInput
                      className={`${
                        locationValue ? "suggestion-border" : "cts-form-control"
                        }`}
                      type="text"
                      value={
                        locationValue ? InputValue : currentLocation.cityName
                      }
                      readonly
                    />
                  </IonCol>
                </IonRow>
              ) : null}
              {isDestination ? (
                <IonRow
                  className="suggestions_item"
                  onMouseDown={() => selectCity({ cityName: deFaultValue })}
                >
                  <IonCol class="suggestions_list">{deFaultValue}</IonCol>
                </IonRow>
              ) : (
                  ""
                )}
              {options.length ? (
                <IonGrid
                  class={`suggestions ${
                    isDestination ? "destination_suggestions" : ""
                    } ${locationValue ? "suggestions-pad" : ""}`}
                >
                  {options.map((SearchCity: any, index: number) => (
                    <IonRow
                      className="suggestions_item"
                      key={index}
                      onMouseDown={() => selectCity(SearchCity)}
                    >
                      <IonCol class="suggestions_list">
                        {SearchCity.cityName}
                      </IonCol>
                    </IonRow>
                  ))}
                </IonGrid>
              ) : (
                  <div className="pickupdate-error">
                    <p>{locationValue ? "" : CityError}</p>
                  </div>
                )}
            </IonCol>
          </IonRow>
        </div>
      ) : null}
    </>
  );
};
