import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { getLoading, getUserDetails, getNetworkStatus } from '../../../selectors/selector';
import { ManageUserProfileFormPage } from '../../../pages/Manage/ManageUser';
import { showNetworkAlert } from '../../../actions/App/action';
import { editUserDetails, removeUserDetails } from '../../../actions/manage/action';
import { handleDelink, handleUpdateSubmit } from '../ManageUserContainer/ManageUserUtil';
import { useAuthService } from '../../../services/Auth';

export const ProfileEditContainer: React.FC<any> = (props: any) => {
  const roles =
    props.userDetails &&
    props.userDetails.userApplicationDetails &&
    props.userDetails.userApplicationDetails
      .reduce((current: any, item: any) => {
        return [...current, ...item.roles];
      }, [])
      .map((role: any) => role.roleName);
  const userProfile = { ...props.userDetails, roles };

  const {
    history: { push }
  } = props;

  const selectedUser = {
    ...userProfile,
    defaultMobileNumber: !userProfile.defaultMobileNumber
      ? userProfile.mobileNumber
      : userProfile.defaultMobileNumber
  };

  const authService = useAuthService();

  return (
    <ManageUserProfileFormPage
      {...props}
      selectedUser={selectedUser}
      isEditable={false}
      module="manage/ProfileDetails"
      handleSubmit={handleUpdateSubmit(props)}
      handleDelink={handleDelink({...props, authService }, true)}
      isSelf={true}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  updateUserAction: (payload: any) => dispatch(editUserDetails(payload)),
  delinkUserAction: (payload: any) => dispatch(removeUserDetails(payload))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps)(ProfileEditContainer);

export default withConnect;
