import React, { useState, useEffect, useCallback } from 'react';
import { IonPage, IonContent, IonList, IonInput, IonItem, IonLabel, IonFooter, IonButton, IonIcon, IonText, IonImg, IonGrid, IonCol, IonRow, isPlatform } from '@ionic/react';
import "./ManageUser.page.scss";
import AppHeader from '../../../components/app/Bars/Bar-header';
import { USER_BACK_TITLE, USER_BACK_MESSAGE, Roles, USER_FORM_ERRORS, DELINK_ACCOUNT_TITLE, DELINK_ACCOUNT_MESSAGE, NO, YES, EDIT_USER, ADD_USER, OOPS_MESSAGE, EMAIL_ERROR, USER_EXIST_MESSAGE, USER_EXIST_TITLE, TRACTOR_NUMBER, EDIT_PROFILE } from '../../../utils/constants';
import { CustomModal } from '../../../components/CustomModal';
import { IRole, IProfile, IAddress, ITransferRequest } from '../../../schemas/manage/manage.Schema';
import { fetchAddressList, handlePhoneNumber, formatPhoneNumber, removeSpecial, roles, defaultForm, getSelectedRoles, userProfileFields, driverFields, getAddressValue, carrierField, handleAlphaNumeric, numberOnlyValidation, isValidString } from './ManageUserUtil';
import _ from "lodash";
import { EnumType } from 'json-to-graphql-query';
import { showAlert } from '../../../actions/App/action';
import ManageUserProfileFormDesktopPage from './ManageUserProfileFormDesktop.page';
import { UserDetails } from 'app/models/userPforile/userProfile.modal';
import { ToastMessage } from 'app/utils/ToastMessage';
import ManageProfileDesktopPage from './ManageProfileDesktop.page';

const ADD_BACK_PATH = '/app/manage/ManageUser';
const EDIT_BACK_PATH = '/app/manage/ManageUser/Profile';
const BACK_TO_PROFILE = '/app/manage/ProfileDetails';

const ManageUserProfileFormPage: React.FC<any> = (props) => {
	const { userDetails: { carrierId, userPrincipalName }, isSelfDesktop = false, isSelf = false, selectedUser = null, validateUserDetails= null, isUserValid = null, resetValidateUser = null, history, isNetworkAvailable, showNetworkAlert } = props;
	const [roleSelection, setRoleSelection] = useState(roles);
	const [emailAddressError, setEmailAddressError] = useState(false);
	const [isUserNotExist, setUserNotExist] = useState(false);
	const [showRoleOptions, setShowRoleOptions] = useState(false);
	const [isDriverSelected, setDriverSelected] = useState(true);
	const [alertProps, setAlertProps] = useState<any>(null);
	const roleTitle = roleSelection.filter(role => role.selected).map(role => role.label).join(', ');
	const roleDesc = roleSelection.filter(role => role.selected).length === 1 ? (roleSelection.find(role => role.selected) as IRole).desc : roleSelection[1].desc;
	const [formFields, setFormFields] = useState<any>({ ...defaultForm, roles: getSelectedRoles(roles), carrierId });
	const [addressList, setAddressList] = useState<Array<IAddress>>([]);
	const [showAddressList, setShowAddressList] = useState(false);
	const [isAddressEdit, setAddressEdit] = useState(true);
	const [formErrors, setFormErrors] = useState<any>({
		firstName: '',
		lastName: '',
		mobileNumber: '',
		tractorNumber: false,
	});
	const [isFormEdit, setFormEdit] = useState(false);
	const [isFormValid, setFormValid] = useState(false);
	const [address, setAddress] = useState('');
	const [isUserRoleAdmin, setUserRoleAdmin] = useState(false);


	const formatRoles = (selectedRoles:any) => {
		const isCarrierAdmin = selectedUser.roles.indexOf('carrierAdmin') !== -1;
		const defaultRoles = selectedUser.roles.map((role:string) => new EnumType(role));
		const finalRoles = getSelectedRoles(selectedRoles);
		return isCarrierAdmin ? [new EnumType('carrierAdmin'), ...finalRoles] : finalRoles.length > 0 ? finalRoles : defaultRoles;
	}

	const handleRoleSelection = (item: IRole) => {
		const roleSelectionList = roleSelection.map(role => ({ ...role, selected: role.label === item.label ? !role.selected : role.selected }));
		if (isUserRoleAdmin || roleSelectionList.filter(role => role.selected).length > 0) {
			const isDriver = roleSelectionList.find(role => role.selected && role.label === 'Driver');
			setDriverSelected(!!isDriver);
			setRoleSelection(roleSelectionList);
			const roles = selectedUser ? formatRoles(roleSelectionList): getSelectedRoles(roleSelectionList);
			setFormFields({ ...formFields, roles });
			if(selectedUser) {
				setFormEdit(true);
			}
		}
	}

	const resetData = () => {
		setRoleSelection(roles);
		setAlertProps(null);
		setShowRoleOptions(false);
		setAddress('');
		setFormValid(false);
		if(!selectedUser) {
			setUserNotExist(false);
			setFormErrors({
				firstName: '',
				lastName: '',
				mobileNumber: ''
			});
			resetValidateUser();
			setFormFields({...defaultForm, roles: getSelectedRoles(roles), carrierId});
		}
	}

	const validateErrors = () => {
		const errors = Object.keys(formErrors).filter(key => formErrors[key] !== false);
		const isManadatoryEmpty = Object.keys(formErrors).filter(key => formFields[key] === '' && key !== 'tractorNumber');
		if (errors.length > 0 || isManadatoryEmpty.length > 0) {
			let errorsText = formErrors;
			errors.forEach((key: string) => {
				errorsText[key] = USER_FORM_ERRORS[key];
			})
			isManadatoryEmpty.forEach((key: string) => {
				errorsText[key] = USER_FORM_ERRORS[key];
			})
			setFormErrors(errorsText);
			setFormFields({ ...formFields });
			return true;
		}
		return false;
	}

	const getFormatedFormFields = (selectedUser:any) => ({
		userId: selectedUser.userPrincipalName,
		roles: [],
		firstName: selectedUser.firstName,
		lastName: selectedUser.lastName,
		email: selectedUser.email,
		mobileNumber: formatPhoneNumber(selectedUser.defaultMobileNumber, true),
		tractorNumber: selectedUser.tractorNumber,
		addressDTOs:  selectedUser.address && selectedUser.address.length > 0  ? selectedUser.address[0]: {
			addressLine1: "",
			addressLine2: "",
			addressType: "",
			city: "",
			state: "",
			postalCode: "",
		},
		carrierId: selectedUser.carrierId,
	});

	useEffect(() => {
		if(isUserValid !== null) {
			setUserNotExist(!isUserValid);
			handleUserExistModal(isUserValid);
		}
	}, [isUserValid]);

	useEffect(() => {
		if(selectedUser) {
			const isCarrierAdmin = selectedUser.roles.indexOf('carrierAdmin') !== -1;
			const selectedRoles = roles.map((role:IRole) => ({...role, selected: selectedUser.roles.indexOf(role.value.toString()) !== -1}));
			const isDriver = selectedRoles.find(role => role.selected && role.label === 'Driver');
			setUserRoleAdmin(isCarrierAdmin);
			setRoleSelection(selectedRoles);
			setDriverSelected(!!isDriver);
			setAddressEdit(false);
			const adminRoles = selectedUser.roles.map((role:string) => new EnumType(role));
			const details:any = getFormatedFormFields(selectedUser);
			setFormFields({
				...details,
				roles: formatRoles(selectedRoles)
			});
			Object.keys(formErrors).forEach(key => {
				formErrors[key] = (details[key] && details[key] != '' && key !== TRACTOR_NUMBER) || key === TRACTOR_NUMBER ? false : '';
			});
			setFormErrors(formErrors);
			setFormEdit(false);
		}
	}, [selectedUser]);

	const transferMember = () => {
		if(!isNetworkAvailable) {
			showNetworkAlert();
			return true;
		}
		const params: ITransferRequest= {
			requester: {
				userId: userPrincipalName,
				carrierId,
				isAdmin: true
			},
			userId: validateUserDetails.userPrincipalName,
			currentCarrierId:  validateUserDetails.carrierId,
			requestedCarrierId: carrierId,
			roles: formFields.roles
		}
		const payload =  { name: validateUserDetails && validateUserDetails.firstName, params }
		props.handleTransfer(payload, resetData, carrierId === validateUserDetails.carrierId); 
	}

	const handleUserExistModal = (showUserModal: boolean) => {
		setAlertProps({
			isOpen: showUserModal,
			title: USER_EXIST_TITLE,
			message: USER_EXIST_MESSAGE.replace('{{EMAIL}}', validateUserDetails && validateUserDetails.userPrincipalName).replace('{{NAME}}', validateUserDetails && validateUserDetails.firstName),
			buttons: [
				{
					label: NO,
					callback: () => {
						setFormFields({...formFields, email: ''});
						setAlertProps(null);
						props.resetValidateUser();
						history.push(ADD_BACK_PATH);
					} 
				},
				{
					label: YES,
					callback: () => {
						transferMember();
					}
				}
			]
		});
	}

	const getBackPath = (path = '') => {
		history.push(path ? path: isSelf ? BACK_TO_PROFILE : (selectedUser ? EDIT_BACK_PATH : ADD_BACK_PATH));
		return true;
	}

	const handleBackButton = (path = '') => {
		if (!isFormEdit) {
			return getBackPath(path);
		}
		setAlertProps({
			isOpen: true,
			title: USER_BACK_TITLE,
			message: USER_BACK_MESSAGE,
			buttons: [
				{
					label: 'Cancel',
					callback: () => {
						setAlertProps(null);
					}
				},
				{
					label: 'Ok',
					callback: () => {
						resetData();
						setFormEdit(false);
						getBackPath();
					}
				}
			]
		});
	}

	const handleErrors = (name: string, value: string) => {
		let errorsText:any = {...formErrors};
		switch (name) {
			case "firstName":
			case "lastName":
				const isValid = value !== '' && isValidString(value);
				errorsText = ({...formErrors, [name]: !isValid? USER_FORM_ERRORS[name]: false })
				break;
			case "tractorNumber":
				const isTractorValid = value === '' || (value !== '' && isValidString(value));
				errorsText = ({...formErrors, [name]: !isTractorValid? USER_FORM_ERRORS[name]: false })
				break;
			case "mobileNumber":
				const isMobValid = handlePhoneNumber(value);
				errorsText = ({...formErrors, [name]: !isMobValid? USER_FORM_ERRORS[name]: false })
				break;
			default:
				break;
		} 
		const errors = Object.keys(errorsText).filter(key => errorsText[key] !== false);
		setFormValid(errors.length === 0);
		setFormErrors(errorsText);
	}

	const handleAddress = (list: Array<IAddress>) => {
		setAddressList(list);
		setShowAddressList(true);
	}

	const callAPI = useCallback(_.debounce((value: string) => fetchAddressList(value, handleAddress), 500), []);

	const handleChange = (event: any) => {
		const { target: { name, value } } = event;
		if(name !== 'emailAddress') {
			handleErrors(name, value);
			setFormEdit(true);
		}
		switch (name) {
			case "emailAddress": 
				setEmailAddressError(false);
				setFormFields({...formFields, email: value});
				break;
			case "mobileNumber":
				setFormFields({ ...formFields, mobileNumber: handlePhoneNumber(value) ? formatPhoneNumber(value, true) : value});
				break;
			case "addressDTOs":
				if (isAddressEdit) {
					callAPI(value);
					setAddress(value);
				}
				break;
			default:
				setFormFields({ ...formFields, [name]: value});
				break;
		}
	}

	const handleAddressSelection = (addressDTOs: IAddress) => {
		setAddressEdit(false);
		setFormFields({ ...formFields, addressDTOs });
		setFormValid(false);
		setShowAddressList(false);
		setAddressList([]);
	}

	const validateNow = () => {
		if(!isNetworkAvailable) {
			showNetworkAlert();
			return true;
		}
		const { email: emailAddress } = formFields;
		const regEx = /^[a-zA-Z0-9.]+@[a-zA-Z0-9]+\.[A-Za-z]+$/;
		if(showRoleOptions) {
			setShowRoleOptions(false);
		}
		if (regEx.test(emailAddress)) {
			const callback = () => ToastMessage.show(OOPS_MESSAGE);
			const useProfileModel = new UserDetails();
			const query = useProfileModel.validateUser(emailAddress);
			props.validateUserAction({query, callback});
			return true;
		} 
		setEmailAddressError(true);
	};

	const handleSubmit = () => {
		if (!isNetworkAvailable) {
			showNetworkAlert();
			return true;
		}

		if(validateErrors()) {
			return true;
		}

		const payload = {
			requester: {
				userId: userPrincipalName,
				carrierId,
				isAdmin: true,
			},
			userDetails: { ...formFields, mobileNumber: removeSpecial(formFields.mobileNumber) }
		}

		props.handleSubmit(payload, resetData);
	}



	const handleDelinkClick = () => {
		if(!isNetworkAvailable) {
			showNetworkAlert();
			return true;
		}
		setAlertProps({
			isOpen: true,
			title: DELINK_ACCOUNT_TITLE,
			message: DELINK_ACCOUNT_MESSAGE,
			buttons: [
				{
					label: NO,
					callback: () => setAlertProps(null)
				},
				{
					label: YES,
					callback: () => {
						const fields:any = getFormatedFormFields(selectedUser);
						const payload = {
							requester: {
								userId: userPrincipalName,
								carrierId,
								isAdmin: true,
							},
							userDetails: { ...fields, roles: [new EnumType('carrierGuest')], mobileNumber: fields.mobileNumber && removeSpecial(fields.mobileNumber), carrierId: '' }
						};
						setAlertProps(null);
						props.handleDelink(payload);
					}
				}
			]
		})
	}

	return (
		<>
			{ isSelfDesktop ? (
				<ManageProfileDesktopPage 
					{...props} 
					title={ selectedUser ? isSelf ? EDIT_PROFILE : EDIT_USER : ADD_USER}
					isEdit={ selectedUser ? true : false}
					isUserRoleAdmin={isUserRoleAdmin}
					handleBackButton={handleBackButton}
					handleRoleSelection={handleRoleSelection}
					validateNow={validateNow}
					emailAddressError={emailAddressError}
					isUserNotExist={isUserNotExist}
					isFormEdit={isFormEdit}
					roleTitle={roleTitle}
					roleDesc={roleDesc}
					roleSelection={roleSelection}
					showRoleOptions={showRoleOptions}
					setShowRoleOptions={setShowRoleOptions}
					formFields={formFields}
					formErrors={formErrors}
					setFormFields={setFormFields}
					handleChange={handleChange}
					isDriverSelected={isDriverSelected}
					alertProps={alertProps}
					setAddressEdit={setAddressEdit}
					showAddressList={showAddressList}
					addressList={addressList}
					handleAddressSelection={handleAddressSelection}
					isAddressEdit={isAddressEdit}
					address={address}
					removeSpecial={removeSpecial}
					getAddressValue={getAddressValue}
					handleSubmit={handleSubmit}
					handleDelinkClick={handleDelinkClick}
					resetData={resetData}
				/>
			) : isPlatform("desktop") ? 
				<ManageUserProfileFormDesktopPage 
					{...props} 
					title={ selectedUser ? isSelf ? EDIT_PROFILE : EDIT_USER : ADD_USER}
					isEdit={ selectedUser ? true : false}
					isUserRoleAdmin={isUserRoleAdmin}
					handleBackButton={handleBackButton}
					handleRoleSelection={handleRoleSelection}
					validateNow={validateNow}
					emailAddressError={emailAddressError}
					isUserNotExist={isUserNotExist}
					roleTitle={roleTitle}
					roleDesc={roleDesc}
					roleSelection={roleSelection}
					showRoleOptions={showRoleOptions}
					setShowRoleOptions={setShowRoleOptions}
					formFields={formFields}
					formErrors={formErrors}
					setFormFields={setFormFields}
					handleChange={handleChange}
					isDriverSelected={isDriverSelected}
					alertProps={alertProps}
					setAddressEdit={setAddressEdit}
					showAddressList={showAddressList}
					addressList={addressList}
					handleAddressSelection={handleAddressSelection}
					isAddressEdit={isAddressEdit}
					address={address}
					removeSpecial={removeSpecial}
					getAddressValue={getAddressValue}
					handleSubmit={handleSubmit}
					handleDelinkClick={handleDelinkClick}
				/> 
			: (
				<IonPage className="manage-userslist-page">
					<AppHeader title={ selectedUser ? isSelf ? EDIT_PROFILE : EDIT_USER : ADD_USER} customBackUrl={handleBackButton} History={props.history} />
					<IonContent class="ion-padding  custom-padding">
						<IonContent class="ion-padding  custom-padding">
							<div className="manage-users-formWrapper">
								{
									!isSelf ? (
										<IonList class="manage-userslist-form">
										<IonItem className="role-info" data-test="select-role" onClick={() => setShowRoleOptions(!showRoleOptions)}>
											<IonLabel position="floating" mode="ios">Role</IonLabel>
											<IonInput
												value={isUserRoleAdmin ? `Admin${roleTitle.length > 0 ? `, ${roleTitle}`: ''}` : (roleTitle || 'Guest')}
												readonly
												type="text"
												class=""
											></IonInput>
											<IonIcon src="/assets/icon/grid-down-arrow.svg"></IonIcon>
											
										</IonItem>
										<IonText class="role-desc">{roleDesc}</IonText>
										{roleSelection && showRoleOptions ? (
											<IonList data-test="roles-list" class="roles-list-grid static">
												{roleSelection.map((role: IRole, index: number) => (
													<IonItem
														key={`role_${index}`}
														data-test="role-list-item"
														onClick={() => handleRoleSelection(role)}
													>
														<i className={`role-check-icon ${role.selected ? 'checked' : ''}`}></i>
														<span className="role-list-label">{role.label}</span>
													</IonItem>
												))}
											</IonList>
										) : null}
										{
											!selectedUser ? (
												<>
													<IonItem className={emailAddressError? 'email-field error': 'email-field '}>
														<IonLabel position="floating" mode="ios">Email Address</IonLabel>
														<IonInput
															value={formFields.email}
															placeholder='Type Your Email Address'
															type="email"
															readonly={isUserNotExist}
															name="emailAddress"
															onIonChange={handleChange}
															data-test="email-field"
															pattern='/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/'
														></IonInput>
													</IonItem>
													{ emailAddressError? (<IonText class="field-error" data-test="email-field-error">{EMAIL_ERROR}</IonText>) : null }
													{
														!isUserNotExist ? (
															<IonItem className="validate-btn-wrapper">
																<IonButton className={'validate-btn'} data-test="validate-btn" onClick={validateNow}>
																	Validate now
																</IonButton>
															</IonItem>
														) : null
													}
												</>
											): null
										}
									</IonList>
	
									) : null
								}
								{
									isUserNotExist || selectedUser ? (
										<IonList class="manage-userslist-form">
										{
											userProfileFields.map((profile: IProfile) => (
												<React.Fragment key={`def_${profile.label}`}>
													<IonItem className={formErrors && formErrors[profile.name] ? 'error' : ''}>
														<IonLabel position="floating" mode="ios">{profile.label}</IonLabel>
														{
															profile.name === 'mobileNumber' ? (
																<IonInput
																	value={formFields[profile.name]}
																	name={profile.name}
																	placeholder={profile.placeholder}
																	pattern="[0-9a-zA-Z]"
																	type="text"
																	maxlength={10}
																	onFocus={() => setFormFields({ ...formFields, mobileNumber: formFields.mobileNumber && removeSpecial(formFields.mobileNumber) })}
																	onIonBlur={handleChange}
																	data-test={`${profile.name}-field`}
																	className={`${profile.className ? profile.className : ''}`}
																></IonInput>
															) : (
																	<IonInput
																		value={formFields[profile.name]}
																		name={profile.name}
																		placeholder={profile.placeholder}
																		pattern="[0-9a-zA-Z]"
																		type="text"
																		data-test={`${profile.name}-field`}
																		onIonChange={handleChange}
																		className={`${profile.className ? profile.className : ''}`}
																	></IonInput>
																)
														}
													</IonItem>
													{formErrors && formErrors[profile.name] ? <IonText class="field-error">{formErrors[profile.name]}</IonText> : null}
												</React.Fragment >
											))
										}
										{
											selectedUser ? (
												<>
													<IonItem>
														<IonLabel position="floating" mode="ios">Email Address</IonLabel>
														<IonInput
															value={formFields.email}
															placeholder='Type Your Email Address'
															type="email"
															readonly
															name="emailAddress"
															data-test={`email-field`}
														></IonInput>
													</IonItem>
													{
														isSelf ? (
															<>
															<IonItem className="role-info" data-test="select-role" onClick={() => setShowRoleOptions(!showRoleOptions)}>
																<IonLabel position="floating" mode="ios">Role</IonLabel>
																<IonInput
																	value={isUserRoleAdmin ? `Admin${roleTitle.length > 0 ? `, ${roleTitle}`: ''}` : (roleTitle || 'Guest')}
																	readonly
																	type="text"
																	class=""
																></IonInput>
																{ isUserRoleAdmin ? <IonIcon src="/assets/icon/grid-down-arrow.svg"></IonIcon> : null}
																
															</IonItem>
															{ isUserRoleAdmin ?<IonText class="role-desc">{roleDesc}</IonText> : null}
															{isUserRoleAdmin && roleSelection && showRoleOptions ? (
																<IonList data-test="roles-list" class="roles-list-grid static">
																	{roleSelection.map((role: IRole, index: number) => (
																		<IonItem
																			key={`role_${index}`}
																			data-test="role-list-item"
																			onClick={() => handleRoleSelection(role)}
																		>
																			<i className={`role-check-icon ${role.selected ? 'checked' : ''}`}></i>
																			<span className="role-list-label">{role.label}</span>
																		</IonItem>
																	))}
																</IonList>
															) : null}
															</>
														): null
													}
												</>
											): null
										}
										{
											isDriverSelected && driverFields.map((profile: IProfile) => (
												<React.Fragment key={profile.label}>
													<IonItem>
														<IonLabel position="floating" mode="ios">{profile.label}</IonLabel>
														<IonInput
															value={profile.name === 'addressDTOs' ? isAddressEdit ? address : getAddressValue(formFields[profile.name]) : formFields[profile.name]}
															name={profile.name}
															placeholder={profile.placeholder}
															type="text"
															pattern="[0-9a-zA-Z]"
															onIonChange={handleChange}
															data-test={`${profile.name}-field`}
															onIonFocus={() => {
																if (profile.name === 'addressDTOs') {
																	setAddressEdit(true);
																}
															}}
															className={`${profile.className ? profile.className : ''}`}
														>
															{profile.name === 'addressDTOs' ? (
																<IonImg
																	slot="end"
																	alt="logo"
																	src="/assets/icon/search_icon.svg"
																	className="search-input-icon"
																/>
															) : null}
														</IonInput>
													</IonItem>
													{ formErrors && formErrors[profile.name]? <IonText class="field-error">{formErrors[profile.name]}</IonText>: null }
													{profile.name === 'addressDTOs' && showAddressList ? (
														<IonGrid class="suggestions">
															{
																addressList.map((address: IAddress, index:number) => (
																	<IonRow key={`addr${index}`}className="suggestions_item" onClick={() => handleAddressSelection(address)}>
																		<IonCol class="suggestions_list">
																			{getAddressValue(address)}
																		</IonCol>
																	</IonRow>
																))
															}
														</IonGrid>
													) : null}
												</React.Fragment>
											))
										}
										<IonItem >
											<IonLabel position="floating" mode="ios">{carrierField.label}</IonLabel>
											<IonInput
												value={carrierId}
												name={carrierField.name}
												placeholder={carrierField.placeholder}
												type="text"
												className={`${carrierField.className ? carrierField.className : ''}`}
												readonly
											></IonInput>
											{
												selectedUser ? (
													<img
														slot="end"
														alt="delink"
														src="/assets/icon/delink.svg"
														className="delink"
														data-test="delink-btn"
														onClick={handleDelinkClick}
													/>
												) : null
											}
											
										</IonItem>
									</IonList>
									): null
								}
							</div>
						</IonContent>
						<IonFooter class="manage-userlist-footer">
							<IonButton
								expand="full"
								onClick={handleSubmit}
								data-test="submit-btn"
								className={ !isFormValid ? 'invalid-btn' : ''}
							> Save
							</IonButton>
						</IonFooter>
					</IonContent>
					{
						alertProps ? <CustomModal {...alertProps} /> : null
					}
		
				</IonPage>
			)
			}
		</>
	);
}
export default ManageUserProfileFormPage;