import React from 'react';
import ReactHtmlParser from 'react-html-parser';

export class HtmlParseComponent extends React.Component<any, any> {
    render() {
        const richText = this.props.richText;
        return <div className= {this.props.isRichText ? "htmlParseDiv" : "parsingDiv"}>{ReactHtmlParser(richText)}</div>;
    }
}
export default HtmlParseComponent;