import React from 'react';
import {
  IonCard,
  IonCardContent,
  IonCol,
  IonRow,
  IonButton,
  IonImg,
  isPlatform
} from '@ionic/react';

export const DriverCard = (props: any) => {
  const { DriverList, showAssignDriverDialog } = props;

  return (
    <div>
      {DriverList &&
        DriverList.map((driver: any, index: any) => (
          <IonCard
            className="driver-card"
            key={index}
            onClick={() => {
              if (showAssignDriverDialog && !isPlatform('desktop')) {
                showAssignDriverDialog(true, driver);
              }
            }}>
            <IonCardContent className="driver-card-content">
              <IonRow>
                <IonCol
                  className="card-price-col DriverImageCol driverCol"
                  size={isPlatform('desktop') ? '1.5' : '4'}>
                  {driver.DriverImg ? (
                    <img src={driver.DriverImg} alt="" className="DriverImage" />
                  ) : (
                    <div className="driver-img-div">
                      <span className="child-img">{`${
                        !!driver.firstName ? driver.firstName[0].toUpperCase() : ''
                      }`}</span>
                      <span className="child-img">{`${
                        !!driver.lastName ? driver.lastName[0].toUpperCase() : ''
                      }`}</span>
                    </div>
                  )}
                </IonCol>
                <IonCol className="card-city-col" size={isPlatform('desktop') ? '7' : '8'}>
                  {driver.firstName &&
                  driver.firstName.length &&
                  driver.lastName &&
                  driver.lastName.length > 0 ? (
                      <p className="driver-name">{`${driver.firstName}  ${driver.lastName}`}</p>
                    ) : (
                      <p>&nbsp;</p>
                    )}
                  <IonRow>
                    <IonCol className="tractorno" size={isPlatform('desktop') ? '2' : '5'}>
                      Tractor No
                    </IonCol>
                    <IonCol className="tractor-number" size={isPlatform('desktop') ? '2' : '7'}>
                      {driver && driver.tractorNumber}
                    </IonCol>
                  </IonRow>
                  <IonRow>
                    <IonCol className="tractorno2" size={isPlatform('desktop') ? '2' : '5'}>
                      Mobile No
                    </IonCol>

                    <IonCol className="tractor-number" size={isPlatform('desktop') ? '3' : '7'}>
                      {driver && driver.mobileNumber}
                    </IonCol>
                  </IonRow>
                </IonCol>
                <IonCol size={isPlatform('desktop') ? '1' : '0'} />

                <IonCol size="2.5" className="Assign-btn-view">
                  <IonButton
                    class="load-btn assign-btn"
                    onClick={() => {
                      if (showAssignDriverDialog) {
                        showAssignDriverDialog(true, driver);
                      }
                    }}>
                    <IonImg className="addUser_img" src="assets/icon/Assign Driver.svg" />
                    Assign
                  </IonButton>
                </IonCol>
              </IonRow>
            </IonCardContent>
          </IonCard>
        ))}
      <p className="end_of_list">End of list</p>
    </div>
  );
};
