import { IDeviceInput } from "../../schemas/App/App.Schema";

export class DeviceRegister {

    private __device_register_state: IDeviceInput = {

        uuId: "",
        upn: "",
        carrierId: "",
        deviceToken: "",
        email: "",
        phone: ""
    };

    constructor() {
    }
    get uuid() {
        return this.__device_register_state.uuId;
    }
    set uuid(value) {
        this.__device_register_state.uuId = value;
    }

    get upn() {
        return this.__device_register_state.upn;
    }
    set upn(value) {
        this.__device_register_state.upn = value;
    }

    get carrierId() {
        return this.__device_register_state.carrierId;
    }
    set carrierId(value) {
        this.__device_register_state.carrierId = value;
    }
    get deviceToken() {
        return this.__device_register_state.deviceToken;
    }
    set deviceToken(value) {
        this.__device_register_state.deviceToken = value;
    }
    get email() {
        return this.__device_register_state.email;
    }
    set email(value) {
        this.__device_register_state.email = value;
    }

    get phone() {
        return this.__device_register_state.phone;
    }
    set phone(value) {
        this.__device_register_state.phone = value;
    }

    get deviceOs() {
        return this.__device_register_state.deviceOs;
    }
    set deviceOs(value) {
        this.__device_register_state.deviceOs = value;
    }
    get deviceOsVersion() {
        return this.__device_register_state.deviceOsVersion;
    }
    set deviceOsVersion(value) {
        this.__device_register_state.deviceOsVersion = value;
    }



    getRequestPayload() {
        return {
            mutation: {
                registerDevice: {
                    __args: {
                        input: this.__device_register_state
                    }
                }
            }
        };
    }


}