import { ManageState } from "../../schemas/manage/manage.Schema";
import * as Constants from "../../utils/constants";
import produce from "immer";

export const initialState: ManageState = {
  profileImg: [],
  manageCardData: [],
  users: {
    data: [],
    loaded: false,
    selectedProfile: null,
    filter: {
      selected: [],
      direction: 'desc'
    },
    validateUser: {
      isUserValid: null,
      details: {}
    },
    transfer: {
      isRequested: false,
      details: {}
    }
  },
  capacity: {
    loaded: false,
    data: [],
    filter: {
      selected: [],
      direction: 'desc'
    },
    selected: null
  },
  carrierProfile: {
    data: null,
    loaded: false
  }
};

export default (state = initialState, action: any) =>
  produce(state, draft => {
    switch (action.type) {
      case Constants.GET_MANAGE_CARD_DATA:
        draft.manageCardData = action.payload;
        break;
      case Constants.MANAGE_USERS_LIST_SUCCESS:
        draft.users.data = action.payload.usersList;
        draft.users.loaded = true;
        draft.users.selectedProfile = null;
        break;
      case Constants.MANAGE_USER_SELECTED:
        draft.users.selectedProfile = action.payload.userProfile;
        break;
      case Constants.MANAGE_USER_FILTER:
        draft.users.filter = action.payload.filter;
        break;
      case Constants.MANAGE_USER_VALIDATE_STATUS:
        draft.users.validateUser = action.payload;
        break;
      case Constants.MANAGE_USER_VALIDATE_RESET:
        draft.users.validateUser = {
          isUserValid: null,
          details: {}
        };
      break;
      case Constants.MANAGE_USER_ADD_SUCCESS:
        draft.users.data = [...draft.users.data, action.payload.member];
        draft.users.filter = initialState.users.filter;
        draft.users.selectedProfile = null;
        break;
      case Constants.MANAGE_USER_EDIT_SUCCESS:
        const { member} = action.payload;
        draft.users.data = draft.users.data.map((item:any) => item.userPrincipalName === member.userPrincipalName ? member: item);;
        draft.users.selectedProfile = member;
        break;
      case Constants.MANAGE_USER_TRANSFER_REQUEST:
        draft.users.transfer = action.payload.transfer;
        break;
      case Constants.MANAGE_USER_TRANSFER_REQUEST_RESET:
        draft.users.transfer = {
          isRequested: false,
          details: {}
        };
        break;
      case Constants.MANAGE_USER_REMOVE_SUCCESS:
        draft.users.data = draft.users.data.filter((user:any) => user.userPrincipalName !== action.payload.userId);
        draft.users.selectedProfile = null;
        break;
      case Constants.MANAGE_USER_LOADED_RESET:
        draft.users.loaded = false;
        break;
      case Constants.MANAGE_CAPACITY_FILTER:
        draft.capacity.filter = action.payload.filter;
        break;
      case Constants.MANAGE_CAPACITY_ADD_SUCCESS:
        draft.capacity.data = [action.payload.capacityItem, ...draft.capacity.data];
        draft.capacity.filter = initialState.capacity.filter;
        break;
      case Constants.MANAGE_CAPACITY_EDIT_SUCCESS:
        const {capacityItem} = action.payload;
        draft.capacity.data =draft.capacity.data.map((item:any) => item.capacityId === capacityItem.capacityId ? capacityItem: item);
        draft.capacity.selected = capacityItem;
        break;
      
      case Constants.MANAGE_CAPACITY_SELECTED:
        draft.capacity.selected = action.payload.capacityItem;
        break;
      case Constants.MANAGE_CAPACITY_LIST_SUCCESS:
        const items = action.payload.isRefresh ? action.payload.capacityList : [...draft.capacity.data, ...action.payload.capacityList];
        draft.capacity.data = items.filter((item: any, index: number) => {
          const findIndex = items.findIndex((newItem: any) => newItem.capacityId === item.capacityId);
          return index === findIndex;
        })
        draft.capacity.selected = null;
        draft.capacity.loaded = true;
        break;
      case Constants.MANAGE_CAPACITY_REMOVE_SUCCESS:
        draft.capacity.data = draft.capacity.data.filter(details => details.capacityId !== (action.payload as any).capacityId)
        break;
      case  Constants.CARRIER_MANAGE_DATA_SUCCESS:
        draft.carrierProfile.data = (action.payload as any).data;
        draft.carrierProfile.loaded = true;
        break;
    }
  });
