import {ActionType, RecentState} from "../../schemas/Recent/Recent.Schema";
import * as Constants from "../../utils/constants";
import produce from "immer";

export const initialState: RecentState = {
  recent: [],
  error: {}
};

export default (state = initialState, action: ActionType) =>
  produce(state, draft => {
    switch (action.type) {
      case Constants.GET_RECENT:
        draft.recent = action.payload;
        break;
      case Constants.UPDATE_FAVORITE_ERROR:
          draft.error = action.payload
          break;
      case Constants.CLEAR_FAVORITE_ERROR:
          draft.error = action.payload
          break;
    }
  });
