import getTime from '../utils/getTimePeriod';

export const getAppointmentTime = (stop: any) => {
  let fromTime = '';
  let toTime = '';
  if (stop.appointmentEarlyDateTime) {
    fromTime = stop.appointmentEarlyDateTime;
    toTime = stop.appointmentLateDateTime;
  } else if (stop.actualETADateTime) {
    fromTime = stop.actualETADateTime;
    toTime = '';
  } else if (stop.plannedEtaDateTime) {
    fromTime = stop.plannedEtaDateTime;
    toTime = '';
  }
  return getTime(fromTime || '', toTime || '');
};

const getStopService = (stopDetail: any) => {
  const stopActivity = stopDetail.specialServices || stopDetail.stopActivity || [];
  return (stopActivity && stopActivity.map((item: any) => item.name).join(',')) || '';
};

export const formatedData = (
  data: any,
  fdProp: string,
  tdProp: string,
  idProp: string,
  tMProp: string,
  showInitial?: boolean
) => {
  const stopDetailsList: any = [];
  if (data && fdProp && tdProp && idProp) {
    data &&
      data.forEach((load: any) => {
        const stopDetails: any = {};
        let loadStop =
          load.loadStop &&
          load.loadStop.slice().sort((load1: any, load2: any) => {
            if (load1.sequenceNo && load2.sequenceNo) {
              return load1.sequenceNo - load2.sequenceNo;
            }
            if (load1.stopSequence && load2.stopSequence) {
              return load1.stopSequence - load2.stopSequence;
            }
            return 0;
          });
        if (!(idProp === 'shipmentId')) {
          loadStop =
            loadStop &&
            loadStop.filter((stop: any) => {
              return stop.stopType !== 'NFR';
            });
        }
        const origin = loadStop && loadStop[0];
        const destination = loadStop && loadStop.slice(-1)[0];
        stopDetails.loadId = load[idProp];
        if (origin) {
          stopDetails.origin_city = `${origin.city}, ${origin.state}`;
          stopDetails.origin_from_date =
            idProp === 'shipmentId'
              ? getAppointmentTime(origin)
              : getTime(origin[fdProp] || '', origin[tdProp] || '');
          stopDetails.origin_deadhead = origin.deadHead;
          stopDetails.origin_load_type = getStopService(origin);
        }
        if (destination) {
          stopDetails.destination_city = `${destination.city}, ${destination.state}`;
          stopDetails.destination_from_date =
            idProp === 'shipmentId'
              ? getAppointmentTime(destination)
              : getTime(destination[fdProp] || '', destination[fdProp] || '');
          stopDetails.destination_deadhead = destination.deadHead;
          stopDetails.destination_load_type = getStopService(destination);
        }
        stopDetails.trailer = load[tMProp];
        stopDetails.price =
          load && load.rate && load.rate.totalCharge ? load.rate.totalCharge : 'Contact for price';
        if (showInitial) {
          let name = 'U';
          let driverName = load.shipmentInvolvedParties.filter((data: any) => {
            return data.partyType === 'PRIMARY DRIVER';
          });
          driverName = driverName.length ? driverName[0].partyName : '';
          if (driverName) {
            const fName = driverName.slice(0, 1);
            const LName =
              driverName.indexOf(' ') > 0
                ? driverName.slice(driverName.indexOf(' ') + 1, driverName.indexOf(' ') + 2)
                : '';
            name = fName.toUpperCase() + LName.toUpperCase();
          }
          stopDetails.driverName = name;
        }
        stopDetailsList.push(stopDetails);
      });
  } else {
    return 'Missing requried parametres';
  }
  return stopDetailsList;
};

export const getYearValues = () => {
  const currentYear = new Date().getFullYear();
  const currentMonth = new Date().getMonth();
  const years = [currentYear];
  if (currentMonth === 11) {
    years.push(currentYear + 1);
  }
  if (currentMonth === 0) {
    years.push(currentYear - 1);
  }
  return years;
};
