import { StopActivityProps } from "app/models/stopActivity.model";

export interface IUserInformation {
  carrierId?: string;
  driverId?: string;
  filter?: IFilter;
}
export interface IFilter {
  carrierAssigned?: Boolean;
  driverAssigned?: Boolean;
  inTransit?: Boolean;
  driverAssignmentPending?: Boolean;
  delivered?: Boolean;
}

export interface ILoadInfromation {
  inTransitCount: Number;
  driverAssignedCount: Number;
  carrierAssignedCount: Number;
  driverAssignedPendingCount: Number;
  inTransitShipments: [IShipmentData];
  driverAssignedShipments: [IShipmentData];
  carrierAssginedShipments: [IShipmentData];
  driverAssignmentPendingShipments: [IShipmentData];
}
export interface IShipmentData {
  [index: number]: any;
  carrierAssignedLoads?: IShipment[];
}

export interface IShipment {
  // disposition: Boolean
  shipmentId: String;
  lineOfBusiness: String;
  transportMode: String;
  totalWeight: Number;
  weightUOM: String;
  totalDistance: Number;
  distanceUOM: String;
  numberOfStops: Number;
  sourceTMS: String;
  assignedCarrierId: String;
  isAppointmentConfirmed: Boolean;
  isHazardous: Boolean;
  isHighValue: Boolean;
  isTemperatureControlled: Boolean;
  isFreezeProtected: Boolean;
  readyDateTime: String;
  readyDateTimeZone: String;
  equipmentType: String;
  predecessorShipmentId: String;
  successorShipmentId: String;
  assignmentStatus: String;
  executionStatus: String;
  masterBillOfLoading: String;
  shipmentServices: String;
  loadStop: [IShipmentStop];
  shipmentReferences: [IShipmentReference];
  shipmentNotes: [IShipmentNote];
  shipmentInvolvedParties: [IShipmentInvolvedParty];
  startCity: String;
  startCountry: String;
  startState: String;
  startDateTime: String;
}
export interface IShipmentStop {
  stopId: String;
  locationId: String;
  stopType: String;
  sequenceNo: Number;
  locationName: String;
  addressLine1: String;
  addressLine2: String;
  city: String;
  state: String;
  country: String;
  postalCode: String;
  timeZone: String;
  longitude: String;
  latitude: String;
  estimatedArrivalDateTime: String;
  plannedArrivalDateTime: String;
  actualArrivalDateTime: String;
  estimatedStopDuration: String;
  plannedDepartureDateTime: String;
  actualDepartureDateTime: String;
  appointmentEarlyDateTime: String;
  appointmentLateDateTime: String;
  estimatedStopDurationUom: String;
  actualETADateTime: string;
  shipmentId: String;
  stopReference: [IShipmentReference];
  locationNotes: [IShipmentNote];
  stopActivity: [StopActivityProps];
  trailerValidation: ITrailerValidation;
}
export interface IShipmentReference {
  id: Number;
  type: String;
  value: String;
  stopId: String;
  shipmentId: String;
}
export interface IShipmentNote {
  id: Number;
  type: String;
  value: String;
  shipmentId: String;
  stopId: String;
}

export enum EquipmentFlow {
  TRAILER_IN = "TRAILER_IN",
  TRAILER_OUT = "TRAILER_OUT",
  INOUT = "INOUT",
  NA = "NA",
}
export interface ITrailerValidation {
  validationCode: String;
  validationMessage: String;
  validationStatus: ValidationStatus;
}
enum ValidationStatus {
  SUCCESS,
  WARNING,
  FAILURE,
}
export interface IShipmentInvolvedParty {
  id: Number;
  partyId: String;
  partyType: String;
  partyName: String;
  partyEmail: String;
  partyPhone: String;
}

