import { ActionType } from "../../schemas/Home/Home.schema";
import { put, call } from "redux-saga/effects";
import { showPullToRefresh, showLoading, hidePullToRefresh, hideLoading } from "../../actions/Login/action";
import { DomainConverter } from "../../utils/common";
import { membersListSuccess, validateUserStatus, addUserDetailsSuccess, editUserDetailsSuccess, removeUserDetailsSuccess } from "../../actions/manage/action";
import request from "../../utils/request";
import { formatPhoneNumber } from "../../pages/Manage/ManageUser/ManageUserUtil";
import { fetchUserDetailsSuccess } from "../../actions/App/action";
import { addDriverSuccess } from "../../actions/carrierAssign/action";

/**
 * Get Manage Users List
 * @param action 
 */
export function* getManageUsersList(action: ActionType): any {
  const { fetchAllUsersQuery, isPullToRefresh } = action.payload as any;
  try {
    yield put(isPullToRefresh ? showPullToRefresh() : showLoading());
    const fetchAllUsersValue = DomainConverter.domainToData<any>(fetchAllUsersQuery);
    const fetchAllUsersResponse = yield call(request, [
      "FETCH_USER_DETAILS",
      "USER_DETAILS",
      null,
      "",
      fetchAllUsersValue,
      "application/json",
    ]);
    const anyList = fetchAllUsersResponse.data ? fetchAllUsersResponse.data.getMembers : [];
    let usersList = [...anyList];
    usersList = usersList.filter((user: any, index: number) => usersList.findIndex((user1: any) => user.userPrincipalName === user1.userPrincipalName) === index);
    yield put(membersListSuccess({ usersList }));
    yield put(isPullToRefresh ? hidePullToRefresh() : hideLoading());
  } catch (e) {
    yield put(isPullToRefresh ? hidePullToRefresh() : hideLoading());
  }
}


/**
 * Validate user by passing an email
 * @param action 
 */
export function* validateUser(action: ActionType): any {
  const { query, callback } = action.payload as any;
  try {
    yield put(showLoading());
    const fetchAllUsersValue = DomainConverter.domainToData<any>(query);
    const response = yield call(request, [
      "FETCH_USER_DETAILS",
      "USER_DETAILS",
      null,
      "",
      fetchAllUsersValue,
      "application/json",
    ]);
    if (response && response.data) {
      const details = (response && response.data && response.data.validateUser) || {};
      const isUserValid = !!(response && response.data && response.data.validateUser);
      yield put(validateUserStatus({ isUserValid, details }))
    } else {
      callback();
    }
    yield put(hideLoading());
  } catch (e) {
    callback();
    yield put(hideLoading());
  }
}


