/**
 * Add user details
 * @param action 
 */
export function* addUser(action:ActionType): any {
  const { query, callback, isCarrierAssign = false } = action.payload as any;
  try {
    yield put(showLoading());
    const fetchAllUsersValue = DomainConverter.domainToData<any>(query);
    const response = yield call(request, [
      "FETCH_USER_DETAILS",
      "USER_DETAILS",
      null,
      "",
      fetchAllUsersValue,
      "application/json",
    ]);
    if(response && response.data && response.data.addMember && response.data.addMember.status) {
      const member = (response && response.data && response.data.addMember && response.data.addMember.member) || {};
      yield put(addUserDetailsSuccess({member}));
      if(isCarrierAssign) {
        yield put(addDriverSuccess({member}));
      }
      callback(true);
    } else {
      callback();
    }
    yield put(hideLoading());
  } catch (e) {
      callback();
      yield put(hideLoading());
  }
}

/**
 * Update user details
 * @param action 
 */
export function* updateUser(action:ActionType): any {
  const { query, isProfile = false, callback } = action.payload as any;
  try {
    yield put(showLoading());
    const fetchAllUsersValue = DomainConverter.domainToData<any>(query);
    const response = yield call(request, [
      "FETCH_USER_DETAILS",
      "USER_DETAILS",
      null,
      "",
      fetchAllUsersValue,
      "application/json",
    ]);
    if(response && response.data && response.data.updateMember && response.data.updateMember.status) {
      const member = (response && response.data && response.data.updateMember && response.data.updateMember.member) || {};
      const roles = (member && member.userApplicationDetails && member.userApplicationDetails.reduce((roles:Array<any>, item:any) => {
        const itemRoles = item.roles && item.roles.map((role:any) => role.roleName) || [] ;
        return [...roles, ...itemRoles];
    }, [])) || [];
      if(isProfile) {
        yield put(fetchUserDetailsSuccess({...member, defaultMobileNumber:member.mobileNumber || '', mobileNumber: member.mobileNumber ? formatPhoneNumber(member.mobileNumber): '',roles}));
      } else {
        yield put(editUserDetailsSuccess({member: {...member, defaultMobileNumber:member.mobileNumber || '', mobileNumber: member.mobileNumber ? formatPhoneNumber(member.mobileNumber): '',roles}}));
      }
      callback(true);
    } else {
      callback();
    }
    yield put(hideLoading());
  } catch (e) {
      callback();
      yield put(hideLoading());
  }
}

/**
 * Remove user details
 * @param action 
 */
export function* removeUser(action:ActionType): any {
  const { query, userId, callback } = action.payload as any;
  try {
    yield put(showLoading());
    const value = DomainConverter.domainToData<any>(query);
    const response = yield call(request, [
      "FETCH_USER_DETAILS",
      "USER_DETAILS",
      null,
      "",
      value,
      "application/json",
    ]);
    if(response && response.data && response.data.updateMember && response.data.updateMember.status) {
      yield put(removeUserDetailsSuccess({userId}));
      callback(true);
    } else {
      callback();
    }
    yield put(hideLoading());
  } catch (e) {
      callback();
      yield put(hideLoading());
  }
}