export enum MESSAGE_TYPE {
  GEO_START_TRIP_LOCATION = "Geo-Start-Trip-Location", // Workflow: (When trip start) Message Type will send to code system without any convertion from BE
  GEO_POSITION = "Geo-Position", // Geo: (GPS Tracking normal pings) Message Type will send to code system without any convertion from BE
  GEOFENCE_PRE_ARRIVAL = "Geo-Fence-Pre-Arrival-", // Geofence: (Pre-Arrival Fence) converted by BE based on REST API request "action", ie: "action": "ENTER" <Geo-Fence-Pre-Arrival>, "action": "EXIT" <Geo-Stop-Depart>
  GEOFENCE_ARRIVAL = "Geo-Fence-", // Geofence: (Arrival Fence) converted by BE based on REST API request "action", ie: "action": "ENTER" <Geo-Fence-Entry>, "action": "EXIT" <Geo-Fence-Exit>
  GEO_STOP_ARRIVAL = "Geo-Stop-Arrival", // Workflow: (stationary state) Message Type will send to code system without any convertion from BE
  GEO_TPOSITION = "Geo-TPosition"
}

