import React from 'react';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { isPlatform } from '@ionic/react';
import { getNetworkStatus, getUserDetails } from '../../../selectors/selector';
import { showNetworkAlert } from '../../../actions/App/action';
import { ToastMessage } from '../../../utils/ToastMessage';
import { OOPS_MESSAGE, ADD_CAPACITY_SUCCESS, EVENT, SCREEN } from '../../../utils/constants';
import { ICapacityPayload } from '../../../schemas/manage/manage.Schema';
import { CapacityModel } from '../../../models/manage/capacity.model';
import { addManageCapacity } from '../../../actions/manage/action';
import { logEvent } from '../../../utils/Analytics';
import ManageCapacityFormPage from 'app/pages/Manage/ManageCapacity/ManageCapacityForm.page';

export const ManagCapacityAddContainer: React.FC<any> = (props: any) => {
  const { isNetWorkAvailable, showNetworkAlert, history, userDetails } = props;

  const handleSubmit = (input: ICapacityPayload, clearFields: Function) => {
    if (!isNetWorkAvailable) {
      showNetworkAlert();
      return true;
    }
    const capacityModel = new CapacityModel();
    const query = capacityModel.addCapacity();
    const callback = (status: boolean) => {
      ToastMessage.show(status ? ADD_CAPACITY_SUCCESS : OOPS_MESSAGE);
      // Analytics implementation
      logEvent(status ? EVENT.UE_MANAGE_CAPACITY_SUCCESS : EVENT.UE_MANAGE_CAPACITY_FAILED, {
        screen: SCREEN.MANAGE_CAPACITY_ADDEDIT,
        carrierId: (userDetails && userDetails.carrierId) || '',
        capacityType: 'lane' // TODO <lane/truck>
      });
      clearFields();
      history.push(`/app/manage${!isPlatform('desktop') ? '/ManageMyCapacity' : ''}`);
    };
    props.addCapacityItem({ params: { query, input }, callback });
  };

  return (
    <ManageCapacityFormPage
      module="manage/ManageMyCapacity"
      {...props}
      handleSubmit={handleSubmit}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  isNetWorkAvailable: getNetworkStatus(),
  userDetails: getUserDetails()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  addCapacityItem: (payload: any) => dispatch(addManageCapacity(payload))
});

export default connect(mapStateToProps, mapDispatchToProps)(ManagCapacityAddContainer);
