import React, { useState, useEffect } from "react";
import {
    IonPage,
    IonContent,
    IonFooter,
    IonButton,
    IonGrid,
    IonCard,
    IonRow,
    IonIcon,
    IonLabel,
    IonCol,
    IonCardHeader,
    IonToggle,
    IonCardContent,
    IonText,
    isPlatform, 
    useIonViewWillEnter
} from "@ionic/react";
import AppHeader from "../../../components/app/Bars/Bar-header";
import { NewDropDown } from "../../../components/shared/NewUIComponents/DropDown";
import PickupData from "../../../components/app/Search/PickupData";
import DeliverypData from "../../../components/app/Search/DeliverypData";
import { Anywhere, DryVan, PickUpdate, Trailer, Origin, Destination, CityError, US, USA } from "../../../utils/constants";
import "./ManageCapacity.scss";
import { PickUpTimeComponent } from "../../../components/shared/pickupTimeComponent";
import { PickupDaysComponent } from "../../../components/shared/PickupDays";
import { ILocationDetails } from "../../../schemas/manage/manage.Schema";
import { SelectComponent } from "../../../components/shared/SelectComponent";
import { states } from "../../../utils/states";
import ManageCapacityFormDesktop from "./ManageCapacityFormDesktop.page";
import { getTime, keyboardhideHandler, keyboardshowHandler, setDetails, stateItems } from "./ManagaCapacityUtil";

const capacityOptions = [
    { name: "Truck", desc: "Spot capacity currently available." },
    { name: "Lane", desc: "Frequently desired origin to destination." },
];

const currentDate = new Date();
let date = new Date();
date.setDate(date.getDate() + 1);

const location: ILocationDetails = {
    city: Anywhere,
    state: null,
    country: null,
    lat: null,
    lng: null,
    address: null
};
const ManageCapacityFormPage: React.FC<any> = (props: any) => {
    const { userDetails, selectedCapacity = null } = props;
    const [showCapacityOptions, setShowCapacityOptions] = useState(false);
    const [capacityType, setCapacityType] = useState(capacityOptions[0]);
    const [trailerType, setTrailerType] = useState(DryVan);
    const [originDetails, setOriginDetails] = useState({} as ILocationDetails);
    const [showOrigin, setShowOrigin] = useState(false);
    const [originRadius, setOriginRadius] = useState(100);
    const [pickUpLocalTime, setPickUpLocalTime] = useState(
        currentDate.toISOString()
    );
    const [pickupFromDate, setPickupFromDate] = useState(
        currentDate.toISOString()
    );
    const [pickupToDate, setPickupToDate] = useState(date.toISOString());
    const [preferredDays, setPreferredDays] = useState([]);
    const [showDelivaryfields, setShowDelivaryfields] = useState(false);
    const [destinationDetails, setDestinationDetails] = useState({ ...location, city: null, address: '' } as ILocationDetails);
    const [destinationRadius, setDestinationRadius] = useState(100);
    const [isStatesShow, setStatesShow] = useState(false);
    const [originError, setOriginError] = useState<any>(false);
    const [destinationError, setDestinationError] = useState<any>(false);
    const [stateError, setStatesError] = useState(false);
    const [pickupTimeError, setPickuptimeError] = useState(false);
    const [pickupDaysError, setPickupDaysError] = useState(false);
    const [isOriginEdit, setOriginEdit] = useState(false);
    const [isDestinationEdit, setDestinationEdit] = useState(false);
    
    const [statesList, setStatesList] = useState(stateItems);

    let dateUpdated = false;

    const handleChange = (event: any) => {
        const { name, value } = event.target;
        switch (name) {
            case Trailer:
                setTrailerType(value);
                break;
            case "fromDate":
                setPickupFromDate(value);
                break;
            case "toDate":
                setPickupToDate(value);
                break;
            case Origin:
                setOriginError(false);
                if(isOriginEdit) {
                    setDetails(event.target, setOriginDetails);
                }
                break;
            case "originRadius":
                setOriginRadius(value);
                break;
            case Destination:
                setDestinationError(false);
                if(isDestinationEdit) {
                    setDetails(event.target, setDestinationDetails);
                }
                break;
            case "destinationRadius":
                setDestinationRadius(value);
                break;
            case 'stateToggle':
                setStatesShow(!isStatesShow);
                break;
        }
    };

    const setSelectedData = () => {
        const statesList = stateItems.map((state:any, index:number) => {
            return {...state, value:selectedCapacity.destinationStates[index] || ''}
        })
        setOriginEdit(false);
        setCapacityType(capacityOptions[selectedCapacity.capacityType === 'TRUCK' ? 0 : 1]);
        setTrailerType(selectedCapacity.trailerType);
        setOriginDetails({ ...selectedCapacity.origin,lat: selectedCapacity.origin.latitude, lng: selectedCapacity.origin.longitude, address: selectedCapacity.origin.city + (selectedCapacity.origin.state ? `, ${selectedCapacity.origin.state}` : '') } as ILocationDetails);
        setOriginRadius(selectedCapacity.origin.radius);
        if(selectedCapacity.pickUpFromDate !== '') {
            setPickUpLocalTime(selectedCapacity.pickUpFromDate);
        }
        if(selectedCapacity.pickUpFromDate && selectedCapacity.pickUpFromDate !== '') {
            setPickupFromDate(selectedCapacity.pickUpFromDate);
        }
        if(selectedCapacity.pickUpToDate && selectedCapacity.pickUpToDate !== '') {
            setPickupToDate(selectedCapacity.pickUpToDate);
        }
        setPreferredDays(selectedCapacity.preferredDays);
        setDestinationDetails({ ...selectedCapacity.destination, address: selectedCapacity.destination.city !== null ? selectedCapacity.destination.city + (selectedCapacity.destination.state ? `, ${selectedCapacity.destination.state}` : '') : Anywhere } as ILocationDetails);
        setDestinationRadius(selectedCapacity.destination.radius || 100);
        setStatesShow(selectedCapacity && selectedCapacity.destinationStates.length > 0);
        setStatesList(statesList);
        setShowDelivaryfields(false);
        setDestinationEdit(false);
        setPickupDaysError(false);
        setPickuptimeError(false);
    }

    useEffect(() => {
        if (selectedCapacity) {
            setSelectedData();
        }
    }, [selectedCapacity]);

    useIonViewWillEnter(() => {
        if(!showOrigin && !selectedCapacity) {
          setShowOrigin(true);
          clearFields();
        }
      });

    const setErrorValue = (value: boolean, name: string) =>  name === Origin ? setOriginError(value): setDestinationError(value);

    const updateLocalCurrentTime = (feildName: string, localTime: string) => {
        if (feildName === PickUpdate) {
            if (!dateUpdated && localTime) {
                setPickupFromDate(pickUpLocalTime);
                dateUpdated = true;
            }
            setPickUpLocalTime(localTime);
        }
    };


    const handleStateChange = ({ name, value }: any) => {
        const list = statesList.map((state: any) => ({ ...state, value: state.name === name ? value.code : state.value }));
        const destinationStates = list.filter(state => state.value !== '').map(state => state.value);
        setStatesError(destinationStates.length === 0);
        setStatesList(list);
    }

    const clearFields = () => {
        setCapacityType(capacityOptions[0]);
        setTrailerType(DryVan);
        setOriginDetails({} as ILocationDetails);
        setOriginRadius(100);
        setPickUpLocalTime(currentDate.toISOString());
        setPickupFromDate(currentDate.toISOString());
        setPickupToDate(date.toISOString());
        setPreferredDays([]);
        setShowDelivaryfields(false);
        setDestinationDetails({ ...location, address: Anywhere } as ILocationDetails);
        setDestinationRadius(100);
        setStatesShow(false);
        setOriginError(false);
        setStatesList(stateItems);
    }

    const handleBackButton = () => {
        if(selectedCapacity)  {
            setSelectedData();
            props.history.push('/app/' + props.module);
        } else {
            setShowOrigin(false);
            const path = isPlatform('desktop') ? '/app/manage' : '/app/' + props.module;
            props.history.push(path);
        }
    }

    const handleSubmit = () => {

        if(!(originDetails && originDetails.city && originDetails.lat && originDetails.lng)) {
            setOriginError(CityError);
            return true;
        }

        if (pickupTimeError && capacityType.name === 'Truck') {
            return true;
        }

        if(capacityType.name === 'Lane' && preferredDays.length === 0) {
            setPickupDaysError(true);
            return true;
        }

        if(capacityType.name === 'Lane' && !(destinationDetails && destinationDetails.city && destinationDetails.city !== Anywhere)) {
            setDestinationError(CityError);
            setShowDelivaryfields(true);
            return true;
          }

        const destinationStates = statesList.filter(state => state.value !== '').map(state => state.value);

        if (isStatesShow && destinationStates.length === 0) {
            setStatesError(true);
            return true;
        }

        setOriginEdit(false);
        setDestinationEdit(false);

        const destination = !isStatesShow && destinationDetails.city !== Anywhere ? {
            destinationCity: destinationDetails.city,
            destinationState: destinationDetails.state,
            destinationCountry: destinationDetails.country === US ? USA:  destinationDetails.country,
            destinationLatitude: destinationDetails.lat,
            destinationLongitude: destinationDetails.lng,
        } : {
            destinationCity: null,
            destinationState: null,
            destinationCountry: null,
            destinationLatitude: null,
            destinationLongitude: null,
        };

        const payload = {
            capacityId: (selectedCapacity && selectedCapacity.capacityId) || null,
            userId: userDetails.userPrincipalName,
            userEmail: userDetails.email,
            userPhone: userDetails.mobileNumber || userDetails.phone,
            capacityType: capacityType.name.toUpperCase(),
            commonCarrierId: userDetails.carrierId,
            trailerType,
            originCity: originDetails.city,
            originState: originDetails.state,
            originRadius,
            originCountry: originDetails.country === US ? USA:  originDetails.country,
            originLongitude: originDetails.lng,
            originLatitude: originDetails.lat,
            ...destination,
            destinationRadius: destination.destinationCity !== null ? destinationRadius: null,
            preferredDays: capacityType.name === 'Lane' ? preferredDays : [],
            pickUpToDate:  capacityType.name !== 'Lane' ? getTime(pickupToDate) : null,
            pickUpFromDate:  capacityType.name !== 'Lane' ? getTime(pickupFromDate) : null,
            destinationStates: isStatesShow ? destinationStates : []
        };

        props.handleSubmit(payload, clearFields);
    };


    return isPlatform('desktop') ? (
        <ManageCapacityFormDesktop 
            {...props} 
            capacityId={selectedCapacity && selectedCapacity.capacityId || null}
            title={selectedCapacity && selectedCapacity.capacityId || null}
            statesList={statesList}
            capacityOptions={capacityOptions}
            showCapacityOptions={showCapacityOptions}
            setShowCapacityOptions={setShowCapacityOptions}
            capacityType={capacityType}
            setCapacityType={setCapacityType}
            trailerType={trailerType}
            originDetails={originDetails}
            originRadius={originRadius}
            pickUpLocalTime={pickUpLocalTime}
            handleChange={handleChange}
            pickupFromDate={pickupFromDate}
            pickupToDate={pickupToDate}
            pickupTimeError={pickupTimeError}
            setPickuptimeError={setPickuptimeError}
            setPreferredDays={setPreferredDays}
            pickupDaysError={pickupDaysError}
            setPickupDaysError={setPickupDaysError}
            showDelivaryfields={showDelivaryfields}
            setShowDelivaryfields={setShowDelivaryfields}
            destinationDetails={destinationDetails}
            destinationRadius={destinationRadius}
            isStatesShow={isStatesShow}
            setStatesShow={setStatesShow}
            handleStateChange={handleStateChange}
            isOriginEdit={isOriginEdit}
            setOriginEdit={setOriginEdit}
            isDestinationEdit={isDestinationEdit}
            setDestinationEdit={setDestinationEdit}
            setErrorValue={setErrorValue}
            handleSubmit={handleSubmit}
            handleBackButton={handleBackButton}
        />
      )
      :(
        <IonPage className="manage-userslist-page">
            <AppHeader
                title={(selectedCapacity && selectedCapacity.capacityId) || 'Add Lane/Truck'}
                customBackUrl={handleBackButton}
                History={props.history}
            />
            <IonContent className="ion-padding add-capacity-content">
                <IonContent className="add-capacity-form-content">
                    <IonGrid class="capacity-grid">
                        <IonRow class="capacity-label-row">
                            <IonLabel class="capacity-label">Capacity Type</IonLabel>
                        </IonRow>
                        <IonRow
                            class="capacity-type"
                            data-test="capacity-type"
                            onClick={() => setShowCapacityOptions(!showCapacityOptions)}
                        >
                            <IonCol class="capacity-name">{capacityType.name}</IonCol>
                            {
                                !selectedCapacity ? (
                                    <IonCol class="dropDown-icon">
                                        <IonIcon src="/assets/icon/grid-down-arrow.svg"></IonIcon>
                                    </IonCol>
                                ): null
                            }
                           
                        </IonRow>
                        <IonRow class="capacity-desc">{capacityType.desc}</IonRow>
                        {!selectedCapacity && capacityOptions && showCapacityOptions ? (
                            <IonGrid class="suggetion-grid"  data-test="capacity-type-list">
                                {capacityOptions.map((capacityType: any, index: number) => (
                                <IonRow
                                    key={index}
                                    data-test="capacity-type-list-item"
                                    onClick={() => {
                                    setCapacityType(capacityType);
                                    setStatesShow(false);
                                    setShowCapacityOptions(false);
                                    }}
                                >
                                    {capacityType.name}
                                </IonRow>
                                ))}
                            </IonGrid>
                            ) : null}
                    </IonGrid>
                    <IonGrid class="add-capacity-grid-mobile">
                        <IonCard class="trailer-type-card">
                            <NewDropDown
                                labelName="Trailer type"
                                TrailerType={trailerType}
                                handleChange={handleChange}
                            />
                        </IonCard>
                        <IonGrid class="add-capacity-pick-up">
                            <IonRow class="pickup-row">Pick-up</IonRow>
                            { (showOrigin ||selectedCapacity) ? <PickupData
                                origin={originDetails.address}
                                originError={originError}
                                originRadius={originRadius}
                                handleChange={handleChange}
                                rangeDisabled={true}
                                isDestination={false}
                                defaultValue={Anywhere}
                                useHandler={false}
                                setErrorValue={setErrorValue}
                                updateLocalCurrentTime={updateLocalCurrentTime}
                                showDate={false}
                                onFocus={() => {
                                    if(!isOriginEdit) {
                                        setOriginEdit(true);
                                    }
                                }}
                            /> : null }
                            {
                                capacityType.name === 'Truck' ? (
                                    <PickUpTimeComponent
                                        pickUpLocalTime={pickUpLocalTime}
                                        handleChange={handleChange}
                                        fromDate={pickupFromDate}
                                        toDate={pickupToDate}
                                        error={pickupTimeError}
                                        handleError={setPickuptimeError}
                                    />
                                ) : (
                                        <PickupDaysComponent
                                            preferredDays={preferredDays}
                                            setPickupDays={setPreferredDays}
                                            showError={pickupDaysError}
                                            handleError={setPickupDaysError}
                                        />
                                    )
                            }

                            <IonCard class="delivery-type-card">
                                <IonCardHeader
                                    class={`${
                                        showDelivaryfields ? "" : "changeColor"
                                        }`}
                                    data-test="delivery-type-accordian"
                                    onClick={() => setShowDelivaryfields(!showDelivaryfields)}
                                >
                                    <span> Delivery</span>
                                    {showDelivaryfields ? (
                                        <IonIcon data-test="delivery-minus-icon" src="assets/icon/minus-icon.svg" />
                                    ) : (
                                            <div className="desitnation-city">
                                                {selectedCapacity && selectedCapacity.destinationStates && selectedCapacity.destinationStates.length === 0 ? `${(destinationDetails.address !== Anywhere  && destinationDetails.address !== '') ? destinationDetails.address : (capacityType.name === 'Truck' ? Anywhere: '')}`: ''}
                                                <IonIcon src="assets/icon/plus.svg" />
                                            </div>
                                        )}
                                </IonCardHeader>
                                <IonCardContent
                                    class={`${
                                        showDelivaryfields ? "fadein" : "fadeout"
                                        }`}
                                >
                                    {
                                        capacityType.name === 'Truck' ? (
                                            <IonText className="states-toggle-wrapper">
                                                <div className="destination-wrapper">
                                                    {isStatesShow ? <span>Destination</span> : null}
                                                </div>
                                                <div className="state-wrapper">
                                                    <span>Select States</span>
                                                    <IonToggle mode="ios" class="state-toggle" checked={isStatesShow} name="stateToggle" onIonChange={handleChange}></IonToggle>
                                                </div>
                                            </IonText>
                                        ) : null
                                    }
                                    {
                                        isStatesShow && capacityType.name === 'Truck' ?
                                            (
                                                <div data-test="state-list-wrapper" className={`states-list-wrapper ${stateError ? 'error' : ''}`}>
                                                    {
                                                        statesList && statesList.map((state: any, index: number) => (
                                                            <SelectComponent key={`state${index}`} options={states} placeholder="Choose the state" label={state.label} name={state.name} value={statesList[index] && statesList[index].value} handleChange={handleStateChange} />
                                                        )
                                                        )
                                                    }
                                                </div>
                                            ) : (
                                                <DeliverypData
                                                    destination={`${destinationDetails.address !== Anywhere ? destinationDetails.address : (capacityType.name === 'Truck' ? Anywhere: '')}`}
                                                    destinationError={destinationError}
                                                    Destination={'DestinationDetails'}
                                                    handleChange={handleChange}
                                                    rangeDisable={destinationDetails && ((capacityType.name === 'Lane' && destinationDetails.address === Anywhere) || destinationDetails.address === '' || destinationDetails.address === null)}
                                                    platform="mobile"
                                                    keyboardshowHandler={keyboardshowHandler}
                                                    keyboardhideHandler={keyboardhideHandler}
                                                    defaultValue={Anywhere}
                                                    isDestination={true}
                                                    useHandler={true}
                                                    setErrorValue={setErrorValue}
                                                    destinationRadius={destinationRadius}
                                                    updateLocalCurrentTime={updateLocalCurrentTime}
                                                    showDate={false}
                                                    onFocus={() => {
                                                        if(!isDestinationEdit) {
                                                            setDestinationEdit(true);
                                                        }
                                                    }}
                                                />
                                            )
                                    }

                                </IonCardContent>
                            </IonCard>
                        </IonGrid>
                    </IonGrid>
                </IonContent>
                <IonFooter class="addTruckFooter">
                    <IonButton expand="full" data-test="capacity-submit" onClick={handleSubmit}>Save</IonButton>
                </IonFooter>
            </IonContent>
        </IonPage>
    );
};
export default ManageCapacityFormPage;
