import React, { useState, useEffect } from "react";
import {
  IonDatetime,
  IonLabel,
  IonRow,
  IonCol,
  IonButton,
  isPlatform,
  IonInput,
} from "@ionic/react";
import { NewInput } from "../../../components/shared/NewUIComponents/OriginInput";
import {
  CityError,
  CONFRIM_START_CTA_TEXT,
  START_LOCATION_LABEL_TEXT,
  ACTUAL_START_LABEL_TEXT,
} from "../../../utils/constants";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import { getYearValues } from "../../../utils/formatData";

export const StartTrip: React.FunctionComponent<any> = (props: any) => {
  const [isConfirmStartButton, setisConfirmStartButton] = useState(
    props.isConfirmStartButton
  );

  const startTripModel: StartTripModel = new StartTripModel();
  const [locationName, setLocationName] = useState("");
  const [timezone, settimezone] = useState("");
  const [originError, setoriginError] = useState("");
  const [lat, setlat] = useState("");
  const [lng, setlng] = useState("");

  const [currentLocationName, setcurrentLocationName] = useState<string>("");
  const [currentLocationCountry, setcurrentLocationCountryName] = useState<
    string
  >("");
  const [currentLocationLat, setcurrentLocationLat] = useState<string>("");
  const [currentLocationLng, setcurrentLocationLng] = useState<string>("");
  const [currentLocationTimezone, setCurrentLocationTimezone] = useState<
    string
  >("");
  const [
    currentLocationTimezoneStatus,
    setCurrentLocationTimezoneStatus,
  ] = useState<boolean>(false);

  const [ActualStart, setActualStart] = useState<string>(
    startTripModel.getCurrentTimeInISO()
  );
  const [isLoadRefreshed, setIsLoadRefreshed] = useState(false);
  const [isError, setisError] = useState(false);
  const [country, setCountry] = useState("");
  const [isCurrentLocationAvailable, setCurrentLocationStatus] = useState<
    boolean
  >(false);
  const cityError = CityError;
  const yearValues = getYearValues() || new Date().getFullYear();

  const setErrorValue = (value: boolean, name: string) => {
    setisError(value);
    if (name === "locationName" && value === true) {
      setoriginError(cityError);
    } else if (name === "locationName" && value === false) {
      setoriginError("");
    }
  };
  const confirmationStart = () => {
    props.confirmStartInformation(
      locationName,
      ActualStart,
      lat,
      lng,
      country || "US",
      timezone
    );
    setIsLoadRefreshed(true);
    setisConfirmStartButton(false);
    // Enable GPS tracking
    if (isPlatform("mobile")) {
      props.GPSEnabledEvent(locationName);
    }
  };
  const handleChangeEvent = (event: any) => {
    {
      if (
        event.target.name === "locationName" &&
        event.target.value.length > 0
      ) {
        setoriginError("");
      }
      if (isCurrentLocationAvailable) {
        setLocationName(event.target.value);
        setlat(event.target.lat);
        setlng(event.target.lng);
        setCountry(event.target.country);
      } else {
        setcurrentLocationName(event.target.value);
        setcurrentLocationCountryName(event.target.country);
        setcurrentLocationLat(event.target.lat);
        setcurrentLocationLng(event.target.lng);

        setLocationName(event.target.value);
        setlat(event.target.lat);
        setlng(event.target.lng);
        setCountry(event.target.country);
        setCurrentLocationStatus(true);
      }
    }
  };

  useEffect(() => {
    setActualStart(startTripModel.getCurrentTimeInISO());
    setLocationName("");
    setlat("");
    setlng("");
    setCountry("");
    settimezone("");
  }, [props.shipmentId]);

  const { hideConfirmStart } = props;
  let _startDate: string = hideConfirmStart ? props.ActualStart : ActualStart;
  _startDate = startTripModel.getTimeForIOS(_startDate);

  return (
    <React.Fragment>
      {isPlatform("mobile") ? (
        <>
          <IonRow>
            <IonCol className="origin-input">
              {(hideConfirmStart && (
                <IonInput
                  value={props.locationOriginValue}
                  className="start-trip-date readonly-origin execution-field"
                  readonly={true}
                  disabled={true}
                />
              )) || (
                <NewInput
                  onTimeZoneChange={(timezone: string) => {
                    if (!currentLocationTimezoneStatus) {
                      setCurrentLocationTimezone(timezone);
                      setCurrentLocationTimezoneStatus(true);
                    }
                    settimezone(timezone);
                  }}
                  Error={false}
                  labelValue=""
                  InputValue={locationName}
                  InputName="locationName"
                  handleChange={handleChangeEvent}
                  isDestination={false}
                  deFaultValue={locationName}
                  useHandler={false}
                  setErrorValue={setErrorValue}
                  locationValue={props.locationValue}
                />
              )}
            </IonCol>
          </IonRow>
          <IonRow>
            <IonCol>
              <IonLabel className="actual-start">
                {ACTUAL_START_LABEL_TEXT}
              </IonLabel>
              <IonDatetime
                displayFormat="MMM DD hh:mm A"
                pickerFormat="YYYY MMM DD hh:mm A"
                className="actual-start-time start-trip-date execution-field"
                minuteValues="00,05,10,15,20,25,30,35,40,45,50,55"
                value={_startDate}
                onIonChange={(e) => {
                  setActualStart(e.detail.value!);
                }}
                yearValues={yearValues}
                readonly={hideConfirmStart}
                disabled={hideConfirmStart}
                mode="ios"
              ></IonDatetime>
            </IonCol>
          </IonRow>
          {!hideConfirmStart ? (
            <IonRow>
              <IonButton
                class="load-btn load-btn2"
                onClick={confirmationStart}
                disabled={
                  locationName === "" ||
                  (locationName !== "" && isError) ||
                  ActualStart === "" ||
                  !props.networkStatus
                }
              >
                {CONFRIM_START_CTA_TEXT}
              </IonButton>
            </IonRow>
          ) : null}
        </>
      ) : (
        <>
          <IonRow class="start-trip-row">
            <IonCol className="start-col" size="3">
              <IonLabel className="location-label">
                {START_LOCATION_LABEL_TEXT}
              </IonLabel>
              {(hideConfirmStart && (
                <IonInput
                  value={props.locationOriginValue}
                  className="start-trip-date readonly-origin execution-field"
                  readonly={true}
                  disabled={true}
                />
              )) || (
                <NewInput
                  onTimeZoneChange={(timezone: string) => {
                    if (!currentLocationTimezoneStatus) {
                      setCurrentLocationTimezone(timezone);
                      setCurrentLocationTimezoneStatus(true);
                    }
                    settimezone(timezone);
                  }}
                  Error={false}
                  labelValue=""
                  InputValue={locationName}
                  InputName="locationName"
                  handleChange={handleChangeEvent}
                  isDestination={false}
                  deFaultValue={locationName}
                  useHandler={false}
                  setErrorValue={setErrorValue}
                  locationValue={props.locationValue}
                />
              )}
            </IonCol>
            <IonCol size="3" className="start-col">
              <IonLabel className="actual-start">
                {ACTUAL_START_LABEL_TEXT}
              </IonLabel>
              <IonDatetime
                displayFormat="MMM DD hh:mm A"
                pickerFormat="YYYY MMM DD hh:mm A"
                minuteValues="00,05,10,15,20,25,30,35,40,45,50,55"
                className={`actual-start-time execution-field ${
                  isConfirmStartButton ? "StartTrip-location2" : "location-inp"
                }`}
                value={_startDate}
                onIonChange={(e) => {
                  setActualStart(e.detail.value!);
                }}
                readonly={hideConfirmStart}
                disabled={hideConfirmStart}
                mode="ios"
              ></IonDatetime>
            </IonCol>
            <IonCol size="6" className="start-btn-col">
              {!hideConfirmStart ? (
                <IonButton
                  class="load-btn load-btn2"
                  onClick={confirmationStart}
                  disabled={
                    locationName === "" ||
                    (locationName !== "" && isError) ||
                    ActualStart === "" ||
                    !props.networkStatus
                  }
                >
                  {CONFRIM_START_CTA_TEXT}
                </IonButton>
              ) : null}
            </IonCol>
          </IonRow>
        </>
      )}
    </React.Fragment>
  );
};
