
import React, { useEffect } from 'react';
import { IonContent, IonList, IonRow, IonCol, IonLabel, IonInput, IonImg, IonGrid, IonItem, IonText, IonPage, IonButton } from '@ionic/react';
import { IonImage } from '../../../components/app/Manage';
import AppHeader from '../../../components/app/Bars/Bar-header'
import { getApplicationInfo, logEvent } from '../../../utils/Analytics';
import { EVENT, SCREEN } from '../../../utils/constants';
import { rolesList } from './ManageUserUtil';

const ManageUserProfileDesktopPage = (props:any) => {
    const { selectedUser, history, isNetworkAvailable, showNetworkAlert, isEditable = true, isSelf = false } = props;

	useEffect(() => {
		// Analytics implementation
        logEvent(EVENT.SV_VIEW_MY_PROFILE,
        {
            screen: SCREEN.MY_PROFILE,
            carrierId: selectedUser.carrierId
        });
	}, []);

	const roles = selectedUser && selectedUser.roles && selectedUser.roles.map((role: any) => {
		const selectedOption = rolesList.find((option: any) => option.value === role);
		return selectedOption ? selectedOption.name : '';
	}).join(' and ') || '';
	const userProfile: Array<any> = selectedUser ? [
		{
			label: "First Name",
			value: selectedUser.firstName
		},
		{
			label: "Last Name",
			value: selectedUser.lastName
		},
		{
			label: "Email Address",
			value: selectedUser.email
		},
		{
			label: "Mobile Number",
			value: selectedUser.mobileNumber
		},
		{
			label: "Role(s)",
			value: roles
		},
		{
			label: "Carrier ID",
			value: selectedUser.carrierId
		},
	] : [];

    return (
        <IonPage className="desktop-page my-load-desktop-page manage-card-content manage-user-profile-desktop">
            <AppHeader
                title="Manage"
            />
            <IonContent class="custom-padding">
                <h1>User Details</h1>
                {
                    !isSelf && selectedUser ? (
                        <IonGrid>
                            <IonRow>
                                <IonCol size="12">
                                    <div className="manage-user-profile-header">
                                        <IonImage firstName={selectedUser.firstName} lastName={selectedUser.lastName} big />
                                        <h4>{selectedUser.firstName} {selectedUser.lastName}</h4>
                                    </div>
                                </IonCol>
                            </IonRow>
                        </IonGrid>
                    ) : null
                }
                <div className="manage-users-wrapper">
                    <IonList>
                        {
                            userProfile.map((profile:any) => (
                                <IonItem className="profile-item" key={profile.label}>
                                    <IonLabel className="profile-label" position="floating" mode="ios">{profile.label}</IonLabel>
                                    <IonInput
                                        value={profile.value}
                                        readonly
                                        type="text"
                                        class="profile-input"
                                    ></IonInput>
                                </IonItem>
                            ))
                        }
                    </IonList>
                </div>
                <div className="manage-user-buttons">
                    <IonGrid class="btn-row-grid">
                        <IonRow
                            className='btn-row'
                        >
                            <IonCol>
                                <IonButton class="load-btn" data-test="back-btn" onClick={() => history.push('/app/manage')}>
                                    Back
                                </IonButton>
                            </IonCol>
                            <IonCol>
                                <IonButton class="load-btn" data-test="edit-btn" onClick={() => history.push('/app/manage/ManageUser/Profile/edit')}>
                                    Edit
                                </IonButton>
                            </IonCol>
                        </IonRow>
                    </IonGrid>
                </div>
            </IonContent>
        </IonPage>
    );
} 
export default ManageUserProfileDesktopPage;