import { VariableType } from 'json-to-graphql-query';

const location = {
  city: true,
  radius: true,
  state: true,
  country: true,
  latitude: true,
  longitude: true
};

export class CapacityModel {
  /**
   * Get Capacity List
   * @param commonCarrierId
   * @param page
   * @param size
   */
  getList(commonCarrierId: string, page: number, size: number) {
    return {
      query: {
        getCarrierCapacity: {
          __args: {
            criteria: {
              commonCarrierId,
              page,
              size
            }
          },
          totalRecords: true,
          capacity: {
            capacityId: true,
            capacityType: true,
            commonCarrierId: true,
            trailerType: true,
            origin: location,
            pickUpFromDate: true,
            pickUpToDate: true,
            preferredDays: true,
            destination: location,
            destinationStates: true,
            lastUpdatedDateTime: true,
            lastUpdatedBy: true,
            lastUpdatedByPhoneNumber: true,
            lastUpdatedByEmail: true
          }
        }
      }
    };
  }

  /**
   * Add Capacity Model
   */
  addCapacity() {
    return {
      addCarrierCapacity: {
        __variables: {
          input: 'CapacityInput'
        },
        addCarrierCapacity: {
          __args: {
            input: new VariableType('input')
          },
          capacityID: true,
          lastUpdatedByUserName: true
        }
      }
    };
  }

  /**
   *  Update Capacity Model
   */
  updateCapacity() {
    return {
      updateCarrierCapacity: {
        __variables: {
          input: 'CapacityInput'
        },
        updateCarrierCapacity: {
          __args: {
            input: new VariableType('input')
          }
        }
      }
    };
  }

  /**
   *
   * @param capacityId
   */
  deleteCapacity(capacityId: string) {
    return {
      mutation: {
        deleteCarrierCapacity: {
          __args: {
            capacityId
          }
        }
      }
    };
  }
}
