import Service from "../services/common.services";

/**
 * Requests a URL, returning a promise
 *
 * @param  {string} url       The URL we want to request
 * @param  {object} [options] The arguments we want to pass to "Axios"
 *
 * @return {object}           The response data
 */
export default function request<T>(args: any) {
  var commonService = new Service<T>();
  if (
    args[0] !== "LOGIN" &&
    args[0] !== "MANAGE" &&
    args[0] !== "SEARCH" &&
    args[0] !== "RECENT" &&
    args[0] !== "APP" &&
    args[1] !== "FETCH_SEARCH_LOADS" &&
    args[0] !== "MYLOAD" &&
    args[0] !== "DRIVERS_LIST" &&
    args[0] !== "LOCATION_TRACKER" &&
    args[0] !== "FETCH_USER_DETAILS" &&
    args[0] !== "REGISTER_DEVICE" &&
    args[0] !== "MORETAB" &&
    args[0] !== "FAVORITE" &&
    args[0] !== "SUBMITFEEDBACK" &&
    args[0] !== "HOME"
  ) {
    return commonService.get(args[0], args[1]);
  } else {
    return commonService.post(
      args[0],
      args[1],
      args[2],
      args[3],
      args[4],
      args[5]
    );
  }
}
