import React, { useEffect, useState } from 'react';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { isPlatform } from '@ionic/react';
import { withRouter } from 'react-router';
import { compose } from 'redux';
import { ManageCapacity } from '../../../pages/Manage/ManageCapacity';
import {
  getNetworkStatus,
  getUserDetails,
  getRefreshStatus,
  getLoading
} from '../../../selectors/selector';
import { showNetworkAlert } from '../../../actions/App/action';
import {
  getManageCapacity,
  getManageCapacityFilter,
  getManageCapacityLoaded
} from '../../../selectors/Manage/selector';
import { CapacityModel } from '../../../models/manage/capacity.model';
import {
  getCapacityList,
  removeManageCapacity,
  setSelectedCapacity,
  setCapacityFilter,
  addManageCapacity
} from '../../../actions/manage/action';
import useInjectSaga from '../../../utils/injectSaga';
import useInjectReducer from '../../../utils/injectReducer';
import reducer from '../../../reducers/manage/reducer';
import saga from '../../../saga/Manage/saga';

const key = 'manage';

export const ManageCapacityContainer: React.FC<any> = (props: any) => {
  const { capacityList, isLoading, isCapacityLoaded, userDetails, getCapacityListAction } = props;
  const capacityModel = new CapacityModel();
  const maxPages = 5;
  const pageSize = isPlatform('desktop') ? 99 : 10;
  const [currentPage, setCurrentPage] = useState(0);
  const [isCapacityEnd, setCapacityEnd] = useState(false);

  const resetValues = () => {
    setCurrentPage(0);
    setCapacityEnd(false);
  };

  const getCapacityList = (isPullToRefresh = false, page = 0, callback: any = false) => {
    const { carrierId } = userDetails;
    const query = capacityModel.getList(carrierId, page, pageSize);
    if (isPullToRefresh) {
      resetValues();
    }
    getCapacityListAction({
      query,
      isPullToRefresh,
      callback
    });
  };

  useEffect(() => {
    if (userDetails && !isCapacityLoaded) {
      resetValues();
      getCapacityList();
    }
  }, [userDetails, isCapacityLoaded]);

  const onPageScroll = async (event: any) => {
    const scroll = await event.currentTarget.getScrollElement();
    if (scroll.scrollTop === 0) {
      return true;
    }
    if (!isPlatform('desktop') && !isCapacityEnd && !isLoading && currentPage < maxPages) {
      const page = currentPage + 1;
      const callback = (itemsCount: number) => {
        if (itemsCount === 0 || itemsCount % pageSize !== 0) {
          setCapacityEnd(true);
        }
      };
      setCurrentPage(page);
      getCapacityList(false, page, callback);
    }
  };

  return (
    <ManageCapacity
      module="manage"
      {...props}
      data={capacityList}
      getCapacityList={getCapacityList}
      onPageScroll={onPageScroll}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  isLoading: getLoading(),
  isCapacityLoaded: getManageCapacityLoaded(),
  isNetWorkAvailable: getNetworkStatus(),
  userDetails: getUserDetails(),
  isPullToRefresh: getRefreshStatus(),
  capacityList: getManageCapacity(),
  filter: getManageCapacityFilter()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  getCapacityListAction: (payload: any) => dispatch(getCapacityList(payload)),
  addCapacityItem: (payload: any) => dispatch(addManageCapacity(payload)),
  removeCapacityAction: (payload: any) => dispatch(removeManageCapacity(payload)),
  setSelectedCapacityAction: (payload: any) => dispatch(setSelectedCapacity(payload)),
  setCapacityFilter: (payload: any) => dispatch(setCapacityFilter(payload))
});

const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });
const withConnect = connect(mapStateToProps, mapDispatchToProps);

export default compose(withSaga, withReducer, withConnect)(withRouter(ManageCapacityContainer));
