export interface Type<T> extends Function {
  new(...args: any[]): T;
}

export class DomainConverter {

  static dataToDomain<T>(domain: Type<T>, dto: any) {
    const __instance = Object.create(domain.prototype);
    __instance.__carrier_state = dto;
    return __instance as T;
  }

  static domainToData<T>(domain: any) {
    const __rqData: any = { query: `${jsonToGraphQLQuery(domain as T)}` }
    console.log(__rqData);
    return JSON.stringify(__rqData);
  }

  static dataToMutation<T>(domain: any, input: any) {
    const __rqData: any = { query: `mutation ${jsonToGraphQLQuery(domain as T)}`, variables: { input}}
    console.log(__rqData);
    return JSON.stringify(__rqData);
  }
}


/**
 * To determine the entirleemnt of specified feature.
* @param {string} _tab Application navigation tab name.
* @param {string} _item feature of specified tab.
* @returns {boolean} status of access on the specified feature.
*/
export function isEntitled(_tab: string, _item?: string) {
  return ((_tab, _item) => {
    var __roleConfig = IONICWrapper.details.keys && IONICWrapper.details.keys.configuration.roleConfigurations;
    var tabScreen = "screen";
    if (!IONICWrapper.details.keys) {
      return;
    }
    if (userRole.length == 1 && userRole.includes(Roles.carrierGuest)) {
      if (_item) {
        return _item == tabScreen ? getActiveTab(_tab, userRole[0], __roleConfig) : getConfig(_tab, _item, userRole[0], __roleConfig);
      } else {
        return true;
      }
    } else {
      var isActive = false;
      for (var i = 0; i <= userRole.length - 1; i++) {
        isActive = (_item === tabScreen || !_item) ? getActiveTab(_tab, userRole[i], __roleConfig) : getConfig(_tab, _item, userRole[i], __roleConfig);
        if (isActive) {
          break;
        }
      }
      return isActive;
    }
  })(_tab, _item)
}

/**
 * To get the active tab when Application is initiated.
* @param {string} tab Application navigation tab name.
* @param {string} role role name.
* @param {Object} Setting role configuration details.
* @returns {boolean} status of access on the specified feature.
*/
function getActiveTab(tab: any, role: any, Setting: any) {
  return Object.keys(Setting).filter((item:string)=>item.includes(tab) && Setting[item]).map((item:any)=> Setting[item][Object.keys(Setting[item])[0]][role]).some(item => item);
}

/**
 * To get the specific feature details as per the Role.
* @param {string} _tab Application navigation tab name.
* @param {string} _item feature name.
* @param {string} _role role name.
* @param {Object} _setting role configuration details.
* @returns {boolean} status of access on the specified feature.
*/
function getConfig(_tab: string, _item:string, _role: any, _setting: any) {
  const __key = _tab +"."+(_item && _item.charAt(0).toUpperCase() + _item.slice(1));
  return _setting[__key][_item][_role];
}

/**
 * To get the tab name to be landed when Application is launched.
* @param {string} role role name.
* @param {Array} userRoles multirole names.
* @returns {boolean} status of access of specified feature.
*/
export async function getDashboard(role?: any, userRoles?:any) {
  var _tabName = "";
  var _tabActive = false;
  enum Tab_Types {home, search, myLoad, manage, more};
  const _roleSetting = IONICWrapper.details.keys.configuration.roleConfigurations;

  if (!_roleSetting) {
    return
  }
  userRole = userRoles;
  if (role) {
    getRole = role;
  }

  for (var i = 0; i <= Object.keys(Tab_Types).length; i++) {
    _tabActive = getActiveTab(Tab_Types[i], getRole, _roleSetting);
    if (_tabActive) {
      _tabName = Tab_Types[i];
      break;
    }
  }
  return _tabName;
}

/**
* To get authentication information and storage API.
*/
export class IONICWrapper {
  private _appVault: any;
  private _appAuth: any;
  private _appKeys: any;
  private _getValue:any = {};
  public static accessToken: any;
  private static instance: IONICWrapper;
  static get details(): IONICWrapper {
    if (!IONICWrapper.instance) {
      IONICWrapper.instance = new IONICWrapper();
    }
    return IONICWrapper.instance;
  }

  /**
   * To get authentication information.
   * @returns {object} consolidated token and user info list.
   */
  get auth() {
    return this._appAuth;
  }

  get keys() {
    return this._appKeys;
  }
  get getValue() {
    return this._getValue;
  }
  /**
 * To get authentication information.
 * @returns {object} gives storeValue and getValue methods to for storing in Vault.
 */
  get vault() {
    return this._appVault;
  }
  getVault(vault?: any) {
    this._appVault = vault || {};
  }
  getAuth(auth?: any) {
    this._appAuth = auth || {};
  }
  getKeys(key?: any) {
    this._appKeys = key || {};
  }

  set setValue(value: any) {
    this._getValue = value || {};
  }
}


export async function setObject(_keyName: string, _obj: any) {
  await Storage.set({
    key: _keyName,
    value: JSON.stringify(_obj)
  });
}

export async function getObject(_keyName: string) {
  const _ret: any = await Storage.get({ key: _keyName });
  return _ret.value && JSON.parse(_ret.value);
}

export async function setItem(_key: any, _value: any) {
  await Storage.set({
    key: _key,
    value: _value
  });
}

export async function getItem(_keyName: string) {
  const _value = await Storage.get({ key: _keyName });
  return _value;
}

export async function removeItem(_keyName: string) {
  await Storage.remove({ key: _keyName });
}

export async function keys() {
  const _keys = await Storage.keys();
  return _keys;
}

export async function clear() {
  await Storage.clear();
}
/**
   * method to check if session is active or not. if not, user will be redirected to landing screen.
   * * @param
   * @returns GPSWrapper boolean.
   */
export async function isAuthExpired() {
  const _expiresAt = await getObject(AUTH_SESSION_NAME);
  const _currentTme = Math.floor(new Date().getTime());
  const isExpired:any = (Math.floor(_currentTme - _expiresAt)/1000) > 3600;
  return isExpired;
}

export class GPSWrapper {

  private static instance: GPSWrapper;

  /**
     * singleton design pattern is used to restricts the instantiation of a class to one "single" instance.
     * * @param
     * @returns GPSWrapper object.
     */
  static get details(): GPSWrapper {
    if (!GPSWrapper.instance) {
      GPSWrapper.instance = new GPSWrapper();
    }
    return GPSWrapper.instance;
  }

  /**
     * This is a wrapper method of initiateGPSconfig.
     * initiateGPSconfig method initialize the BackgroundGeolocation plugin Configuration.
     * The Basic Configuration properlies are set. 
     * Rest of the custom properlies will be set as per need basis  
     * * @param
     * @returns null.
     */
  initiateGPSconfig() {
    initiateGPSconfig();
  }

  /**
     * This is a wrapper method of getCurrentPosition.
     * getCurrentPosition method provides the current location latitude and longitude
     * * @param {*} get CurrentGPS callback to process the current location lat, long.
     * @returns null.
     */
  getCurrentPosition(getCurrentGPS: any) {
    getCurrentPosition(getCurrentGPS);
  }

  /**
     *  This is a wrapper method of startGPStracking.
     * startGPStracking method will start the GPS tracking as well as geofence tracking.
     * * @param shipmentDetails to strat the GPS tracking as well as geofence
     *  i.g. activity,shipmentId, carrierScac, userId, sourceSystem
     * @returns null.
     */
  startGPStracking(shipmentDetails: any) {
    startGPStracking(shipmentDetails);

  }

  /**
     *  This This is a wrapper method of sendGeoStartTripLocation
     * * @param shipmentDetails to send the Geo Start Trip Location
     *  i.g. activity,shipmentId, carrierScac, userId, sourceSystem
     * @returns null.
     */
  sendGeoStartTripLocation(shipmentDetails: any) {
    sendGeoStartTripLocation(shipmentDetails);

  }

  /**
       * It will stop the GPS tracking as well as geofence tracking.
       * remove all the GPS tracking data which are stored in local DB.
       * * @param
       * @returns null.
       */
  stopGPS() {
    getGPSinstance().stop(async function () {
      console.log("- stop success");
      setGPStrackingShipmentID(null);
      await removeItem(DB_GPS_ACTIVATED_SHIPMENT);
    });
    getGPSinstance().removeGeofences();   //Destroy all [[Geofence]].
    getGPSinstance().destroyLocations();  //Remove all records in SDK's SQLite database.
    // getGPSinstance().reset(); //Resets the plugin configuration to documented default-values.
  }

  /**
     * This is a wrapper method of setGPStrackingShipmentID.
     * setGPStrackingShipmentID method will provide the GPS tracking enabled Shipment ID
     * * @param
     * @returns GPS_Tracking_ShipmentID.
     */
  getGPStrackingShipmentID() {
    return getGPStrackingShipmentID();
  }

  getLoadDetailsTrackedShipment() {
    return getLoadDetailsTrackedShipment();
  }

  async getState(){
    return await getGPSinstance().getState();
  }

  /**
     * This is a wrapper method of setGPStrackingShipmentID.
     * setGPStrackingShipmentID set the GPS tracking enabled Shipment ID
     * * @param GPS_Tracking_ShipmentID
     * @returns null.
     */
  setGPStrackingShipmentID(shipmentID: any) {
    setGPStrackingShipmentID(shipmentID);
  }

  setLoadDetailsTrackedShipment(loadDetailsRefreshedShipment: any){
    setLoadDetailsTrackedShipment(loadDetailsRefreshedShipment);
  }

  /**
       * This is a wrapper method of getGeofenceSequenceNo.
       * getGeofenceSequenceNo method will GeofenceSequenceNo
       * * @param
       * @returns GeofenceSequenceNo.
       */
  getGeofenceSequenceNo() {
    return getGeofenceSequenceNo();
  }

  /**
     * This is a wrapper method of setGeofenceSequenceNo.
     * setGeofenceSequenceNo set the GeofenceSequenceNo value
     * * @param sequenceNo
     * @returns null.
     */
  setGeofenceSequenceNo(sequenceNo: any) {
    setGeofenceSequenceNo(sequenceNo);
  }
}

/**
     * R2Configuration is a singleton Class
     * used to set and get the R2ConfigurationFlag.
     * 
     */
export class R2Configuration {

  private static instance: R2Configuration;
  private static R2ConfigurationFlag: boolean = true;

  /**
     * singleton design pattern is used to restricts the instantiation of a class to one "single" instance.
     * * @param
     * @returns R2Configuration object.
     */
  static get details(): R2Configuration {
    if (!R2Configuration.instance) {
      R2Configuration.instance = new R2Configuration();
      R2Configuration.R2ConfigurationFlag = (KEYS.RELEASE === ENV.UAT || KEYS.RELEASE === ENV.PROD) ? true : false;
    }
    return R2Configuration.instance;
  }

  /**
     * This method is used to set the R2ConfigurationFlag.
     * * @param {*} flag.
     * @returns null.
     */
  setR2FeatureEnable(flag: boolean) {
    R2Configuration.R2ConfigurationFlag = flag;
  }

  /**
     * This method is used to get the R2ConfigurationFlag.
     * * @param
     * @returns R2ConfigurationFlag.
     */
  getR2FeatureEnable() {
    return R2Configuration.R2ConfigurationFlag;
  }
}

