import { IUserInformation } from "../schemas/Exicution/exicution.schema";

export class ExicutionLoads {
  private user_details: IUserInformation = {};

  get carrierId() {
    return this.user_details.carrierId;
  }

  set carrierId(value) {
    if (value) {
      this.user_details.carrierId = value;
    }
  }
  get driverId() {
    return this.user_details.driverId;
  }

  set driverId(value) {
    if (value) {
      this.user_details.driverId = value;
    }
  }
  get filter() {
    return this.user_details.filter;
  }

  set filter(value) {
    if (value) {
      this.user_details.filter = value;
    }
  }
  // To send arguments conditionaly.
  args = () => {
    if (this.user_details.driverId) {
      return {
        carrierId: this.user_details.carrierId,
        driverId: this.user_details.driverId,
        filter: this.user_details.filter,
      };
    } else {
      return {
        carrierId: this.user_details.carrierId,
        filter: this.user_details.filter,
      };
    }
  };
  loadDetails = {
    shipmentId: true,
    lineOfBusiness: true,
    transportMode: true,
    totalWeight: true,
    weightUOM: true,
    totalDistance: true,
    distanceUOM: true,
    numberOfStops: true,
    sourceTMS: true,
    driverAssignmentStatus: true,
    assignedCarrierId: true,
    assignedTMSCarrierId: true,
    isAppointmentConfirmed: true,
    isHazardous: true,
    isHighValue: true,
    isTemperatureControlled: true,
    isFreezeProtected: true,
    equipmentType: true,
    executionStatus: true,
    masterBillOfLoading: true,
    startState: true,
    startCity: true,
    startCountry: true,
    startDateTime: true,
    shipmentServices: {
      serviceDescription: true,
    },
    loadStop: {
      stopType: true,
      sequenceNo: true,
      locationName: true,
      addressLine1: true,
      addressLine2: true,
      city: true,
      state: true,
      country: true,
      estimatedArrivalDateTime: true,
      plannedArrivalDateTime: true,
      actualArrivalDateTime: true,
      estimatedStopDuration: true,
      plannedDepartureDateTime: true,
      actualDepartureDateTime: true,
      appointmentEarlyDateTime: true,
      appointmentLateDateTime: true,
      actualETADateTime:true,
      plannedEtaDateTime: true,
    },
    shipmentInvolvedParties: {
      id: true,
      partyId: true,
      partyType: true,
      partyName: true,
      partyEmail: true,
      partyPhone: true,
    },
    assignmentTransaction: {
      transactionId: true,
      driverId: true,
      driverName: true
    }
  };
  getRequestPayload() {
    return {
      query: {
        getShipments: {
          __args: this.args(),
          inTransitCount: true,
          driverAssignedCount: true,
          carrierAssignedCount: true,
          driverAssignedPendingCount: true,
          deliveredCount: true,
          inTransitShipments: this.loadDetails,
          driverAssignedShipments: this.loadDetails,
          carrierAssginedShipments: this.loadDetails,
          driverAssignmentPendingShipments: this.loadDetails,
          deliveredShipments: this.loadDetails,
        },
      },
    };
  }
}
