import React, { useState } from 'react';
import { IonCard, IonItem, isPlatform, IonIcon, IonButton } from '@ionic/react';
import { useAuthService } from '../../services/Auth';
import './Logout.scss';
import { clear, GPSWrapper } from '../../utils/common';
import { LogoutPopUp } from '../../components/app/LogoutPopup/LogoutPopup';
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
} from '../../utils/constants';

const LogOutPage: React.FC<any> = (props) => {
  const authService = useAuthService();
  const [showLogoutModal, setShowLogoutModal] = useState(false);

  async function azureLogout() {
    if (isPlatform("capacitor")) {
      GPSWrapper.details.stopGPS();
    }
    setShowLogoutModal(false);
    await clear();
    await authService.logout();
    await props.logout();
  }
  const showLgoutPopUp = () => {
    //Cannot log out without data connection. Alert message will be shown to check network connectivity.
    if (!props.isNetworkAvailable) {
      props.showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK,
      });
    } else {
      setShowLogoutModal(true);
    }
  };

  return (
    <>
      {props.isRegistrationPage ? (
        isPlatform("mobile") ? (
          <IonIcon
            className="page-close"
            icon="close"
            onClick={() => showLgoutPopUp()}
          />
        ) : (
          <IonButton
            expand="full"
            className="cancel-button"
            onClick={() => showLgoutPopUp()}
          >
            Cancel
          </IonButton>
        )
      ) : (
        <IonCard onClick={() => showLgoutPopUp()} class="menu_list_card_logout">
          <IonItem lines="none" class="Logout_item">
            Log Out
            <img
              slot="end"
              alt="logo"
              src="/assets/icon/Logout-Icon.svg"
              className="logoutImg"
            />
          </IonItem>
        </IonCard>
      )}
      <LogoutPopUp
        showLogoutModal={showLogoutModal}
        closeShowModal={() => {
          setShowLogoutModal(false);
        }}
        logOut={async () => azureLogout()}
        isRegistrationPage={props.isRegistrationPage}
      />
    </>
  );
};
export default LogOutPage;
