import React, { PureComponent, useEffect, useState } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { createStructuredSelector } from "reselect";
// import { HomePage } from '../../pages/Home';
import useInjectSaga from "../../utils/injectSaga";
import useInjectReducer from "../../utils/injectReducer";
import {
  getApiErrorMessage,
  getRecommendedFilter,
  getTotalRecommendedRecords,
  getTotalWatchedRecords,
  getWatchedFilter,
  makeSelectRecommended,
  makeSelectRecommendedLoaded,
  makeSelectWatched,
  makeSelectWatchedLoaded,
} from "../../selectors/Home/selector";
import { RecommendedModel } from "../../models/home/Loads.model";
import reducer from "../../reducers/Home/reducer";
import saga from "../../saga/Home/saga";
import {
  addWatchLoad,
  clearApiResponseError,
  recommendedLoadsRequest,
  removeWatchLoad,
  setRecommendedLoadFilter,
  setWatchedLoadFilter,
  updateData,
  watchedLoadsRequest,
} from "../../actions/Home/action";
import {
  getDeepLinkURI,
  getNetworkStatus,
  getRefreshStatus,
  getUserDetails,
} from "../../selectors/selector";
import { showAlert } from "app/actions/App/action";
import { HomePage } from "../../pages/Home";
import * as Constants from "../../utils/constants";
import {
  doSorting,
  getRecSortingValues,
  onAnalyticsLoadPosting,
  toggleWatchLoad,
} from "./HomeContainerUtil";
import { WatchedLoadModel } from "app/models/home/WatchedLoads.model";
import { logEvent, setScreenName } from "app/utils/Analytics";
import { isEntitled } from "app/utils/common";

const key = "carrier";
export const HomeContainer: React.FC<any> = (props: any) => {
  const {
    history,
    userDetails,
    recommendedfilter,
    filter,
    getWatchedLoadsData,
    isWatchedLoaded,
    isRecommenedLoaded,
    getRecommendedLoadsData,
  } = props;
  const recommendedSection = Constants.RECOMMENDED_TAB;
  const watchedSection = Constants.WATCHED_TAB;
  var sections = [];
  const {
    location: { pathname: path },
  } = history;
  isEntitled("home", recommendedSection) && sections.push(recommendedSection);
  isEntitled("home", watchedSection) && sections.push(watchedSection);
  const [segment, setSegment] = useState<any>({
    name: path === "/app/home/watched" ? sections[1] : sections[0],
    data: {},
  });
  const [recommendedloadData, setRecommendedLoad] = useState<any>([]);
  const [watchedLoad, setWatchedLoad] = useState<any>([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [recommendedCurrentpage, setRecommendedcurrentPage] = useState(0);
  const [isWatchLoadEnd, setWatchedLoadEnd] = useState(false);
  const [isRecommendedEnd, setRecommendedLoadEnd] = useState(false);
  const pageSize = 5;

  const setPaginationValues = (page: number = 0, status: boolean = false) => {
    setCurrentPage(page);
    setWatchedLoadEnd(status);
  };

  const setRecPaginationValues = (
    page: number = 0,
    status: boolean = false
  ) => {
    setRecommendedcurrentPage(page);
    setRecommendedLoadEnd(status);
  };

  const getRecommendedLoads = (
    refreshStatus: boolean = false,
    page: number = 0,
    callback: any = false,
    sortColumn: string = "load_id",
    sortDirection: string = Constants.SORTING_ORDER_ASC,
    sorRefresh = false
  ) => {
    if (refreshStatus) {
      setRecPaginationValues();
    }
    const { isAsc, filterBy } = getRecSortingValues(recommendedfilter);
    if (
      userDetails.carrierId &&
      userDetails.carrierId !== null &&
      userDetails.carrierId !== ""
    ) {
      const defSortDirection = isAsc
        ? Constants.SORTING_ORDER_ASC
        : Constants.SORTING_ORDER_DESC;
      const _RecommendedModel: RecommendedModel = new RecommendedModel();
      _RecommendedModel.carrierId =
        (userDetails && userDetails.carrierId) || "";
      _RecommendedModel.carrierScac =
        (userDetails && userDetails.carrierScac) || "";
      _RecommendedModel.carrierTier =
        (userDetails && userDetails.carrierTier) || "";
      _RecommendedModel.page = page;
      _RecommendedModel.size = pageSize;
      _RecommendedModel.sortColumn = sortColumn || filterBy;
      _RecommendedModel.sortDirection = sortDirection || defSortDirection;
      return props.recommendedData({
        query: _RecommendedModel.getRequestPayload(),
        refreshStatus,
        sorRefresh,
        callback,
      });
    }
  };

  const getWatchedLoads = (
    refreshStatus: boolean = false,
    page: number = 0,
    callback: any = false,
    sortColumn: string = "load_id",
    sortDirection: string = Constants.SORTING_ORDER_ASC
  ) => {
    if (refreshStatus) {
      setPaginationValues();
    }
    if (
      userDetails.carrierId &&
      userDetails.carrierId !== null &&
      userDetails.carrierId !== ""
    ) {
      const _WatchedLoadModel: WatchedLoadModel = new WatchedLoadModel();
      _WatchedLoadModel.carrierId = userDetails && userDetails.carrierId;
      _WatchedLoadModel.page = page;
      _WatchedLoadModel.userId =
        (userDetails && userDetails.userPrincipalName) || "";
      _WatchedLoadModel.size = pageSize;
      _WatchedLoadModel.sortDirection = sortDirection;
      _WatchedLoadModel.sortColumn = sortColumn;
      return props.watchedData({
        query: _WatchedLoadModel.getRequestPayload(),
        refreshStatus,
        callback,
      });
    }
  };

  /**
   *  Sort data according to filter if any data changes in redux
   */
  const sortWatchedData = () => {
    const selected = filter.selected;
    const isAsc = filter.direction === Constants.SORTING_ORDER_ASC;
    let filterBy = Constants.sortByOptions[0].value;
    if (selected.length > 0) {
      filterBy = selected
        .filter((item: any) => item.selected)
        .map((item: any) => item.value)
        .join("");
    }
    const sortedList = doSorting(getWatchedLoadsData, isAsc, filterBy);
    setWatchedLoad(sortedList);
  };

  const sortRecommendedData = () => {
    const { isAsc, filterBy } = getRecSortingValues(recommendedfilter);
    const sortedList = doSorting(
      props.getRecommendedLoadsData,
      isAsc,
      filterBy
    );
    setRecommendedLoad(sortedList);
  };

  useEffect(() => {
    sortWatchedData();
    if (isWatchedLoaded && getWatchedLoadsData.length < pageSize) {
    setPaginationValues(0, true);
    }
  }, [getWatchedLoadsData]);

  useEffect(() => {
    sortRecommendedData();
    if (isRecommenedLoaded && getRecommendedLoadsData.length < pageSize) {
     setRecPaginationValues(0, true);
    }
  }, [getRecommendedLoadsData]);

  useEffect(() => {
    sortWatchedData();
  }, [filter]);

  useEffect(() => {
    sortRecommendedData();
  }, [recommendedfilter]);

  useEffect(() => {
    //  getNetworkStatus();
    logEvent(Constants.EVENT.SV_HOME, {
      screen: Constants.SCREEN.HOME,
      tab: Constants.TAB.RECOMMENDED,
    });
    // Analytics implementation
    onAnalyticsLoadPosting(segment.name);
  }, []);

  useEffect(() => {
    if (userDetails && getWatchedLoadsData.length === 0 && !isWatchedLoaded) {
      getWatchedLoads();
    }
  }, [userDetails, getWatchedLoadsData]);

  useEffect(() => {
    if (userDetails &&  getRecommendedLoadsData.length === 0 && !isRecommenedLoaded) {
      getRecommendedLoads();
    }
  }, [userDetails, getRecommendedLoadsData]);

  useEffect(() => {
    setScreenName(
      segment.name === Constants.RECOMMENDED_TAB
        ? "screen_recommended"
        : "screen_watched"
    );
    logEvent(Constants.EVENT.UE_VIEW_RECOMMENDED_TAB, {
      screen: Constants.SCREEN.HOME,
      tab: Constants.TAB.RECOMMENDED,
    });
  }, []);

  return (
    <HomePage
      {...props}
      recommendedloadData={recommendedloadData}
      getWatchedLoads={getWatchedLoads}
      getRecommendedLoads={getRecommendedLoads}
      segment={segment}
      setSegment={setSegment}
      sections={sections}
      watchedLoad={watchedLoad}
      currentPage={currentPage}
      recommendedCurrentpage={recommendedCurrentpage}
      isWatchLoadEnd={isWatchLoadEnd}
      isRecommendedEnd={isRecommendedEnd}
      doSorting={doSorting}
      setRecommendedcurrentPage ={setRecommendedcurrentPage}
      setCurrentPage ={setCurrentPage}
      setRecommendedLoadEnd={setRecommendedLoadEnd}
      setWatchedLoadEnd={setWatchedLoadEnd}
      toggleWatchLoad={toggleWatchLoad}
      pageSize={pageSize}
      onAnalyticsLoadPosting={onAnalyticsLoadPosting}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  deepLinkURI: getDeepLinkURI(),
  userDetails: getUserDetails(),
  getWatchedLoadsData: makeSelectWatched(),
  pullToRefreshStatus: getRefreshStatus(),
  filter: getWatchedFilter(),
  recommendedfilter: getRecommendedFilter(),
  isNetworkAvailable: getNetworkStatus(),
  apiError: getApiErrorMessage(),
  getRecommendedLoadsData: makeSelectRecommended(),
  isRecommenedLoaded: makeSelectRecommendedLoaded(),
  isWatchedLoaded: makeSelectWatchedLoaded(),
  totalWatchedRecords: getTotalWatchedRecords(),
  totalRecommendedRecords: getTotalRecommendedRecords(),
});

export const mapDispatchToProps = (dispatch: Function) => ({
  watchedData: (data: any) => dispatch(watchedLoadsRequest(data)),
  showAlert: (data: any) => dispatch(showAlert(data)),
  addWatchLoad: (payload: any) => dispatch(addWatchLoad(payload)),
  removeWatchLoad: (payload: any) => dispatch(removeWatchLoad(payload)),
  setWatchedFilter: (payload: any) => dispatch(setWatchedLoadFilter(payload)),
  setRecommendedFilter: (payload: any) =>
    dispatch(setRecommendedLoadFilter(payload)),
  clearApiResponseError: () => dispatch(clearApiResponseError()),
  recommendedData: (payload: any) => dispatch(recommendedLoadsRequest(payload)),
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });
export default compose(
  withSaga,
  withReducer,
  withConnect
)(React.memo(HomeContainer));
