import HTTP from './httpServices';
import { KEYS } from 'app/utils/constants';

/**
   * Provide the Geo location (latitude, longitude) from city state name
     * Google API is used to search the location
     *@param {*} {string} postal code 
     * @returns geo coordinates (latitude, longitude)
     */
export function getGeoCoderLatLong(cityState: string) {
    return new Promise(function (resolve: any, reject: any) {
        (new HTTP()).__requestRestApi(
            `https://maps.googleapis.com/maps/api/geocode/json?key=${KEYS.GOOGLE_MAPS_API_KEY}&address=${cityState}`,
            {},
            'get'
        ).then((response: any) => {
            console.log("response.data.results[0].geometry.location:::", response.data.results[0].geometry.location);
            resolve(response.data.results[0].geometry.location);
        }).catch((error: any) => {
            reject(new Error(error));
        });
    });
}
