import { createSelector } from "reselect";
import { createAtom } from "mobx";
import { myLoadInitialState } from "./../../reducers/myload/reducer";
import { MyLoadCount } from "../../schemas/myload/myload.Schema";
import { loadData } from "../../actions/Home/action";

export const selectData = (state: any) => state.myload || myLoadInitialState;

const getIdParam = (_state: any, props: any) => {
  const stringParam =
    props && props.match && props.match.params && props.match.params["id"];
  return stringParam;
};
const getModule = (_state: any, props: any) => {
  const stringParam =
    props && props.match && props.match.params && props.match.params[0];
  return stringParam;
};

// export const makeSelectImg = () => createSelector([selectData], manage => manage.profileImg);
export const makeMyLoadCardData = () =>
  createSelector(
    [selectData],
    (myload) => myload.myLoadCardData || myLoadInitialState.myLoadCardData
  );

export const getCount = () =>
  createSelector([selectData], (myload) => {
    var _count: MyLoadCount = myload.myLoadCount;
    return _count;
  });

export const getLoaddata = () =>
  createSelector([selectData], (myload) => {
    return {
      carrierLoad: myload.carrierAssigned || [],
      driverLoad: myload.driverAssigned || [],
    };
  });

export function getDriverAssignedLoads() {
  return createSelector([selectData], (myload) => {
    return myload.driverAssigned || [];
  });
}

export function getCarrierAssignedLoads() {
  return createSelector([selectData], (myload) => {
    return myload.carrierAssigned || [];
  });
}

export function getInTransitLoads() {
  return createSelector([selectData], (myload) => {
    return myload.inTransit || [];
  });
}

export function getRecentDeliveredLoads() {
  return createSelector([selectData], (myload) => {
    return myload.delivered || [];
  });
}

export const getCarrierLoad = () =>
  createSelector(selectData, getIdParam, getModule, (loaddata, id, module) => {
    if (module === "MyLoad/CarrierAssigned") {
      return (
        (loaddata &&
          loaddata.carrierAssigned.find((x: any) => x.shipmentId === id)) ||
        {}
      );
    } else if (module === "MyLoad/DriverAssigned") {
      return (
        (loaddata &&
          loaddata.driverAssigned.find((x: any) => x.shipmentId === id)) ||
        {}
      );
    } else if (module === "MyLoad/InTransit") {
      return (
        (loaddata &&
          loaddata.inTransit.find((x: any) => x.shipmentId === id)) ||
        {}
      );
    } else if (module === "MyLoad/Delivered") {
      return (
        (loaddata &&
          loaddata.delivered.find((x: any) => x.shipmentId === id)) ||
        {}
      );
    } else if (module === "MyLoad/shipmentUpdateScreen") {
      let data = [
        ...loaddata.driverAssigned,
        ...loaddata.inTransit,
        ...loaddata.delivered,
      ];
      return (data && data.find((x: any) => x.shipmentId === id)) || {};
    }
  });

export const getConfirmStartInformation = () =>
  createSelector(
    [selectData],
    (myload) => (myload && myload.confirmStart) || {}
  );

export const getStartTripShipmentStatusMessage = () =>
  createSelector(selectData, (shipmentData) => {
    return shipmentData && shipmentData.confirmStartStatusMessage;
  });
export const getBookLoadStatus = () =>
  createSelector(selectData, (BuyLoad) => BuyLoad.buy || {});

export const getShipmentDetails = () =>
  createSelector(selectData, (getShipment) => {
    let obj = {};
    if (getShipment.shipment.length > 0) {
      obj = {
        shipments: getShipment.shipment,
        hasShipment: getShipment.shipment.length > 0 ? true : false,
      };
    }
    return obj || [];
  });

export const buyloadShipmentStatus = () =>
  createSelector(
    selectData,
    (clearResponse) => clearResponse.buyLoadResponseStatus
  );

export const getWarningDialogMessage = () =>
  createSelector(
    [selectData],
    (myload) =>
      myload.warningDialogMessage || myLoadInitialState.warningDialogMessage
  );
export const updateStopInfo = () =>
  createSelector(
    selectData,
    (myload) => (myload && myload.updateStop) || {}
  );

