import React, { useEffect } from 'react';
import {
  IonButton,
  IonModal,
  IonCard,
  IonCardHeader,
  IonCol,
  IonCardContent,
  IonRow,
  IonToast
} from '@ionic/react';
import { IStopReferenceProps } from '../../../schemas/StartTrip/StartTrip';
import {
  REFERENCE,
  REFERENCE_TYPE_TEXT,
  REFERENCE_VALUE_TEXT,
  REFERENCE_DAILOAG_HEADING,
  EVENT,
  SCREEN,
  NO_REFERENCE_MSG
} from '../../../utils/constants';
import { logEvent } from '../../../utils/Analytics';

export const ReferenceDialog: React.FunctionComponent<IStopReferenceProps> = (
  props: IStopReferenceProps
) => {
  const [referenceModal, setReferenceModal] = React.useState(false);
  const [showToast, setShowToast] = React.useState(false);
  const { stopDetail, shipment } = props;
  const stopReference =
    stopDetail && stopDetail.stopReference && stopDetail.stopReference.length
      ? stopDetail.stopReference
      : [];
  const shipmentReference =
    shipment && shipment.shipmentReferences && shipment.shipmentReferences.length
      ? shipment.shipmentReferences
      : [];
  const references = [...stopReference, ...shipmentReference];
  const referenceClick = (): void => {
    if (references && references.length) {
      setReferenceModal(true);
    } else {
      setShowToast(true);
    }
  };
  const closeReferenceModal = (): void => {
    setReferenceModal(false);
  };
  // Analytics implementation
  useEffect(() => {
    logEvent(EVENT.SV_VIEW_REFERENCE, {
      screen: SCREEN.MYLOADS_NEEDS_PPWK,
      carrierId: props.carrierId,
      shipmentId: props.stopDetail.shipmentId,
      stopId: props.stopDetail.stopId
    });
  }, []);

  return (
    <>
      {referenceModal && (
        <>
          <IonModal
            isOpen={referenceModal}
            cssClass="dropdown-modal"
            backdropDismiss={false}
            onDidDismiss={() => closeReferenceModal()}>
            <div className="modal-div" onClick={() => closeReferenceModal()} data-test="modal-div">
              <IonCard className="referencemodal-card">
                <IonCardHeader className="start-trip-cardContent" onClick={closeReferenceModal}>
                  {REFERENCE_DAILOAG_HEADING}
                </IonCardHeader>
                <IonCardContent className="reference-card-content">
                  <IonRow className="reference-header">
                    <IonCol className="reference-type-header" size="6">
                      {REFERENCE_TYPE_TEXT}
                    </IonCol>
                    <IonCol className="reference-type-header" size="6">
                      {REFERENCE_VALUE_TEXT}
                    </IonCol>
                  </IonRow>

                  {references &&
                    references.map((reference: any, index: number) => (
                      <IonRow key={index} className="Reference-table-row">
                        <IonCol className="reference-val" size="6">
                          {reference.type}
                        </IonCol>
                        <IonCol className="reference-val" size="6">
                          {reference.value}
                        </IonCol>
                      </IonRow>
                    ))}
                </IonCardContent>
              </IonCard>
            </div>
          </IonModal>
        </>
      )}
      <IonToast
        isOpen={showToast}
        message={NO_REFERENCE_MSG}
        duration={5000}
        color="dark"
        onDidDismiss={() => {
          setShowToast(false);
        }}
        mode="md"
      />
      <IonButton className="load-btn reference-btn" onClick={referenceClick}>
        {REFERENCE}
      </IonButton>
    </>
  );
};
