import React, { PureComponent } from "react";
import { IonButton, IonImg, isPlatform, IonRow, IonCol } from "@ionic/react";
import { R2Configuration } from "../../utils/common";

interface errorProps {
  constantText: any;
  loads?: any;
  constantButton: any;
  addCapacity?: any;
  redirectTo?: any;
}
export class RecommendedError extends PureComponent<errorProps, any> {
  render() {
    return (
      <div className="No-Recommended">
        <img
          alt="logo"
          src="assets/icon/no-search-icon.svg"
          width="79.01px"
          height="84.59px"
        />
        <p className="recommended-Error">{this.props.constantText}</p>
        {isPlatform("desktop") ? (
          <IonRow>
            <IonCol size="6">
              <div className="SearchLoadBtn">
                <IonButton
                  expand="full"
                  shape="round"
                  onClick={this.props.loads}
                  size="large"
                  type="submit"
                  class="RecommendedSearch"
                >
                  <IonImg
                    src="assets/icon/SearchError.svg"
                    class="load-search"
                  />
                  {this.props.constantButton}
                </IonButton>
              </div>
            </IonCol>
            {this.props.addCapacity && !R2Configuration.details.getR2FeatureEnable()? (
              <>
                <IonCol size="6">
                  <div className="SearchLoadBtn">
                    <IonButton
                      expand="full"
                      shape="round"
                      onClick={this.props.addCapacity}
                      size="large"
                      type="submit"
                      class="RecommendedSearch"
                    >
                      <IonImg
                        src="assets/images/manage-add-capacity.svg"
                        class="load-search"
                      />
                      Add capacity
                    </IonButton>
                  </div>
                </IonCol>
              </>
            ) : null}
          </IonRow>
        ) : (
          <>
            {" "}
            <div className="SearchLoadBtn">
              <IonButton
                expand="full"
                shape="round"
                onClick={this.props.loads}
                size="large"
                type="submit"
                class="RecommendedSearch"
              >
                <IonImg src="assets/icon/SearchError.svg" class="load-search" />
                {this.props.constantButton}
              </IonButton>
            </div>
            {this.props.addCapacity  && !R2Configuration.details.getR2FeatureEnable() ? (
              <>
                <div className="recommended-error-btn-p">OR</div>

                <div>
                  <IonButton
                    expand="full"
                    shape="round"
                    onClick={this.props.addCapacity}
                    size="large"
                    type="submit"
                    class="RecommendedSearch"
                  >
                    <IonImg
                      src="assets/images/manage-add-capacity.svg"
                      class="load-search"
                    />
                    Add capacity
                  </IonButton>
                </div>
              </>
            ) : null}
          </>
        )}
      </div>
    );
  }
}
