import React from 'react';
import {
  IonModal,
  IonCardContent,
  IonCardHeader,
  IonCard,
  IonRow,
  IonCol,
  IonButton,
  isPlatform
} from '@ionic/react';
import * as Constants from '../../../utils/constants';
import './BookLoadModal.scss';
import { logEvent } from '../../../utils/Analytics';

interface BookLoadModalProps {
  isBookLoadModal: boolean;
  closeBookLoadModal: Function;
  bookedDetails: any;
  buyLoad: any;
  declineBookLoad: any;
  carrierId: any;
  lead: string;
}
export const BookLoadModal: React.FC<BookLoadModalProps> = ({
  isBookLoadModal,
  closeBookLoadModal,
  bookedDetails,
  buyLoad,
  declineBookLoad,
  carrierId,
  lead
}) => {
  const acceptBookLoad = () => {
    buyLoad();
    // Analytics implementation
    logEvent(Constants.EVENT.UE_LOAD_RATE_ACCEPT, {
      screen: Constants.SCREEN.LOAD_DETAILS_RATE,
      carrierId,
      lead
    });
  };
  const decimalTotalCharge = (Math.round(bookedDetails.totalCharge*100)/100).toFixed(2);
  return (
    <>
      <IonModal
        isOpen={isBookLoadModal}
        cssClass="dropdown-modal"
        backdropDismiss={false}
        onDidDismiss={() => closeBookLoadModal()}>
        <div className="modal-div" onClick={() => closeBookLoadModal()}>
          <IonCard className="BookLoadCard">
            <IonCardHeader className="start-trip-cardContent" onClick={() => closeBookLoadModal()}>
              {Constants.VIEW_RATE_DETAILS}
            </IonCardHeader>
            <IonCardContent className="book-card-content">
              <div className="book-load-cardContent">
                <IonRow>
                  <IonCol className="reference-type-header book-header" size="4">
                    {Constants.CHARGE_DESCRIPTION}
                  </IonCol>
                  <IonCol className="reference-type-header book-load-col2 book-header" size="8">
                    <IonRow>
                      <IonCol size="4.5" className="book-load-col3 ">
                        {Constants.RATE_QUALIFIER}
                      </IonCol>
                      <IonCol size="3" className="book-load-col3 ">
                        {Constants.BOOLOAD_QTY}
                      </IonCol>
                      <IonCol size="4.5" className="book-load-col">
                        {Constants.BOOKLOAD_TOTAL}
                      </IonCol>
                    </IonRow>
                  </IonCol>
                </IonRow>
                <IonCardContent className="rate-card-scroll">
                  <BookLoadTable
                    bookloadName={bookedDetails.lineHaulCharge.chargeDescription}
                    rateQualifier={bookedDetails.lineHaulCharge.rateQualifier}
                    quantity={bookedDetails.lineHaulCharge.quantity}
                    charge={bookedDetails.lineHaulCharge.charge}
                    unitCharge={bookedDetails.lineHaulCharge.unitCharge}
                  />
                  <BookLoadTable
                    bookloadName={bookedDetails.fuelCharge.chargeDescription}
                    rateQualifier={bookedDetails.fuelCharge.rateQualifier}
                    quantity={bookedDetails.fuelCharge.quantity}
                    charge={bookedDetails.fuelCharge.charge}
                    unitCharge={bookedDetails.fuelCharge.unitCharge}
                  />

                  {bookedDetails.accessorials.map((bookLoad: any, index: number) => (
                    <BookLoadTable
                      key={index}
                      bookloadName={bookLoad.accessorialDescription}
                      rateQualifier={bookLoad.accessorialCharge.rateQualifier}
                      quantity={bookLoad.accessorialCharge.quantity}
                      charge={bookLoad.accessorialCharge.charge}
                      unitCharge={bookedDetails.accessorialCharge.unitCharge}
                    />
                  ))}
                </IonCardContent>
                <IonRow className="book-load-row">
                  <IonCol>Total</IonCol>
                  <IonCol className="Total-count">${decimalTotalCharge}</IonCol>
                </IonRow>
              </div>
            </IonCardContent>
            <p className="book-load-p">{Constants.BOOK_LOAD_MODAL_ACCEPT}</p>
            <IonRow class="accept-btn-row">
              <IonCol size={isPlatform('mobile') ? '' : '5'}>
                <IonButton
                  data-test="ToastMessage-IonButton1"
                  class="load-btn "
                  onClick={() => declineBookLoad()}>
                  {Constants.BOOKLOAD_DECLINE_CTA_TEXT}
                </IonButton>
              </IonCol>
              <IonCol size={isPlatform('mobile') ? '0' : '0.8'} />
              <IonCol size={isPlatform('mobile') ? '' : '5'}>
                <IonButton
                  data-test="acceptBuyLoad"
                  class="load-btn"
                  onClick={() => acceptBookLoad()}>
                  {Constants.BOOKLOAD_ACCEPT_CTA_TEXT}
                </IonButton>
              </IonCol>
            </IonRow>
          </IonCard>
        </div>
      </IonModal>
    </>
  );
};

export const BookLoadTable = (props: any) => {
  const decimalCharge = (Math.round(props.charge*100)/100).toFixed(2);
  return (
    <>
      <IonRow className="book-header-border">
        <IonCol className="reference-val " size="4.5">
          {props.bookloadName}
        </IonCol>
        <IonCol size="7.5">
          <IonRow>
            <IonCol className="reference-val bookload-OK book-load-col3" size="4.5">
              {`${props.unitCharge}/${props.rateQualifier}`}
            </IonCol>
            <IonCol className="reference-val book-load-col3  bookload-OK" size="3">
              {props.quantity}
            </IonCol>
            <IonCol className="reference-val book-load-col " size="4.5">
              ${decimalCharge}
            </IonCol>
          </IonRow>
        </IonCol>
      </IonRow>
    </>
  );
};
