import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { withRouter } from 'react-router';
import { createStructuredSelector } from 'reselect';
import { getLoad, makeSelectWatched, makeSelectRecommended } from '../../selectors/Home/selector';
import { withIonLifeCycle } from '@ionic/react';
import { getPlatform, getNetworkStatus, getUserDetails } from '../../selectors/selector';
import LoadDetailsPage from '../../pages/LoadDetails/LoadDetails.page';
import { addWatchLoad, removeWatchLoad } from '../../actions/Home/action';
import {
  buyLoadRequest,
  buyLoadShipmentRequest,
  buyLoadStatusRemove
} from '../../actions/BookLoad/action';
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  watchedErrorText,
  WATCHED_LOAD_REMOVE
} from '../../utils/constants';
import { showAlert } from '../../actions/App/action';
import useInjectSaga from '../../utils/injectSaga';
import useInjectReducer from '../../utils/injectReducer';
import reducer from '../../reducers/myload/reducer';
import { getBookLoadStatus, getShipmentDetails } from '../../selectors/Myload/selector';
import saga from '../../saga/MyLoad/saga';
import { WatchedLoadModel } from '../../models/home/WatchedLoads.model';
import { ToastMessage } from '../../utils/ToastMessage';
import { IONICWrapper } from "../../utils/common";

const key = 'myload';
export class LoadDetailsContainer extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {};
  }

  ionViewWillEnter() {
    if(IONICWrapper.details.getValue.shipmentId){
        this.props.history.push(`/app/${ this.props.match.params[0]}`);
        IONICWrapper.details.setValue = {shipmentId: '' };
      }
  }
 
  toggleWatchLoad = (status: boolean) => {
    const {
      loadDetails,
      history: {
        push,
        location: { pathname },
      },
      isNetworkAvailable,
      showAlert,
    } = this.props;
    const { carrierId, userPrincipalName: userId } = this.props.userDetails;
    const { loadId } = loadDetails;

    if (!isNetworkAvailable) {
      showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK,
      });
      return true;
    }

    const watchDate = new Date().toISOString();
    const payload = {
      carrierId,
      userId,
      origin: '',
      loadId,
      watchDate,
    };

    const watchLoadModel = new WatchedLoadModel();
    const watchLoadCallback = (): void =>
      this.props.addWatchLoad({
        query: watchLoadModel.addWatchLoad(payload),
        loadDetails,
      });

    const callback = (error = false) => {
      if (error) {
        ToastMessage.show(watchedErrorText);
        return true;
      }
      ToastMessage.open({
        message: WATCHED_LOAD_REMOVE,
        showUndo: true,
        callback: watchLoadCallback,
      });
      if (pathname && pathname.includes('watched')) {
        push('/app/home/watched');
      }
      if (pathname && pathname.includes('recommended')) {
        this.props.recommendedeLoad(this.props.params.id);
        push('/app/home/recommended');
      }
    };
    if (status) {
      this.props.addWatchLoad({
        query: watchLoadModel.addWatchLoad(payload),
        loadDetails,
      });
    } else {
      this.props.removeWatchLoad({
        query: watchLoadModel.removeWatchLoad(payload),
        loadId,
        callback,
      });
    }
  };

  render() {
    const { loadDetails } = this.props;
    let isWatched;
    if (this.props.watchedLoadList.length > 0) {
      isWatched =
        !!this.props.watchedLoadList &&
        this.props.watchedLoadList.find((details: any) => details.loadId === loadDetails.loadId);
    } else {
      isWatched = false;
    }
    return (
      <LoadDetailsPage
        {...this.props}
        loadDetails={loadDetails}
        module={this.props.match.params[0]}
        loadId={this.props.match.params.id}
        networkStatus={this.props.networkStatus}
        showAlert={this.props.showAlert}
        isNetworkAvailable={this.props.isNetworkAvailable}
        isWatched={isWatched}
        toggleWatchLoad={this.toggleWatchLoad}
        buyLoadRequest={this.props.buyLoadRequest}
      />
    );
  }
}
const mapStateToProps = createStructuredSelector({
  loadDetails: getLoad(),
  userDetails: getUserDetails(),
  getPlatform: getPlatform(),
  networkStatus: getNetworkStatus(),
  isNetworkAvailable: getNetworkStatus(),
  watchedLoadList: makeSelectWatched(),
  recommendedLoadList: makeSelectRecommended(),
  bookLoadStatus: getBookLoadStatus(),
  getShipmentDetails: getShipmentDetails()
});
export const mapDispatchToProps = (dispatch: Function) => ({
  showAlert: (data: any) => dispatch(showAlert(data)),
  addWatchLoad: (payload: any) => dispatch(addWatchLoad(payload)),
  removeWatchLoad: (payload: any) => dispatch(removeWatchLoad(payload)),
  buyLoadRequest: (payload: any) => dispatch(buyLoadRequest(payload)),
  buyLoadShipmentRequest: (payload: any, renderCallback: Function) =>
    dispatch(buyLoadShipmentRequest(payload, renderCallback)),
  buyLoadStatusRemove: () => dispatch(buyLoadStatusRemove()),
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });
export default compose<any>(withSaga, withReducer, withConnect)(withRouter(withIonLifeCycle(LoadDetailsContainer)));
