import { createSelector } from 'reselect';
import { initialState } from "./../../reducers/manage/reducer";

const selectData = (state: any) => state.manage || initialState;

// export const makeSelectImg = () => createSelector([selectData], manage => manage.profileImg);
export const makeManageCardData = () =>
    createSelector(
        [selectData],
        manage => manage.manageCardData || initialState.manageCardData);

/**
 *  Manage User Selectors
 */
export const getManageUsers = () =>
    createSelector(
        [selectData],
        manage => manage.users.data || initialState.users.data);
        
export const getManageUsersLoaded = () =>
    createSelector(
        [selectData],
        manage => manage.users.loaded || initialState.users.loaded);
    
export const getSelectedManageUser = () =>
    createSelector(
        [selectData],
        manage => manage.users.selectedProfile || initialState.users.selectedProfile);

export const getSelectedFilter = () =>
    createSelector(
        [selectData],
        manage => manage.users.filter || initialState.users.filter);

export const isUserValidated = () =>
    createSelector(
        [selectData],
        manage => manage.users.validateUser.isUserValid);

export const getValidateUserDetails = () =>
    createSelector(
        [selectData],
        manage => manage.users.validateUser.details || initialState.users.validateUser.details);

export const isTransferRequested = () =>
    createSelector(
        [selectData],
        manage => manage.users.transfer.isRequested || initialState.users.transfer.isRequested);

export const getTransferRequestDetails = () =>
    createSelector(
        [selectData],
        manage => manage.users.transfer.details || initialState.users.transfer.details);

