import React, { useEffect, useState, useMemo } from "react";
import {
  isPlatform,
  IonModal,
  IonContent,
  IonToolbar,
  IonButtons,
  IonTitle,
  IonIcon,
  IonLoading,
} from "@ionic/react";
import { GoogleMap } from "../../../components/shared/GoogleMap/map/map";
import { Places } from "../../../components/shared/GoogleMap/map/places";
import { MapType } from "../../../components/shared/GoogleMap/map/map.type";

import "./LoadMap.scss";

export interface ILoadMapSatelliteProps {
  locationName: string;
  shipmentId: string;
  position?: any;
  showSatelliteMap: boolean;
  onDismissSatelliteMap: () => void;
}

export interface ILoadMapSatelliteState {
  isMapVisible: boolean;
  isShowMap: boolean;
}

export class LoadMapSatellite extends React.Component<
ILoadMapSatelliteProps,
ILoadMapSatelliteState
> {
  private readonly container = React.createRef<HTMLDivElement>();

  private map?: google.maps.Map;

  private readonly service?: google.maps.places.PlacesService;

  private infowindow?: google.maps.InfoWindow;

  constructor(props: ILoadMapSatelliteProps) {
    super(props);
  }

  initMap = (): void => {
    if (!this.props.showSatelliteMap) {
      return;
    }
    this.infowindow = new google.maps.InfoWindow();
    this.map = new google.maps.Map(this.container.current!, {
      center: this.props.position,
      zoom: 18,
      mapTypeId: "hybrid",
      mapTypeControl: false,
      streetViewControl: true,
      zoomControl: true,
      fullscreenControl: false,
    });
    this.createMarker(this.props.position);
  };

  createMarker = (pposition: any) => {
    const marker = new google.maps.Marker({
      map: this.map,
      position: pposition,
      icon: "assets/images/destination.svg",
    });

    google.maps.event.addListener(marker, "click", () => {
      if(this.infowindow) {
        this.infowindow.setContent(this.props.locationName);
        this.infowindow.open(this.map, marker);
      }
    });
  };

  componentDidMount() {
    setTimeout(() => {
      this.initMap();
    }, 1000);
  }

  componentDidUpdate(prevProps: ILoadMapSatelliteProps) {
    setTimeout(() => {
      this.initMap();
    }, 1000);
  }

  render() {
    const isDesktop = isPlatform("desktop");
    const {
      shipmentId,
      showSatelliteMap,
      onDismissSatelliteMap,
    } = this.props;

    return (
      <>
        <IonModal
          isOpen={showSatelliteMap}
          onDidDismiss={() => onDismissSatelliteMap()}
          animated={false}
          cssClass={`${
            isDesktop ? "satellite-view-desktop" : "satellite-view-mobile"
          }`}
        >
          <IonContent fullscreen={isDesktop}>
            <IonToolbar className="satellite-toolbar">
              <IonButtons
                slot="end"
                onClick={() => {
                  onDismissSatelliteMap();
                }}
              >
                <IonIcon className="satellite-close" icon="close" />
              </IonButtons>
              <IonTitle className="satellite-title">{`${
                isDesktop ? "Satellite view" : shipmentId
              }`}</IonTitle>
            </IonToolbar>
            <div className="satellite-map" ref={this.container}></div>
          </IonContent>
        </IonModal>
      </>
    );
  }
}
