import {
  IonCard,
  IonCardContent,
  IonGrid,
  IonRow,
  IonCol,
  IonImg,
  IonIcon
} from "@ionic/react";
import * as React from "react";
import { LoadTileHeader } from "../../../components/app/home/Load-Tile-Header";
import { LoadTileFooter } from "../../../components/app/home/Load-Tile-Footer";
import { CariierAssignLoadTileHeader } from "../../../components/app/home/carrier-assign-load-header";
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  MODULES,
  OFFLINE_MODE_STATE
} from "../../../utils/constants";

export const CardContent = (props: any) => {
  let name = "";
  if (props.showInitial) {
    let driverName: any = "";
    if (
      props.module !== MODULES.DRIVER_ASSIGNED_MODULE ||
      (props.module === MODULES.DRIVER_ASSIGNED_MODULE &&
        props.driverAssignmentStatus === true)
    ) {
      driverName =
        props.shipmentInvolvedParties &&
        props.shipmentInvolvedParties.filter((data: any) => {
          return data.partyType === "PRIMARY DRIVER";
        });
      driverName =
        driverName && driverName.length ? driverName[0].partyName : "";
    }

    if (
      props.driverAssignmentStatus === false &&
      props.module === MODULES.DRIVER_ASSIGNED_MODULE
    ) {
      driverName =
        props.assignmentTransaction &&
        props.assignmentTransaction[0] &&
        props.assignmentTransaction[0].driverName;
    }

    if (driverName) {
      const fName = driverName.slice(0, 1);
      const LName = driverName.includes(" ")
        ? driverName.slice(
            driverName.indexOf(" ") + 1,
            driverName.indexOf(" ") + 2
          )
        : "";
      name = fName.toUpperCase() + LName.toUpperCase();
    } else {
      name = "U";
    }
  }

  const onButtonClick = (e: any) => {
    if (props.isNetworkAvailable || OFFLINE_MODE_STATE) {
      e.stopPropagation();
      props.onButtonClick(e);
    } else {
      props.showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK
      });
    }
  };

  const handleStarButton = (e: any) => {
    e.preventDefault();
    props.toggleWatchLoad(props.loadId);
  };

  return (
    <IonCardContent className="card-content">
      <IonGrid class="load-grid">
        <IonRow>
          <IonCol className="card-price-col" size="4">
            <LoadTileFooter {...props} isCarrierLoad={props.isCarrierLoad} />
          </IonCol>
          <IonCol className="card-city-col">
            {props.isCarrier === true ? (
              <CariierAssignLoadTileHeader
                loadStop={props.loadStop}
                onButtonClick={(e: any) => {
                  onButtonClick(e);
                }}
                buttonName={props.buttonName}
              />
            ) : (
              <LoadTileHeader
                loadStop={props.loadStop}
                isLoadDetails={props.isLoadDetails}
                showExpandView={props.showExpandView}
                module={props.module}
                isCarrierLoad={props.isCarrierLoad}
              />
            )}
          </IonCol>
          {props.isWatch ? (
            <IonCol
              size="0.5"
              className="star_icon_col"
              onClick={handleStarButton}
            >
              <IonImg className="star_icon" src="assets/icon/watched-color.svg" />
            </IonCol>
          ) : null}
        </IonRow>
        <IonRow>
          <IonCol size="4">
            {props.showInitial && name ? (
              <IonGrid className="driver-name-div">{name}</IonGrid>
            ) : null}
          </IonCol>
          <IonCol>
            {props.buttonName ? (
              <IonRow
                class="assign-driver-row"
                onClick={
                  !props.isButtonHidden
                    ? (e: any) => onButtonClick(e)
                    : () => {}
                }
              >
                <IonCol
                  class={`${
                    props.showInitial
                      ? "change-driver-col"
                      : "assign-driver-icon-col"
                  }`}
                >
                  {props.showInitial ? (
                    <IonGrid
                      hidden={props.isButtonHidden}
                      className="chnages-driver-icon-div"
                    >
                      <IonIcon src="assets/icon/reassign-driver.svg" />
                    </IonGrid>
                  ) : (
                    <div className="assign-driver-icon-div">
                      <IonIcon src="assets/icon/add-user-icon.svg" />
                    </div>
                  )}
                </IonCol>
                <IonCol class="label-col">
                  {!props.isButtonHidden && props.buttonName}
                </IonCol>
              </IonRow>
            ) : (
              <IonRow class="assign-driver-row"></IonRow>
            )}
          </IonCol>
        </IonRow>
      </IonGrid>
    </IonCardContent>
  );
};
class LoadTile extends React.Component<any, any> {
  _updatedLoads: any;
  _source: any;
  _destination: any;
  constructor(props: any) {
    super(props);
    this.state = {
      stopCount: this.props.stopCount,
      pageName: "loadPage"
    };
    this._source = this.props.loadStop && this.props.loadStop[0];
    this._destination =
      this.props.loadStop &&
      this.props.loadStop[this.props.loadStop.length - 1];
  }

  private readonly getNavigationUrl = (): string | undefined => {
    let navigationUrl: string | undefined;
    let loadId = "";
    if (this.props.isCarrier) {
      loadId = this.props.shipmentId;
    } else {
      loadId = this.props.loadId;
    }

    if (
      this.props.module === MODULES.INTRANSIT_MODULE ||
      this.props.module === MODULES.DRIVER_ASSIGNED_MODULE ||
      this.props.module === MODULES.RECENTLY_DELIVERED_MODULE ||
      this.props.module === MODULES.CARRIER_ASSIGNED_MODULE
    ) {
      navigationUrl = undefined;
    } else {
      navigationUrl = `/app/${this.props.module}/${loadId}`;
    }
    return navigationUrl;
  };

  public onCardClick = () => {
    if (
      (this.props.module === MODULES.INTRANSIT_MODULE ||
        this.props.module === MODULES.DRIVER_ASSIGNED_MODULE ||
        this.props.module === MODULES.RECENTLY_DELIVERED_MODULE ||
        this.props.module === MODULES.CARRIER_ASSIGNED_MODULE) &&
      this.props.onRowCallback
    ) {
      this.props.onRowCallback(this.props.shipmentLoad);
    }
  };

  public render() {
    return (
      <>
        {this.props.isLoadDetails === true ? (
          <IonCard className="ion-card-updated">
            <CardContent {...this.props} />
          </IonCard>
        ) : (
          <IonCard
            routerLink={this.getNavigationUrl()}
            className="ion-card-updated"
            onClick={this.onCardClick}
          >
            <CardContent {...this.props} />
          </IonCard>
        )}
      </>
    );
  }
}

export { LoadTile };
