import {
  IDriverListRequest
} from "../../schemas/carrierAssign/CarrierAssign.schema";

export class DriverList {
  private __DriverList_state: IDriverListRequest = {};

  get carrierId() {
    return this.__DriverList_state.carrierId;
  }

  set carrierId(value) {
    if (value) {
      this.__DriverList_state.carrierId = value;
    }
  }

  get role() {
    return this.__DriverList_state.role;
  }

  set role(value) {
    if (value) {
      this.__DriverList_state.role = value;
    }
  }

  getRequestPayload() {
    return {
      query: {
        getMembers: {
          __args: {
            input: {
              carrierId: this.__DriverList_state.carrierId,
              role: this.__DriverList_state.role
            }
          },
          userPrincipalName: true,
          firstName: true,
          lastName: true,
          companyName: true,
          email: true,
          liablePartyId: true,
          liablePartyName: true,
          lastSelectedCustomer: true,
          lastSelectedCarrier: true,
          carrier: true,
          carrierId: true,
          tractorNumber: true,
          internalUser: true,
          customer: true,
          registered: true,
          mobileNumber: true,
          address: {
            addressLine1: true,
            addressLine2: true,
            addressType: true,
            city: true,
            state: true,
            postalCode: true
          },
          phone: {
            number: true,
            type: true
          }
        }
      }
    };
  }
}
