import React, { useState, useEffect } from "react";
import { IonLabel } from "@ionic/react";
import { ITrailerOutProps } from "../../../schemas/StartTrip/StartTrip";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import { ComboBox } from "./trailerComboBox";

export const TrailerOut: React.FunctionComponent<ITrailerOutProps> = (
  props: ITrailerOutProps
) => {
  const model: StartTripModel = new StartTripModel();
  const isOtmType = model.isOTMTypeShipment(props.shipment);
  const trailerOut = model.getTrailerOut(
    props.loadStop,
    props.isNextStop,
    props.shipment
  );

  useEffect(() => {
    props.onChange(trailerOut.trailerOut);
    if (trailerOut && trailerOut.activity) {
      props.changeActivity(trailerOut.activity);
    }
  }, [JSON.stringify(trailerOut), props.shipmentId, props.timestamp]);
  return (
    <>
      <IonLabel className="ETA-label">Trailer Out</IonLabel>
      <ComboBox
        value={props.value}
        onChange={(value: string) => props.onChange(value)}
        showError={props.showError}
        disabled={props.readonly || props.isNextStop}
        onFocus={() => {
          props.expandCurrentStopForcefully();
        }}
      />
    </>
  );
};
