import React, { useState, useEffect } from "react";
import { IonGrid, IonRow, IonCol, IonLabel, IonDatetime } from "@ionic/react";
import { PickUpdateError } from "../../../utils/constants";
import { getYearValues } from "../../../utils/formatData";

const PickUpTimeComponent: React.FC<any> = (props: any) => {
  const { fromDate, toDate, pickUpLocalTime, handleError } = props;
  const [showError, setShowError] = useState(false);
  const handleChange = (event: any) => {
    props.handleChange(event);
  };
  const consecYears = getYearValues() || new Date().getFullYear();
  useEffect(() => {
    if (fromDate && toDate) {
      const value = toDate.split("T")[0] < fromDate.split("T")[0];
      setShowError(value);
      handleError(value);
    }
  }, [fromDate, toDate]);

  useEffect(() => {
    setShowError(props.error);
  }, [props.error]);

  return (
    <>
      <IonGrid class="time-window">
        <IonRow class="pickuptime-title">Pick-up Date</IonRow>
        <IonRow>
          <IonCol>
            <IonLabel class="pickup-date-title">From Date</IonLabel>
            <IonDatetime
              displayFormat="MMM DD, YYYY"
              min={pickUpLocalTime && pickUpLocalTime.split("T")[0]}
              value={fromDate}
              yearValues={consecYears}
              onIonChange={(event) => handleChange(event)}
              mode="ios"
              name="fromDate"
            ></IonDatetime>
          </IonCol>
          <IonCol>
            <IonGrid class="to-date-grid">
              <IonLabel class="pickup-date-title">To Date</IonLabel>
              <IonDatetime
                displayFormat="MMM DD, YYYY"
                min={pickUpLocalTime && pickUpLocalTime.split("T")[0]}
                value={toDate}
                yearValues={consecYears}
                onIonChange={(event) => handleChange(event)}
                mode="ios"
                name="toDate"
              ></IonDatetime>
            </IonGrid>
          </IonCol>
        </IonRow>
      </IonGrid>
      {showError ? (
        <IonGrid className="pickupdate-error">
          <p>{PickUpdateError}</p>
        </IonGrid>
      ) : null}
    </>
  );
};

export default React.memo(PickUpTimeComponent);
