import React from "react";
import { isPlatform, IonLoading, IonGrid } from "@ionic/react";
import { GoogleMap } from "../../../components/shared/GoogleMap/map/map";
import {
  MapType,
  DirectionsWaypoint,
  DirectionLocation
} from "../../../components/shared/GoogleMap/map/map.type";
import { DirectionsService } from "../../../components/shared/GoogleMap/map/directionsService";
import "./LoadMap.scss";
import { GoogleMapsWrapper } from "../../../utils/common";

export interface ILoadStops {
  appointmentFrom?: Date;
  appointmentTo?: Date;
  city?: string;
  country?: string;
  deadHead?: string;
  postalCode?: string;
  specialServices?: any[];
  state?: string;
  stopId?: string;
  stopType?: string; // P-Pickup D- Drop
  timeZone?: string;
  stopSequence?: number;
  sequenceNo?: number;
  longitude?: string;
  latitude?: string;
  locationName?: string;
}
export interface ILoadMapProps {
  loadStop: ILoadStops[];
  isNetworkAvailable?: boolean;
}

interface ILoadMapState {
  isShowMap: boolean;
  origin: DirectionLocation;
  destination: DirectionLocation;
  waypoints: DirectionsWaypoint[];
  isMapVisible: boolean;
  isShowAddrInfoWindow: boolean;
}

export class LoadMap extends React.PureComponent<ILoadMapProps, ILoadMapState> {
  constructor(props: ILoadMapProps) {
    super(props);
    this.state = {
      isShowMap: false,
      origin: {
        locationName: "",
        locationType: "",
        index: -1,
        isMarkerAdded: false,
        latLng: { lat: "", lng: "" },
        postalCode: "",
        city: "",
        state: "",
      },
      destination: {
        locationName: "",
        locationType: "",
        index: -1,
        isMarkerAdded: false,
        latLng: { lat: "", lng: "" },
        postalCode: "",
        city: "",
        state: ""
      },
      waypoints: [],
      isMapVisible: false,
      isShowAddrInfoWindow: true
    };
  }

  async componentDidMount(): Promise<void> {
    const locationDetails = await this.getLocations(this.props);
    if (locationDetails.origin) {
      this.setState({
        origin: locationDetails.origin
      });
    }
    if (locationDetails.destination) {
      this.setState({
        destination: locationDetails.destination
      });
    }
    this.setState({
      waypoints: locationDetails.waypoints || [],
      isShowMap: true,
      isMapVisible: false
    });
  }

  async componentDidUpdate(prevProps: ILoadMapProps): Promise<void> {
    const locationDetails = await this.getLocations(this.props);
    const oldLocationDetails = await this.getLocations(prevProps);
    if (
      JSON.stringify(locationDetails) !== JSON.stringify(oldLocationDetails)
    ) {
      if (locationDetails.origin) {
        this.setState({
          origin: locationDetails.origin
        });
      }
      if (locationDetails.destination) {
        this.setState({
          destination: locationDetails.destination
        });
      }
      this.setState({
        waypoints: locationDetails.waypoints || [],
        isShowMap: true,
        isMapVisible: false
      });
    }
  }

  directionRenderCallback = (): void => {
    this.setState({ isMapVisible: true });
  };

  getLocations = async (
    data: ILoadMapProps
  ): Promise<Partial<ILoadMapState>> => {
    let { loadStop = [] } = data;
    let origin: DirectionLocation = {
      locationName: "",
      locationType: "",
      index: -1,
      isMarkerAdded: false,
      latLng: { lat: "", lng: "" },
      postalCode: "",
      city: "",
      state:""
    };
    let destination: DirectionLocation = {
      locationName: "",
      locationType: "",
      index: -1,
      isMarkerAdded: false,
      latLng: { lat: "", lng: "" },
      postalCode: "",
      city: "",
      state: ""
    };
    const waypoints: DirectionsWaypoint[] = [];
    const totalPoints = loadStop.length;
    loadStop = loadStop.slice().sort((load1: ILoadStops, load2: ILoadStops) => {
      if (load1.sequenceNo && load2.sequenceNo) {
        return load1.sequenceNo - load2.sequenceNo;
      }
      if (load1.stopSequence && load2.stopSequence) {
        return load1.stopSequence - load2.stopSequence;
      }
      return 0;
    });

    loadStop.forEach((item: ILoadStops, index: number) => {
      if (index === 0) {
        origin = {
          locationName: `${item.locationName} ${item.city} ${item.state} ${item.country}`,
          locationType: item.stopType || "",
          latLng: { lat: item.latitude || "", lng: item.longitude || "" },
          index,
          isMarkerAdded: false,
          postalCode: item.postalCode,
          city: item.city,
          state: item.state
        };
      } else if (index + 1 === totalPoints) {
        destination = {
          locationName: `${item.locationName} ${item.city} ${item.state} ${item.country}`,
          locationType: item.stopType || "",
          index,
          isMarkerAdded: false,
          latLng: { lat: item.latitude || "", lng: item.longitude || "" },
          postalCode: item.postalCode,
          city: item.city,
          state: item.state
        };
      } else {
        waypoints.push({
          location: {
            locationName: `${item.locationName} ${item.city} ${item.state} ${item.country}`,
            locationType: item.stopType || "",
            index,
            isMarkerAdded: false,
            latLng: { lat: item.latitude || "", lng: item.longitude || "" },
            postalCode: item.postalCode
          },
          stopover: true
        });
      }
    });

    if (origin.city && origin.state && (!origin.latLng.lat || !origin.latLng.lng)) {
      this.setState({isShowAddrInfoWindow: false});
      await this.setLatLngByCityName(origin.city +","+ origin.state, origin);
    } else {
      this.setState({isShowAddrInfoWindow: true});
    }

    if (destination.city && destination.state && (!destination.latLng.lat || !destination.latLng.lng)) {
      await this.setLatLngByCityName(destination.city +"," + destination.state, destination);
    }
    return { origin, destination, waypoints };
  };

  setLatLngByCityName = async (cityState: string, place: any) => {
    const location: any = await GoogleMapsWrapper.details.getGeoCoderLatLong(cityState);
    place.isMarkerAdded = true;
    place.latLng.lat = location.lat.toString();
    place.latLng.lng = location.lng.toString();
  };

  render() {
    const { origin, destination, waypoints, isMapVisible, isShowAddrInfoWindow } = this.state;
    const isDesktop = isPlatform("desktop");
    return (
      <>
        <IonLoading
          isOpen={!isMapVisible}
          spinner={null}
          message={`<img src= "assets/images/Schneider_Spinner-transparent.gif" className="loader-image" />`}
          show-backdrop={false}
          cssClass="loader-message"
        />
        <div
          className="load-map"
          style={{
            display: `${isMapVisible ? "block" : "none"}`,
            position: 'relative'
          }}
        >
            <IonGrid class="map-grid" style={{ height: "250px", overflow: "hidden", position: 'absolute', top: 0, left: 0, width: '100%'}}>
            {this.state.isShowMap && (
              <GoogleMap
                defaultCenter={{ lng: "105.2551", lat: "54.526" }}
                zoom={10}
                mapTypeControl={false}
                streetViewControl={false}
                mapTypeId={MapType.roadmap}
                zoomControl={isDesktop}
                fullscreenControl
              >
                <DirectionsService
                  origin={origin}
                  destination={destination}
                  waypoints={waypoints}
                  isShowAddrInfoWindow ={isShowAddrInfoWindow}
                  optimizeWaypoints
                  directionRenderCallback={this.directionRenderCallback}
                />
              </GoogleMap>
            )}
          </IonGrid>
        </div>
      </>
    );
  }
}
