export const  setDetails = (details: any, callback: Function) => {
	const { country, lat, lng, value: address } = details;
	const [city, state] = address.split(', ');
	return callback({
		city,
		state,
		country,
		lat,
		lng,
		address
	});
};

export const keyboardshowHandler = () => {
	const headerDiv = document.getElementsByClassName(
		"add-capacity-grid-mobile"
	)[0];
	headerDiv.classList.add("scroll-up");
};

export const keyboardhideHandler = () => {
	const headerDiv = document.getElementsByClassName(
		"add-capacity-grid-mobile"
	)[0];
	headerDiv.classList.remove("scroll-up");
};

export const formatStatesList = (items:any) => {
	return items.map((value: any) => {
		const state = states.find((state:any) => state.code === value);
		return (state && state.name) || value;
	  });
}