import { IonRow, IonCol } from '@ionic/react';
import { SortOnly } from 'app/components/core/SortOnly';
import React from 'react';
import * as Constants from '../../utils/constants';

export const HomeTitle = (props:any) => {

    const dropDownOptions = {
        options: Constants.sortByOptions,
        setFilter: props.setWatchedFilter,
        direction: props.filter.direction,
    };

    const recommendedDropdownOptions = {
        options: Constants.recommendedSortByOptions,
        setFilter: props.setRecommendedFilter,
        direction: props.recommendedfilter.direction,
    };

    const sortProps = props.segment.name === Constants.WATCHED_TAB ? {
        dropdown: dropDownOptions,
        direction: props.filter.direction,
        storeOptions: props.filter.selected
    }: { 
        dropdown: recommendedDropdownOptions,  
        direction: props.recommendedfilter.direction, 
        storeOptions: props.recommendedfilter.selected 
    };
 
    return ((props.watchedLoad &&
        props.watchedLoad.length > 0 &&
        props.segment.name === Constants.WATCHED_TAB) ||
        (props.recommendedloadData &&
          props.recommendedloadData.length > 0 &&
          props.segment.name === Constants.RECOMMENDED_TAB)) ? (
        <div className="short-div">
          {(props.segment.name === Constants.RECOMMENDED_TAB ||
            props.segment.name === Constants.WATCHED_TAB) && (
            <IonRow class="short-row">
              <IonCol size="5" class="rec_text">
                {props.segment.name === Constants.RECOMMENDED_TAB ? (
                  <span className="recommendations_num">
                    {props.totalRecommendedRecords}{" "}
                  </span>
                ) : (
                  <span className="recommendations_num">
                    {props.totalWatchedRecords}{" "}
                  </span>
                )}
                {props.segment.name === Constants.RECOMMENDED_TAB &&
                props.recommendedloadData.length > 1
                  ? Constants.RECOMMENDATION_COUNTS
                  : props.segment.name === Constants.RECOMMENDED_TAB &&
                  props.recommendedloadData.length === 1
                  ? Constants.RECOMMENDATION_COUNT
                  : props.segment.name === Constants.WATCHED_TAB && props.watchedLoad.length > 1
                  ? Constants.WATCHED_COUNTS
                  : props.segment.name === Constants.WATCHED_TAB && props.watchedLoad.length === 1
                  ? Constants.WATCHED_COUNT
                  : null}
              </IonCol>
              <IonCol size="7" class="sort_select">
                <div className="select_div">
                  <SortOnly
                    {...sortProps}
                  />
                </div>
              </IonCol>
            </IonRow>
          )}
        </div>
      ) : null;
};