export function* buyLoadResults(action: any) {
  const { query, data } = action.payload;
  try {
    yield put(showLoading());
    const bookloadRequest = DomainConverter.dataToMutation<any>(query, data);
    const bookloadresponse = yield call(request, [
      "MYLOAD",
      "BUY_LOAD",
      null,
      "",
      bookloadRequest,
      "application/json",
    ]);
    yield put(buyLoadResponse(bookloadresponse));
    yield put(hideLoading());
  } catch (e) {
    yield put(hideLoading());
  }
}

export function* getShipmentResults(action: any) {
  try {
    if (action && action.payload && action.payload.data.isPullToRefresh) {
      yield put(showPullToRefresh());
    } else {
      yield put(showLoading());
    }
    const getShipmentRequest = DomainConverter.domainToData<any>(
      action.payload.data.query
    );
    const getShipmentResponse = yield call(request, [
      "MYLOAD",
      "CARRIER",
      null,
      "",
      getShipmentRequest,
      "application/json",
    ]);
    yield put(buyLoadShipmentResponse(getShipmentResponse));
    if (action.payload.callback) {
      action.payload.callback();
    }
    const myLoadStore = yield select(selectors.selectData);
    const shipment = (myLoadStore && myLoadStore.shipment) || {};
    if (action.payload.data.module === MODULES.CARRIER_ASSIGNED_MODULE) {
      const selectedValue = (myLoadStore && myLoadStore.carrierAssigned) || [];
      const res = selectedValue.map(
        (obj: any) =>
          shipment.find((o: any) => o.shipmentId === obj.shipmentId) || obj
      );
      yield put(loadCarrierData(res, res.length));
    }
    if (action.payload.data.module === MODULES.DRIVER_ASSIGNED_MODULE) {
      const selectedValue = (myLoadStore && myLoadStore.driverAssigned) || [];
      const res = selectedValue.map(
        (obj: any) =>
          shipment.find((o: any) => o.shipmentId === obj.shipmentId) || obj
      );
      yield put(loadAssignedrData(res, res.length));
      yield put(shipmentForStartTrip(shipment[0]));
      yield put(shipmentForStartTripTimestamp(new Date().getTime().toString()));
    }
    if (action.payload.data.module === MODULES.INTRANSIT_MODULE) {
      const selectedValue = (myLoadStore && myLoadStore.inTransit) || [];
      const res = selectedValue.map(
        (obj: any) =>
          shipment.find((o: any) => o.shipmentId === obj.shipmentId) || obj
      );
      yield put(inTransitData(res, res.length));
      yield put(shipmentForStartTrip(shipment[0]));
      yield put(shipmentForStartTripTimestamp(new Date().getTime().toString()));
    }
    if (action.payload.data.module === MODULES.RECENTLY_DELIVERED_MODULE) {
      const selectedValue = (myLoadStore && myLoadStore.delivered) || [];
      const res = selectedValue.map(
        (obj: any) =>
          shipment.find((o: any) => o.shipmentId === obj.shipmentId) || obj
      );
      yield put(deliveredData(res, res.length));
      yield put(shipmentForStartTrip(shipment[0]));
      yield put(shipmentForStartTripTimestamp(new Date().getTime().toString()));
    }
    if (action && action.payload && action.payload.data.isPullToRefresh) {
      yield put(hidePullToRefresh());
    } else {
      yield put(hideLoading());
    }
  } catch (e) {
    if (action && action.payload && action.payload.data.isPullToRefresh) {
      yield put(hidePullToRefresh());
    } else {
      yield put(hideLoading());
    }
  }
}
export default function* rootSaga() {
  yield all([
    takeEvery(constants.PROFILE_IMAGE_SET, profileImg),
    takeEvery(constants.MYLOAD_CARD_DATA, MyLoadCardDataData),
    takeEvery(constants.SHIPMENT_LOADS, fetchShipments),
    takeEvery(
      constants.CarrierActionTypes.CONFIRM_START_INFORMATION,
      confirmStartSaga
    ),
    takeEvery(constants.CarrierActionTypes.UPDATE_STOP_INFORMATION, updateStop),

    takeEvery(constants.CarrierActionTypes.START_TRIP, stratTrip),
    takeEvery(constants.BUYLOAD_DATA_REQUEST, buyLoadResults),
    takeEvery(
      constants.CarrierActionTypes.COMPLETE_TRIP_API_REQUEST,
      completeTripSaga
    ),
    takeEvery(constants.BUYLOAD_SHIPMENT_REQUEST, getShipmentResults),
  ]);
}
