import React, { useEffect, useState } from "react";
import {
  IonPage,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonList,
  IonInput,
  IonItem,
  IonLabel,
  IonText,
  isPlatform
} from "@ionic/react";
import "./ManageUser.page.scss";
import AppHeader from "../../../components/app/Bars/Bar-header";
import { IonImage } from "../../../components/app/Manage/IonImage";
import { getApplicationInfo, logEvent } from "../../../utils/Analytics";
import {
  EVENT,
  SCREEN,
  CHANGE_PASSWORD_TITLE,
  CHANGE_PASSWORD_MESSAGE,
  BUTTONS
} from "../../../utils/constants";
import { rolesList, formatPhoneNumber } from "./ManageUserUtil";
import { ToastMessage } from "../../../utils/ToastMessage";
import { R2Configuration } from "../../../utils/common";
import { CustomModal } from "../../../components/CustomModal";
import { useAuthService } from "../../../services/Auth";

interface IProfile {
  label: string;
  value: string;
}

const ManageUserProfilePage: React.FC<any> = props => {
  const authService = useAuthService();
  const {
    selectedUser,
    history,
    isNetworkAvailable,
    showNetworkAlert,
    isEditable = true,
    isSelf = false
  } = props;
  const [isAlertOpen, setShowAlert] = useState(false);
  const alertProps = {
    title: CHANGE_PASSWORD_TITLE,
    message: CHANGE_PASSWORD_MESSAGE,
    buttons: [
      {
        label: BUTTONS.CANCEL,
        callback: () => {
          setShowAlert(false);
        }
      },
      {
        label: BUTTONS.OK,
        callback: () => {
          authService.changePassword();
          setShowAlert(false);
        }
      }
    ]
  };
  useEffect(() => {
    // Analytics implementation
    logEvent(EVENT.SV_VIEW_MY_PROFILE, {
      screen: SCREEN.MY_PROFILE,
      carrierId: selectedUser.carrierId
    });
  }, []);

  const roles =
    (selectedUser &&
      selectedUser.roles &&
      selectedUser.roles
        .map((role: any) => {
          const selectedOption = rolesList.find(
            (option: any) => option.value === role
          );
          return selectedOption ? selectedOption.name : "";
        })
        .join(" and ")) ||
    "";
  const userProfile: Array<IProfile> = selectedUser
    ? [
        {
          label: "First Name",
          value: selectedUser.firstName
        },
        {
          label: "Last Name",
          value: selectedUser.lastName
        },
        {
          label: "Email Address",
          value: selectedUser.email
        },
        {
          label: "Mobile Number",
          value: formatPhoneNumber(selectedUser.mobileNumber)
        },
        {
          label: "Role(s)",
          value: roles
        },
        {
          label: "Carrier ID",
          value: selectedUser.carrierId || "NA"
        }
      ]
    : [];

  const editData = () => {
    if (R2Configuration.details.getR2FeatureEnable()) {
      ToastMessage.show();
      return true;
    }
    if (isNetworkAvailable) {
      history.push(
        "/app/" +
          props.module +
          (isSelf ? "/ProfileDetails/edit" : "/Profile/edit")
      );
    } else {
      showNetworkAlert();
    }
  };
  return selectedUser ? (
    <IonPage className="manage-userslist-page">
      <AppHeader
        title={isSelf ? "My Profile" : "User Profile" }
        backUrl={"/app/" + props.module}
        History={props.history}
        editData={editData}
      />
      <IonContent class="custom-padding">
        {!isSelf ? (
          <IonGrid>
            <IonRow>
              <IonCol size="12">
                <div className="manage-user-profile-header">
                  <IonImage
                    firstName={selectedUser.firstName}
                    lastName={selectedUser.lastName}
                    big
                  />
                  <h4>
                    {selectedUser.firstName} {selectedUser.lastName}
                  </h4>
                </div>
              </IonCol>
            </IonRow>
          </IonGrid>
        ) : null}
        <div className="manage-users-wrapper">
          <IonList>
            {userProfile.map((profile: IProfile) => (
              <IonItem className="profile-item" key={profile.label}>
                <IonLabel
                  className="profile-label"
                  position="floating"
                  mode="ios"
                >
                  {profile.label}
                </IonLabel>
                <IonInput
                  value={profile.value}
                  readonly
                  type="text"
                  class="profile-input"
                ></IonInput>
              </IonItem>
            ))}
            {isSelf ? (
              <IonItem
                className="changePwd"
                key={"change-pwd"}
                data-test="change-pwd"
                lines="none"
                onClick={() => {
                  if (isNetworkAvailable) {
                    setShowAlert(true);
                  } else {
                    showNetworkAlert();
                  }
                }}
              >
                <IonText className="change-pwd">Change Password</IonText>
                <img
                  slot="end"
                  alt="logo"
                  src="/assets/icon/more-arrow.svg"
                  className="moresideimg"
                />
              </IonItem>
            ) : null}
          </IonList>
        </div>
      </IonContent>
      <CustomModal {...alertProps} isOpen={isAlertOpen} />
    </IonPage>
  ) : null;
};
export default ManageUserProfilePage;
