import React, { useEffect } from "react";
import {
  DirectionsRequest,
  TravelMode,
  DirectionsWaypoint,
  DirectionLocation,
} from "./map.type";

export class DirectionsService extends React.PureComponent<DirectionsRequest> {
  private initiallyLoaded: boolean;
  private readonly markers: any[];
  private markerLocation: DirectionLocation[];
  private isOriginIdentified = false;
  private isDestinationIdentified = false;

  private readonly icons = {
    start: "assets/images/origin.svg",
    waypoint: "assets/images/stops.svg",
    end: "assets/images/destination.svg",
    nfr: "assets/images/stops-green.svg",
  };
  constructor(props: DirectionsRequest) {
    super(props);
    this.initiallyLoaded = false;
    this.markers = [];
    this.markerLocation = [];
  }

  render() {
    return null;
  }

  componentDidMount() {
    this.renderDirections();
  }

  componentDidUpdate(prevProps: DirectionsRequest) {
    if (
      JSON.stringify(this.props.origin) !== JSON.stringify(prevProps.origin) ||
      JSON.stringify(this.props.destination) !==
        JSON.stringify(prevProps.destination) ||
      JSON.stringify(this.props.waypoints) !==
        JSON.stringify(prevProps.waypoints) ||
      this.initiallyLoaded === false
    ) {
      this.initiallyLoaded = true;
      this.renderDirections();
    }
  }

  renderDirections = () => {
    const {
      origin,
      destination,
      travelMode = TravelMode.DRIVING,
      waypoints,
      optimizeWaypoints,
      map,
      google,
      directionsService,
      directionsRenderer,
      directionRenderCallback,
    } = this.props as any;

    if (google) {
      let serviceWaypoints: { location: string; stopover?: boolean }[] = [];
      if (waypoints) {
        serviceWaypoints = waypoints.map(
          (item: DirectionsWaypoint, index: number) => {
            const lat =
              (item.location &&
                item.location.latLng &&
                item.location.latLng.lat) ||
              "";
            const lng =
              (item.location &&
                item.location.latLng &&
                item.location.latLng.lng) ||
              "";

            return {
              location: new google.maps.LatLng(lat, lng),
              stopover: item.stopover,
            };
          }
        );
      }
      const originLat = (origin && origin.latLng && origin.latLng.lat) || "";
      const originLng = (origin && origin.latLng && origin.latLng.lng) || "";

      const destinationLat =
        (destination && destination.latLng && destination.latLng.lat) || "";
      const destinationLng =
        (destination && destination.latLng && destination.latLng.lng) || "";

      const request = {
        origin: new google.maps.LatLng(originLat, originLng),
        destination: new google.maps.LatLng(destinationLat, destinationLng),
        travelMode,
        waypoints: serviceWaypoints,
        optimizeWaypoints,
      };

      this.clearAllMarker();
      this.clearMarkerLocations();
      directionsService.route(request, (result: any, status: string) => {
        if (status === "OK") {
          const route: any[] = result.routes[0].legs;
          if (route) {
            const total = route.length;
            this.addMarkerLocations();

            let allDestination: DirectionLocation[] = this.markerLocation.filter(
              (item: DirectionLocation) => {
                return item.locationType === "D";
              }
            );

            allDestination = allDestination
              .slice()
              .sort(
                (a: DirectionLocation, b: DirectionLocation) =>
                  a.index - b.index
              );

            this.clearAllMarker();
            route.forEach((item: any, index: number) => {
              this.drawMarkerWithCustomPin(
                item.start_location,
                item.start_address,
                map,
                allDestination[allDestination.length - 1].index
              );
              if (index + 1 === total) {
                this.drawMarkerWithCustomPin(
                  item.end_location,
                  item.end_address,
                  map,
                  allDestination[allDestination.length - 1].index
                );
              }
            });
          }
          const _map = directionsRenderer.getMap();
          directionsRenderer.setDirections(result);
          _map.setZoom(14);
        } else {
          directionsRenderer.setDirections({ routes: [] });
        }
        directionRenderCallback();
      });
    }
  };

  drawMarkerWithCustomPin = (
    startLocation: any,
    startAddress: string,
    map: any,
    destinationIndex: number
  ) => {
    let iconPath = this.icons.waypoint;
    const locationResponse: {
      locationType: string;
      index: number;
    } = this.addMarkerToTheLocation(startAddress);

    if (locationResponse.locationType === "P" && !this.isOriginIdentified) {
      iconPath = this.icons.start;
      this.isOriginIdentified = true;
    } else if (
      locationResponse.locationType === "D" &&
      !this.isDestinationIdentified &&
      locationResponse.index === destinationIndex
    ) {
      iconPath = this.icons.end;
      this.isDestinationIdentified = true;
    } else if (locationResponse.locationType === "NFR") {
      iconPath = this.icons.nfr;
    }

    this.createMarker(startLocation, startAddress, map, iconPath);
  };

  createMarker = (position: any, address: string, map: any, icon: any) => {
    const marker = new google.maps.Marker({
      position,
      map,
      icon,
    });
    
    if(!this.props.isShowAddrInfoWindow){
      let data = address.split(', ');
      if(data && data.length> 1){
        address = data[1] + ", " + data[2];
      }
    }

    const infoWindow = new google.maps.InfoWindow({
      content: address,
    });
    
    google.maps.event.addDomListener(marker, "click", function() {
      infoWindow.open(map, marker);
    });
    this.markers.push(marker);
  };

  clearAllMarker = () => {
    if (this.markers) {
      this.markers.forEach((marker) => {
        marker.setMap(null);
      });
    }
  };
  addMarkerLocations = () => {
    const { origin, destination, waypoints } = this.props;
    let _markerLocations: DirectionLocation[] = [];
    _markerLocations.push(origin);
    _markerLocations.push(destination);
    if (waypoints) {
      waypoints.forEach((item: DirectionsWaypoint, index: number) => {
        _markerLocations.push(item.location);
      });
    }
    _markerLocations = _markerLocations
      .slice()
      .sort((a: DirectionLocation, b: DirectionLocation) => a.index - b.index);

    this.markerLocation = _markerLocations;
  };
  addMarkerToTheLocation = (
    locationName: string
  ): { locationType: string; index: number } => {
    let locationResponse: { locationType: string; index: number } = {
      locationType: "",
      index: 0,
    };

    if (this.markerLocation) {
      const locationParts: string[] = locationName.split(",");
      let locationMatched = false;
      this.markerLocation.forEach(
        (location: DirectionLocation, index: number) => {
          if (locationMatched) {
            return false;
          }
          let name: string = location.locationName;
          name = name.toUpperCase();
          let countSubstr = 0;
          locationParts.forEach((str: string) => {
            if (name.includes(str.toUpperCase())) {
              countSubstr++;
            }
          });

          if (
            // locationParts.length - 1 <= countSubstr &&
            2 <= countSubstr &&
            !this.markerLocation[index].isMarkerAdded
          ) {
            this.markerLocation[index].isMarkerAdded = true;
            locationResponse = {
              locationType: this.markerLocation[index].locationType,
              index,
            };
            locationMatched = true;
          }
        }
      );
    }

    return locationResponse;
  };
  clearMarkerLocations = () => {
    this.markerLocation = [];
    this.isDestinationIdentified = false;
    this.isOriginIdentified = false;
  };
}
