import React, { useEffect } from "react";
import { IonDatetime, IonLabel } from "@ionic/react";
import { IArrivalProps } from "../../../schemas/StartTrip/StartTrip";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import { getYearValues } from "../../../utils/formatData";

const model: StartTripModel = new StartTripModel();
export const Arrival: React.FunctionComponent<IArrivalProps> = (
  props: IArrivalProps
) => {
  const arrivalDate = model.getArrival(props.loadStop, props.isNextStop);
  const yearVales = getYearValues() || new Date().getFullYear();

  useEffect(() => {
    props.onChange(arrivalDate);
  }, [arrivalDate, props.shipmentId, props.timestamp]);

  // new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString();

  return (
    <>
      <IonLabel className="arrival-label ">
        {model.getArrivalLabelName(props.loadStop, props.isNextStop)}
      </IonLabel>

      <IonDatetime
        readonly={model.isArrivalReadonly(props.loadStop) || props.isNextStop}
        disabled={model.isArrivalReadonly(props.loadStop) || props.isNextStop}
        displayFormat="MMM DD hh:mm A"
        pickerFormat="YYYY MMM DD hh:mm A"
        value={props.value}
        minuteValues="00,05,10,15,20,25,30,35,40,45,50,55"
        onIonChange={e => {
          if (e.detail.value) {
            props.onChange(e.detail.value);
          }
        }}
        yearValues={yearVales}
        display-timezone={props.timeZone}
        className={`start-trip-location4 execution-field arrival-width2 ${
          model.isArrivalReadonly(props.loadStop) ? "hide-border" : ""
        } ${props.isError ? "execution-validation-error" : ""}`}
        mode="ios"
      />
    </>
  );
};
