import {
  IonContent,
  IonPage,
  isPlatform,
  IonToast, IonInfiniteScroll, IonInfiniteScrollContent
} from "@ionic/react";
import React from "react";
import "./Home.page.scss";
import { SegmentContent } from "../../pages/Home/Segment-content.page";
import AppHeader from "../../components/app/Bars/Bar-header";
import { PullToRefresh } from "../../components/PullToRefresh";
import * as Constants from "../../utils/constants";
import { HomeTitle } from "./HomeTitle";

export const Home: React.FC<any> = (props:any) => {

  const onPageScroll = async (event: any) => {
    if (
      props.segment.name === Constants.WATCHED_TAB &&
      !isPlatform("desktop") &&
      !props.pullToRefreshStatus &&
      !props.loading
    ) {
      const pages = Math.floor(props.totalWatchedRecords / props.pageSize);
      const maxPages = props.totalWatchedRecords % props.pageSize === 0 ? pages - 1 : pages;
      if (!props.isWatchLoadEnd && props.currentPage < maxPages) {
        const page = props.currentPage + 1;
        const callback = (itemsCount: number) => {
          if (itemsCount === 0 || itemsCount % props.pageSize !== 0) {
            props.setWatchedLoadEnd(true);
          }
        };
        props.setCurrentPage(page);
        props.getWatchedLoads(false, page, callback);

        // Analytics implementation
        props.onAnalyticsLoadPosting(props.segment.name);
      } else {
        props.setWatchedLoadEnd(true);
      }
    }
    if (
      props.segment.name === Constants.RECOMMENDED_TAB &&
      !isPlatform("desktop") &&
      !props.pullToRefreshStatus &&
      !props.loading
    ) {
      recommendedpagination();
    }
    (event.target as HTMLIonInfiniteScrollElement).complete();
  };

  const recommendedpagination = () => {
    const recommenedPages = Math.floor(props.totalRecommendedRecords / props.pageSize);
    const maxrecommendedpages =
      props.totalRecommendedRecords % props.pageSize === 0
        ? recommenedPages - 1
        : recommenedPages;
    if (!props.isRecommendedEnd && props.recommendedCurrentpage < maxrecommendedpages) {
      const recommendedPage = props.recommendedCurrentpage + 1;
      const callback = (itemsCount: number) => {
        if (itemsCount === 0 || itemsCount % props.pageSize !== 0) {
          props.setRecommendedLoadEnd(true);
        }
      };
      props.setRecommendedcurrentPage(recommendedPage);
      props.getRecommendedLoads(false, recommendedPage, callback);
      // Analytics implementation
      props.onAnalyticsLoadPosting(props.segment.name);
    } else {
      props.setRecommendedLoadEnd(true);
    }
  };

  return (
    <IonPage className="desktop-page">
      <IonToast data-test="ion-toast"
        isOpen={!!props.apiError}
        message={props.apiError}
        duration={5000}
        color="dark"
        onDidDismiss={() => {
          props.clearApiResponseError();
        }}
      />
      <AppHeader
        title="Loads"
        getSegment={(e: any) => {
          props.setSegment({ name: e.detail.value as any, data: {} });
        }}
        segments={props.sections}
        activeSegment={props.segment.name}
      />
      <IonContent
        className="ion-padding custom-padding load-page-content home-page"
        class="background"
      >
        <PullToRefresh data-test="pull-to-refresh"
          onPullToRefresh={
            props.segment.name === Constants.WATCHED_TAB ? props.getWatchedLoads : props.getRecommendedLoads
          }
          status={props.pullToRefreshStatus}
        />
        <HomeTitle {...props} />
        <SegmentContent
          data-test="Segment-Content"
          history={props.history}
          type={props.segment.name}
          isloaded={props.loading}
          recommendedloads={props.recommendedloadData}
          watchedLoad={props.watchedLoad}
          isWatchLoadEnd={props.isWatchLoadEnd}
          isRecommendedLoadEnd={props.isRecommendedEnd}
          load={props.segment.data}
          setView={(load: any) => {
            props.setSegment({ name: "View", data: load });
          }}
          showAlert={props.showAlert}
          isNetworkAvailable={props.isNetworkAvailable}
          toggleWatchLoad={props.toggleWatchLoad(props)}
        />
        <IonInfiniteScroll onIonInfinite={onPageScroll}>
          <IonInfiniteScrollContent  loadingSpinner={null} loadingText={''}>
          </IonInfiniteScrollContent>
        </IonInfiniteScroll>
      </IonContent>
    </IonPage>
  );
};

export default Home;
