import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { isPlatform } from '@ionic/react';
import LoadDetailsPage from '../../pages/LoadDetails/LoadDetails.page';
import { getPlatform, getUserDetails, getNetworkStatus } from '../../selectors/selector';
import { getCarrierLoad } from '../../selectors/Myload/selector';
import { AssignDriver } from '../../actions/carrierAssign/action';
import reducer from '../../reducers/carrierAssign/reducer';
import saga from '../../saga/CarrierAssign/saga';
import useInjectReducer from '../../utils/injectReducer';
import useInjectSaga from '../../utils/injectSaga';
import { DriverList } from '../../models/home/CarrierAssign.model';
import { getDriverList } from '../../selectors/carrierAssign/selector';
import { showAlert } from '../../actions/App/action';

const key = 'carrierAssigned';
class MyLoadDetailsContainer extends Component<any, any> {
  __model: DriverList;

  constructor(props: any) {
    super(props);
    this.state = {};
    this.__model = new DriverList();
  }

  assignDriverBtn = () => {
    const loadId = this.props.loadDetails.shipmentId;
    if (this.props.userDetails && this.props.userDetails.carrierId) {
      const modelList: DriverList = new DriverList();
      modelList.carrierId = this.props.userDetails.carrierId;
      modelList.role = 'carrierDriver';
      this.props.AssignDriver(modelList.getRequestPayload());
      if (isPlatform('mobile')) {
        this.props.history.push(`/app/MyLoad/driverAssign/${loadId}`, {
          loadDetails: JSON.stringify(this.props.loadDetails),
          pathname: this.props.location.pathname
        });
      }
    }
  };
getValue = (key:any)=>{
 return this.props &&
      this.props.history &&
      this.props.history.location &&
      this.props.history.location.state &&
      this.props.history.location.state[key];
}
  render() {
    const { loadDetails, match, desktopDriverList, isNetworkAvailable } = this.props;
    const bookload = this.getValue("shipments");
    const bookloadStatus = this.getValue("isBookLoad");
    const prevModule = this.getValue("module");
    let shipmentDetails = loadDetails;
    if(Object.keys(loadDetails).length === 0 && bookloadStatus && bookload){
      shipmentDetails = bookload[0];
    }
    return (
      <LoadDetailsPage
        {...this.props}
        driverList={
          desktopDriverList && desktopDriverList.data && desktopDriverList.data.getMembers
        }
        assignDriverBtn={this.assignDriverBtn}
        loadDetails={shipmentDetails}
        module={match.params[0]}
        loadId={match.params.id}
        showExpandView
        isCarrierLoad
        networkStatus={isNetworkAvailable}
        bookloadStatus = {bookloadStatus}
        prevModule = {prevModule}
      />
    );
  }
}
const mapStateToProps = createStructuredSelector({
  loadDetails: getCarrierLoad(),
  getPlatform: getPlatform(),
  desktopDriverList: getDriverList(),
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus()
});

const mapDispatchToProps = (dispatch: Function) => ({
  AssignDriver: (data: any) => dispatch(AssignDriver(data)),
  showAlert: (data: any) => dispatch(showAlert(data))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(MyLoadDetailsContainer);
