import {
  IonContent,
  IonPage,
  IonList,
  IonGrid,
  IonRow,
  IonCol,
  IonLoading
} from "@ionic/react";
import { toastController } from "@ionic/core";
import React, { Component } from "react";
import { createStructuredSelector } from "reselect";
import { connect } from "react-redux";
import { compose } from "redux";
import AppHeader from "../../../components/app/Bars/Bar-header";
import { Loads } from "../../../components/app/home/Loads";
import { LoadTile } from "../../../components/app/home/Load-Tile";
import "./CarrierAssigned.scss";
import CarrierAssignedDropdown from "../../../components/shared/carrierAssignedDropDown/carrierAssignedDropDown";
import DriverNameFilter from "../../../components/shared/CarrierAssignFilter/DriverNameFilter";
import {
  makeMyLoadCardData,
  getCount
} from "../../../selectors/Myload/selector";
import { IONICWrapper, isEntitled } from "../../../utils/common";
import {
  getRefreshStatus,
  getNetworkStatus
} from "../../../selectors/selector";
import PullToRefresh from "../../../components/PullToRefresh/PullToRefresh";
import {
  shipmentForStartTrip,
  shipmentForStartTripTimestamp
} from "../../../actions/carrierAssign/action";
import sortLoads from "../../../utils/getSortedLoads";
import { GetShipment } from "../../../models/home/GetShipment.model";
import { buyLoadShipmentRequest } from "../../../actions/BookLoad/action";
import {
  ExecutionStatus,
  MODULES,
  EVENT,
  SCREEN
} from "../../../utils/constants";
import { getApplicationInfo, logEvent } from "../../../utils/Analytics";
import { clearUpdateStopInformationResponse } from "app/actions/myload/action";

export class CarrierAssignedPage extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      isMyLoad: true,
      actualData: [],
      filteredData: [],
      selectedLoad: null
    };
  }

  componentDidMount() {
    const loads = sortLoads(this.props.loads);
    this.setState({
      filteredData: loads,
      actualData: loads
    });
    // Analytics implementation
    this.sendAnalyticsEvent(
      this.props.subModule,
      this.props.userDetails.carrierId
    );
  }

  componentDidUpdate(prevProps: any, prevState: any) {
    if (
      JSON.stringify(this.props.loads) !== JSON.stringify(prevProps.loads) ||
      JSON.stringify(prevState.filteredData) !==
        JSON.stringify(this.state.filteredData)
    ) {
      let { filteredData } = this.state;
      if (
        this.props.subModule === MODULES.CARRIER_ASSIGNED_MODULE ||
        !isEntitled("myLoad", "carrierAssigned")
      ) {
        filteredData = this.props.loads;
      } else {
        filteredData = this.state.filteredData;
      }
      const loads = sortLoads(filteredData);
      this.setState({
        filteredData: loads,
        actualData: this.props.loads
      });
    }
  }

  /**
   * Send Analytics Event base on screen
   * * @param {*} pageName, carrierId
   * @returns null.
   */
  sendAnalyticsEvent = (pageName: string, carrierId: any) => {
    let event: string;
    let screen: string;
    let event1 = "";
    let screen1 = "";
    switch (pageName) {
      case MODULES.CARRIER_ASSIGNED_MODULE:
        event = EVENT.SV_VIEW_CARRIER_ASSIGNED;
        screen = SCREEN.MYLOADS_CARRIER_ASSIGNED;
        break;
      case MODULES.DRIVER_ASSIGNED_MODULE:
        event = EVENT.SV_ASSIGN_DRIVER;
        screen = SCREEN.MYLOADS_DRIVER_ASSIGNMENT;
        event1 = EVENT.SV_VIEW_DRIVER_ASSIGNED;
        screen1 = SCREEN.MYLOADS_DRIVER_ASSIGNED;
        break;
      case MODULES.INTRANSIT_MODULE:
        event = EVENT.SV_VIEW_IN_TRANSIT;
        screen = SCREEN.MYLOADS_IN_TRANSIT;
        break;
      case MODULES.RECENTLY_DELIVERED_MODULE:
        event = EVENT.SV_VIEW_RECENTLY_DEL;
        screen = SCREEN.MYLOADS_RECENTLY_DEL;
        break;
      default:
        return;
    }
    logEvent(event, {
      screen,
      carrierId
    });

    if (event1 !== "") {
      logEvent(event1, {
        screen: screen1,
        carrierId
      });
    }
  };

  showToaster = async () => {
    const toast = await toastController.create({
      color: "dark",
      duration: 300,
      message: "Driver Assigned.",
      mode: "md",
      cssClass: "driver-assign-confrim"
    });
    await toast.present();
    await toast.dismiss(this.props.chnageValue());
  };

  selectDriver = (e: any, loadDetails: any) => {
    e.preventDefault();
    if (this.props.isInPullToRefresh) return 0; // Pull to Refresh in Process prevent Assign driver Functionality.
    if (this.props.buttonName === "Assign Driver") {
      this.props.AssignDriverbtn(loadDetails);
    }
  };

  filterByDriver = (filteredData: any) => {
    if (isEntitled("myLoad", "carrierAssigned")) {
      this.setState({ filteredData });
    }
  };

  // redirect to selected page
  redirect = (url: string) => {
    if (url) {
      this.props.history.push(url);
    }
  };

  startTripOk = async () => {
    const DriverAssignedStartTrip = this.props.subModule;
    const { selectedLoad } = this.state;
    let isUserCarrierDriver = false;
    let isGPStrackingDisabled = false;
    await IONICWrapper.details.auth.user.roles.forEach(
      async (role: any, count: any) => {
        if (role === "carrierDriver") {
          isUserCarrierDriver = true;
        }
      }
    );
    if (!isUserCarrierDriver) {
      isGPStrackingDisabled = true;
    }
    this.props.setShipmentForStartTrip(selectedLoad);
    this.setState({ isStartTrip: false, selectedLoad: null }, () =>
      this.props.history.push({
        pathname: `/app/MyLoad/shipmentUpdateScreen/${selectedLoad.shipmentId}`,
        state: {
          isCurrentLocationOn: isGPStrackingDisabled,
          isLocation: false,
          module: DriverAssignedStartTrip,
          withGPS: true,
          startTripShipment: selectedLoad,
          moduleName: "INTRANSIT"
        }
      })
    );
  };

  dismissSortingLoader = () => {
    this.setState({ isSorting: false });
  };

  /*
   * fetch the all shipment details while hitting the load for load details or execution page
   * @data - existing shipment details
   */
  getShipmentDetails = (data: any): any => {
    const shipmentId = (data && data.shipmentId) || "";
    const getShipmentmodel: GetShipment = new GetShipment();
    const module = this.props.subModule;
    getShipmentmodel.shipmentIds = shipmentId;
    this.props.buyLoadShipmentRequest({
      query: getShipmentmodel.getRequestShipmentPayload(),
      shipmentId,
      module,
      isPullToRefresh: false
    });
    if (
      this.props.subModule === MODULES.INTRANSIT_MODULE ||
      (data &&
        data.executionStatus === ExecutionStatus.STARTED &&
        this.props.subModule === MODULES.DRIVER_ASSIGNED_MODULE) ||
      this.props.subModule === MODULES.RECENTLY_DELIVERED_MODULE
    ) {
      this.setState({ isStartTrip: true, selectedLoad: data }, () => {
        this.startTripOk();
      });
    } else {
      this.props.history.push(`/app/${this.props.subModule}/${shipmentId}`);
    }
    this.props.clearUpdateStopInfo();
  };

  render() {
    if (this.props.getToasterValue) {
      this.showToaster();
    }
    // conditionally pass value to dropdown
    const [...myLoadCard] = this.props.myLoadCard;

    return (
      <>
        <IonLoading
          isOpen={this.state.isSorting}
          onDidDismiss={this.dismissSortingLoader}
        />
        <IonPage className="desktop-page carrier-assign-page">
          <AppHeader
            title="My Loads"
            isMyLoad={this.state.isMyLoad}
            backUrl={`/app/${this.props.module}`}
            History={this.props.history}
          />
          <IonContent className="carrierAssignedContent">
            <PullToRefresh
              onPullToRefresh={this.props.getLoads}
              status={this.props.isInPullToRefresh}
            />

            <IonGrid class="sub-header-grid">
              <IonRow class="sub-header-row">
                <IonCol class="count-col">
                  {this.props.showFilter ? (
                    <DriverNameFilter
                      data={this.state.actualData}
                      driverNameList={this.props.driverNameList}
                      onSelect={this.filterByDriver}
                      module={this.props.subModule}
                    />
                  ) : (
                    <>
                      {this.state.actualData && this.state.actualData.length}
                      {isEntitled("myLoad", "driverLoads") ? (
                        <>
                          {this.props.subModule === "MyLoad/DriverAssigned" ? (
                            <> Assigned</>
                          ) : (
                            <>
                              {this.props.subModule === "MyLoad/InTransit" ? (
                                <> In-Transit</>
                              ) : (
                                <>
                                  {" "}
                                  {this.state.actualData &&
                                  this.state.actualData.length <= 1
                                    ? "Load"
                                    : "Loads"}
                                </>
                              )}
                            </>
                          )}
                        </>
                      ) : (
                        <> Assigned</>
                      )}
                    </>
                  )}
                </IonCol>
                <IonCol class="dropdown-col">
                  <CarrierAssignedDropdown
                    options={myLoadCard}
                    page={this.props.subModule}
                    redirect={this.redirect}
                    loadsCount={this.props.myLoadCount}
                  />
                </IonCol>
              </IonRow>
            </IonGrid>
            <Loads segmentName="" loads={this.state.filteredData}>
              {({ loads }: { loads?: any }) => {
                return (
                  <IonList className="loadTilePad">
                    {loads &&
                      loads.map((load: any, index: number) => (
                        <LoadTile
                          key={index}
                          {...load}
                          module={this.props.subModule}
                          onButtonClick={(e: any) => this.selectDriver(e, load)}
                          isCarrier
                          isButtonHidden={this.props.isButtonHidden}
                          buttonName={this.props.buttonName}
                          showInitial={this.props.showInitial}
                          isCarrierLoad
                          shipmentLoad={load}
                          showAlert={this.props.showAlert}
                          isNetworkAvailable={this.props.isNetworkAvailable}
                          onRowCallback={(selectedLoad: any) => {
                            this.getShipmentDetails(selectedLoad);
                          }}
                          showLoadNo={true}
                          data-test="LoadTile"
                        />
                      ))}
                  </IonList>
                );
              }}
            </Loads>
            <div className="end-of-list">End of list</div>
          </IonContent>
        </IonPage>
      </>
    );
  }
}
const mapStateToProps = createStructuredSelector({
  myLoadCard: makeMyLoadCardData(),
  myLoadCount: getCount(),
  isInPullToRefresh: getRefreshStatus(),
  isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  setShipmentForStartTrip: (data: any) => {
    dispatch(shipmentForStartTrip(data));
    dispatch(shipmentForStartTripTimestamp(new Date().getTime().toString()));
  },
  buyLoadShipmentRequest: (payload: any) =>
    dispatch(buyLoadShipmentRequest(payload)),
  clearUpdateStopInfo: () => dispatch(clearUpdateStopInformationResponse())
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
export default compose(withConnect)(CarrierAssignedPage);
