import * as React from 'react';

import {
  IonHeader,
  IonRow,
  IonCol,
  IonImg,
  IonGrid,
  IonButton,
  IonIcon,
  isPlatform
} from '@ionic/react';
import './search-header.scss';
import { withRouter, RouteComponentProps } from 'react-router';
import BackIcon from '../../../utils/BackIcon-js/Back';
import cross from '../../../utils/crossIcon-js/cross';
import NetworkErrorToaster from '../../../components/NetworkErrorToaster/NetworkErrorToaster';
import { SegmentControll } from '../../../components/app/Bars/segment-controll';
import ImageCapture from '../../../components/ImageCapture/ImageCapture';
import {
  END_USER_TITLE,
  LOADS_TITLE,
  SEARCH_TITLE,
  ADD_DRIVER_DISPATCHER_TITLE,
  FREQUENTLY_ASKED_QUESTIONS_TITLE,
  MENU_TITLE
} from '../../../utils/constants';

interface HeaderProps extends RouteComponentProps {
  title: any;
  getSegment?: any;
  segments?: any;
  isAddDriver?: any;
  activeSegment?: any;
  isMap?: any;
  backUrl?: any;
  editData?: any;
  viewMap?: any;
  viewDeviceInfo?: any;
  isDeviceInfo?: any;
  isSearch?: any;
  tab?: any;
  isLoadDetails?: any;
  History?: any;
  isMyLoad?: any;
  isStar?: boolean;
  isWatched?: boolean;
  toggleWatchLoad?: Function;
  addPage?: Function;
  customBackUrl?: Function;
  imageCapature?: any;
}
const AppHeader: React.FC<HeaderProps> = ({
  title,
  getSegment,
  segments,
  activeSegment,
  backUrl,
  isAddDriver,
  isMap,
  editData,
  viewMap,
  isDeviceInfo,
  viewDeviceInfo,
  isSearch,
  tab,
  isLoadDetails,
  History,
  isMyLoad,
  isStar,
  isWatched,
  toggleWatchLoad,
  addPage,
  customBackUrl,
  imageCapature
}) => {
  let displayValue = '';
  if (tab && tab.indexOf('/') !== -1) {
    displayValue = tab.replace('/', '\xa0\xa0\xa0|\xa0\xa0\xa0');
  } else {
    displayValue = tab;
  }
  const navigate = () => {
    if (isPlatform('desktop') && backUrl === '/app/search/results') {
      backUrl = '/app/search';
    }
    History.push(backUrl);
  };
  return (
    <IonHeader
      class={
        title === 'Manage'
          ? 'page-manage'
          : title === 'Loads' || title === 'Search'
          ? `page-header`
          : `page-header-searchResults`
      }>
      <IonRow class="header-desc">
        <IonCol size="2">
          {(backUrl || customBackUrl) && (
            //  <IonBackButton class="cross_backbutton" icon={cross} text="" defaultHref={backUrl}  ></IonBackButton>
            <IonButton
              onClick={() => (customBackUrl ? customBackUrl() : navigate())}
              className={`header-back-button ${
                title === 'Select A Driver' ? 'cross_backbutton' : ''
              }`}>
              <IonIcon icon={title === 'Select A Driver' ? cross : BackIcon} />
            </IonButton>
          )}
        </IonCol>

        <IonCol
          class="title-col"
          size={
            title === END_USER_TITLE ||
            title === ADD_DRIVER_DISPATCHER_TITLE
              ? '10'
              : '7' && title === MENU_TITLE
                ? '5'
                : '7'
          }>
          <div
            id={
              title === LOADS_TITLE || title === SEARCH_TITLE
                ? `header-title`
                : title === END_USER_TITLE ||
                  title === ADD_DRIVER_DISPATCHER_TITLE
                  ? `header-title-more`
                  : `header-title-sm`
            }>
            {title === "Frequently Asked Questions" ? "FAQ" : title}
          </div>
        </IonCol>
        <IonCol size={title === 'Menu' ? '5' : '3'} class="header-desc-col">
          {isMap && (
            <>
              <div id="header-icon" className="edit_icon">
                <IonImg
                  alt="logo"
                  src="assets/icon/shipment-header-map.svg"
                  onClick={() => viewMap()}
                />
                <div className="start-trip-header">Map</div>
              </div>
            </>
          )}
          {addPage && (
            <>
              <div id="header-icon" className="edit_icon">
                <IonImg alt="logo" src="assets/icon/Add_Black.svg" onClick={() => addPage()} />
                <div className="edit_text">Add</div>
              </div>
            </>
          )}
          {isDeviceInfo && (
            <>
              <div id="header-icon" onClick={() => viewDeviceInfo()} className="edit_icon">
                <IonImg alt="logo" src="assets/icon/help.svg" />
                <div className="start-trip-header">Device Info</div>
              </div>
            </>
          )}
          {editData && (
            <>
              <div id="header-icon" className="edit_icon" onClick={() => editData()}>
                <IonImg alt="logo" src="assets/icon/edit.svg" />
                <div className="edit_text">Edit</div>
              </div>
            </>
          )}
          {/* {isSearch && <div id="header-icon"><IonImg alt="logo" src="assets/icon/search_white.svg" /></div>}
        {isAddDriver && <><div id="header-icon" className="edit_icon"><IonImg alt="logo"
        src="assets/icon/Add_Black.svg" onClick={() => isAddDriver()} /><div className="edit_text">Add</div></div></>} */}
          {isStar && (
            <IonIcon
              class="star-icon"
              src={`assets/icon/${isWatched ? 'watched-color' : 'watch-icon'}.svg`}
              onClick={() => toggleWatchLoad && toggleWatchLoad(!isWatched)}
            />
          )}
          {isAddDriver && (
            <>
              <div id="header-icon" className="edit_icon">
                <IonImg alt="logo" src="assets/icon/Add_Black.svg" onClick={() => isAddDriver()} />
                <div className="edit_text">Add</div>
              </div>
            </>
          )}{' '}
        </IonCol>
      </IonRow>
      {backUrl || title ? (
        <IonGrid className="desktop-header-desc">
          {backUrl && (
            <IonGrid class="desktop-hdeader-button">
              <IonButton onClick={() => navigate()} class="desktop-back-button">
                {displayValue}
              </IonButton>
              <span className="desktop-title">
                {isLoadDetails ? (
                  <>{`\xa0\xa0|\xa0\xa0\xa0Load #${title}`}</>
                ) : (
                  <>{`\xa0\xa0|\xa0\xa0\xa0${title}`}</>
                )}
              </span>
            </IonGrid>
          )}
          {title && title !== 'Manage' ? <IonGrid className="header-title">{title}</IonGrid> : null}
        </IonGrid>
      ) : null}
      {imageCapature ? <ImageCapture {...imageCapature} /> : null}
      <SegmentControll segments={segments} getSegment={getSegment} activeSegment={activeSegment} />
      <NetworkErrorToaster />
    </IonHeader>
  );
};

export default withRouter(AppHeader);
