import {
    Plugins,
    PushNotification,
    PushNotificationToken,
    PushNotificationActionPerformed,
} from '@capacitor/core';

import { getItem, setItem } from '../utils/common';
import { store } from "../store";
import { SaveDeepLinkURI } from '../actions/Login/action'
import { registerDeviceToken } from '../actions/App/action';
import { DeviceRegister } from '../models/home/DeviceRegister.model'
export const usePushNotification = () => {
    const { PushNotifications, Device } = Plugins;
    const { FCMPlugin } = Plugins;
    let userDetails: { carrierId: String; email: String; mobileNumber: String; userPrincipalName: String };
    let registerPush = (_userDetails: any) => {
        userDetails = _userDetails;
        // Register with Apple / Google to receive push via APNS/FCM
        if (PushNotifications.requestPermissions)
            PushNotifications.register();
        else {
            PushNotifications.requestPermission().then(() => {
                PushNotifications.register();
            })
        }

        addPushListeners();
    }

    let addPushListeners = async () => {
        PushNotifications.removeAllListeners();
        // On succcess, we should be able to receive notifications
        PushNotifications.addListener('registration',
            async (token: PushNotificationToken) => {

                try {
                    // 1. when app launch call GetDevice token method - getDeviceToken().
                    let deviceToken = await getItem('deviceToken')
                    let newToken: string = "";
                    // if (isPlatform('android')) {
                    //     newToken = token.value;
                    // } else {
                    //     await FCMPlugin.getToken().then((res: any) => {
                    //         newToken = res.token;
                    //         console.log("TOEKEN:", newToken);
                    //         setItem('deviceToken', res.token);
                    //     });

                    // }
                    newToken = token.value;
                    console.log("App Push Registration Done: device Token from Device", deviceToken.value);

                    if (deviceToken.value !== newToken) {
                        console.log('App Push Registration Done.:First App Launch', newToken);
                        let deviceID = await Device.getInfo();
                        let __deviceRegister = new DeviceRegister();
                        await setItem('deviceToken', newToken);// 
                        __deviceRegister.uuid = deviceID.uuid;
                        __deviceRegister.upn = userDetails && userDetails.userPrincipalName || "";
                        __deviceRegister.carrierId = userDetails && userDetails.carrierId//"TruckStop11234";
                        __deviceRegister.deviceOs = deviceID.operatingSystem;
                        __deviceRegister.deviceOsVersion = deviceID.osVersion;
                        __deviceRegister.email = userDetails && userDetails.email;//"abc@schneider.com";
                        __deviceRegister.phone = userDetails && userDetails.mobileNumber || ""
                            && userDetails.mobileNumber.trim().replace('+', '') || "";//"635214789";
                        __deviceRegister.deviceToken = newToken;

                        // TODO : deviceID.uuid 
                        store.dispatch(registerDeviceToken(__deviceRegister.getRequestPayload()));
                    }
                    console.log('App Push Registration Done.:Sub Sequent App Launch')
                    deviceToken = await getItem('deviceToken')
                }
                catch (e) {
                    console.log('App Registration Catch Error.', e)
                }

            }
        );

        // Some issue with your setup and push will not work
        PushNotifications.addListener('registrationError',
            (error: any) => {
            }
        );

        // Show us the notification payload if the app is open on our device
        PushNotifications.addListener('pushNotificationReceived',
            (notification: PushNotification) => {
                console.log('pushNotificationReceived, token:' + JSON.stringify(notification));
                presentAlertConfirm(notification)
            }
        );

        // Method called when tapping on a notification
        PushNotifications.addListener('pushNotificationActionPerformed',
            (notification: PushNotificationActionPerformed) => {
                console.log('pushNotificationActionPerformed, token: ' + JSON.stringify(notification.notification));
                store.dispatch((SaveDeepLinkURI(notification.notification.data)));

            }
        );
    }

    const presentAlertConfirm = (notification: any) => {
        const alert = document.createElement('ion-alert');
        // alert.cssClass = 'my-custom-class';
        alert.header = notification.title || "Driver Assigned";
        alert.message = `${notification.body}`;
        alert.buttons = [
            {
                text: 'View Now',
                role: 'Ok',
                cssClass: "firstBtn",
                handler: () => {
                    store.dispatch((SaveDeepLinkURI(notification.data)));
                }
            },
            {
                text: 'View Later',
                role: 'Cancel',
                cssClass: "secondBtn",
                handler: () => {
                }
            }
        ];

        document.body.appendChild(alert);
        return alert.present();
    }
    const unRegisterPush = () => {
        PushNotifications.removeAllListeners();
    }

    return {
        registerPush,
        unRegisterPush
    }
}
