import { IONICWrapper} from "../utils/common";
import HTTP from '../services/httpServices';
import { KEYS } from '../utils/constants';
import Service from "../services/common.services";

/**
     * Generate Azure Maps Bearer Token.
     * Bearer Token is valid for 60 minutes. If the Token will expire new token will be generated.
     * * @param {*} getToken callback for Azure Maps Bearer Token. 
     * @returns null.
     */
export async function generateAzureMapsBearerToken(getToken: any) {
    let accessToken = IONICWrapper.details.vault && ( await IONICWrapper.details.vault.getValue('AZURE_MAPS_ACCESS_TOKEN'));
    if (accessToken) {
        let jsonObj = JSON.parse(accessToken);
        let currentUnixTime = (new Date()).getTime() / 1000;
        if (currentUnixTime < jsonObj.expires_on) {
            getToken("Bearer " + jsonObj.access_token);
        } else {
            await generateAzureMapsAccessToken(getToken);
        }
    } else {
        await generateAzureMapsAccessToken(getToken);
    }
}

/**
     * Generate Azure Maps Access Token and store in local DB.
     *@param {*} getToken callback for Azure Maps Bearer Token. 
     * @returns null.
     */
export async function generateAzureMapsAccessToken(getToken: any) {
    const commonService = new Service();
    const response: any = await commonService.get("AZURE_MAPS", "ACCESS_TOKEN");
    IONICWrapper.details.vault && await IONICWrapper.details.vault.storeValue('AZURE_MAPS_ACCESS_TOKEN', JSON.stringify(response));
    getToken("Bearer " + response.access_token);
}

/**
     * Search current Address based on geo coordinates (lat, long).
     * * @param {string} query string for Azure Maps search.
     *@param {*} getAddress callback for Azure Maps address search. 
     * @returns null.
     */
export async function searchCurrentAddress(query: string, getAddress: any) {
    await generateAzureMapsBearerToken((bearer_token: any) => {
        (new HTTP()).__requestRestApi(
            `https://atlas.microsoft.com/search/address/reverse/json?api-version=1.0&query=${query}`,
            {
                "x-ms-client-id": KEYS.AZURE_MAP_CLIENT_ID,
                "Authorization": bearer_token
            },
            'get'
        ).then((response: any) => {
            getAddress(response.data.addresses);
        }).catch(async (error: any) => {
            IONICWrapper.details.vault && await IONICWrapper.details.vault.removeValue('AZURE_MAPS_ACCESS_TOKEN');
        });
    });
}

/**
     * Search list of address based on search address query params.
     * * @param {string} query string for Azure Maps search.
     *@param {*} getAddress callback for Azure Maps address search. 
     * @returns null.
     */
export async function searchAddress(query: string, getAddress: any) {
    await generateAzureMapsBearerToken((bearer_token: any) => {
        (new HTTP()).__requestRestApi(
            `https://atlas.microsoft.com/search/address/json?typeahead=true&api-version=1.0&language=en-US&countrySet=US,CA&view=Auto&query=${query}`,
            {
                "x-ms-client-id": KEYS.AZURE_MAP_CLIENT_ID,
                "Authorization": bearer_token
            },
            'get'
        ).then((response: any) => {
            getAddress(response.data.results);
        }).catch(async (error: any) => {
            IONICWrapper.details.vault && await IONICWrapper.details.vault.removeValue('AZURE_MAPS_ACCESS_TOKEN');
        });
    });
}

/**
     * Search Timezone By Coordinates.
     * * @param {string} query string for Azure Maps search.
     *@param {*} getTimezone callback for Azure Maps Timezone search. 
     * @returns null.
     */
export async function searchTimezoneByCoordinates(query: any, getTimezone: any) {
    await generateAzureMapsBearerToken((bearer_token: any) => {
        (new HTTP()).__requestRestApi(
            `https://atlas.microsoft.com/timezone/byCoordinates/json?api-version=1.0&language=en-US&countrySet=US,CA&view=Auto&query=${query}`,
            {
                "x-ms-client-id": KEYS.AZURE_MAP_CLIENT_ID,
                "Authorization": bearer_token
            },
            'get'
        ).then((response: any) => {
            getTimezone(response.data.TimeZones);
        }).catch(async (error: any) => {
            IONICWrapper.details.vault && await IONICWrapper.details.vault.removeValue('AZURE_MAPS_ACCESS_TOKEN');
        });
    });
}


