import { ActionType, GPSState } from "../../schemas/App/App.Schema";
import * as Constants from "../../utils/constants";
import produce from "immer";
import { IONICWrapper } from "app/utils/common";

export const initialState: GPSState = {
  GPSData: [],
  UserDetails: {},
  deviceRegistration: "",
  showAlert: false,
  alertMessage: "",
  alertTitle: "",
  moreContent: {},
  offlineJSON: null,
  configParams: {},
  searchParams: null,
  pullToRefreshState: null,
  access_token: null,
  registerUserStatusMessage: {},
  session: {},
  shipmentParams: null
};

export default (state = initialState, action: ActionType) =>
  produce(state, draft => {
    switch (action.type) {
      case Constants.UPDATE_GPS_COORDS:
        draft.GPSData = [...draft.GPSData, action.payload];
        break;
      case Constants.FETCH_USER_DETAILS_SUCCESS:
        draft.UserDetails = action.payload;
        break;
      case Constants.LOGOUT:
        draft.UserDetails = {};
        draft.GPSData = [];
        break;
      case Constants.REGISTER_DEVICE_RESPONSE_ACTION:
        draft.deviceRegistration = action.payload.errors;
        break;
      case Constants.SHOW_ALERT_MESSAGE:
        draft.showAlert = action.payload.isShowAlert;
        draft.alertMessage = action.payload.alertMessage;
        draft.alertTitle = action.payload.alertTitle;
        break;
      case Constants.HIDE_ALERT_MESSAGE:
        draft.showAlert = false;
        draft.alertMessage = "";
        draft.alertTitle = "";
        break;
      case Constants.MORE_TAB_CONTENT_RESPONSE:
        draft.moreContent = action.payload;
        break;
      case Constants.MORE_TAB_CONTENT_RESPONSE_DUMMY:
        draft.offlineJSON.contentParams = action.payload;
        break;
      case Constants.CREATE_OFFLINE_JSON:
        draft.offlineJSON = action.payload;
        console.log(draft.offlineJSON);
        break;
      case Constants.GET_CONFIG_PARAMS:
        draft.configParams = action.payload;
        break;
      case Constants.UPDATE_SEARCH_PARAMS_OFFLINE:
        draft.offlineJSON[IONICWrapper.details.auth.user.upn].searchParams = {
          ...draft.offlineJSON[IONICWrapper.details.auth.user.upn].searchParams,
          ...action.payload
        };
        draft.searchParams = { ...draft.searchParams, ...action.payload };
        console.log(draft.searchParams);

        break;
      case Constants.UPDATE_SHIPMENT_INFO_OFFLINE:
        draft.offlineJSON[IONICWrapper.details.auth.user.upn].shipmentParams =
          action.payload;
        draft.shipmentParams = action.payload;
        break;
      case Constants.UPDATE_PULL_TO_REFRESH_STATE:
        draft.pullToRefreshState = action.payload;
        break;
      case Constants.STORE_ACCESS_TOKEN:
        draft.access_token = action.payload;
      case Constants.REGISTER_USER_STATUS_MESSAGE:
        draft.registerUserStatusMessage = action.payload;
        break;

      case Constants.CLEAR_REGISTER_USER_STATUS_MESSAGE:
        draft.registerUserStatusMessage = {};
        break;
      case Constants.SESSION_INFO:
        draft.session = action.payload.session;
        break;
    }
  });
