import { IonImg, IonGrid, IonIcon } from "@ionic/react";
import React from "react";
import getTime from "../../../utils/getTimePeriod";
import { getAppointmentTime } from "../../../utils/formatData";
import {
  StopActivityProps,
  getStopActivity,
} from "../../../models/stopActivity.model";

const StopDetails = (props: any) => {
  const stopDetail = props.stopDetails;
  const [showAddress, setShowAddress] = React.useState(false);

  React.useEffect(() => {
    if (showAddress === true) {
      setShowAddress(false);
    }
  }, [JSON.stringify(props.stopDetails)]);

  return (
    <IonGrid class="stop-data-grid">
      {props.index < props.length - 1 ? (
        <>
          {props.index === 0 ? (
            <IonImg
              className="stop-icon"
              src="assets/icon/origin-pointer.svg"
            />
          ) : (
            <IonImg className="stop-icon" src="assets/icon/stop-pointer.svg" />
          )}
        </>
      ) : (
        <IonImg className="path_icon" src="assets/icon/dst-pointer.svg" />
      )}
      <IonGrid className="stop-address-grid">
        <p className="origin_city">
          {stopDetail && stopDetail.state === null
            ? stopDetail && stopDetail.city
            : `${stopDetail && stopDetail.city}, ${stopDetail && stopDetail.state}`}
          {props.showExpandView ? (
            <>
              {showAddress ? (
                <IonIcon
                  src="assets/icon/minus-icon.svg"
                  class="plus-icon"
                  onClick={() => setShowAddress(!showAddress)}
                />
              ) : (
                <IonIcon
                  src="assets/icon/plus.svg"
                  class="plus-icon"
                  onClick={() => setShowAddress(!showAddress)}
                />
              )}
            </>
          ) : null}
        </p>
        {props.showExpandView && stopDetail ? (
          <IonGrid
            class={`complete-address ${showAddress ? "fadein" : "fadeout"}`}
          >
            {stopDetail && stopDetail.locationName && (
              <p>{stopDetail.locationName}</p>
            )}
            {stopDetail && stopDetail.addressLine1 && (
              <p> {stopDetail.addressLine1}</p>
            )}
            {stopDetail && stopDetail.addressLine2 && (
              <p> {stopDetail.addressLine2}</p>
            )}
            {stopDetail &&
              (stopDetail.city ||
                stopDetail.state ||
                stopDetail.postalCode) && (
                <p>
                  {" "}
                  {stopDetail.city},{stopDetail.state},{stopDetail.postalCode}
                </p>
              )}
          </IonGrid>
        ) : null}
        {props.index === 0 ? (
          <p className="origin_milehead load_header_elements">
            {stopDetail &&
              stopDetail.specialServices &&
              stopDetail.specialServices.length &&
              stopDetail.specialServices.map((service: any, index: any) => (
                <span key={index}>{service.specialServiceDesc}</span>
              ))}
            {getStopActivity((stopDetail && stopDetail.stopActivity) || [])}{" "}
          </p>
        ) : (
          <>
            {" "}
            {props.index === props.length - 1 ? (
              <p className="origin_milehead load_header_elements">
                {stopDetail &&
                  stopDetail.specialServices &&
                  stopDetail.specialServices.length &&
                  stopDetail.specialServices.map((service: any, index: any) => (
                    <span key={index}>
                      {service.specialServiceDesc}
                    </span>
                  ))}
                {getStopActivity((stopDetail && stopDetail.stopActivity) || [])}{" "}
              </p>
            ) : (
              <>
                {" "}
                {props.index === props.length - 1 ? (
                  <p className="origin_milehead load_header_elements">
                    {stopDetail &&
                      stopDetail.specialServices &&
                      stopDetail.specialServices.length &&
                      stopDetail.specialServices.map(
                        (service: any, index: any) => (
                          <span key={index}>
                            {service.specialServiceDesc}
                            {stopDetail.specialServices.length ? ", " : " "}
                          </span>
                        )
                      )}
                    {getStopActivity(
                      (stopDetail && stopDetail.stopActivity) || []
                    )}{" "}
                    {props.isCarrierLoad ? null : (
                      <>{stopDetail && stopDetail.deadHead} deadhead </>
                    )}
                  </p>
                ) : (
                  <p className="origin_milehead load_header_elements">
                    Stop off {props.index} :{" "}
                    {getStopActivity(
                      (stopDetail && stopDetail.stopActivity) || []
                    )}{" "}
                  </p>
                )}
              </>
            )}
          </>
        )}
        <p className="origin_dateTime load_header_elements">
          {props.appointment}
        </p>
      </IonGrid>
    </IonGrid>
  );
};

class LoadTileHeader extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
  }

  public render() {
    let loadStop = [];
    let sortedLoadStops = this.props.loadStop || [];
    sortedLoadStops = sortedLoadStops.slice().sort((load1: any, load2: any) => {
      if (load1.sequenceNo && load2.sequenceNo) {
        return load1.sequenceNo - load2.sequenceNo;
      }
      if (load1.stopSequence && load2.stopSequence) {
        return load1.stopSequence - load2.stopSequence;
      }
      return 0;
    });
    if (!this.props.isCarrierLoad) {
      sortedLoadStops = sortedLoadStops.filter((stop: any) => {
        return stop.stopType !== "NFR";
      });
    }

    if (this.props.isLoadDetails) {
      loadStop = sortedLoadStops;
    } else {
      loadStop[0] = sortedLoadStops && sortedLoadStops[0];
      loadStop[1] =
        sortedLoadStops && sortedLoadStops[sortedLoadStops.length - 1];
    }
    const calculateTime = (stop: any) => {
      if (this.props.isCarrierLoad) {
        return getAppointmentTime(stop);
      }
      return getTime(stop.appointmentFrom || "", stop.appointmentTo || "");
    };

    return (
      <IonGrid class="stop-details-grid">
        {loadStop
          ? loadStop.map((stop: any, index: any) => (
            <StopDetails
              key={index}
              stopDetails={stop}
              appointment={(stop && calculateTime(stop)) || ""}
              length={loadStop.length}
              index={index}
              showExpandView={this.props.showExpandView}
              isCarrierLoad={this.props.isCarrierLoad}
            />
          ))
          : null}
      </IonGrid>
    );
  }
}

export { LoadTileHeader };
