import React, { useState } from 'react';
import { IonCol, IonModal, IonRow, IonIcon, IonText } from '@ionic/react';
import "./FilterWithSort.scss";

export const FilterWithSort: React.FC<any> = (props: any) => {
    const { dropdown: { options, setFilter, direction }, isDesktop } = props;
    const selectedOptions = options.map((option: any, index: number) => ({ ...option, selected: index === 0}));
    const [isModalOpen, setModalOpen] = useState(false);
    const [selectedOption, setSelectedOptions] = useState<any>(selectedOptions);
    const [filterOption, setFilterOptions] = useState<any>([]);
    const [sortOrder, setSortOrder] = useState(direction);
    const selectItems = selectedOption.filter((option: any) => option.selected);
    const title = selectItems.map((option: any) => `${option.name}${selectItems.length === 1 && option.name !== "Any" ? 's' : ''}`).join(' + ');
    const btnTitle = selectItems.map((option: any) => option.name).join(' + ');
    const openModel = () => {
        setFilterOptions(selectedOption);
        setModalOpen(true);
    }

    const setFilterOption = (opion: any) => {
        const options = filterOption.map((item: any) => {
            return {
                ...item,
                selected: item.name === opion.name ? !item.selected : false
            }
        });
        const selected = options.find((option: any) => option.selected);
        if (selected) {
            setFilterOptions(options);
        }
    }

    const sortData = (direction = sortOrder) => {
        setFilter({
            filter: {
                selected: filterOption,
                direction
            }
        })
    };

    const confirmSelection = () => {
        sortData();
        setSelectedOptions(filterOption);
        setModalOpen(false);
    }

    const handleSorting = () => {
        const direction = sortOrder === 'asc' ? 'desc' : 'asc';
        setSortOrder(direction);
        sortData(direction);
    }

    return (
        <IonRow class="list-items-header">
            {!isDesktop ? (
                <IonCol>
                    <span className="list-items-title">
                        List of {title}
                    </span>
                </IonCol>
            ) : null}
            <IonCol>
                <div className="list-items-filter">
                    { isDesktop ? <>
                        <IonIcon src="assets/icon/add.svg" class="manage_add_icon"></IonIcon>
                        <IonText class="add-driver-text" onClick={() => props.addPage()}>Add Driver</IonText> </> : null}
                    <IonText
                        item-left
                        class="filter-dropdown"
                        onClick={openModel}
                    >
                        {btnTitle}
                        <i className="down"></i>
                    </IonText>
                    <IonText class="filter-sort" onClick={handleSorting}>
                        <IonIcon src={`assets/icon/sort icon${sortOrder === 'asc' ? ' downward' : ''}.svg`}></IonIcon>
                    </IonText>
                </div>
                <IonModal
                    show-backdrop={true}
                    backdrop-dismiss={false}
                    isOpen={isModalOpen}
                    cssClass="dropdown-modal"
                    onDidDismiss={() => {
                        setModalOpen(false)
                    }}
                >
                    <div className="search_options">
                        <IonRow class="dropdown-row dropdown-heading">
                            <IonCol>
                                <p className="sortBy_text">Filter By</p>
                            </IonCol>
                        </IonRow>
                        {
                            filterOption.map((option: any, k: any) => (
                                <IonRow
                                    className="dropdown-row filter-option"
                                    key={k}
                                    onClick={() => setFilterOption(option)}
                                >
                                    <IonCol className={option.selected ? 'checked' : ''}>
                                        {option.name} <i className="check"></i>
                                    </IonCol>
                                </IonRow>
                            ))
                        }
                        <IonRow class="dropdown-handler">
                            <IonCol size="1"></IonCol>
                            <IonCol
                                size="5"
                                className="dropdown_cancel"
                                onClick={() => setModalOpen(false)}
                            >
                                <span>Cancel</span>
                            </IonCol>
                            <IonCol
                                size="5"
                                className="dropdown_cancel dropdown_ok"
                                onClick={confirmSelection}
                            >
                                <span>OK</span>
                            </IonCol>
                            <IonCol size="1"></IonCol>
                        </IonRow>
                    </div>
                </IonModal>

            </IonCol>
        </IonRow>
    );
}
