/**
 *  Add Watch Load
 */
export function* addWatchLoad(data: any) {
  const { query, loadDetails } = data.payload;
  try {
    yield put(showLoading());
    let value = DomainConverter.domainToData<any>(query);
    const repos = yield call(request, [
      "RECENT",
      "RECENT",
      null,
      "",
      value,
      "application/json"
    ]);
    yield put(addWatchLoadSuccess(loadDetails));
    yield put(hideLoading());
  } catch (e) {
    yield put(hideLoading());
  }
}

/**
 *  Remove Watch Load
 */
export function* removeWatchLoad(data: any) {
  const { query, loadId, callback } = data.payload;
  try {
    yield put(showLoading());
    let value = DomainConverter.domainToData<any>(query);
    const response = yield call(request, [
      "RECENT",
      "RECENT",
      null,
      "",
      value,
      "application/json"
    ]);
    yield put(removeWatchLoadSuccess({ loadId }));
    yield put(hideLoading());
    callback(response.errors ? true : false);
  } catch (e) {
    callback(true);
    yield put(hideLoading());
  }
}

export default function* rootSaga() {
  yield all([
    takeLatest(constants.UPDATE_DATA, updateData),
    takeLatest(constants.RECOMMENDED_LOADS_REQUEST, recommendedLoads),
    takeLatest(constants.WATCHEDLOAD_DATA_REQUEST, watchedLoads),
    takeLatest(constants.ADD_WATCH_LOAD, addWatchLoad),
    takeLatest(constants.REMOVE_WATCH_LOAD, removeWatchLoad)
  ]);
}
