import { observable, autorun, runInAction, action } from 'mobx';
import { IonicAuth, AuthResult } from '@ionic-enterprise/auth';
import { isPlatform } from '@ionic/react';
import { nativeAzureConfig, webAzureConfig } from './environment';
import { VaultService } from './vault.service';
import { User, IDToken } from '../schemas/user';
import { KEYS, Roles, NO_AUTH_MSG} from '../utils/constants';
import { getDashboard, IONICWrapper, setObject, getObject, clear } from '../utils/common';
import Environment from '../environments/environment';
import { store } from '../store';
import { toastController } from "@ionic/core";
import { fetchUserDetails } from '../actions/App/action';
import { UserDetails } from '../models/userPforile/userProfile.modal';
import { AuthMode } from '@ionic-enterprise/identity-vault';
import { ToastMessage } from '../utils/ToastMessage';


export class AuthenticationService extends IonicAuth<IDToken> {
  private vaultService: VaultService;

  /** Access token exists and the vault is unlocked. */
  @observable public isLoggedIn: boolean;
  @observable public user?: User;
  @observable public accessToken?: string;
  public tabName?: string;
  public isActive:boolean;
  onVaultLockChange: () => void;
  constructor(vaultService: VaultService) {
    const config = isPlatform('capacitor') ? nativeAzureConfig : webAzureConfig;
    vaultService.setAuthMode(AuthMode.SecureStorage);
    config.tokenStorageProvider = vaultService;
    super(config);
    this.vaultService = vaultService;
    this.isLoggedIn = false;
    this.tabName = 'login';
    this.isActive = false;
    this.onVaultLockChange = autorun(() => { this.setLoginState(this.vaultService.isLocked); });
  }

  async login(): Promise<void> {
    await this.vaultService.logout();
    // await this.vaultService.setDesiredAuthMode();

    try {
      await super.login(KEYS.DISCOVERY_URL);
    } catch (err) {
      // This is to handle the password reset case for Azure AD
      //  This only applicable to Azure AD.
      
      const message: string = err.message;

      if (message !== undefined && !message.startsWith('AADB2C90118')) {
        await ToastMessage.show(NO_AUTH_MSG);
      }
       console.log('login error:', +err);
      
      // This is the error code returned by the Azure AD servers on failure.
      if (message !== undefined && message.startsWith('AADB2C90118')) {
        // The address you pass back is the custom user flow (policy) endpoint
        super.login(KEYS.AZURE_RESET_URL).catch((err:any) =>{
          if (err.message !== undefined && err.message.startsWith('AADB2C90091')) {
            this.login();
          }
        });
      } else {
        await ToastMessage.show(NO_AUTH_MSG);
      }
    }
  }

  async changePassword(): Promise<void> {
    try {
      await super.login(KEYS.AZURE_RESET_URL).catch((err:any) =>{
      });;
    } catch (err) {
    }
  }

/**
  * To fetch data from the Web Service API.
  * @param {Array} role collection of role Names.
  * @returns {string} selected Role.
  */
  getRole(role:Array<string>){
    enum Role_Type {carrierDispatcher, carrierDriver, carrierAdmin};
    let __role:string= "";
    if(role.length === 1){
      return role[0];
    }
    for(var i=0; i <= Object.keys(Role_Type).length;i++){
      if(role.includes(Role_Type[i])){
        __role = Role_Type[i];
        break;
      }
    }
    return __role;
  }

  /**
  * method to check if session is active or not. if not user will be redirected to landing screen.
  * @param {boolean} tabSession flag for avoiding rerendering.
  * @param {any} setSession to invalidate the flag.
  */
  async hasSession(tabSession: boolean, setSession: any) {
    if (tabSession) {
      setSession(false);
      if (!this.isActive) {
        
        const isSessionActive = await this.isAuthenticated();

        if (!isSessionActive) {
            this.isLoggedIn = false;
        }
      }
      this.isActive = false;
    }
  }

  async isAuthenticated(): Promise<boolean> {
    const isVaultLocked = await this.vaultService.isLocked;
    return !isVaultLocked && (await super.isAuthenticated());
  }

  async setLoginState(isLocked: boolean, authResult: AuthResult = {}) {
    if (isLocked) {
      this.isLoggedIn = false;
      return
    }
 
    this.isActive = true;

    const { accessToken } = authResult;

    // swallow vault errors
    try {
      const [hasSession, idTokenInfo, accessTokenString] = await Promise.all([
        this.vaultService.hasStoredSession(),
        this.getIdToken(),
        accessToken ? Promise.resolve(accessToken) : this.getAccessToken()
      ]) as [boolean, IDToken, string] // https://github.com/microsoft/TypeScript/issues/33752;

      await(new Environment(((process.env.REACT_APP_ENV && process.env.REACT_APP_ENV.trim()) || 'dev') as any)).getConfig(IONICWrapper.details, this.vaultService, KEYS.SUBSCRIPTION_KEY,(await this.getAuthResponse()).id_token, KEYS.API_HOST);
      if (this.tabName === 'login') {
        var manage: any = await this.getIdToken();
        this.tabName = await getDashboard(
          manage.roles ? this.getRole(manage.roles) : Roles.carrierGuest
          , manage.roles);
      }
     
      IONICWrapper.details.getVault(await this.vaultService.getVault());

      IONICWrapper.accessToken = accessTokenString;
      IONICWrapper.details.getAuth({
        tokens: await this.getAuthResponse(),
        user: await this.getIdToken(),
      });

      // batch state updates
      runInAction(() => {
        this.vaultService.hasSessionStored = hasSession;
        this.user = this.getUserInfo(idTokenInfo);
        this.accessToken = accessTokenString;
        this.isLoggedIn = true;
        // getApplicationInfo().then(session => {
        //   store.dispatch(setSessionInfo({session}))
        // })
      });
      this.getUserDetails();
    } catch (err) {}
  }

  async onLoginSuccess(authResult: AuthResult) {
    // we pass isLocked as false, since we have access to all tokens in memory
    this.setLoginState(false, authResult);
  }

  @action
  async onLogout() {
    this.isLoggedIn = false;
    this.user = undefined;
    this.accessToken = undefined;
    this.vaultService.logout();
    this.vaultService.hasSessionStored = false;
    await clear();
    this.tabName = 'login';
  }

  private getUserInfo(idToken?: IDToken): User | undefined {
    if (!idToken) return;

    let email = idToken.email;
    if (idToken.emails instanceof Array) {
      email = idToken.emails[0];
    }

    return {
      id: idToken.sub,
      email: email!, // one or the other will exist
      firstName: idToken.firstName,
      lastName: idToken.lastName,
    };
  }
  private getUserDetails(){
    const userDetails = new UserDetails();
    const token = (IONICWrapper.details.auth &&
    IONICWrapper.details.auth.tokens.access_token) ||
  "";
  const data = userDetails.getRequestPayload(
    IONICWrapper.details.auth && IONICWrapper.details.auth.user.upn
  )
    store.dispatch(fetchUserDetails({ token: token, body: data }))
  }
}

/**
  * class to share the authentication instance to other modules to consumes the its features.
*/
export class AuthSingleton {
  private static instance: AuthenticationService | undefined = undefined;
  private static vault: VaultService | any = undefined;
  static getInstance(vaultService?:VaultService): AuthenticationService {
    this.vault = vaultService;
     if (this.instance === undefined)
        this.instance = new AuthenticationService(this.vault);
        return this.instance;
  }
} 
