import React from 'react';
import { IonCardContent, IonGrid, IonRow, IonCol, IonImg, IonCard } from '@ionic/react';
import "./IonUser.scss";
import { IonImage } from './IonImage';
const IonUser: React.FC<any> = (props:any) => {
    const { user, onUserClick, isDesktop } = props;

    return (
        <IonCard class="ion-user-content" onClick={() => onUserClick(user)}>
            <IonCardContent className="card-content ion-user-card">
                <IonGrid class="ion-user-grid">
                    <IonRow>
                        <IonCol className="ion-user-col" size={isDesktop ? '1': '4'}>
                            <IonImage firstName={user.firstName} lastName={user.lastName} normal/>
                        </IonCol>
                        <IonCol className="ion-user-col" size={isDesktop ? '11': '8'}>
                            <div className="ion-user-content">
                                <h4 className="ion-user-title">{user.firstName} {user.lastName}</h4>
                                <div className="ion-user-info">
                                    <label>Email</label>
                                    <span>{user.email}</span>
                                </div>
                                <div className="ion-user-info">
                                    <label>Mobile No</label>
                                    <span>{user.mobileNumber}</span>
                                </div>
                            </div>
                            
                        </IonCol>
                    </IonRow>
                </IonGrid>
            </IonCardContent>
        </IonCard>
        
    );
};

export default IonUser;
