import React, { useState, useEffect } from "react";
import { IonPage, isPlatform, IonRow, IonCol } from "@ionic/react";
import "./ManageUser.page.scss";
import AppHeader from "../../../components/app/Bars/Bar-header";
import ListItems from "../../../components/shared/ListItems";
import {
  MANAGE_USERS_TITLE,
  KEYS,
  ENV,
  ASC_DIRECTION
} from "../../../utils/constants";
import IonUser from "../../../components/app/Manage/IonUser";
import { ManageUserEmpty } from "./ManageUserEmpty.page";
import { formatPhoneNumber } from "./ManageUserUtil";
import { R2Configuration } from "../../../utils/common";
import { ToastMessage } from "../../../utils/ToastMessage";
import PullToRefresh from "app/components/PullToRefresh/PullToRefresh";

const ManageUserPage: React.FC<any> = props => {
  const {
    isPullToRefresh,
    getUsersList,
    users,
    selectedUser,
    history,
    isNetworkAvailable,
    showNetworkAlert,
    setFilter,
    filters
  } = props;
  const options = [
    { value: "carrierAny", name: "Any" },
    { value: "carrierDriver", name: "Driver" },
    { value: "carrierDispatcher", name: "Dispatcher" },
    { value: "carrierAdmin", name: "Admin" }
  ];
  const [usersList, setUsersList] = useState(users);

  const dropdown = {
    options,
    data: users,
    filterParam: "roles",
    setFilter,
    direction: filters.direction
  };

  const addPage = () => {
    if (R2Configuration.details.getR2FeatureEnable()) {
      //KEYS.RELEASE === ENV.UAT || KEYS.RELEASE === ENV.PROD) {
      ToastMessage.show();
      return;
    }
    if (isNetworkAvailable) {
      history.push(`/app/${props.module}/ManageUser/Add`);
    } else {
      showNetworkAlert();
    }
  };

  const onUserClick = (userProfile: any) => {
    if (R2Configuration.details.getR2FeatureEnable()) {
      //KEYS.RELEASE === ENV.UAT || KEYS.RELEASE === ENV.PROD) {
      ToastMessage.show();
      return;
    }
    if (isNetworkAvailable) {
      selectedUser({ userProfile });
      history.push(`/app/${props.module}/ManageUser/Profile`);
    } else {
      showNetworkAlert();
    }
  };

  const filterData = () => {
    const { filterParam } = dropdown;
    const { selected, direction } = filters;
    const selectedOptions = selected.filter((type: any) => type.selected);
    if (selectedOptions.length === 0 || selectedOptions[0].name === "Any") {
      return [...users].sort((user1: any, user2: any) => {
        if (
          user1 &&
          user1.updatedTimeStamp &&
          user2 &&
          user2.updatedTimeStamp
        ) {
          return new Date(user1.updatedTimeStamp).getTime() >
            new Date(user2 && user2.updatedTimeStamp).getTime()
            ? direction === ASC_DIRECTION
              ? 1
              : -1
            : direction === ASC_DIRECTION
            ? -1
            : 1;
        }
        return direction === ASC_DIRECTION ? -1 : 1;
      });
    }
    const list = users.filter((item: any) => {
      return (
        selectedOptions.filter(
          (type: any) =>
            item[filterParam].findIndex(
              (value: any) => value === type.value
            ) !== -1
        ).length > 0
      );
    });
    return direction === "asc" ? list.reverse() : list;
  };

  useEffect(() => {
    const data = filterData().map((user: any) => ({
      ...user,
      defaultMobileNumber: user.mobileNumber,
      mobileNumber: formatPhoneNumber(user.mobileNumber)
    }));
    setUsersList(data);
  }, [users]);

  const renderData = (isDesktop: boolean = false) => {
    return (
      <ListItems
        addPage={addPage}
        title={MANAGE_USERS_TITLE}
        dropdown={dropdown}
        isDesktop={isDesktop}
      >
        <PullToRefresh
          status={isPullToRefresh}
          onPullToRefresh={getUsersList}
        />
        <IonRow className={isDesktop ? "manage-users-list-desktop" : ""}>
          <IonCol size="12">
            {usersList.map((user: any) => (
              <IonUser
                key={user.userPrincipalName}
                user={user}
                isDesktop={isDesktop}
                onUserClick={onUserClick}
              />
            ))}
            {usersList.length > 0 ? (
              <div className="end-of-list">End of list</div>
            ) : (
              <ManageUserEmpty
                message={""}
                addPage={addPage}
                constantButton="Add"
              />
            )}
          </IonCol>
        </IonRow>
      </ListItems>
    );
  };

  return isPlatform("desktop") ? (
    renderData(true)
  ) : (
    <IonPage className="manage-userslist-page">
      <AppHeader
        title="Manage Users"
        backUrl={"/app/" + props.module}
        History={props.history}
        addPage={addPage}
      />
      {renderData()}
    </IonPage>
  );
};
export default React.memo(ManageUserPage);
