import { createSelector } from "reselect";
import { initalSearchState } from "./../../reducers/Search/reducer";

const selectData = (state: any) => state.search;

export const makeSearch = () =>
  createSelector(selectData, search =>  search.loads || initalSearchState.loads);

export const getBuyLoadApiError= () =>
createSelector(selectData, search =>  search.showBuyLoadAPIError || initalSearchState.showBuyLoadAPIError);
  