import React, { PureComponent } from "react";
import {
  IonContent,
  IonList,
  IonRow,
  IonCol,
  IonSegment,
  IonSegmentButton,
  IonBadge,
  IonPage,
  isPlatform
} from "@ionic/react";
import { Loads } from "../../../components/app/home/Loads";
import { LoadTile } from "../../../components/app/home/Load-Tile";
import Dropdown from "../../../components/core/Dropdown/dropdown";
import AppHeader from "../../../components/app/Bars/Bar-header";
import { sortByOptions } from "../../../utils/mock_Data";
import { RecommendedError } from "../../../pages/Home/RecommendedError";
import "./SearchResults.page.scss";
import {
  searchResultsErrorText,
  searchResultsErrorButton,
  EVENT,
  SCREEN,
  TAB,
  MONTH_NAMES
} from "../../../utils/constants";
import { Search } from "../../../models/home/Search.model";
import PullToRefresh from "../../../components/PullToRefresh/PullToRefresh";
import { logEvent, setScreenName } from "../../../utils/Analytics";

class SearchResultPage extends PureComponent<any, any> {
  private pageSize: number = 5;

  constructor(props: any) {
    super(props);
    this.state = {
      tab: true,
      loadData: this.props.results ? this.props.results.searchLoads : [],
      editSearch: true,
      currentPage: 0,
      sortDirection: "asc",
      sortColumn:
        (this.props &&
          this.props.searchInput &&
          this.props.searchInput.sortColumn) ||
        "pickUpdate",
      totalRecords: this.props.totalRecords || 0,
      searchParamData: this.props.searchInput || new Search(),
      isPageEnd: false
    };
  }

  componentDidMount() {
    setScreenName("screen_search_results");
    logEvent("ue_loads_search_scroll_view", { page: "search" });

    // Analytics implementation
    logEvent(EVENT.SV_SEARCH_RESULT, {
      screen: SCREEN.SEARCH_RESULT,
      lead: TAB.SEARCHED
    });
  }

  componentDidUpdate(prevProps: any) {
    let totalRecords = this.state.totalRecords;
    let searchParamData = this.state.searchParamData;
    let currentPage = this.state.currentPage;

    if (
      this.props.searchInput &&
      JSON.stringify(this.props.searchInput) !==
        JSON.stringify(prevProps.searchInput)
    ) {
      searchParamData = this.props.searchInput;
      currentPage = 0;
    }
    if (this.props.totalRecords !== prevProps.totalRecords) {
      totalRecords = this.props.totalRecords;
    }
    this.setState({
      loadData: this.props.results ? this.props.results.searchLoads : [],
      searchParamData,
      totalRecords,
      currentPage
    });
  }

  sortedData = (selectedValue: string, asc: boolean | undefined) => {
    this.props.onResetPagination();
    this.setState(
      {
        sortColumn: selectedValue,
        currentPage: 0,
        sortDirection: asc === true ? "asc" : "desc"
      },
      () => {
        this.searchData(false);
      }
    );
  };
  newSearch = () => {
    this.props.history.push("/app/search");
  };
  getCurrentModel = () => {
    let __model: Search = new Search();
    const userRole =
      this.props.userDetails &&
      this.props.userDetails.userApplicationDetails &&
      this.props.userDetails.userApplicationDetails[0].roles.map(
        (carrierName: any, index: number) => {
          return carrierName.roleName;
        }
      );
    let searchModel: Search = this.state.searchParamData;
    if (__model && searchModel) {
      __model.originCity = searchModel.originCity;
      __model.originState = searchModel.originState;
      __model.originRadius = searchModel.originRadius;
      __model.pickUpdate = searchModel.pickUpdate;
      __model.dropDate = searchModel.dropDate;
      __model.destinationCity = searchModel.destinationCity;
      __model.destinationState = searchModel.destinationState;
      __model.destinationRadius = searchModel.destinationRadius;
      __model.favorite = searchModel.favorite;
      __model.trailerType = searchModel.trailerType;
      __model.userID =
        (this.props.userDetails && this.props.userDetails.userPrincipalName) ||
        "";
      __model.carrierId =
        (this.props.userDetails && this.props.userDetails.carrierId) || "";
      __model.page = this.state.currentPage;
      __model.size = this.pageSize;
      __model.sortColumn = this.state.sortColumn;
      __model.originCountry = searchModel.originCountry;
      __model.destinationCountry = searchModel.destinationCountry;
      __model.sortDirection = this.state.sortDirection;
      __model.carrierScac =
        (this.props.userDetails && this.props.userDetails.carrierScac) || "";
      __model.carrierTier =
        (this.props.userDetails && this.props.userDetails.carrierTier) || "";
      __model.userRole = userRole && userRole[0];
    }
    return __model;
  };
  searchData = (isPagination: boolean) => {
    this.props.handleSortingPagination(
      this.getCurrentModel(),
      isPagination,
      false
    );
  };

  searchOnPull = (isHideLoader: boolean = true) => {
    this.props.onResetPagination();
    this.setState({ currentPage: 0 });
    this.props.handleSortingPagination(
      this.state.searchParamData,
      false,
      isHideLoader
    );
  };

  OnScroll = async (event: any) => {
    const scroll = await event.currentTarget.getScrollElement();
    if (scroll.scrollTop === 0) {
      return true;
    }
    if (!isPlatform("desktop") && !this.props.isInPullToRefresh) {
      const pageSize = this.pageSize;
      const totalRecords: number = this.state.totalRecords;
      const totalPages: number =
        parseInt(`${totalRecords / pageSize}`) +
        (totalRecords % pageSize > 0 ? 1 : 0);
      const { currentPage } = this.state;

      if (totalRecords > pageSize && currentPage < totalPages - 1) {
        this.setState({ currentPage: currentPage + 1 }, () => {
          this.searchData(true);
        });
      } else {
        this.setState({ isPageEnd: true });
      }
    }
  };
  dateFormatChange = (date: any) => {
    if (!/\d{4}-[0-9]\d-[0-9]\dT[0-9]\d:[0-9]\d/.test(date)) {
      return null;
    }
    date = date.split("T");
    let dateString = date[0];
    let timeString = date[1];
    let _date = dateString.split("-");
    let _year = parseInt(_date[0]);
    let _month = MONTH_NAMES[parseInt(_date[1]) - 1];
    let _day = parseInt(_date[2]);
    return _day + " " + _month + " " + _year;
  };

  render() {
    let params = { ...this.props.params };
    if (params) {
      if (params.destination === "Anywhere") {
        delete params.destinationRadius;
        delete params.dropdate;
      }
      if (params.originRadius) {
        params["Origin Radius"] = params.originRadius;
        delete params.originRadius;
      }
      if (params.pickUpdate) {
        params["Pick up date"] = params.pickUpdate;
        delete params.pickUpdate;
      }
      if (params.TrailerType) {
        params["Trailer type"] = params.TrailerType;
        delete params.TrailerType;
      }
      if (params.destinationRadius) {
        params["Destination Radius"] = params.destinationRadius;
        delete params.destinationRadius;
      }
      params["Pick up date"] =
        params["Pick up date"] &&
        // new Date(params["Pick up date"].replace(/ /, "T"))
        //   .toDateString()
        //   .slice(4);
        this.dateFormatChange(params["Pick up date"]);
      console.log("SEARCH", params["Pick up date"]);

      if (!!!params.dropdate) {
        delete params.dropdate;
      } else {
        params.dropdate = this.dateFormatChange(params.dropdate);
        console.log("SEARCH", params.dropdate);
      }
      delete params.favorite;
    }

    return (
      <IonPage className="desktop-page search-result-page">
        <div
          data-test="on-reset"
          onClick={() => {
            this.props.onResetPagination();
          }}
        >
          <AppHeader
            title="Search Results"
            backUrl={"/app/search"}
            editData={() =>
              this.props.history.push("/app/search", {
                params: this.props.params
              })
            }
            History={this.props.history}
          />
        </div>

        <IonContent
          class="search_results_container"
          scrollEvents={true}
          onIonScrollEnd={this.OnScroll}
        >
          <PullToRefresh
            data-test="pull-to-refresh"
            onPullToRefresh={this.searchOnPull}
            status={this.props.isInPullToRefresh}
          ></PullToRefresh>
          {this.props.params && (
            <IonSegment scrollable className="ion-segment">
              {Object.entries(params).map(
                (filter: any, index: any) =>
                  Object.entries(params)[index][1] !== "" && (
                    <IonSegmentButton
                      className="ion-segment-button"
                      key={index}
                    >
                      <IonBadge class="search_floating_badge">
                        {filter[0] + ": " + filter[1]}
                      </IonBadge>
                    </IonSegmentButton>
                  )
              )}
            </IonSegment>
          )}
          {this.props.results &&
          this.props.results.searchLoads &&
          this.props.results.searchLoads.length ? (
            <div>
              <div className="short-div">
                <IonRow class="short-row">
                  <IonCol size="5" class="rec_text">
                    <span className="recommendations_num">
                      {this.state.totalRecords || 0} Matches
                    </span>
                  </IonCol>
                  <IonCol size="7" class="sort_select">
                    <div className="select_div">
                      <Dropdown
                        options={sortByOptions}
                        loadData={this.state.loadData}
                        sortedData={this.sortedData}
                      />
                    </div>
                  </IonCol>
                </IonRow>
              </div>
              <Loads segmentName={""} loads={this.state.loadData}>
                {({ loads }: { loads?: any }) => {
                  return (
                    <IonList className="searchResults_list">
                      {loads.map((load: any, index: number) => (
                        <LoadTile
                          key={index}
                          {...load}
                          module="search/results"
                        />
                      ))}
                    </IonList>
                  );
                }}
              </Loads>
              {this.state.isPageEnd ? (
                <p className="end_of_list">End of list</p>
              ) : null}
            </div>
          ) : (
            <RecommendedError
              constantText={searchResultsErrorText}
              loads={this.newSearch}
              constantButton={searchResultsErrorButton}
            />
          )}
        </IonContent>
      </IonPage>
    );
  }
}
export default SearchResultPage;
