import * as React from "react";
import { IonRow, IonSegment, IonSegmentButton, IonLabel } from "@ionic/react";

export interface SegmentProps {
  getSegment?: any;
  segments?: any;
  activeSegment?: any;
}

export const SegmentControll: React.FC<SegmentProps> = ({
  getSegment,
  segments,
  activeSegment,
}) => {
  return (
    <>
      {segments && (
        <IonRow class="segment-row">
          <IonSegment
            mode="ios"
            onIonChange={(e) => getSegment(e)}
            class="segment_control_tabs"
          >
            {segments.map((name: any, index: number) => (
              <IonSegmentButton
                mode="ios"
                key={index}
                value={name}
                checked={activeSegment === name}
              >
                <IonLabel>{name}</IonLabel>
              </IonSegmentButton>
            ))}
          </IonSegment>
        </IonRow>
      )}
    </>
  );
};
