import React from 'react';
import { IonPage, IonContent, IonList, IonItem, IonLabel, IonInput, IonGrid, IonRow, IonCol, IonButton } from '@ionic/react';
import AppHeader from '../../../components/app/Bars/Bar-header';

const ManageCapacityViewDesktop = (props:any) => {
    const { item, listItems, history: { push } } = props;
    return (
        <IonPage className="desktop-page my-load-desktop-page manage-card-content manage-capacity-view-desktop">
            <AppHeader title={'Manage'} imageCapature={props} />
            <IonContent className="custom-padding">
                <h1>{item.capacityId}</h1>
                {
                    listItems.map((item:any, index: number) => (
                        <div className="manage-capacity-wrapper" key={`main_${item.title}_${index}`}>
                            { item.title ? (
                                <h3 className={item.value?'underline': ''}>
                                    {item.title}
                                    <span>{item.value}</span>
                                </h3>
                            ): null}
                            <IonList className="manage-capcity-list">
                                {
                                    item.childrens.map((children:any) => (
                                        <IonItem key={children.title} data-test={children.slug} className="manage-capacity-item">
                                            <IonLabel position="floating" mode="ios">{children.title}</IonLabel>
                                            <IonInput
                                                value={children.value}
                                                readonly
                                                type="text"
                                                className="manage-input-field"
                                                onClick={children.callback ? children.callback: ()=> {}}
                                            ></IonInput>
                                        </IonItem>
                                    ))
                                }
                            </IonList>
                        </div>
                    ))
                }
                <div className="manage-capacity-buttons">
                    <IonButton class="load-btn" data-test="back-btn" onClick={() => push('/app/manage')}>
                        Back
                    </IonButton>
                    <IonButton class="load-btn"  data-test="edit-btn"  onClick={() => push('/app/manage/ManageMyCapacity/Edit')}>
                        Edit
                    </IonButton>
                </div>
            </IonContent>
        </IonPage>
    )
}

export default ManageCapacityViewDesktop;