import React from 'react';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { getNetworkStatus, getUserDetails } from '../../../selectors/selector';
import { showNetworkAlert } from '../../../actions/App/action';
import { ICapacityPayload } from '../../../schemas/manage/manage.Schema';
import { CapacityModel } from '../../../models/manage/capacity.model';
import { ToastMessage } from '../../../utils/ToastMessage';
import { EDIT_CAPACITY_SUCCESS, OOPS_MESSAGE } from '../../../utils/constants';
import { ManageCapacityFormPage } from '../../../pages/Manage/ManageCapacity';
import { getSelectedManageCapacity } from '../../../selectors/Manage/selector';
import { updateManageCapacity } from '../../../actions/manage/action';

const path = 'manage/ManageMyCapacity';

export const ManageCapacityEditContainer: React.FC<any> = (props: any) => {
  const { isNetWorkAvailable, showNetworkAlert, history, selectedCapacity, userDetails } = props;

  const handleSubmit = (input: ICapacityPayload, clearFields: Function) => {
    if (!isNetWorkAvailable) {
      showNetworkAlert();
      return true;
    }
    const lastUpdatedBy = `${userDetails.firstName} ${userDetails.lastName}`;
    const capacityModel = new CapacityModel();
    const query = capacityModel.updateCapacity();
    const callback = (status: boolean) => {
      ToastMessage.show(status ? EDIT_CAPACITY_SUCCESS : OOPS_MESSAGE);
      if (status) {
        history.push(`/app/${path}/View`);
      }
    };
    props.updateCapacityItem({ params: { query, input, lastUpdatedBy }, callback });
  };

  return selectedCapacity ? (
    <ManageCapacityFormPage module={`${path}/View`} {...props} handleSubmit={handleSubmit} />
  ) : null;
};

const mapStateToProps = createStructuredSelector({
  isNetWorkAvailable: getNetworkStatus(),
  userDetails: getUserDetails(),
  selectedCapacity: getSelectedManageCapacity()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  updateCapacityItem: (payload: any) => dispatch(updateManageCapacity(payload))
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageCapacityEditContainer);
