import React from "react";
import { IonList, IonImg, isPlatform } from "@ionic/react";
import { RecommendedError } from "./RecommendedError";
import { LoadTile } from "../../components/app/home/Load-Tile";
import { formatedData } from "../../utils/formatData";
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  watchedEmptyText,
  recommendedErrorText,
  recommendedErrorButton,
  OFFLINE_MODE_STATE
} from "../../utils/constants";
import DataTableComponent from "../../components/shared/dataTableComponent/data-tabel.page";

export interface SegmentProps {
  type: any;
  recommendedloads: any;
  setView: any;
  watchedLoad: any;
  load: any;
  isloaded: boolean;
  history: any;
  isNetworkAvailable: boolean;
  showAlert: any;
  toggleWatchLoad: Function;
  isWatchLoadEnd?: boolean;
  isRecommendedLoadEnd?: boolean;
}

export const SegmentContent: React.FC<SegmentProps> = ({
  type,
  recommendedloads,
  setView,
  watchedLoad,
  load,
  isloaded,
  history,
  isNetworkAvailable,
  showAlert,
  toggleWatchLoad,
  isWatchLoadEnd = false,
  isRecommendedLoadEnd = false
}) => {
  const newSearch = () => {
    history && history.push("/app/search");
  };

  const redirect = (load: any) => {
    if (isNetworkAvailable) {
      if (type === "watched") {
        const url = `/app/home/watched/${load.loadId}`;
        history.push(url);
      }
      if (type === "recommended") {
        const url = `/app/home/recommended/${load.loadId}`;
        history.push(url);
      }
    } else {
      showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK
      });
    }
  };
  const addCapacity = () => {
    history && history.push("/app/manage/ManageMyCapacity");
  };
  const columns = [
    {
      name: "Origin",
      selector: "origin_city",
      sortable: true,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },
    {
      name: "Origin Date",
      selector: "origin_from_date",
      sortable: true,
      minWidth: "123px"
    },
    {
      name: "DH-O",
      selector: "origin_deadhead",
      sortable: true
    },
    {
      name: "Origin Load Type",
      selector: "origin_load_type",
      sortable: true
    },
    {
      name: "Destination",
      selector: "destination_city",
      sortable: true,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },
    {
      name: "Destination Date",
      selector: "destination_from_date",
      minWidth: "123px",
      sortable: true
    },
    {
      name: "DH-D",
      selector: "destination_deadhead",
      sortable: true
    },
    {
      name: "Destination Load Type",
      selector: "destination_load_type",
      sortable: true
    },
    {
      name: "Trailer Type",
      selector: "trailer",
      sortable: true
    },
    {
      name: "Rate ($)",
      selector: "price",
      sortable: true,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },
    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) => (
        <IonImg className="star_icon" src="assets/icon/star-color.svg" />
      )
    },
    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) => (
        <img
          slot="end"
          alt="logo"
          src="/assets/icon/more-arrow.svg"
          className="moresideimg"
        />
      )
    }
  ];
  const columns2 = [
    {
      name: "Origin",
      selector: "origin_city",
      sortable: true,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },
    {
      name: "Origin Date",
      selector: "origin_from_date",
      sortable: true,
      minWidth: "123px"
    },
    {
      name: "DH-O",
      selector: "origin_deadhead",
      sortable: true
    },
    {
      name: "Origin Load Type",
      selector: "origin_load_type",
      sortable: true
    },
    {
      name: "Destination",
      selector: "destination_city",
      sortable: true,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },
    {
      name: "Destination Date",
      selector: "destination_from_date",
      minWidth: "123px",
      sortable: true
    },
    {
      name: "DH-D",
      selector: "destination_deadhead",
      sortable: true
    },
    {
      name: "Destination Load Type",
      selector: "destination_load_type",
      sortable: true
    },
    {
      name: "Trailer Type",
      selector: "trailer",
      sortable: true
    },
    {
      name: "Rate ($)",
      selector: "price",
      sortable: true,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },

    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) => (
        <img
          slot="end"
          alt="logo"
          src="/assets/icon/more-arrow.svg"
          className="moresideimg"
        />
      )
    }
  ];

  return (
    <>
      {type === "recommended" && (
        <>
          {recommendedloads && recommendedloads.length ? (
            <>
              <IonList className="loadTilePad" data-test="watched-tile">
                {recommendedloads &&
                  recommendedloads.map((load: any, index: number) => (
                    <LoadTile
                      key={index}
                      {...load}
                      module="home/recommended"
                      isRecommended
                      // toggleWatchLoad={toggleWatchLoad}
                    />
                  ))}
              </IonList>
              {
                 (isPlatform("mobile") && isRecommendedLoadEnd) ? (
                  <div className="end-of-list">End of list</div>
                ) : null
              }
              {isPlatform("desktop") ? (
                <DataTableComponent
                  columns={columns2}
                  data={formatedData(
                    recommendedloads,
                    "appointmentFrom",
                    "appointmentTo",
                    "loadId",
                    "transportMode",
                    false
                  )}
                  showEndofList
                  showHeader
                  titleText={
                    type === "recommended" && recommendedloads.length > 1
                      ? `${recommendedloads.length} ` + `Recommendations`
                      : `${recommendedloads.length} ` + `Recommendation`
                  }
                  pagination
                  redirect={redirect}
                />
              ) : null}
            </>
          ) : !isloaded ? (
            <RecommendedError
              constantText={recommendedErrorText}
              constantButton={recommendedErrorButton}
              loads={newSearch}
              addCapacity={addCapacity}
            />
          ) : (
            ""
          )}
        </>
      )}
      {type === "watched" && (
        <>
          {watchedLoad.length ? (
            <>
              <IonList className="loadTilePad" data-test="watched-tile">
                {watchedLoad.map((load: any, index: number) => (
                  <LoadTile
                    key={index}
                    {...load}
                    module="home/watched"
                    isWatch
                    toggleWatchLoad={toggleWatchLoad}
                  />
                ))}
              </IonList>
              {(isPlatform("mobile") && isWatchLoadEnd)? (
                <div className="end-of-list">End of list</div>
              ) : null}
              
              {isPlatform("desktop") ? (
                <DataTableComponent
                  columns={columns}
                  data={formatedData(
                    watchedLoad,
                    "appointmentFrom",
                    "appointmentTo",
                    "loadId",
                    "transportMode",
                    false
                  )}
                  showEndofList
                  showHeader
                  titleText={watchedLoad && `${watchedLoad.length} ${type}`}
                  pagination
                  redirect={redirect}
                />
              ) : null}
            </>
          ) : !isloaded ? (
            <RecommendedError
              constantText={watchedEmptyText}
              constantButton={recommendedErrorButton}
              loads={newSearch}
              redirectTo="app/search"
            />
          ) : (
            ""
          )}
        </>
      )}
    </>
  );
};

export default React.memo(SegmentContent);
