import React, { Component } from "react";
import {
  IonContent,
  IonPage,
  IonCard,
  IonList,
  IonLabel,
  IonItem
} from "@ionic/react";
import "./Menu.page.scss";
import { Device } from "@capacitor/core";
import { AppVersion } from "@ionic-enterprise/app-version";
import { isEntitled } from "app/utils/common";
import AppHeader from "../../components/app/Bars/Bar-header";
import { LogOutContainer } from "../../containers/LogOutContainer";
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  EVENT,
  SCREEN,
  NO_ACCESS_MSG,
  OFFLINE_MODE_STATE
} from "../../utils/constants";
import { logEvent } from "../../utils/Analytics";
import { ToastMessage } from "../../utils/ToastMessage";

export const ListItem = (props: any) => {
  const ionItemClick = (event: any, name: string, uid: any): void => {
    event.preventDefault();
    const urlName = name.replace(/ /g, "");
    // ToastMessage.show();
    /* NETWORK CHECK CONDITION */
    if (isEntitled("more", "moreContent")) {
      if (props.isNetworkAvailable || OFFLINE_MODE_STATE) {
        Object.values(
          props.menuData.data.getUserHelpContent.menuEntries
        ).forEach((data: any) => {
          if (data.uid === uid) {
            props.history.push(`/app/more/${urlName}`, {
              heading: name,
              contextData: data
            });
          }
        });
      } else {
        props.showAlert({
          isShowAlert: true,
          alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
          alertTitle: NO_NEWORK
        });
      }
    } else {
      ToastMessage.show(NO_ACCESS_MSG);
    }
  };
  const heading = props && props.headings && props.headings.title;
  const subHeading = props && props.headings && props.headings.group;
  return (
    <>
      <IonLabel class="menu_list_heading">{heading}</IonLabel>
      <IonCard mode="md" class="menu_list_card2" data-test="ion-card">
        <IonList mode="ios">
          {subHeading &&
            subHeading.map((lable: any, index: number) => (
              <IonItem
                data-test="ion-item"
                class="menu_list_item"
                key={index}
                onClick={(event: any) =>
                  ionItemClick(event, lable.menuItem, lable.uid)
                }
                lines={index === subHeading.length - 1 ? "none" : "inset"}
              >
                {lable.menuItem}
                <img
                  slot="end"
                  alt="logo"
                  src="/assets/icon/more-arrow.svg"
                  className="rightArrow"
                />
              </IonItem>
            ))}
        </IonList>
      </IonCard>
    </>
  );
};

export interface IMenuListState {
  tab: boolean;
  toggleBtn: boolean;
}

export class MenuPage extends Component<any, IMenuListState> {
  constructor(props: any) {
    super(props);
    this.state = {
      tab: false,
      toggleBtn: false
    };
  }

  componentDidMount(): void {
    // Analytics implementation
    logEvent(EVENT.SV_MORE_MENU, {
      screen: SCREEN.MORE_MENU,
      carrierId:
        this.props && this.props.userDetails && this.props.userDetails.carrierId
    });
  }

  render() {
    const menuHeadingList =
      this.props &&
      this.props.menuData &&
      this.props.menuData.data &&
      this.props.menuData.data.getUserHelpContent &&
      this.props.menuData.data.getUserHelpContent.menuItems;
    // console.log("Menu page",this.props)
    return (
      <IonPage className=" menu_page">
        <AppHeader
          title="Menu"
          isDeviceInfo
          viewDeviceInfo={async () => {
            const deviceInfo = await Device.getInfo();
            const appVersion = await AppVersion.getVersionNumber();
            this.props.history.push("/app/more/deviceInfo", {
              deviceInfo,
              appVersion
            });
          }}
        />
        <IonContent class="menu_page_content" data-test="IonPage-menu-page">
          <div className="menu_page_div">
            <LogOutContainer {...this.props} isRegistrationPage={false}/>
            {menuHeadingList &&
              menuHeadingList.map((headings: any, index: number) => (
                <ListItem
                  key={index}
                  headings={headings}
                  {...this.props}
                  submitFeedback={this.props.submitFeedback}
                />
              ))}
          </div>
        </IonContent>
      </IonPage>
    );
  }
}
export default MenuPage;
