import React from 'react';
import { IonContent } from '@ionic/react';
import "./ListItems.scss";
import { FilterWithSort } from '../../../components/core/FilterWithSort/FilterWithSort';

const ListItems:React.FC<any> = (props:any) => {
    const {isDesktop, dropdown, children, addPage} = props;
    return (
        <IonContent class={`${isDesktop? "mange-user-desktop" : "custom-padding"}`}>
            <FilterWithSort dropdown={dropdown}  addPage= {addPage} isDesktop={isDesktop}/>
            {children}
        </IonContent>
    );
}

export default ListItems;
