import React from 'react';
import { IonAlert } from '@ionic/react';
import "./Alert.scss";

export const Alert = (props: any) => {
    return (
        <>
            <IonAlert
                isOpen={props.isOpen}
                message={props.message}
                backdropDismiss={true}
                header={props.title}
                onDidDismiss={props.dismiss}
                buttons={
                    [
                        {
                            text: 'ok',
                            role: 'ok',
                            cssClass: 'firstBtn',
                            handler: () => { props.close() }
                        }

                    ]
                } />
        </>
    )
}
