import React, { PureComponent } from "react";
import {
  IonImg,
  IonRow,
  IonCol,
  IonButton,
  IonGrid,
  isPlatform,
  IonTextarea,
  IonAlert,
} from "@ionic/react";
import StarRatingComponent from "react-star-rating-component";
import "./Feedback.scss";
import { Market } from "@ionic-native/market";
import { Plugins } from '@capacitor/core';
import * as Constants from "../../../utils/constants";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { submitFeedback } from "../../../actions/Menu/action";
import { makeSubmitFeedback } from "../../../selectors/Menu/selector";
import { Device } from "@capacitor/core";
import { AppVersion } from "@ionic-enterprise/app-version";
import {
  getUserDetails,
  getLoading,
  getNetworkStatus,
} from "../../../selectors/selector";

import { compose } from "redux";
import useInjectSaga from "../../../utils/injectSaga";
import useInjectReducer from "../../../utils/injectReducer";
import reducer from "../../../reducers/Menu/reducer";
import saga from "../../../saga/Menu/saga";
import { Feedback } from "../../../models/home/Menu.model";
import { ToastMessage } from "../../../utils/ToastMessage";
import { showAlert } from "app/actions/App/action";
import {
  deviceModel,
  manufacturer,
  operationSystem,
  osVersion,
} from "app/utils/helper";

const key = "Menu";
const { Browser } = Plugins;

export class FeedbackComponent extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      rating: 0,
      feedback: "",
      isShowModal: false,
      isShowPopup: false,
    };
    this.onStarClick = this.onStarClick.bind(this);
  }
  onStarClick(nextValue: any, prevValue: any, name: string) {
    this.setState({ rating: nextValue });
  }

  onSubmitFeedbackClick = async () => {
    const deviceInfo = isPlatform("capacitor") ? await Device.getInfo() : "";
    const appVersionNumber = isPlatform("capacitor")
      ? await AppVersion.getVersionNumber()
      : "";
    let __model: Feedback = new Feedback();
    __model.appVersion = isPlatform("capacitor") ? appVersionNumber : "";
    __model.upn = this.props.userDetails.userPrincipalName;
    __model.deviceUUID = isPlatform("capacitor")
      ? deviceInfo && deviceInfo.uuid
      : "";
    __model.carrierId = this.props.userDetails.carrierId;
    __model.carrierName = this.props.userDetails.companyName;
    __model.rating = this.state.rating;
    __model.feedback = this.state.feedback;
    __model.firstName = this.props.userDetails.firstName;
    __model.lastName = this.props.userDetails.lastName;
    __model.devicePlatform = isPlatform("capacitor") ? operationSystem() : "";
    __model.deviceManufacturer = isPlatform("capacitor") ? manufacturer() : "";
    __model.deviceOSVersion = isPlatform("capacitor")
      ? `${operationSystem()} ${osVersion()}`
      : "";
    __model.deviceModel = isPlatform("capacitor") ? deviceModel() : "";
    this.props.submitFeedback(
      __model.getRequestPayload(),
      this.feedBackCallback
    );
  };
  feedBackCallback = () => {
    if (
      this.props &&
      this.props.submitFeedbackCall &&
      this.props.submitFeedbackCall.data &&
      this.props.submitFeedbackCall.data.feedback === true &&
      this.state.isShowPopup
    ) {
      this.setState({ isShowModal: false, rating: 0 });
    } else {
      this.setState({ isShowModal: true });
    }
  };

  feedbackHandler = (event: any) => {
    this.setState({ [event.target.name]: event.target.value });
    if (event.target.name === "feedback") {
      this.setState({ feedback: event.target.value });
    }
  };

  notNowButtonClick = () => {
    this.props.notNowClick();
    this.setState({ isShowPopup: true });
    this.onSubmitFeedbackClick();
  };

  onSureClick = async () => {
    if (!this.props.isNetworkAvailable) {
      this.props.showAlert({
        isShowAlert: true,
        alertMessage: Constants.PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: Constants.NO_NEWORK,
      });
      return;
    }
    
    if (isPlatform("ios")) {
      await Browser.open({ url: 'itms-apps://itunes.apple.com/app/id1250562051' });
    } else if (isPlatform("android")) {
      await Browser.open({ url: 'market://details?id=com.schneider.compass' });
      // Market.open("com.schneider.compass").then((response:any) => {
      //   console.log(response);
      // }).catch((error:any) => {
      //   ToastMessage.show(error);
      // });
    }
  };
  render() {
    const { rating } = this.state;

    return (
      <div className="feedbackDiv" data-test="Feedback-div">
        <IonAlert
          isOpen={this.state.isShowModal}
          message={`We value your feedback and strive to improve your FrieghtPower experience.`}
          backdropDismiss
          mode="ios"
          header="Thank you for your feedback!"
          onDidPresent={() =>
            setTimeout(
              () => (
                this.setState({ rating: 0, isShowModal: false, feedback: "" }),
                this.props.notNowClick()
              ),
              2000
            )
          }
        />
        <IonGrid className="feedback-grid">
          <p className="feedback-p notLikely">{Constants.NOT_LIKELY}</p>
          <p className="feedback-p likely">{Constants.VERY_LIKELY}</p>
          <IonRow className="feedback-row">
            <IonCol className="feedback-column">
              <StarRatingComponent
                name="rate1"
                starCount={5}
                value={rating}
                onStarClick={this.onStarClick}
                starColor={"#FF6114"}
                className="starRating"
                renderStarIcon={(index: any, value: any) => (
                  <IonImg
                    className={
                      index == 5 ? "starRatingIcon4" : "starRatingIcon"
                    }
                    alt="Star"
                    src={
                      index <= value
                        ? "assets/icon/star-color.svg "
                        : "assets/icon/load-star.svg"
                    }
                  ></IonImg>
                )}
              />
            </IonCol>
          </IonRow>
        </IonGrid>
        {rating == 0 ? null : (
          <>
            {isPlatform("mobile") ? (
              rating <= 3 && rating >= 1 ? (
                <IonGrid className="starRating-grid">
                  <p className="feedbackImprove">
                    {Constants.IMPROVING_SERVICE}
                  </p>
                  <IonGrid className="text-div">
                    <IonTextarea
                      maxlength={2000}
                      placeholder="Start typing here"
                      onIonChange={(e) => this.feedbackHandler(e)}
                      name="feedback"
                      value={this.state.feedback}
                    ></IonTextarea>
                  </IonGrid>
                  <IonRow className="submitBtn-row">
                    <IonCol className="feedback-cancel">
                      <IonButton
                        data-test="cancel-button-mobile"
                        data-kind="primary"
                        expand="block"
                        type="submit"
                        class="submit_btn"
                        onClick={() => {
                          this.setState({ rating: 0 });
                        }}
                      >
                        Cancel
                      </IonButton>
                    </IonCol>
                    <IonCol>
                      <IonButton
                        data-test="submit-button-mobile"
                        data-kind="primary"
                        expand="block"
                        type="submit"
                        class="submit_btn"
                        onClick={() => {
                          this.setState({ isShowPopup: false });
                          this.onSubmitFeedbackClick();
                        }}
                      >
                        Submit
                      </IonButton>
                    </IonCol>
                  </IonRow>
                </IonGrid>
              ) : (
                <IonGrid className="review-grid">
                  <p className="starRating-p review-p">
                    {Constants.GOOD_RATING}
                  </p>
                  <p className="starRating-p appStore-p">
                    {Constants.APPSTORE_REVIEW}
                  </p>
                  <IonRow className="starRating-row">
                    <IonCol className="starRating-col">
                      <IonButton
                        data-kind="primary"
                        data-test="notNow-button"
                        onClick={() => this.notNowButtonClick()}
                        type="submit"
                        class="call_btn"
                      >
                        Not now
                      </IonButton>
                    </IonCol>
                    <IonCol>
                      <IonButton
                        data-test="sureOnClick"
                        data-kind="primary"
                        type="submit"
                        class="call_btn"
                        onClick={() => this.onSureClick()}
                      >
                        Sure
                      </IonButton>
                    </IonCol>
                  </IonRow>
                </IonGrid>
              )
            ) : (
              <IonGrid className="starRating-grid">
                {rating <= 3 && rating >= 1 ? (
                  <p className="feedbackImprove">
                    {Constants.IMPROVING_SERVICE}
                  </p>
                ) : (
                  <p className="feedbackImprove">{Constants.MORE_FEEDBACK}</p>
                )}
                <IonGrid className="text-div">
                  <IonTextarea
                    maxlength={2000}
                    placeholder="Start typing here"
                    onIonChange={(e) => this.feedbackHandler(e)}
                    name="feedback"
                    value={this.state.feedback}
                  ></IonTextarea>
                </IonGrid>
                <IonRow className="submitBtn-row">
                  <IonCol className="feedback-cancel">
                    <IonButton
                      data-test="cancel-button"
                      data-kind="primary"
                      expand="block"
                      type="submit"
                      class="submit_btn"
                      onClick={() => {
                        this.setState({ rating: 0 });
                      }}
                    >
                      Cancel
                    </IonButton>
                  </IonCol>
                  <IonCol>
                    <IonButton
                      data-test="submit-button"
                      data-kind="primary"
                      expand="block"
                      type="submit"
                      class="submit_btn"
                      onClick={() => this.onSubmitFeedbackClick()}
                    >
                      Submit
                    </IonButton>
                  </IonCol>
                </IonRow>
              </IonGrid>
            )}
          </>
        )}
      </div>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  submitFeedbackCall: makeSubmitFeedback(),
  userDetails: getUserDetails(),
  isLoading: getLoading(),
  isNetworkAvailable: getNetworkStatus(),
});

export const mapDispatchToProps = (dispatch: Function) => ({
  submitFeedback: (data: any, callback: Function) =>
    dispatch(submitFeedback(data, callback)),
  showAlert: (data: any) => dispatch(showAlert(data)),
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose<any>(
  withConnect,
  withReducer,
  withSaga
)(React.memo(FeedbackComponent));
