import React, { useState, useEffect } from 'react';
import { IonCol, IonModal, IonRow, IonIcon, IonGrid } from '@ionic/react';
import './FilterOnly.scss';

const FilterOnly: React.FC<any> = (props: any) => {
  const {
    dropdown: { options, setFilter },
    filter
  } = props;
  const selectedOptions = options.map((option: any, index: number) => ({
    ...option,
    selected: index === 0
  }));
  const [isModalOpen, setModalOpen] = useState(false);
  const [selectedOption, setSelectedOptions] = useState<any>(selectedOptions);
  const [filterOption, setFilterOptions] = useState<any>([]);
  const sortOrder = 'asc';
  const selectItems = selectedOption.filter((option: any) => option.selected);
  const title = selectItems.map((option: any) => `${option.name}`).join('');
  const openModel = () => {
    setFilterOptions(selectedOption);
    setModalOpen(true);
  };

  const setFilterOption = (opion: any) => {
    const options = filterOption.map((item: any) => {
      return {
        ...item,
        selected: item.name === opion.name ? !item.selected : false
      };
    });
    const selected = options.find((option: any) => option.selected);
    if (selected) {
      setFilterOptions(options);
    }
  };

  const sortData = () => {
    setFilter({
      filter: {
        selected: filterOption,
        direction: sortOrder
      }
    });
  };

  const confirmSelection = () => {
    sortData();
    setSelectedOptions(filterOption);
    setModalOpen(false);
  };

  useEffect(() => {
    const selectedOptions =
      filter && filter.length > 0
        ? filter
        : options.map((option: any, index: number) => ({ ...option, selected: index === 0 }));
    setSelectedOptions(selectedOptions);
  }, [filter]);

  return (
    <IonRow class="list-items-header">
      <IonCol class="list-item-button">
      <IonGrid class="filter-display-text"> {title} <IonIcon src="assets/icon/filter.svg" onClick={() => openModel()} /></IonGrid>
        <IonModal
          show-backdrop
          backdrop-dismiss={false}
          isOpen={isModalOpen}
          cssClass="dropdown-modal"
          onDidDismiss={() => {
            setModalOpen(false);
          }}>
          <div className="search_options">
            <IonRow class="dropdown-row dropdown-heading">
              <IonCol>
                <p className="sortBy_text">Filter By</p>
              </IonCol>
            </IonRow>
            {filterOption.map((option: any, k: any) => (
              <IonRow
                className="dropdown-row filter-option"
                key={k}
                onClick={() => setFilterOption(option)}>
                <IonCol className={option.selected ? 'checked' : ''}>
                  {option.name} <i className="check"></i>
                </IonCol>
              </IonRow>
            ))}
            <IonRow class="dropdown-handler">
              <IonCol size="1" />
              <IonCol size="5" className="dropdown_cancel" onClick={() => setModalOpen(false)}>
                <span>Cancel</span>
              </IonCol>
              <IonCol size="5" className="dropdown_cancel dropdown_ok" onClick={confirmSelection}>
                <span>OK</span>
              </IonCol>
              <IonCol size="1" />
            </IonRow>
          </div>
        </IonModal>
      </IonCol>
    </IonRow>
  );
};

export default FilterOnly;
