import React, { useState } from 'react';
import { IonContent, IonList, IonRow, IonCol, IonLabel, IonInput, IonImg, IonGrid, 
	IonItem, IonText, IonButton, IonIcon } from '@ionic/react';
import { formatPhoneNumber,  removeSpecial, userProfileFields, driverFields, getAddressValue, carrierField } from './ManageUserUtil';
import { IRole, IAddress, IProfile } from '../../../schemas/manage/manage.Schema';
import {  USER_BACK_TITLE, USER_BACK_MESSAGE, 
	 CHANGE_PASSWORD_TITLE, CHANGE_PASSWORD_MESSAGE, BUTTONS 
} from '../../../utils/constants';
import { CustomModal } from '../../../components/CustomModal';
import { useAuthService } from 'app/services/Auth';

const ManageProfileDesktopPage = (props: any) => {
	const authService = useAuthService();
	const [isEdit, setEditMode] = useState(false);
	const { userDetails: { carrierId }, selectedUser } = props;
	const [alertProps, setAlertProps] = useState<any>(null);
	
	const handleBackMethod = () => {
		if (!props.isFormEdit) {
			setEditMode(false);
			return true;
		}
		setAlertProps({
			isOpen: true,
			title: USER_BACK_TITLE,
			message: USER_BACK_MESSAGE,
			buttons: [
				{
					label: 'Cancel',
					callback: () => {
						setAlertProps(null);
					}
				},
				{
					label: 'Ok',
					callback: () => {
						props.resetData();
						setEditMode(false);
						setAlertProps(null);
					}
				}
			]
		});
	}

	const userProfile: Array<any> = selectedUser ? [
		{
			label: "First Name",
			value: selectedUser.firstName
		},
		{
			label: "Last Name",
			value: selectedUser.lastName
		},
		{
			label: "Email Address",
			value: selectedUser.email
		},
		{
			label: "Mobile Number",
			value: formatPhoneNumber(selectedUser.mobileNumber)
		},
		{
			label: "Role(s)",
			value: props.isUserRoleAdmin ? `Admin${props.roleTitle.length > 0 ? `, ${props.roleTitle}` : ''}` : (props.roleTitle || 'Guest')
		},
		{
			label: "Carrier ID",
			value: selectedUser.carrierId || 'NA'
		},
	] : [];

	const handleChangePassword = () => {
		if (!props.isNetworkAvailable) {
			props.showNetworkAlert();
			return true;
		}
		setAlertProps({
			isOpen: true,
			title: CHANGE_PASSWORD_TITLE,
			message: CHANGE_PASSWORD_MESSAGE,
			buttons: [
				{
					label: BUTTONS.CANCEL,
					callback: () => {
						setAlertProps(null);
					}
				},
				{
					label: BUTTONS.OK,
					callback: () => {
						authService.changePassword();
						setAlertProps(null);
					}
				}
			]
		})
	}
	
	return (
		<IonContent class="custom-padding my-profile-desktop">
			{isEdit ? (
				<>
					<div className="manage-users-wrapper">
						<IonList>
							<IonItem className="profile-item role-info" data-test="select-role" onClick={() => props.setShowRoleOptions(!props.showRoleOptions)}>
								<IonLabel position="floating" mode="ios">Role</IonLabel>
								<IonInput
									value={props.isUserRoleAdmin ? `Admin${props.roleTitle.length > 0 ? `, ${props.roleTitle}` : ''}` : (props.roleTitle || 'Guest')}
									readonly
									type="text"
									class=""
								></IonInput>
								{props.isUserRoleAdmin ? <IonIcon src="/assets/icon/grid-down-arrow.svg" className="role-icon"></IonIcon> : null}
								{props.isUserRoleAdmin ? <IonText class="role-desc">{props.roleDesc}</IonText> : null}
							</IonItem>
							{props.isUserRoleAdmin && props.roleSelection && props.showRoleOptions ? (
								<IonList data-test="roles-list" class="roles-list-grid static">
									{props.roleSelection.map((role: IRole, index: number) => (
										<IonItem
											key={`role_${index}`}
											data-test="role-list-item"
											onClick={() => props.handleRoleSelection(role)}
										>
											<i className={`role-check-icon ${role.selected ? 'checked' : ''}`}></i>
											<span className="role-list-label">{role.label}</span>
										</IonItem>
									))}
								</IonList>
							) : null}
						</IonList>
						<IonList>
							{
								userProfileFields.map((profile: IProfile) => (
									<React.Fragment key={`def_${profile.label}`}>
										<IonItem className={props.formErrors && props.formErrors[profile.name] ? 'profile-item error' : 'profile-item'}>
											<IonLabel position="floating" mode="ios">{profile.label}</IonLabel>
											{
												profile.name === 'mobileNumber' ? (
													<IonInput
														value={props.formFields[profile.name]}
														name={profile.name}
														placeholder={profile.placeholder}
														pattern="[0-9a-zA-Z]"
														type="text"
														maxlength={10}
														onFocus={() => props.setFormFields({ ...props.formFields, mobileNumber: props.formFields.mobileNumber && removeSpecial(props.formFields.mobileNumber) })}
														onIonBlur={props.handleChange}
														data-test={`${profile.name}-field`}
														className={`${profile.className ? profile.className : ''}`}
													></IonInput>
												) : (
														<IonInput
															value={props.formFields[profile.name]}
															name={profile.name}
															placeholder={profile.placeholder}
															pattern="[0-9a-zA-Z]"
															type="text"
															data-test={`${profile.name}-field`}
															onIonChange={props.handleChange}
															className={`${profile.className ? profile.className : ''}`}
														></IonInput>
													)
											}
											{props.formErrors && props.formErrors[profile.name] ? <IonText class="field-error">{props.formErrors[profile.name]}</IonText> : null}
										</IonItem>
									</React.Fragment >
								))
							}
							<IonItem className="profile-item">
								<IonLabel position="floating" mode="ios">Email Address</IonLabel>
								<IonInput
									value={props.formFields.email}
									placeholder='Type Your Email Address'
									type="email"
									readonly
									name="emailAddress"
								></IonInput>
							</IonItem>

							{
								props.isDriverSelected && driverFields.map((profile: IProfile) => (
									<React.Fragment key={profile.label}>
										<IonItem className="profile-item">
											<IonLabel position="floating" mode="ios">{profile.label}</IonLabel>
											<IonInput
												value={profile.name === 'addressDTOs' ? props.isAddressEdit ? props.address : getAddressValue(props.formFields[profile.name]) : props.formFields[profile.name]}
												name={profile.name}
												placeholder={profile.placeholder}
												type="text"
												pattern="[0-9a-zA-Z]"
												onIonChange={props.handleChange}
												data-test={`${profile.name}-field`}
												onIonFocus={() => {
													if (profile.name === 'addressDTOs') {
														props.setAddressEdit(true);
													}
												}}
												className={`${profile.className ? profile.className : ''}`}
											>
												{profile.name === 'addressDTOs' ? (
													<IonImg
														slot="end"
														alt="logo"
														src="/assets/icon/search_icon.svg"
														className="search-input-icon"
													/>
												) : null}
											</IonInput>
											{props.formErrors && props.formErrors[profile.name] ? <IonText class="field-error">{props.formErrors[profile.name]}</IonText> : null}
											{profile.name === 'addressDTOs' && props.showAddressList ? (
												<IonGrid class="suggestions">
													{
														props.addressList.map((address: IAddress, index: number) => (
															<IonRow key={`addr${index}`} className="suggestions_item" onClick={() => props.handleAddressSelection(address)}>
																<IonCol class="suggestions_list">
																	{getAddressValue(address)}
																</IonCol>
															</IonRow>
														))
													}
												</IonGrid>
											) : null}
										</IonItem>

									</React.Fragment>
								))
							}
							<IonItem className="profile-item" >
								<IonLabel position="floating" mode="ios">{carrierField.label}</IonLabel>
								<IonInput
									value={carrierId || '-'}
									name={carrierField.name}
									placeholder={carrierField.placeholder}
									type="text"
									readonly
									className={`${carrierField.className ? carrierField.className : ''}`}
								></IonInput>
								{
									selectedUser.carrierId ? (
										<img
											slot="end"
											alt="delink"
											src="/assets/icon/delink.svg"
											className="delink"
											onClick={props.handleDelinkClick}
										/>
									) : null
								}

							</IonItem>
						</IonList>
					</div>
					<div className="manage-user-buttons">
						<IonButton data-kind="primary" type="submit" data-test="back-btn" class="load-btn" onClick={() => handleBackMethod()}>Back</IonButton>
						<IonButton data-kind="primary" type="submit" data-test="submit-btn" class="" className={!props.isFormValid ? 'load-btn invalid-btn' : 'load-btn btn_save '} onClick={() => { setEditMode(false); props.handleSubmit(); }}>Save</IonButton>
					</div>
					
				</>
			) : <>
				<div className="manage-users-wrapper">
					<IonList>
						{
							userProfile.map((profile: any) => (
								<IonItem className="profile-item" key={profile.label}>
									<IonLabel className="profile-label" position="floating" mode="ios">{profile.label}</IonLabel>
									<IonInput
										value={profile.value}
										readonly
										type="text"
										class="profile-input"
									></IonInput>
								</IonItem>
							))
						}
						<IonItem className=" profile-item changePwd" key={'change-pwd'} lines="none" onClick={() => handleChangePassword()}>
							<IonText className="change-pwd">
								Change Password
							</IonText>
							<img
								slot="end"
								alt="logo"
								src="/assets/icon/more-arrow.svg"
								className="moresideimg"
							/>
						</IonItem>
					</IonList>
				</div>
				<div className="manage-user-buttons">
					<IonButton class="load-btn" data-test="edit-btn" onClick={() => { setEditMode(true) }}>
						Edit
					</IonButton>
				</div>
			</>}
			{
				alertProps ? <CustomModal {...alertProps} /> : null
			}
		</IonContent>
	);
}
export default ManageProfileDesktopPage;