import React from 'react';
import {ManageUserProfilePage} from '../../../pages/Manage/ManageUser';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { getUserDetails, getNetworkStatus } from '../../../selectors/selector';
import { getSelectedManageUser } from '../../../selectors/Manage/selector';
import { showNetworkAlert } from '../../../actions/App/action';
import { isPlatform } from '@ionic/react';
import ManageUserProfileDesktopPage from '../../../pages/Manage/ManageUser/ManageUserProfileDesktop.page';


export const ManageUserProfileContainer: React.FC<any> = (props: any) => {
    return isPlatform('desktop') ? <ManageUserProfileDesktopPage {...props} module="manage/ManageUser" /> :(<ManageUserProfilePage {...props} module="manage/ManageUser" />);
}

const mapStateToProps = createStructuredSelector({
    userDetails: getUserDetails(),
    selectedUser: getSelectedManageUser(),
    isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
    showNetworkAlert: () => dispatch(showNetworkAlert()),
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageUserProfileContainer);

