import { ActionType, SearchState } from "../../schemas/Search/Search.schema";
import * as Constants from "../../utils/constants";
import produce from "immer";

export const initalSearchState: SearchState = {
  loads: {
    searchLoads: [],
    totalRecords: 0,
  },
  params: {},
  allLoads: [],
  showBuyLoadAPIError: false
};

export default (state = initalSearchState, action: ActionType) =>
  produce(state, (draft) => {
    switch (action.type) {
      case Constants.SEARCH_DATA:
        draft.params = action.payload;
        break;
      case "loadSearchData":
        const payload = action.payload && (action.payload as any);
        const isPagination = payload.isPagination;
        draft.loads = {
          searchLoads: isPagination
            ? draft.loads.searchLoads.concat(
                (payload && payload.data && payload.data.searchLoads) || []
              )
            : (payload && payload.data && payload.data.searchLoads) || [],
          totalRecords:
            (payload && payload.data && payload.data.totalRecords) || 0,
        };
        break;
      case Constants.SAVE_ALL_SEARCH_DATA:
        const payload_ = action.payload && (action.payload as any);
        const load = {
          searchLoads:
            (payload_ && payload_.data && payload_.data.searchLoads) || [],
          totalRecords:
            (payload_ && payload_.data && payload_.data.totalRecords) || 0,
        };
        draft.allLoads = [...draft.allLoads, load];
        break;
      case Constants.CLEAR_SEARCH_RESULST:
        draft.loads = action.payload;
        break;
      case Constants.REMOVE_SEARCH_RESULTS:
        const removedLoad = action.payload && (action.payload as any);
        draft.loads.searchLoads = draft.loads.searchLoads.filter(
          (details: any) => details.loadId !== removedLoad
        );
        draft.loads.totalRecords = draft.loads.searchLoads.length;
        break;
      case Constants.SHOW_BUYLOAD_API_ERROR:
        draft.showBuyLoadAPIError = action.payload && (action.payload as any);
    }
  });
