import React, { useState, useEffect } from "react";
import { IDepartureProps } from "../../../schemas/StartTrip/StartTrip";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import { IonDatetime, IonLabel } from "@ionic/react";
import { getYearValues } from "../../../utils/formatData";

const model: StartTripModel = new StartTripModel();
export const Departure: React.FunctionComponent<IDepartureProps> = (
  props: IDepartureProps
) => {
  const departureDate = model.getDeparture(props.loadStop, props.isNextStop);
  const yearValues = getYearValues() || new Date().getFullYear();

  useEffect(() => {
    props.onChange(departureDate);
  }, [departureDate, props.shipmentId, props.timestamp]);
  // new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString();
  const readyonly = model.isDepartureReadonly(props.loadStop, props.arrivalVal);
  return (
    <React.Fragment>
      <IonLabel className="arrival-label ">
        {model.getDepartureLabelName(props.loadStop)}
      </IonLabel>

      <IonDatetime
        readonly={readyonly || props.isNextStop}
        disabled={readyonly || props.isNextStop}
        displayFormat="MMM DD hh:mm A"
        pickerFormat="YYYY MMM DD hh:mm A"
        value={props.value}
        onIonChange={e => {
          if (e.detail.value) {
            props.onChange(e.detail.value);
          }
        }}
        yearValues={yearValues}
        display-timezone={props.timeZone}
        minuteValues="00,05,10,15,20,25,30,35,40,45,50,55"
        className={`start-trip-location4 arrival-width2 execution-field ${
          readyonly && props.value ? "hide-border" : ""
        } ${props.isError ? "execution-validation-error" : ""}`}
        mode="ios"
      ></IonDatetime>
    </React.Fragment>
  );
};
