import React from 'react';
import { IonModal, IonLabel, IonCol, IonButton, IonText } from '@ionic/react';
import "./CustomModal.scss";

const CustomModal:React.FC<any> = (props:any) => {
    const { isOpen, title, message, buttons } = props;

    return (
        <IonModal
        isOpen={isOpen}
        cssClass="custom-modal"
        backdropDismiss={false}
      >
        <header className="custom-modal-header">
          <IonLabel className="custom-modal-title" color="dark">
                {title}
          </IonLabel>
        </header>
        <section>
          <IonText className="custom-modal-message">
              <div  dangerouslySetInnerHTML={{__html: message}}></div>
          </IonText>
        </section>
        <footer>
            {
                buttons.map(((button:any, index:number) => {
                    return (
                        <IonCol key={`${title}_${index}`}>
                            <IonButton class="btn load-btn" data-test={`${button.label.toLowerCase()}-btn`} onClick={button.callback}>
                                {button.label}
                            </IonButton>
                        </IonCol>
                    )
                }))
            }
        </footer>
      </IonModal>
    )
}

export default CustomModal;
