import { createSelector } from 'reselect';
import { initalState as globalState } from '../reducers/Login/reducer';
import { initialState as appInititalState } from '../reducers/App/reducer';

const selectGlobal = (state: any) => state.global || globalState;
const selectApp = (state: any) => state.App;

export const makeSelectGlobal = () =>
  createSelector([selectGlobal], global => global || globalState);

export const getLoading = () => createSelector([selectGlobal], global => global.loading || false);

export const getCurrentUser = () =>
  createSelector([selectGlobal], global => global.currentUser || globalState.currentUser);

export const getLoginError = () =>
  createSelector([selectGlobal], global => global.error || globalState.error);

export const getUserData = () =>
  createSelector([selectGlobal], global => global.userData || globalState.userData);

export const getShowPermissionAlert = () =>
  createSelector(
    [selectGlobal],
    global => global.showPermissionAlert || globalState.showPermissionAlert
  );

export const getPermissionAlertMessage = () =>
  createSelector(
    [selectGlobal],
    global => global.permissionAlertMessage || globalState.permissionAlertMessage
  );

export const getPermissionAlertTitle = () =>
  createSelector(
    [selectGlobal],
    global => global.permissionAlertTitle || globalState.permissionAlertTitle
  );
export const makeDriverAssign = () =>
  createSelector([selectGlobal], global => global.Hidden || globalState.Hidden);

export const getGPSDetails = () =>
  createSelector([selectApp], App => App.GPSData || appInititalState.GPSData);

export const getPlatform = () =>
  createSelector([selectGlobal], global => global.deviceInfo.platform || globalState.deviceInfo);

export const getLoginToken = () =>
  createSelector([selectGlobal], global => {
    return global.loginToken || globalState.loginToken;
  });

export const getDeepLinkURI = () =>
  createSelector([selectGlobal], global => {
    return global.deepLinkURI || globalState.deepLinkURI;
  });

export const getSession = () =>
  createSelector([selectGlobal], global => {
    return global.hasSession;
  });
export const getUserDetails = () =>
  createSelector(
    [selectApp],
    user => user && user.UserDetails

    // commented the below line of code to avoid unnessery component rendering
    // appInititalState.UserDetails
  );

export const getDeviceRegistrationResult = () =>
  createSelector(
    [selectApp],
    appState => (appState && appState.deviceRegistration) || appInititalState.deviceRegistration
  );

export const getRefreshStatus = () =>
  createSelector(
    [selectGlobal],
    global => global.showPullToRefresh || globalState.showPullToRefresh
  );

export const getNetworkStatus = () =>
  createSelector(
    [selectGlobal],
    global => global.networkStatus.connected || globalState.networkStatus.connected
  );

export const getShowAlert = () =>
  createSelector([selectApp], App => App.showAlert || appInititalState.showAlert);

export const getAlertMessage = () =>
  createSelector([selectApp], App => App.alertMessage || appInititalState.alertMessage);

export const getAlertTitle = () =>
  createSelector([selectApp], App => App.alertTitle || appInititalState.alertTitle);

export const getMoreContent = () =>
  createSelector([selectApp], App => App.moreContent || appInititalState.moreContent);

export const getOfflineData = () => createSelector([selectApp], App => App.offlineJSON || appInititalState.offlineJSON)
export const getConfigParams = () => createSelector([selectApp], App => App.configParams || appInititalState.configParams)
export const getSearchParams = () => createSelector([selectApp], App => App.searchParams || appInititalState.searchParams)
export const getShipmentParams = () => createSelector([selectApp], App => App.shipmentParams || appInititalState.shipmentParams)
export const getPullToRefreshState = () => createSelector([selectApp], App => App.pullToRefreshState || appInititalState.pullToRefreshState)
export const getRegisterUserStatusMessage = () =>
  createSelector(selectApp, userData => {
    return userData && userData.registerUserStatusMessage;
  });
export const getNotificationData = () =>
  createSelector([selectGlobal], global => {
    return global.notificationData || globalState.notificationData;
  });