import React from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { withRouter, RouteComponentProps } from 'react-router';
import useInjectSaga from '../../../utils/injectSaga';
import useInjectReducer from '../../../utils/injectReducer';
import {
  getLoading,
  getRefreshStatus,
  getNetworkStatus,
  getUserDetails
} from '../../../selectors/selector';
import { SearchResultPage } from '../../../pages/Search/SearchResults';
import { makeSearch } from '../../../selectors/Search/selector';
import reducer from '../../../reducers/Search/reducer';
import saga from '../../../saga/Search/Search.saga';
import { SEARCH_DATA, fetchResults } from '../../../actions/Search/action';

import { showAlert } from '../../../actions/App/action';

const key = 'search';
let params = {};
export interface ResultProps extends RouteComponentProps {
  searchData: any;
  location: any;
  data: any;
  loading: any;
  history: any;
  fectchData: any;
  userDetails: any;
  isInPullToRefresh: boolean;
  isNetworkAvailable: boolean;
  showAlert: Function;
}
export const SearchResultContainer: React.FC<ResultProps> = ({
  searchData,
  location,
  data,
  loading,
  history,
  fectchData,
  userDetails,
  isInPullToRefresh,
  isNetworkAvailable,
  showAlert
}) => {
  const searchInput = location && location.state && location.state.searchInput;

  if (location.state && location.state.params) {
    params = location.state.params;
  }
  if (history.location) {
    return (
      <SearchResultPage
        results={{
          searchLoads: data.searchLoads || [],
          totalRecords: data.totalRecords || 0
        }}
        params={params}
        history={history}
        userDetails={userDetails}
        searchInput={searchInput}
        totalRecords={data.totalRecords}
        handleSortingPagination={(inputs: any, isPagination?: boolean, isHideLoader: boolean = false) => {
          fectchData(inputs.getRequestPayload(), isPagination || false, isHideLoader);
        }}
        onResetPagination={() => {
          // setSearchLoads([]);
        }}
        isInPullToRefresh={isInPullToRefresh}
        isNetworkAvailable={isNetworkAvailable}
        showAlert={showAlert}
      />
    );
  }
  return null;
};

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  data: makeSearch(),
  userDetails: getUserDetails(),
  isInPullToRefresh: getRefreshStatus(),
  isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  searchData: (data: any) => dispatch(SEARCH_DATA(data)),
  fectchData: (searchdata: any, isPagination: boolean, isHideLoader: boolean) =>
    dispatch(fetchResults(searchdata, isPagination, isHideLoader)),
  showAlert: (data: any) => dispatch(showAlert(data))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(withRouter(SearchResultContainer));
