import React, { Component } from 'react';
import './carrierAssignedDropDown.scss';
import { IonRow, IonCol, IonModal } from '@ionic/react';

import { ToastMessage } from '../../../utils/ToastMessage';

class CarrierAssignedDropdown extends Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      isDropdown: false,
      asc: true,
      slectedValue: this.props.options.filter((opt: any) => {
        return opt.myLoadRouting.indexOf(this.props.page) > 0;
      })[0]
    };
  }

  onConfrim = (option: any): void => {
    const { redirect } = this.props;
    this.setState({ isDropdown: false }, () => {
      if (option.myLoadRouting === '' || option.myLoadRouting === '/app/MyLoad/LocationTracker') {
        // Functionality coming soon
        ToastMessage.show();
      } else {
        redirect(option.myLoadRouting);
      }
    });
  };

  dropdownClick = (): void => {
    this.setState({ isDropdown: true });
  };

  render() {
    const { options, loadsCount } = this.props;
    const { slectedValue, isDropdown } = this.state;
    return (
      <>
        <div>
          <div className="carrier_select">
            <div onClick={this.dropdownClick} className="search_sort_button" data-test="dropdown">
              <p className="page-title-p">{slectedValue.title}</p>
            </div>
            <i onClick={this.dropdownClick} className="down"></i>
          </div>

          <IonModal
            isOpen={isDropdown}
            cssClass="dropdown-modal"
            backdropDismiss={false}
            onDidDismiss={() => {
              this.setState({
                isDropdown: false
              });
            }}>
            <div className="modal-div" onClick={() => this.setState({ isDropdown: false })}>
              <div className="search_options_carrier">
                {options.map((option: any, key: any) => (
                  <IonRow
                    className="carrier-dropdown"
                    key={key}
                    onClick={() => {
                      this.onConfrim(option);
                    }}>
                    <IonCol size="10">{option.title}</IonCol>
                    <IonCol size="2">
                      {option.title === 'Manage Financial' ? (
                        ' '
                      ) : (
                        <div className={loadsCount[option.count] ? 'count-div circle-div2' : ''}>
                          {loadsCount[option.count] && loadsCount[option.count] > 99
                            ? 99
                            : loadsCount[option.count] || ''}
                        </div>
                      )}
                    </IonCol>
                  </IonRow>
                ))}
              </div>
            </div>
          </IonModal>
        </div>
      </>
    );
  }
}
export default CarrierAssignedDropdown;
