import React, { useState, useEffect, memo } from 'react';
import { IonLabel, IonButton, IonModal, IonRow, IonCol, IonGrid } from '@ionic/react';
import '../../../pages/Search/New/NewPage.scss';
import { NewDropDownData } from '../../../utils/mock_Data';
import { Trailer } from '../../../utils/constants';
interface DropDownProps {
    labelName: string,
    TrailerType: string,
    handleChange: any,

}
export const NewDropDown: React.FC<DropDownProps> = memo((props:any) => {
    const { labelName, TrailerType, handleChange } = props;
    const [showOptions, setShowOptions] = useState(false);
    const [clickedOption, setclickedOption] = useState({ name: TrailerType, value: TrailerType });
    const [selectedOption, setselectedOption] = useState({ name: TrailerType, value: TrailerType });
    const hideDropdownMenu = (option: any) => {
        setclickedOption(option);
    }
    const onConfrim = (option: any) => {
        setselectedOption(option);
        setShowOptions(false);
        const event = { target: { name: Trailer, value: option.value } }
        handleChange(event);
    }

    useEffect(() => {
        setclickedOption({name: TrailerType, value: TrailerType })
        setselectedOption({name: TrailerType, value: TrailerType })
    }, [props.TrailerType])

    return (
        <>
        <IonGrid class=" trailer-ion-item">
            <IonLabel mode="ios" class="trailer_type_label">{labelName}</IonLabel>
            <IonButton type="button" class="dropdown-button trailer_type_dropdown" data-test ="dropdown-click"
            onClick={() => (setShowOptions(true))}>
                {selectedOption.value} <i className="down"></i>
            </IonButton>
        </IonGrid>
        <IonModal isOpen={showOptions} cssClass="dropdown-modal"
            backdropDismiss={false}
            onDidDismiss={() => (setShowOptions(false))}
            >
            {/* <div className="modal-div" onClick={() => (setShowOptions(false))}> */}
                <div className="search_options_trailer">
                    <IonRow class="dropdown-heading"><IonCol> <p className="sortBy_text">{labelName}</p> </IonCol></IonRow>
                    <div className="options_content_trailer">
                        {NewDropDownData.map((option: any, k: any) => (
                            <IonRow class="dropdown-row" key={k} data-test="hide-dropdown" onClick={() => hideDropdownMenu(option)}>
                                <IonCol class={option.name === clickedOption.name ? 'checked' : ''}>{option.value} <i className="check"></i> </IonCol></IonRow>
                        ))}
                    </div>
                    <IonRow class="dropdown-handler">
                        <IonCol size="1"></IonCol>
                        <IonCol className="dropdown_cancel" size="5" data-test="dropdown-cancel" onClick={() => { setShowOptions(false);
                             setclickedOption(selectedOption) } }>
                            <span>Cancel</span>
                        </IonCol>
                        <IonCol className="dropdown_cancel" size="5" data-test="confirm-cancel" onClick={() => onConfrim(clickedOption)}>
                            <span>OK</span>
                        </IonCol>
                        <IonCol size="1"></IonCol>

                    </IonRow>
                </div>
                {/* </div> */}
            </IonModal>
        </>
    );
});
