import React from "react";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { isPlatform } from "@ionic/react";
import { compose } from "redux";
import { getUserDetails, getNetworkStatus } from "../../../selectors/selector";
import {
  isUserValidated,
  getValidateUserDetails
} from "../../../selectors/Manage/selector";
import { showNetworkAlert } from "../../../actions/App/action";
import {
  addUserDetails,
  validateUser,
  validateUserReset,
  transferUser
} from "../../../actions/manage/action";
import { UserDetails } from "../../../models/userPforile/userProfile.modal";
import { ToastMessage } from "../../../utils/ToastMessage";
import {
  OOPS_MESSAGE,
  MANAGE_USER_SUCCESS_MESSAGE,
  MANAGE_USER_TRANSFER_EXIST,
  MANAGE_USER_TRANSFER_MESSAGE
} from "../../../utils/constants";
import AddDriverPage from "../../../pages/Execute/AddDriver/AddDriver";
import useInjectSaga from "../../../utils/injectSaga";
import useInjectReducer from "../../../utils/injectReducer";
import reducer from "../../../reducers/manage/reducer";
import saga from "../../../saga/Manage/saga";

const key = "manage";

export const AddDriverContainer: React.FC<any> = (props: any) => {
  const {
    history: { push, location },
    match
  } = props;
  const userProfileModel = new UserDetails();
  const path = (location && location.state && location.state.loadModule) || "";
  const title = (match && match.params && match.params.id) || "";
  const backToPage = () => {
    push(`/app/${path}`);
  };

  const handleSubmit = (payload: any, restData: Function) => {
    const query = userProfileModel.addUser(payload);
    const callback = (success = false) => {
      if (success) {
        restData();
        ToastMessage.show(MANAGE_USER_SUCCESS_MESSAGE);
        backToPage();
        return true;
      }
      ToastMessage.show(OOPS_MESSAGE);
    };
    props.addUserAction({ query, callback, isCarrierAssign: true });
  };

  const handleTransfer = (
    payload: any,
    restData: Function,
    isCarrierSame = false
  ) => {
    const { name, params } = payload;
    props.resetValidateUser();
    if (isCarrierSame) {
      restData();
      ToastMessage.show(MANAGE_USER_TRANSFER_EXIST);
      backToPage();
      return true;
    }
    const query = userProfileModel.transferMember(params);
    const callback = (success = false) => {
      restData();
      if (success) {
        ToastMessage.open({
          message: MANAGE_USER_TRANSFER_MESSAGE.replace("{{NAME}}", name),
          btnTitle: "Ok",
          showUndo: true,
          callback: () => {}
        });
      } else {
        ToastMessage.show(OOPS_MESSAGE);
      }
      backToPage();
    };
    props.transferUserAction({ query, callback });
  };

  return (
    <AddDriverPage
      {...props}
      title={title}
      module={path}
      handleSubmit={handleSubmit}
      handleTransfer={handleTransfer}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus(),
  isUserValid: isUserValidated(),
  validateUserDetails: getValidateUserDetails()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  validateUserAction: (payload: any) => dispatch(validateUser(payload)),
  addUserAction: (payload: any) => dispatch(addUserDetails(payload)),
  resetValidateUser: () => dispatch(validateUserReset()),
  transferUserAction: (payload: any) => dispatch(transferUser(payload))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(AddDriverContainer);
