import React, { Component } from 'react';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { compose } from 'redux';
import MyloadPage from '../../../pages/Execute/MyloadCommonPage.page';
import {
  getCarrierLoads,
  getAssignedLoads,
  getInTransitLoad,
  getDeliveredLoad,
  getDriverList
} from '../../../selectors/carrierAssign/selector';
import useInjectSaga from '../../../utils/injectSaga';
import useInjectReducer from '../../../utils/injectReducer';
import { AssignDriver } from '../../../actions/carrierAssign/action';

import saga from '../../../saga/CarrierAssign/saga';
import reducer from '../../../reducers/carrierAssign/reducer';
import { DriverList } from '../../../models/home/CarrierAssign.model';
import { isEntitled } from '../../../utils/common';

import { makeMyLoadCardData, getCarrierLoad } from '../../../selectors/Myload/selector';
import { getUserDetails } from '../../../selectors/selector';
import { buyLoadShipmentRequest } from '../../../actions/BookLoad/action';

const key = 'carrierAssigned';
class MyloadSectionContainer extends Component<any, any> {
  state = {
    shipmentId: '',
    pageName: ''
  };

  segment: string[] = [];

  AssignDriverBtn = () => {
    if (this.props.userDetails && this.props.userDetails.carrierId) {
      const modelList: DriverList = new DriverList();
      modelList.carrierId = this.props.userDetails.carrierId;
      modelList.role = 'carrierDriver';
      this.props.AssignDriver(modelList.getRequestPayload());
    }
  };

  componentDidMount = () => {
    this.props.myLoadCard.forEach((element: any) => {
      this.segment.push(element.title);
    });
  };

  componentDidUpdate = (prevProps: any) => {
    if (
      JSON.stringify(prevProps.loadDetails) !== JSON.stringify(this.props.loadDetails) &&
      this.props.loadDetails.length > 0
    ) {
      this.props.history.push(`${this.state.pageName}/${this.state.shipmentId}`);
    }
  };

  render() {
    const pathName = this.props.history.location.pathname;
    const currentSegment = pathName.slice(pathName.lastIndexOf('/') + 1);
    let segmentName = '';
    switch (currentSegment) {
      case 'CarrierAssigned':
        segmentName = 'Carrier Assigned';
        break;
      case 'DriverAssigned':
        if (isEntitled('myLoad', 'driverLoads')) {
          segmentName = 'Loads Assigned';
        } else {
          segmentName = 'Driver Assigned';
        }
        break;
      case 'InTransit':
        segmentName = 'In-Transit';
        break;
      case 'Delivered':
        segmentName = 'Recently Delivered Loads';
        break;
      default:
        segmentName = 'Carrier Assigned';
    }
    return (
      <MyloadPage
        {...this.props}
        AssignDriver={this.AssignDriverBtn}
        driverList={
          this.props.desktopDriverList &&
          this.props.desktopDriverList.data &&
          this.props.desktopDriverList.data.getMembers
        }
        segment={this.segment}
        currentSegment={segmentName}
      />
    );
  }
}
const mapStateToProps = createStructuredSelector({
  loads: getCarrierLoads(),
  desktopDriverList: getDriverList(),
  driverAssigned: getAssignedLoads(),
  myLoadCard: makeMyLoadCardData(),
  userDetails: getUserDetails(),
  inTransitLoads: getInTransitLoad(),
  deliveredLoads: getDeliveredLoad(),
  loadDetails: getCarrierLoad()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  buyLoadShipmentRequest: (payload: any) => dispatch(buyLoadShipmentRequest(payload)),
  AssignDriver: (data: any) => dispatch(AssignDriver(data))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(MyloadSectionContainer);
export { MyloadSectionContainer };
