import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { getCarrierProfile, getCarrierProfileLoaded } from '../../../selectors/Manage/selector';
import { CarrierProfileModel } from '../../../models/manage/carrier-profile.model';
import { ToastMessage } from '../../../utils/ToastMessage';
import { OOPS_MESSAGE, EVENT, SCREEN } from '../../../utils/constants';
import { logEvent } from '../../../utils/Analytics';
import { getUserDetails, getRefreshStatus } from '../../../selectors/selector';
import { loadCarrierData } from '../../../actions/manage/action';
import CarrierProfilePage from '../../../pages/Manage/CarrierProfile/CarrierProfile.page';

export const CarrierProfileContainer = (props: any) => {
  const { loaded, userDetails, loadCarrierProfile } = props;
  const carrierProfileModel = new CarrierProfileModel();
  const getCarrierProfile = (isPullToRefresh = false) => {
    const { carrierId } = userDetails;
    const query = carrierProfileModel.getProfile(carrierId);
    const callback = () => {
      ToastMessage.show(OOPS_MESSAGE);
    };
    loadCarrierProfile({
      query,
      isPullToRefresh,
      callback
    });
  };

  useEffect(() => {
    if (!loaded) {
      getCarrierProfile();
    }
  }, [loaded]);

  useEffect(() => {
    logEvent(EVENT.SV_VIEW_CARRIER_PROFILE, {
      screen: SCREEN.CARRIER_PROFILE,
      carrierId: props.userDetails.carrierId
    });
  }, []);

  return <CarrierProfilePage {...props} backUrl="manage" getCarrierProfile={getCarrierProfile} />;
};

const mapStateToProps = createStructuredSelector({
  userDetails: getUserDetails(),
  loaded: getCarrierProfileLoaded(),
  isPullToRefresh: getRefreshStatus(),
  carrierProfile: getCarrierProfile()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  loadCarrierProfile: (payload: any) => dispatch(loadCarrierData(payload))
});

export default connect(mapStateToProps, mapDispatchToProps)(CarrierProfileContainer);
