/* Network Toaster component*/
/* Error Messge will be displayed when app goes offline. */
import React from 'react';
import "./NetworkErrorToaster.scss";
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { getNetworkStatus } from '../../selectors/selector';
import { CHECK_NETWORK_MESSAGE } from '../../utils/constants';

const NetworkErrorToaster = (props: any) => {
  return (
    <>
      {!props.isNetworkAvailable
        ? <div className="no-network-div">{CHECK_NETWORK_MESSAGE}</div>
        : null}
    </>
  )
}

const mapStateToProps = createStructuredSelector({
  isNetworkAvailable: getNetworkStatus(),
});


export default connect(mapStateToProps, null)(NetworkErrorToaster);
