import React, { Component, useState } from "react";
import {
  IonContent,
  IonPage,
  IonCard,
  IonList,
  IonRow,
  IonCol,
  IonCardHeader,
  IonCardContent,
  IonIcon,
  IonImg,
  IonLabel,
  IonGrid,
  IonText,
  isPlatform
} from "@ionic/react";
import AppHeader from "../../../components/app/Bars/Bar-header";
import "./carrierProfile.scss";
import { CarrierInsuranceCard } from "../../../components/app/carrierInsuranceCard/carrierInsuranceCard";
import { CarrierInfoAccordian } from "../../../components/app/carrierInsuranceCard/CarrierInfoAccordian";
import { manageCarrierdata, manageContactdata } from "./CarrierProfile.utils";
import PullToRefresh from "../../../components/PullToRefresh/PullToRefresh";
import { capitalize } from "lodash";
import CarrierProfileDesktop from "./CarrierProfileDesktop";
import {
  CARRIER_CERITIFCATION,
  CARRIER_QUALIFICATION_STATUS,
  CARRIER_INSURENCE,
  CARRIER_CONTACT_DETAILS,
  CARRIER_INFO,
  CARRIER_PROFILE_FOOTER
} from "../../../utils/constants";
import moment from "moment";

const CarrierProfilePage = (props: any) => {
  const {
    carrierProfile,
    history,
    backUrl,
    isPullToRefresh,
    getCarrierProfile
  } = props;
  const [showCarrier, setShowCarrier] = useState(true);
  const [showContact, setShowContact] = useState(true);
  const [showCarrierInsurance, setShowCarrierInsurance] = useState(true);
  if (isPlatform("desktop")) {
    const cprops = {
      ...props,
      showCarrier,
      setShowCarrier,
      showContact,
      setShowContact,
      showCarrierInsurance,
      setShowCarrierInsurance
    };
    return <CarrierProfileDesktop {...cprops} />;
  }

    return (
        <IonPage className="desktop-page" data-test="IonPage-Manage">
            <AppHeader title="Carrier profile" backUrl={"/app/" + backUrl} History={history} />
            <IonContent className="ion-padding custom-padding recent_container">
                <PullToRefresh status={isPullToRefresh} onPullToRefresh={getCarrierProfile} />
                {
                   carrierProfile && carrierProfile.map((profile:any, index: number) => {
                       const certifications = (profile && profile.certifications && profile.certifications.filter((certificate:any)=> {
                            return (certificate.certificationCode.toLowerCase() !== 'hazmat' || (certificate.certificationCode.toLowerCase() === 'hazmat') && new Date(moment(certificate.certificateExpireDate).format('YYYY-MM-DD')).getTime() >= new Date().getTime());
                       })) || [];
                       return (
                           <React.Fragment  key={`profile${index}`}>
                                <CarrierInfoAccordian showAccordian={showCarrier} accordianClick={() => setShowCarrier(!showCarrier)} carrierHeader={CARRIER_INFO} data={manageCarrierdata(profile)} />
                                <CarrierInfoAccordian showAccordian={showContact} accordianClick={() => setShowContact(!showContact)} carrierHeader={CARRIER_CONTACT_DETAILS} data={manageContactdata(profile)} />
                                <CarrierInsuranceCard showAccordian={showCarrierInsurance} accordianClick={() => setShowCarrierInsurance(!showCarrierInsurance)} carrierHeader={CARRIER_INSURENCE} data={profile.insurances} />
                                <IonCard class="carrier-card">
                                        <IonCardHeader class={'card-header-pad carrier-accordion-header'}>
                                            <IonLabel className="carrier-card-title">{CARRIER_CERITIFCATION}</IonLabel>
                                        </IonCardHeader>
                                        <IonCardContent class={"accordianCardContent"} >
                                            {
                                                certifications.map((certificate: any, index:number) => (
                                                    <IonGrid className="cert-grid" key={`cer${index}`}>
                                                        <IonRow className="cert-header">
                                                            <IonCol size='2'><IonImg alt="logo" src={`/assets/icon/${certificate.certificationDesc.toLowerCase()}.svg`} className="certificate-png" /></IonCol>
                                                            <IonCol size='6'>
                                                                <IonText className="cert-desc">{capitalize(certificate.certificationDesc)}</IonText>
                                                            </IonCol>
                                                            <IonCol size='4'><IonList className="HazmatDate date_approved">{( certificate && certificate.certificateExpireDate && certificate.certificationCode.toLowerCase() === 'hazmat') ? moment(certificate.certificateExpireDate).format('MM/DD/YYYY'): ''}</IonList></IonCol>
                                                        </IonRow>
                                                        <IonLabel className="inner-card-title">{CARRIER_QUALIFICATION_STATUS}</IonLabel>
                                                        <IonRow>
                                                            <IonCol size='2'><IonIcon src="assets/icon/tick-icon.svg" className="tick-icon" /></IonCol>
                                                            <IonCol size='6' className="Approved">
                                                                <IonText className="qualficiation-desc">{capitalize(certificate.qualificationStatus)}</IonText>
                                                            </IonCol>
                                                            <IonCol size='4'>
                                                                <IonText className="qualficiation-expiry"></IonText>
                                                            </IonCol>
                                                        </IonRow>
                                                    </IonGrid>
                                                ))
                                            }
                                        </IonCardContent>
                                </IonCard>
                           </React.Fragment>
                       )
                   }) 
                }
                
                <div className="contact"  dangerouslySetInnerHTML={{__html: CARRIER_PROFILE_FOOTER}}></div>
            </IonContent>
        </IonPage>
    );
}

export default CarrierProfilePage;
