import { IWatchLoadRequest, IAddWatchLoad, IRemoveWatchLoad } from "../../schemas/Home/Home.schema";
import { setObject, getObject } from "../../utils/common";
export class WatchedLoadModel {
  constructor() {}

  private __watch_state: IWatchLoadRequest = {
    userId: "",
    carrierId: "",

    page: 0,
    size: 10,
    sortColumn: "pickupDate",
    sortDirection: "asc",
  };
  get sortColumn() {
    return this.__watch_state.sortColumn;
  }
  set sortColumn(value) {
    if (value) {
      this.__watch_state.sortColumn = value;
    }
  }
  get userId() {
    return this.__watch_state.userId;
  }
  set userId(value) {
    if (value) {
      this.__watch_state.userId = value;
    }
  }
  get carrierId() {
    return this.__watch_state.carrierId;
  }
  set carrierId(value) {
    if (value) {
      this.__watch_state.carrierId = value;
    }
  }
  get sortDirection() {
    return this.__watch_state.sortDirection;
  }

  set sortDirection(value) {
    if (value) {
      this.__watch_state.sortDirection = value;
    }
  }

  get page() {
    return this.__watch_state.page;
  }
  set page(value) {
    if (value) {
      this.__watch_state.page = value;
    }
  }

  get size() {
    return this.__watch_state.size;
  }
  set size(value) {
    if (value) {
      this.__watch_state.size = value;
    }
  }

  setofflineWatchedLoads(_loads: []) {
    return setObject("Carrier-offline-Watched-Loads", _loads);
  }

  getofflineWatchedLoads() {
    return getObject("Carrier-offline-Watched-Loads");
  }
  setWeight() {
    this.validLoad();
  }

  private validLoad() {}

  addWatchLoad(data: IAddWatchLoad) {
      return {
        mutation: {
          newUserWatchedLoads: {
            __args: {
              input: data
            },
            id: true
          }
        }
      };
  }

  removeWatchLoad(data: IRemoveWatchLoad) {
    return {
      mutation: {
        deleteUserWatchedLoads: {
          __args: {
            input: data
          }
        }
      }
    };
  }

  getRequestPayload() {
    return {
      query: {
        getUserWatchedLoads: {
          __args: {
            input: this.__watch_state,
          },
          totalRecords: true,
          loadPostings: {
            loadId: true,
            transportMode: true,
            termsAndConditions: true,
            loadPostingStatus: true,
            sourceSystem: true,
            hasAppointments: true,
            isHazardous: true,
            isHighValue: true,
            isTemperatureControlled: true,
            totalDistance: true,
            distanceUOM: true,
            totalWeight: true,
            weightUOM: true,
            numberOfStops: true,
            rate: {
              binRateIdentifier: true,
              binSource: true,
              totalCharge: true,
              lineHaulCharge: {
                charge: true,
                unitCharge: true,
                quantity: true,
                chargeDescription:true,
                rateQualifier: true
              },
              fuelCharge: {
                charge: true,
                unitCharge: true,
                quantity: true,
                chargeDescription:true,  
                rateQualifier: true
              },
              accessorials:  {
                accessorialCode: true,
              accessorialDescription: true,
              accessorialType: true,
              accessorialCharge: {
                charge: true,
                unitCharge: true,
                quantity: true,
                rateQualifier: true
              }
              }
            },
            loadStop: {
              stopId: true,
              stopType: true,
              city: true,
              state: true,
              postalCode: true,
              country: true,
              appointmentFrom: true,
              appointmentTo: true,
              timeZone: true,
              deadHead: true,
              stopSequence: true,
              specialServices: {
                serviceDesc: true,
                specialServiceDesc: true,
              },
            },
          },
        },
      },
    };
  }
}
