import {
    ActionType,
    MenuState
  } from "../../schemas/Menu/Menu.Schema";
  import * as Constants from "../../utils/constants";
  import produce from "immer";
  
  export const initialMenuState: MenuState = {
    submitFeedback: []
  };
  
  export default (state = initialMenuState, action: ActionType) =>
    produce(state, draft => {
      switch (action.type) {
        case Constants.GET_SUBMIT_FEEDBACK:
          draft.submitFeedback = action.payload;
          break;
      }
    });