import React, { Component } from "react";
import {
  IonPage,
  IonContent,
  IonRow,
  IonCol,
  IonIcon,
  IonButton,
  IonToast,
  IonFooter,
  isPlatform
} from "@ionic/react";
import { connect } from "react-redux";
import { compose } from "redux";
import { createStructuredSelector } from "reselect";
import { CallNumber } from "@ionic-native/call-number";
import AppHeader from "../../../components/app/Bars/Bar-header";
import ShipmentUpdateCard from "./ShipmentCard";
import useInjectSaga from "../../../utils/injectSaga";
import useInjectReducer from "../../../utils/injectReducer";
import { LoadMapStartTrip } from "../../../components/app/LoadMap/LoadMapStartTrip";
import "./ShipmentUpdateScreen.scss";
import {
  loadAssignedrData,
  inTransitData,
  completeTripApiRequest,
  deliveredData
} from "../../../actions/carrierAssign/action";
import {
  getInTransitLoads,
  getDriverAssignedLoads,
  getConfirmStartInformation,
  getStartTripShipmentStatusMessage,
  getRecentDeliveredLoads,
} from "../../../selectors/Myload/selector";
import saga from "../../../saga/CarrierAssign/saga";
import reducer from "../../../reducers/carrierAssign/reducer";
import {
  getUserDetails,
  getNetworkStatus,
  getRefreshStatus
} from "../../../selectors/selector";
import { clearConfirmStartInformationStatusMessage, clearUpdateStopInformationResponse } from "../../../actions/myload/action";
import {
  getStartTripShipment,
  getStartTripShipmentTimeStamp
} from "../../../selectors/carrierAssign/selector";
import { IShipmentUpdateScreenState } from "../../../schemas/StartTrip/StartTrip";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import {
  COMPLETE_TRIP_CTA_TEXT,
  CALL_US_TEXT,
  CALL_US_NUMBER,
  ExecutionStatus,
  DB_GPS_ACTIVATED_SHIPMENT,
  SOUND_SRC,
  EVENT,
  SCREEN,
  KEYS,
  ENV,
  USER_TYPE,
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK
} from "../../../utils/constants";
import { CompleteTripModel } from "../../../models/home/CompleteTrip.model";
import {
  getItem,
  GPSWrapper,
  setItem,
  removeItem
} from "../../../utils/common";
import { logEvent } from "../../../utils/Analytics";
import { LoadMapSatellite } from "../LoadMap/LoadMapSatellite";
import PullToRefresh from "../../../components/PullToRefresh/PullToRefresh";
import { buyLoadShipmentRequest } from "../../../actions/BookLoad/action";
import { GetShipment } from "../../../models/home/GetShipment.model";
import { showAlert } from "../../../actions/App/action";

class ShipmentUpdateScreen extends Component<any, IShipmentUpdateScreenState> {
  currentHistory: any;

  constructor(props: any) {
    super(props);
    this.state = {
      isCompleteTrip: false,
      loadDetails: {},
      showSatelliteMap: false,
      satelliteLocation: "",
      showHybridSatelliteMap: false,
      hybridSatelliteLocation: "",
      satelliteLatLng: {
        lng: "",
        lat: ""
      },
      showDialog: false,
      loadStop: [],
      // isGPSEnabled: false,
      isConfirmStartClicked: false
      // isGPSstartPingSent: false,
    };
    this.currentHistory = this.props.history.location;
  }


  /**
   * enableGPS service based on user type and Execution Status
   * * @param {*} props data
   * @returns null.
   */
  enableGPS = async (params: any, userDetails: any) => {
    if (isPlatform("capacitor")) {
      const _shipment = (await getItem(DB_GPS_ACTIVATED_SHIPMENT)).value; // retived shipmentID from local storage
      const gpsActivatedShipmentID =
        _shipment && JSON.parse(_shipment).shipmentID;
      params.shipmentInvolvedParties.map(async (party: any, index: any) => {
        if (
          !gpsActivatedShipmentID &&
          params.executionStatus &&
          party.partyId &&
          party.partyId.toUpperCase() === userDetails.email.toUpperCase() && // Checked Assigned load for same user or not
          party.partyType.toUpperCase() === USER_TYPE.PRIMARY_DRIVER && // Checked user type is PRIMARY_DRIVER or not
          (params.executionStatus.toUpperCase() === ExecutionStatus.STARTED ||
            params.executionStatus === ExecutionStatus.IN_TRANSIT) // GPS only started for Execution Status as STARTED or IN_TRANSIT
        ) {
          const shipment = {
            userId: userDetails.email,
            carrierId: userDetails.carrierId,
            activity: "",
            carrierScac: "",
            shipmentID: params.shipmentId,
            sourceTMS: params.sourceTMS,
            loadStops: params.loadStop
          };
          // Start the GPS service
          GPSWrapper.details.startGPStracking(shipment);
          await setItem(DB_GPS_ACTIVATED_SHIPMENT, JSON.stringify(shipment)); // Store the shipmentID in local storage to check this shipment is started or not

          // Analytics implementation
          this.sendAnalyticsEvent(
            EVENT.UE_START_TRIP,
            SCREEN.SHIPMENT_DETAILS,
            userDetails.carrierId,
            params.shipmentId
          );

          // Eneble Audio notification for UNT env for start GPS
          if (KEYS.RELEASE === ENV.UNT) {
            const sound = new Audio();
            sound.src = SOUND_SRC.START_TRIP;
            sound.play();
          }
        }
      });
    }
  };

  /**
   * Stop the GPS based on Execution Status
   * * @param {*} props data
   * @returns null.
   */
  disableGPS = async (params: any) => {
    if (isPlatform("capacitor")) {
      const _shipment = (await getItem(DB_GPS_ACTIVATED_SHIPMENT)).value; // retived shipmentID from local storage
      const gpsActivatedShipmentID =
        _shipment && JSON.parse(_shipment).shipmentID;

      if (
        gpsActivatedShipmentID &&
        params.executionStatus &&
        gpsActivatedShipmentID === params.shipmentId && // Checked same shipmentID or not to Stop GPS
        (params.executionStatus.toUpperCase() === ExecutionStatus.DELIVERED ||
          params.executionStatus.toUpperCase() === ExecutionStatus.COMPLETED)
      ) {
        // GPS only Stop for Execution Status as DELIVERED or COMPLETED

        await removeItem(DB_GPS_ACTIVATED_SHIPMENT);
        GPSWrapper.details.stopGPS();
        this.setState({
          // isGPSEnabled: false,
          isConfirmStartClicked: false
          // isGPSstartPingSent: false,
        });

        if (KEYS.RELEASE === ENV.UNT) {
          // Eneble Audio notification for UNT env for Stop GPS
          const sound = new Audio();
          sound.src = SOUND_SRC.STOP_TRIP;
          sound.play();
        }
      }
    }
  };

  // Analytics implementation
  sendAnalyticsEvent = (
    event: any,
    screen: any,
    carrierId: string,
    shipmentId: string
  ) => {
    logEvent(event, {
      screen,
      carrierId,
      shipmentId
    });
  };

  /**
   * GPS tracking wii start, if driver confirms any CTA
   * * @param location
   * @returns null.
   */
  GPSEnabledEvent = async (location?: any) => {
    await this.enableGPS(this.state.loadDetails, this.props.userDetails);
    // Confirm Start Clicked event for GPS
    if (location) {
      await this.sendGPSstartPing(
        this.state.loadDetails,
        this.props.userDetails
      );
    }
  };

  completeTripClick = () => {
    if (!this.props.networkStatus) {
      this.props.showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK
      });
      return;
    }
    const { loadDetails = {} } = this.state;
    this.completeTripRequest(loadDetails.sourceTMS, loadDetails.shipmentId);
  };

  completeTripRequest(sourceSystem: string, shipmentId: string) {
    const { userDetails = {} } = this.props;

    const objModel: CompleteTripModel = new CompleteTripModel();
    this.props.shipmentCompleteTrip(
      objModel.completeTrip({
        carrierId: userDetails.carrierId,
        shipmentId,
        sourceSystem,
        userId: userDetails.userPrincipalName
      }),
      shipmentId
    );

    // Analytics implementation
    logEvent(EVENT.UE_COMPLETE_TRIP, {
      screen: SCREEN.SHIPMENT_DETAILS,
      carrierId: userDetails.carrierId,
      shipmentId
    });
  }

  callusClick = () => {
    const callNumber = CALL_US_NUMBER.OTM;
    CallNumber.callNumber(callNumber, true)
      .then(res => {})
      .catch(err => {});
  };

  private readonly getLoadStops = (): any[] => {
    let loadStop: any[] =
      (this.state.loadDetails && this.state.loadDetails.loadStop) || [];
    loadStop = loadStop.slice().sort((load1: any, load2: any) => {
      if (load1.sequenceNo && load2.sequenceNo) {
        return load1.sequenceNo - load2.sequenceNo;
      }
      if (load1.stopSequence && load2.stopSequence) {
        return load1.stopSequence - load2.stopSequence;
      }
      return 0;
      
    });

    return loadStop;
  };

  getValueFromHistoryState = (key: string) => {
    if (key) {
      return (
        this.props &&
        this.props.history &&
        this.props.history.location.state &&
        this.props.history.location.state[key]
      );
    }
    return null;
  };

  // send Geo Start Trip Location Notification to backend
  sendGPSstartPing = async (params: any, userDetails: any) => {
    if (isPlatform("capacitor")) {
      const _shipment = (await getItem(DB_GPS_ACTIVATED_SHIPMENT)).value; // retived shipmentID from local storage
      const gpsActivatedShipmentID =
        _shipment && JSON.parse(_shipment).shipmentID;
      params.shipmentInvolvedParties.map(async (party: any, index: any) => {
        if (
          gpsActivatedShipmentID &&
          gpsActivatedShipmentID === params.shipmentId && // if the GPS already started (gpsActivated ShipmentID already present in local DB)
          party.partyId &&
          party.partyId.ignoreCase === userDetails.email.ignoreCase && // Checked Assigned load for same user or not
          party.partyType.toUpperCase() === USER_TYPE.PRIMARY_DRIVER && // Checked user type is PRIMARY_DRIVER or not
          // && !this.state.isGPSstartPingSent
          // && this.state.isConfirmStartClicked
          userDetails &&
          this.props
        ) {
          const shipment = {
            userId: userDetails.email,
            carrierId: userDetails.carrierId,
            activity: "",
            carrierScac: "",
            shipmentID: params.shipmentId,
            sourceTMS: params.sourceTMS,
            loadStops: params.loadStop
          };
          GPSWrapper.details.sendGeoStartTripLocation(shipment);
        }
      });
    }
  };

  getUpdatedShipment = () => {
    const shipmentId =
      (this.state.loadDetails && this.state.loadDetails.shipmentId) || "";
    const getShipmentmodel: GetShipment = new GetShipment();
    getShipmentmodel.shipmentIds = shipmentId;
    this.props.buyLoadShipmentRequest({
      query: getShipmentmodel.getRequestShipmentPayload(),
      shipmentId,
      module: this.getValueFromHistoryState("module"),
      isPullToRefresh: true
    });
    this.props.clearUpdateStopInfo();
  };

  render() {
    const currentLocation = this.getValueFromHistoryState(
      "isCurrentLocationOn"
    );
    const module = this.getValueFromHistoryState("module");
    const starTripModel = new StartTripModel();

    return (
      <IonPage>
        <LoadMapSatellite
          locationName={this.state.hybridSatelliteLocation}
          position={{
            lat: this.state.satelliteLatLng.lat
              ? parseFloat(this.state.satelliteLatLng.lat)
              : "",
            lng: this.state.satelliteLatLng.lng
              ? parseFloat(this.state.satelliteLatLng.lng)
              : ""
          }}
          shipmentId={
            this.state.loadDetails && this.state.loadDetails.shipmentId
          }
          showSatelliteMap={this.state.showHybridSatelliteMap}
          onDismissSatelliteMap={() => {
            this.setState({ showHybridSatelliteMap: false });
          }}
        />
        <IonToast
          isOpen={this.getConfirmStartStatus()}
          message={this.getConfirmStartMessage()}
          duration={5000}
          color="dark"
          onDidDismiss={() => {
            this.props.clearConfirmReasponse();
          }}
        />
        <LoadMapStartTrip
          showSatelliteMap={this.state.showSatelliteMap}
          locationName={this.state.satelliteLocation}
          loadStop={
            (this.state.loadDetails && this.state.loadDetails.loadStop) || []
          }
          onDismissSatelliteMap={() => {
            this.setState({ showSatelliteMap: false });
          }}
          shipmentId={
            (this.state.loadDetails && this.state.loadDetails.shipmentId) || ""
          }
        />

        <AppHeader
          title={
            (this.state.loadDetails && this.state.loadDetails.shipmentId) || ""
          }
          backUrl={`/app/${module}`}
          isMap
          History={this.props.history}
          viewMap={() => {
            this.setState({ showSatelliteMap: true });
          }}
        />
        <IonContent className="shipment-content">
          <IonContent class="shipment-page-content">
            <PullToRefresh
              onPullToRefresh={this.getUpdatedShipment}
              status={this.props.isInPullToRefresh}
            />
            <ShipmentUpdateCard
              timestamp={this.props.timestamp}
              currentLocation={currentLocation}
              locationValue
              completeTripClick={this.completeTripClick}
              GPSEnabledEvent={this.GPSEnabledEvent}
              completeTripRequest={this.completeTripRequest}
              shipmentloadStop={this.getLoadStops()}
              networkStatus={this.props.networkStatus}
              shipmentId={
                (this.state.loadDetails && this.state.loadDetails.shipmentId) ||
                ""
              }
              carrierId={this.props.userDetails.carrierId}
              loadDetails={this.state.loadDetails}
              moduleName={this.props.moduleName}
              refreshShipmentsLoads={this.refreshShipmentsLoads}
              refreshShipmentInDriverLoads={this.refreshDriverList}
              openSatelliteMap={(
                locationName: string,
                latLng: {
                  lng: string;
                  lat: string;
                }
              ) => {
                this.setState({
                  hybridSatelliteLocation: locationName,
                  showHybridSatelliteMap: true,
                  satelliteLatLng: {
                    lng: latLng.lng,
                    lat: latLng.lat
                  }
                });
              }}
            />
            <IonRow class="btn-call-chat">
              <IonCol>
                <IonButton class="load-btn" onClick={this.callusClick}>
                  <IonIcon src="assets/icon/Phone.svg" /> {CALL_US_TEXT}
                </IonButton>
              </IonCol>
              {/* <IonCol>
                <IonButton class="load-btn">
                  <IonIcon src="assets/icon/chat.svg" /> {CHAT_TEXT}
                </IonButton>
              </IonCol> */}
            </IonRow>

            {/* <IonGrid class="document-grid">
            <IonButton class="add-documnet-btn-mobile">
              <IonIcon src="assets/icon/plus.svg" /> Add Document
            </IonButton>{" "}
          </IonGrid>
          <div className="document-shipment">
            <DocumentCard />
          </div> */}
          </IonContent>
          <IonFooter class="loadPageFooter">
            {(starTripModel.isAllStopCompleted(this.getLoadDetails()) &&
              !starTripModel.isTripCompleted(this.getLoadDetails()) && (
              <IonButton
                expand="full"
                class="complete-load-btn"
                onClick={this.completeTripClick}
              >
                <img
                  src="/assets/icon/complete-trip.svg"
                  className="complete-icon"
                  alt=""
                />
                {COMPLETE_TRIP_CTA_TEXT}
              </IonButton>
            )) ||
              null}
          </IonFooter>
        </IonContent>
      </IonPage>
    );
  }

  async componentDidMount() {
    this.getValueFromHistoryState("withGPS");
    const loadDetails = this.getLoadDetails();
    this.setState({ loadDetails });
  }

  async componentDidUpdate(
    prevProps: any,
    prevState: IShipmentUpdateScreenState
  ) {
    let loadDetails = this.getLoadDetails();
    let isForceUpdate = false;
    if (
      JSON.stringify(prevProps.confirmStartInfo) !==
      JSON.stringify(this.props.confirmStartInfo)
    ) {
      loadDetails = this.props.confirmStartInfo;
      isForceUpdate = true;
    }
    if (
      JSON.stringify(loadDetails) !== JSON.stringify(this.state.loadDetails) ||
      isForceUpdate
    ) {
      this.setState({ loadDetails });
    }

    if (isPlatform("capacitor")) {
      const _shipment = (await getItem(DB_GPS_ACTIVATED_SHIPMENT)).value; // retived shipmentID from local storage
      const gpsActivatedShipmentID = _shipment && JSON.parse(_shipment).shipmentID;
      if (!gpsActivatedShipmentID || (gpsActivatedShipmentID && gpsActivatedShipmentID === this.state.loadDetails.shipmentId)) {
        // If no GPS tracking or same shipment which is already started
        // Sending the refreshed loadDetails
        GPSWrapper.details.setLoadDetailsTrackedShipment(
          this.state.loadDetails.loadStop
        );
      }
      // GPS tracking will be stop, once  ExecutionStatus is DELIVERED
      await this.disableGPS(loadDetails);
    }
  }

  /*
   * refreshing the driver list
   * @shipment -vload details.
   * @returns - void.
   */
  private readonly refreshDriverList = (shipment: any) => {
    const shipmentId: string = shipment && shipment.shipmentId;
    if (!shipmentId) {
      return;
    }

    let driverLoad: any[] = this.props.driverLoads.filter(
      (item: any) => item.shipmentId !== shipmentId
    );

    driverLoad = driverLoad.concat({ ...shipment });
    this.props.refreshDriverLoads(driverLoad);
  };

  private readonly getLoadDetails = (): any => {
    return this.props.shipmentToStartTrip || {};
  };

  private readonly getConfirmStartStatus = () => {
    const response = this.props.startTripShipmentStatusMessage || {};
    return response.status === false;
  };

  private readonly getConfirmStartMessage = () => {
    const response = this.props.startTripShipmentStatusMessage || {};
    return response.message;
  };

  private readonly refreshShipmentsLoads = (shipment: any) => {
    if (shipment.executionStatus === ExecutionStatus.IN_TRANSIT) {
      this.removeLoadsFromDriverToIntransit(shipment);
    } else if (shipment.executionStatus === ExecutionStatus.DELIVERED) {
      this.removeLoadsFromIntransitToDelivered(shipment);
    }
  };

  private readonly removeLoadsFromDriverToIntransit = (shipment: any) => {
    const shipmentId: string = shipment && shipment.shipmentId;
    if (!shipmentId) {
      return;
    }

    const driverLoad: any[] = this.props.driverLoads.filter(
      (item: any) => item.shipmentId !== shipmentId
    );

    const isExists =
      this.props.driverLoads.filter(
        (item: any) => item.shipmentId === shipmentId
      ).length > 0;

    if (isExists) {
      let inTransitLoads: any[] = this.props.inTransitLoads || [];
      if (shipment) {
        inTransitLoads = inTransitLoads.concat({ ...shipment });
      }

      this.props.refreshDriverLoads(driverLoad);
      this.props.refreshInTransitLoad(inTransitLoads);
    }
  };

  private readonly removeLoadsFromIntransitToDelivered = (shipment: any) => {
    const shipmentId: string = shipment && shipment.shipmentId;
    if (!shipmentId) {
      return;
    }

    const inTransitLoads: any[] = this.props.inTransitLoads.filter(
      (item: any) => item.shipmentId !== shipmentId
    );

    const isExists =
      this.props.inTransitLoads.filter(
        (item: any) => item.shipmentId === shipmentId
      ).length > 0;

    if (isExists) {
      let recentDelivered: any[] = this.props.recentDelivered || [];
      if (shipment) {
        recentDelivered = recentDelivered.concat({ ...shipment });
      }

      this.props.refereshRecentDelivered(recentDelivered);
      this.props.refreshInTransitLoad(inTransitLoads);
    }
  };
}

const mapStateToProps = createStructuredSelector({
  driverLoads: getDriverAssignedLoads(),
  inTransitLoads: getInTransitLoads(),
  recentDelivered: getRecentDeliveredLoads(),
  userDetails: getUserDetails(),
  confirmStartInfo: getConfirmStartInformation(),
  networkStatus: getNetworkStatus(),
  shipmentToStartTrip: getStartTripShipment(),
  timestamp: getStartTripShipmentTimeStamp(),
  startTripShipmentStatusMessage: getStartTripShipmentStatusMessage(),
  isInPullToRefresh: getRefreshStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  refreshDriverLoads: (data: any[]) => {
    dispatch(loadAssignedrData(data, data.length));
  },
  refreshInTransitLoad: (data: any[]) => {
    dispatch(inTransitData(data, data.length));
  },
  refereshRecentDelivered: (data: any[]) => {
    dispatch(deliveredData(data, data.length));
  },
  clearConfirmReasponse: () => {
    dispatch(clearConfirmStartInformationStatusMessage());
  },
  shipmentCompleteTrip: (data: any, shipmentId: string) => {
    dispatch(completeTripApiRequest(data, shipmentId));
  },
  showAlert: (data: any) => dispatch(showAlert(data)),
  buyLoadShipmentRequest: (payload: any) =>
    dispatch(buyLoadShipmentRequest(payload)),
  clearUpdateStopInfo: () => dispatch(clearUpdateStopInformationResponse())
});

const key = "carrierAssigned";

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose<any>(
  withSaga,
  withReducer,
  withConnect
)(ShipmentUpdateScreen);
export { ShipmentUpdateScreen };
