import React, { useState, useEffect } from 'react';
import { IonCard, IonCardHeader, IonCardContent, IonIcon } from '@ionic/react';
import { ToastMessage } from '../../../utils/ToastMessage';
import { logEvent } from '../../../utils/Analytics';
import { TERM_AND_CONDITION_1, TERM_AND_CONDITION_2 } from '../../../utils/constants';
const TermsAndCondition = (props: any) => {
    const [showTerms,setshowTerms] = useState(false);

    useEffect(() => {
        props.setTermsAndConState();
      }, []);

    return (
        <IonCard class="ion-card" >
            <IonCardHeader class={`accordion-header ${showTerms ? '' : 'changeColor'}`} onClick={() => {
                logEvent('ue_load_terms_view', {page: 'Load Details'});
                 setshowTerms(!showTerms)
            }}>
                <span>Terms <span className="tm-class">&amp;</span> Conditions </span>
                {
                    showTerms ?
                        <IonIcon className="loadDetails-plusIcon" src="assets/icon/minus-icon.svg" />
                        :
                        <IonIcon className="loadDetails-plusIcon" src="assets/icon/plus.svg" />
                }
            </IonCardHeader>
            <IonCardContent class={`term-class ${showTerms ? 'fadein' : 'fadeout'}`}>
                <p className="terms_conditions_p">{TERM_AND_CONDITION_1}</p>
                <p className="terms_conditions_p">{TERM_AND_CONDITION_2}</p>
            </IonCardContent>
        </IonCard>
    )
}
export default TermsAndCondition;
