import React, { useState, useEffect } from 'react';
import {
  IonModal,
  IonCardContent,
  IonCardHeader,
  IonCard,
  IonRow,
  IonCol,
  IonButton
} from '@ionic/react';
import * as Constants from '../../../utils/constants';
import './BookLoadModal.scss';
import { BOOKLOAD_THANK_YOU_MESSAGE } from '../../../utils/constants';

export const BookLoadConfirmModal = (props: any) => {
  const [confirmModal, setConfirmModal] = useState(false);
  const confirmModalclose = (): void => {
    setConfirmModal(false);
  };
  const confirmBookLoadOk = (): void => {
    setConfirmModal(false);
    props.confirmBookLoad();
  };
  useEffect(() => {
    setConfirmModal(true);
  }, [props.confirmModal]);

  return (
    <IonModal
      isOpen={confirmModal}
      cssClass="dropdown-modal"
      backdropDismiss={false}
      onDidDismiss={() => confirmModalclose()}>
      <IonCard className="bookLoad-thankyou">
        <IonCardHeader className="start-trip-cardContent">
          {props.bookLoadResponse.status === Constants.BOOKLOAD_STATUS_OK
            ? Constants.BOOKLOAD_THANKYOU_HEADER
            : props.bookLoadResponse.status === Constants.BOOKLOAD_STATUS_REJECTED
            ? Constants.BOOKLOAD_REQUEST_REJECTED_HEADER
            : props.bookLoadResponse.status === Constants.BOOKLOAD_ONHOLD
            ? Constants.BOOKLOAD_ON_HOLD
            : props.bookLoadResponse.status === Constants.BOOKLOAD_CANCELLED
            ? Constants.BOOKLOAD_CANCELLED_MESSAGE
            : null}
        </IonCardHeader>
        <IonCardContent>
          <p className="bookload-thanku-p">
            {props.bookLoadResponse.status === Constants.BOOKLOAD_STATUS_OK
              ? BOOKLOAD_THANK_YOU_MESSAGE.replace('{{LOAD_ID}}', props.loadId)
              : props.bookLoadResponse.status === Constants.BOOKLOAD_STATUS_REJECTED
              ? props.bookLoadResponse.message
              : props.bookLoadResponse.status === Constants.BOOKLOAD_ONHOLD
              ? props.bookLoadResponse.message
              : props.bookLoadResponse.status === Constants.BOOKLOAD_CANCELLED
              ? props.bookLoadResponse.message
              : null}
          </p>
          <IonRow className="book-load-thankyou-row" data-test-id="buyload-validation">
            <IonCol size="3" />
            <IonCol className="bookload-OK" size="6">
              <IonButton class="load-btn" onClick={confirmBookLoadOk} data-test="confirm-bookload">
                Ok
              </IonButton>
            </IonCol>
          </IonRow>
        </IonCardContent>
      </IonCard>
    </IonModal>
  );
};

export const GetShipmentModal = (props: any) => {
  const [isGetShipmentModal, setisGetShipmentModal] = useState(props.isGetShipmentModal);
  const getShipmentOk = () => {
    setisGetShipmentModal(false);
    props.declineBookLoad();
  };
  useEffect(() => {
    setisGetShipmentModal(props.isGetShipmentModal);
  }, [props.isGetShipmentModal]);
  return (
    <IonModal
      isOpen={isGetShipmentModal}
      cssClass="dropdown-modal"
      backdropDismiss={false}
      onDidDismiss={() => props.closeBookLoadModal()}>
      <IonCard className="bookLoad-thankyou">
        <IonCardHeader className="start-trip-cardContent">
          {Constants.BOOKLOAD_THANKYOU_HEADER}
        </IonCardHeader>
        <IonCardContent>
          <p className="bookload-thanku-p">{Constants.BOOKLOAD_THANK_YOU_ERROR_MESSAGE}</p>
          <IonRow className="book-load-thankyou-row">
            <IonCol size="3" />
            <IonCol className="bookload-OK" size="6">
              <IonButton class="load-btn" onClick={getShipmentOk} data-test="getShipment">
                Ok
              </IonButton>
            </IonCol>
          </IonRow>
        </IonCardContent>
      </IonCard>
    </IonModal>
  );
};
