import { IRecommendedLoadRequest } from "../../schemas/RecommendedLoads/RecommendedLoads.schema";
import { setObject, getObject } from "../../utils/common";

export class RecommendedModel {
  constructor() {}

  private __recommended_state: IRecommendedLoadRequest = {
    carrierId: "",
    carrierScac: "",
    carrierTier: "",
    page: 0,
    size: 5,
    sortColumn:"pickupDate",
    sortDirection:'asc'

  };
  get carrierId() {
    return this.__recommended_state.carrierId;
  }
  set carrierId(value) {
    if (value) {
      this.__recommended_state.carrierId = value;
    }
  }
  get carrierScac() {
    return this.__recommended_state.carrierScac;
  }

  set carrierScac(value) {
    if (value) {
      this.__recommended_state.carrierScac = value;
    }
  }
  get carrierTier() {
    return this.__recommended_state.carrierTier;
  }

  set carrierTier(value) {
    if (value) {
      this.__recommended_state.carrierTier = value;
    }
  }

  get page() {
    return this.__recommended_state.page;
  }
  set page(value) {
    if (value) {
      this.__recommended_state.page = value;
    }
  }

  get size() {
    return this.__recommended_state.size;
  }
  set size(value) {
    if (value) {
      this.__recommended_state.size = value;
    }
  }
  get sortColumn() {
    return this.__recommended_state.sortColumn;
  }
  set sortColumn(value) {
    if (value) {
      this.__recommended_state.sortColumn = value;
    }
  }
  get sortDirection() {
    return this.__recommended_state.sortDirection;
  }
  set sortDirection(value) {
    if (value) {
      this.__recommended_state.sortDirection = value;
    }
  }


  persistLoads(_loads: []) {
    return setObject('Carrier-rcmd-Loads', _loads);
}

getLoads() {
    return getObject('Carrier-rcmd-Loads');
}
getRequestPayload() {
    return {
      query: {
        recommendedLoads: {
          __args: {
            input: this.__recommended_state,
          },
          totalRecords: true,
          loadPostings :{
            loadId: true,
            loadPostingStatus: true,
            sourceSystem : true,
            hasAppointments: true,
            isHazardous: true,
            isHighValue: true,
            isTemperatureControlled: true,
            totalDistance: true,
            distanceUOM: true,
            totalWeight: true,
            weightUOM: true,
            numberOfStops: true,
            transportMode: true,
            termsAndConditions: true,
            rate :{
              binRateIdentifier: true,
              binSource: true,
              totalCharge: true,
              lineHaulCharge :{
                charge: true,
                unitCharge: true,
                quantity: true,
                rateQualifier: true,
            chargeDescription: true,
              },
              fuelCharge :{
                charge: true,
                unitCharge: true,
                quantity: true,
                rateQualifier: true,
            chargeDescription: true,
              },
              accessorials :{
                accessorialCode: true,
                accessorialDescription: true,
                accessorialType: true,
                accessorialCharge :{
                  charge: true,
                  unitCharge: true,
                  quantity: true,
                  rateQualifier: true,
            chargeDescription: true,
                }
              }
            },
            loadStop :{
              stopSequence: true,
              stopType: true,
              activityType: true,
              city: true,
              state: true,
              postalCode: true,
              country: true,
              stopId: true,
              appointmentFrom: true,
              appointmentTo: true,
              timeZone: true,
              deadHead: true,
              specialServices:{

                specialServiceDesc: true,
              }      
            }
          }
        },
      },
    };
  }
}
