import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { isPlatform } from '@ionic/react';
import ManagePage from '../../pages/Manage/Manage.page';
import useInjectSaga from '../../utils/injectSaga';
import useInjectReducer from '../../utils/injectReducer';
import {
  makeManageCardData,
  isTransferRequested,
  getTransferRequestDetails
} from '../../selectors/Manage/selector';
import reducer from '../../reducers/manage/reducer';
import saga from '../../saga/Manage/saga';
import {
  ManageCard,
  profileImageSet,
  transferRequestReset,
  confirmTransferRequest,
  transferRequest
} from '../../actions/manage/action';
import { showPermissionAlert, logout } from '../../actions/Login/action';
import { getLoading, getUserDetails } from '../../selectors/selector';
import { UserDetails } from '../../models/userPforile/userProfile.modal';
import { ToastMessage } from '../../utils/ToastMessage';
import { OOPS_MESSAGE } from '../../utils/constants';
import { GPSWrapper, clear } from '../../utils/common';
import { useAuthService } from '../../services/Auth';

const key = 'manage';
const ManageContainer = (props: any) => {
  const { history } = props;
  const authService = useAuthService();
  useEffect(() => {
    if (props.manageCard.length === 0) {
      props.manageData();
    }
  }, []);

  useEffect(() => {
    const transfer =
      (history && history.location && history.location.state && history.location.state.transfer) ||
      false;
    if (transfer) {
      props.transferRequest({ transfer });
    }
  }, [history]);

  async function azureLogout() {
    if (isPlatform('capacitor')) {
      GPSWrapper.details.stopGPS();
    }
    await clear();
    await authService.logout();
    await props.logout();
  }

  const handleTransfer = (payload: any) => {
    const userProfileModel = new UserDetails();
    const query = userProfileModel.confirmTransfer(payload);
    const callback = async (status = false) => {
      if (!status) {
        ToastMessage.show(OOPS_MESSAGE);
      }
      if (status && payload.confirm) {
        await azureLogout();
        return true;
      }
      props.transferRequestReset();
    };
    props.confirmTransfer({ query, callback });
  };

  return <ManagePage {...props} handleTransfer={handleTransfer} />;
};

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  manageCard: makeManageCardData(),
  userDetails: getUserDetails(),
  isTransferRequest: isTransferRequested(),
  transferDetails: getTransferRequestDetails()
});

const mapDispatchToProps = (dispatch: Function) => ({
  manageData: () => dispatch(ManageCard()),
  transferRequest: (payload: any) => dispatch(transferRequest(payload)),
  showPermissionAlert: (data: any) => dispatch(showPermissionAlert(data)),
  profileImg: (data: any) => dispatch(profileImageSet(data)),
  transferRequestReset: () => dispatch(transferRequestReset()),
  confirmTransfer: (payload: any) => dispatch(confirmTransferRequest(payload)),
  logout: () => dispatch(logout())
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(ManageContainer);
