import { IonGrid, IonInput, IonRow } from '@ionic/react';
import React, { useState, useEffect, useRef, MutableRefObject } from 'react';
import { BOBTAIL, dropDownOptions, RequiredField } from '../../../utils/constants';

export interface IComboxProps {
  value: string;
  onChange: (value: string) => void;
  showError: boolean;
  disabled: boolean;
  onFocus: () => void;
}
export const ComboBox: React.FunctionComponent<IComboxProps> = (props: IComboxProps) => {
  const [showDropDown, setShowDropDown] = useState<boolean>(false);
  const [TValue, setTValue] = useState<string>("");
  const textPattern = /^[a-zA-Z0-9, ]*$/;
  const [dropDownValues, setDropDownValues] = useState(dropDownOptions);
  const refVariable = useRef<HTMLIonInputElement>() as MutableRefObject<HTMLIonInputElement>;

  useEffect(() => {
    setTValue(props.value);
  }, [props.value]);

  const handleValueChange = (event: any) => {
    const isValueExists = event && event.target && event.target.value;
    event.target.value = (isValueExists && event.target.value.toUpperCase()) || '';
    const preValue = (TValue && TValue.trim()) || '';
    /* Restrict user from entering special charecter */
    if (!textPattern.test(event.target.value)) {
      event.target.value =
        (isValueExists && event.target.value.replace(/[^a-zA-Z0-9 ]*/g, '')) || '';
    }
    const bobtailPattern = /^BOBTAIL./;
    /* Restrict the user from entering values after BOBTAIL is entered */
    if (
      isValueExists &&
      event.target.value.indexOf(BOBTAIL) !== -1 &&
      bobtailPattern.test(event.target.value)
    ) {
      event.target.value = BOBTAIL;
    } else {
      /**
       * User should able to enter only 6 charecter with SNLU or TA.
       */
      if (
        isValueExists &&
        event.target.value.indexOf('SNLU') !== -1 &&
        event.target.value.length > 10
      ) {
        event.target.value = preValue;
      } else if (
        isValueExists &&
        event.target.value.indexOf('TA') !== -1 &&
        event.target.value.length > 8
      ) {
        event.target.value = preValue;
      } else if (isValueExists && event.target.value.length > 10) {
        event.target.value = preValue;
      }
    }
    setTValue(event.target.value);
    if (TValue !== event.target.value) {
      const newValue = (isValueExists && event.target.value.trim()) || '';
      if (preValue !== newValue) {
        const newOptions: any = dropDownOptions.filter((option: any) => {
          return option.indexOf(event.target.value) !== -1;
        });
        setShowDropDown(!!newOptions.length);
        setDropDownValues(newOptions);
      }
    }
    if (TValue === BOBTAIL) {
      setShowDropDown(false);
    }
    if(isValueExists && (event.target.value === "SNLU" || event.target.value === "TA")){
      refVariable.current.setFocus();
    }
    props.onChange((isValueExists && event.target.value) || '');
  };
  return (
    <>
      <IonInput
        class="tariler-feild"
        value={TValue}
        onClick={() => {
          if (!dropDownValues.length || TValue) {
            setShowDropDown(false);
          } else {
            setShowDropDown(true);
          }
        }}
        onIonChange={event => handleValueChange(event)}
        disabled={props.disabled}
        onIonBlur={() => setShowDropDown(false)}
        onIonFocus={props.onFocus}
        ref={refVariable}
      />
      {showDropDown && !props.disabled && (
        <IonGrid class="option-grid">
          {dropDownValues &&
            dropDownValues.map((value: string, index: any) => (
              <IonRow
                onMouseDown={() => {
                  setTValue(value);
                  setShowDropDown(false);
                }}
                key={index}>
                {value}
              </IonRow>
            ))}
        </IonGrid>
      )}
      {props.showError && (
        <div className="pickupdate-error error-message" style={{ textAlign: 'left' }}>
          <p>{RequiredField}</p>
        </div>
      )}
    </>
  );
};
