import React from 'react';
import { IonContent, IonList, IonLabel, IonGrid, IonRow, IonCol, IonCard, IonCardHeader, IonCardContent, IonImg, IonText, IonIcon } from '@ionic/react';
import { CarrierInfoAccordian } from '../../../components/app/carrierInsuranceCard/CarrierInfoAccordian';
import { manageCarrierdata, manageContactdata } from './CarrierProfile.utils';
import { CarrierInsuranceCard } from '../../../components/app/carrierInsuranceCard/carrierInsuranceCard';
import { capitalize } from 'lodash';
import { CARRIER_PROFILE_INFO, CARRIER_INSURENCE, CARRIER_CONTACT_DETAILS, CARRIER_INFO, CARRIER_CERITIFCATION, CARRIER_QUALIFICATION_STATUS, CARRIER_PROFILE_FOOTER } from '../../../utils/constants';
import moment from 'moment';

const CarrierProfileDesktop = (props:any) => {
    const { carrierProfile} = props;
    return (
         <IonContent className="ion-padding custom-padding carrier-profile-desktop">
            {
            carrierProfile && carrierProfile.map((profile:any) => {
                const certifications = (profile && profile.certifications && profile.certifications.filter((certificate:any)=> {
                    return (certificate.certificationCode.toLowerCase() !== 'hazmat' || (certificate.certificationCode.toLowerCase() === 'hazmat') && new Date(certificate.certificateExpireDate) >= new Date(moment().format('YYYY-MM-DD HH:MM')));
               })) || [];
                return (
                    <>
                        <IonText className="carrier-text">{CARRIER_PROFILE_INFO}</IonText>
                        <CarrierInfoAccordian showAccordian={!props.showCarrier} accordianClick={() => props.setShowCarrier(!props.showCarrier)} carrierHeader={CARRIER_INFO} data={manageCarrierdata(profile)} />
                        <CarrierInfoAccordian showAccordian={!props.showContact} accordianClick={() => props.setShowContact(!props.showContact)} carrierHeader={CARRIER_CONTACT_DETAILS} data={manageContactdata(profile)} />
                        <CarrierInsuranceCard showAccordian={!props.showCarrierInsurance} accordianClick={() => props.setShowCarrierInsurance(!props.showCarrierInsurance)} carrierHeader={CARRIER_INSURENCE} data={profile.insurances} />
                        <IonGrid>
                            <IonRow>
                                <IonCol size="6">
                                    <IonLabel className="carrier-card-title">{CARRIER_CERITIFCATION}</IonLabel>
                                </IonCol>
                                <IonCol size="6">
                                    <IonLabel className="carrier-card-title">{CARRIER_QUALIFICATION_STATUS}</IonLabel>
                                </IonCol>
                            </IonRow>
                            {
                                certifications.map((certificate: any) => (
                                    <IonRow>
                                        <IonCol size="6">
                                            <IonGrid className="cert-grid">
                                                <IonRow className="cert-header">
                                                    <IonCol size='1'><IonImg alt="logo" src={`/assets/icon/${certificate.certificationDesc.toLowerCase()}.svg`} className="certificate-png" /></IonCol>
                                                    <IonCol size='6'>
                                                        <IonText className="cert-desc">{capitalize(certificate.certificationDesc)}</IonText>
                                                    </IonCol>
                                                    <IonCol size='5'><IonList className="HazmatDate date_approved"></IonList></IonCol>
                                                </IonRow>
                                            </IonGrid>
                                        </IonCol>
                                        <IonCol size="6">
                                            <IonGrid className="cert-grid">
                                                <IonRow>
                                                    <IonCol size='1'><IonIcon src="assets/icon/tick-icon.svg" className="tick-icon" /></IonCol>
                                                    <IonCol size='6' className="Approved">
                                                        <IonText className="qualficiation-desc">{capitalize(certificate.qualificationStatus)}</IonText>
                                                    </IonCol>
                                                    <IonCol size='5'>
                                                        <IonText className="qualficiation-expiry">{( certificate && certificate.certificateExpireDate && certificate.certificationCode.toLowerCase() === 'hazmat') ? moment(certificate.certificateExpireDate).format('MM/DD/YYYY'): ''}</IonText>
                                                    </IonCol>
                                                </IonRow>
                                            </IonGrid>
                                        </IonCol>
                                    </IonRow>
                                ))
                            }
                        </IonGrid>
                    </>
                );
            }) 
            }
            <div className="contact"  dangerouslySetInnerHTML={{__html: CARRIER_PROFILE_FOOTER}}></div>
        </IonContent>
    )
}

export default CarrierProfileDesktop;