import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { ManageUserProfileFormPage } from '../../../pages/Manage/ManageUser';
import { getUserDetails, getNetworkStatus } from '../../../selectors/selector';
import { getSelectedManageUser } from '../../../selectors/Manage/selector';
import { showNetworkAlert } from '../../../actions/App/action';
import { editUserDetails, removeUserDetails } from '../../../actions/manage/action';
import { handleDelink, handleUpdateSubmit } from './ManageUserUtil';

export const ManageUserProfileEditContainer: React.FC<any> = (props: any) => {
  return (
    <ManageUserProfileFormPage
      {...props}
      handleSubmit={handleUpdateSubmit(props, true)}
      handleDelink={handleDelink({...props})}
      module="manage/ManageUser/Profile"
    />
  );
};

const mapStateToProps = createStructuredSelector({
  userDetails: getUserDetails(),
  selectedUser: getSelectedManageUser(),
  isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  updateUserAction: (payload: any) => dispatch(editUserDetails(payload)),
  delinkUserAction: (payload: any) => dispatch(removeUserDetails(payload))
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageUserProfileEditContainer);
