import React, { useState, useEffect } from 'react';
import {
  IonButton,
  IonRow,
  IonCol,
  isPlatform,
  IonModal,
  IonCard,
  IonCardHeader,
  IonCardContent,
  IonCheckbox,
  IonLabel,
  IonItem
} from '@ionic/react';
import { useAuthService } from '../../services/Auth';
import '../../pages/Login';
import { useNetwork } from '../../utils/useNetwork';
import { LOGIN, EVENT, SCREEN, NA, KEYS, ENV, ENABLE_R2_FEATURE } from '../../utils/constants';
import { getApplicationInfoBeforeLogin, logEvent } from '../../utils/Analytics';
import { R2Configuration } from '../../utils/common';

const LoginContainer: React.FC = () => {
  const { getNetworkStatus, networkStatus } = useNetwork();
  const [openModal, getOpenModal] = useState(false);
  const [R2FeatureEnable, setR2FeatureEnable] = useState(false);
  const [showR2ConfigurationModal, setShowR2ConfigurationModal] = useState(false);
  const authService = useAuthService();

  useEffect(() => {
    // Analytics implementation
    getApplicationInfoBeforeLogin().then(res => {
      logEvent(EVENT.SV_GET_STARTED, {
        screen: SCREEN.GET_STARTED,
        channel: res.channel,
        os: res.os,
        device_name: res.device_name,
        model: res.model,
        sessionId: NA,
        userId: NA,
        carrierId: NA
      });
    });

    if (KEYS.RELEASE === ENV.PROD) {
      R2Configuration.details.setR2FeatureEnable(true);
      setR2FeatureEnable(true);
    } else {
      R2Configuration.details.setR2FeatureEnable(false);
      setR2FeatureEnable(false);
    }
  }, []);

  const tryAction = async (fn: () => Promise<any>, defaultError: string) => {
    getNetworkStatus();
    logEvent(EVENT.UE_SIGN_IN, { screen: SCREEN.GET_STARTED, sessionId: NA });
    if (networkStatus.connected === false) {
      getOpenModal(true);
    }
    try {
      await fn();
    } catch (err) {
      console.error(err);
    }
  };
  const onClickR2Configuration = (): void => {
    if (KEYS.RELEASE !== ENV.PROD) {
      setShowR2ConfigurationModal(true);
    }
  };

  const setR2ConfigurationFlag = (): void => {
    setShowR2ConfigurationModal(false);
    R2Configuration.details.setR2FeatureEnable(R2FeatureEnable);
  };

  return (
    <>
      <IonModal isOpen={showR2ConfigurationModal} cssClass="dropdown-modal">
        <IonCard className="bookLoad-thankyou">
          <IonCardHeader className="start-trip-cardContent">
            {ENABLE_R2_FEATURE.CONFIGURATION_TXT}
          </IonCardHeader>
          <IonCardContent>
            <IonItem lines="none" className="R2ConfigurationModal-item">
              <IonCheckbox
                checked={R2FeatureEnable}
                onIonChange={e => setR2FeatureEnable(e.detail.checked)}
              />
              <IonLabel>{ENABLE_R2_FEATURE.ENABLE_R2_FEATURE_TXT}</IonLabel>
            </IonItem>
            <IonRow className="book-load-thankyou-row">
              <IonCol size="3" />
              <IonCol className="bookload-OK" size="6">
                <IonButton class="load-btn" onClick={() => setR2ConfigurationFlag()}>
                  {ENABLE_R2_FEATURE.OKAY}
                </IonButton>
              </IonCol>
            </IonRow>
          </IonCardContent>
        </IonCard>
      </IonModal>

      {openModal ? (
        <div>
          <div className="networkError_div"></div>
          <div className="popup">
            <p className="no-network">{LOGIN.NO_NETWORK_TITLE}</p>
            <p className="network-content">{LOGIN.NO_NETWORK_TEXT}</p>
            <IonButton className="retry_button" onClick={()=>getOpenModal(false)}>
              {LOGIN.Ok_TEXT}
            </IonButton>
          </div>
        </div>
      ) : null}
      <div className="Landing-screen-content">
        {isPlatform('mobile') ? (
          <>
            <p className="getStarted" data-test="Get-Started" onClick={onClickR2Configuration}>
              {LOGIN.GET_STARTED_TEXT}
            </p>
            <p className="StartwithSignin" data-test="Start-With">
              {LOGIN.SIGNUP_TEXT}
            </p>
            <img src="assets/images/Mid illustration.png" className="mid-illustration" alt="" />
          </>
        ) : (
          <>
            <div className="globe-image">
              <p className="getStarted" data-test="Get-Started" onClick={onClickR2Configuration}>
                {LOGIN.GET_STARTED_TEXT}
              </p>
              <p className="StartwithSignin" data-test="Start-With">
                {LOGIN.SIGNUP_TEXT}
              </p>
              <img src="assets/icon/mid-icon.svg" className="midpart-icon" alt="" />
            </div>
          </>
        )}
        <div className="signup-buttons">
          <IonRow>
            <IonCol
              data-test="SignIn-btn"
              className="signIn-col"
              onClick={() => {
                tryAction(authService.login.bind(authService), 'There was an error logging in');
              }}>
              <IonButton class="load-btn-Signup" data-test="signin-button">
                <span className="signup-btn">{LOGIN.GET_STARTED_TEXT}</span>
              </IonButton>
            </IonCol>
          </IonRow>
        </div>
      </div>
    </>
  );
};

export default LoginContainer;
