import React, { useEffect, useState } from "react";
import { IonRefresher, IonRefresherContent } from "@ionic/react";
import { RefresherEventDetail } from "@ionic/core";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { updatePullToRefreshState } from "../../actions/App/action";

interface PullToEventModel {
  detail: {
    complete: Function;
  };
}

// Pull To refresh : wrapper class to refresh/Update UI with latest response from API calls.

const PullToRefresh = (props: any) => {
  const { status, isHideLoader = true } = props;

  const [event, setEvent] = useState({} as PullToEventModel);

  useEffect(() => {
    if (!status) {
      event && event.detail && event.detail.complete();
    }
  }, [status]);

  // emitted this method when user Pull down content.
  const onRefresh = (event1: CustomEvent<RefresherEventDetail>) => {
    props.updatePullToRefreshState && props.updatePullToRefreshState(true);
    setEvent(event1);
    if (!props.onPullToRefresh) {
      event1 && event1.detail && event1.detail.complete();
    }
    props && props.onPullToRefresh && props.onPullToRefresh(isHideLoader);
    props.updatePullToRefreshState &&  props.updatePullToRefreshState(null);
  };

  return (
    <IonRefresher
      slot="fixed"
      className="pull-to-refresh"
      onIonRefresh={onRefresh}
    >
      <IonRefresherContent
        pullingText={`<img src= "assets/images/Schneider_Spinner-transparent.gif" className="loader-image" />`}
        refreshingSpinner={null}
      ></IonRefresherContent>
    </IonRefresher>
  );
};

export const mapDispatchToProps = (dispatch: Function) => ({
  updatePullToRefreshState: (state: boolean) =>
    dispatch(updatePullToRefreshState(state))
});

export default connect(null, mapDispatchToProps)(PullToRefresh);
export { PullToRefresh };

PullToRefresh.prototype = {
  onPullToRefresh: PropTypes.func.isRequired,
  isHideLoader: PropTypes.bool,
  status: PropTypes.bool.isRequired
};
