import HTTP from '../services/httpServices';
import getURLDetails from "../services/endPoints";
import { Environment_Dev } from '../environments/environments.dev';
import { Environment_Unt } from '../environments/environments.unt';
import { Environment_Uat } from '../environments/environments.uat';
import { Environment_Prod } from '../environments/environments.prod';

// Environments :-
// 1: prod -> Production Envirionment
// 2: uat -> User Acceptance Environment
// 3: unt -> User Acceptance Environment
// 4: dev -> Develpoment Environment
export enum Environments {
  prod,
  alpha,
  uat,
  unt,
  dev
}
type EnviName = keyof typeof Environments;

/**
 * To get configurable information for the specified Environment.
 * @param {string} enviName environment name.
 */
export default class Environment {
  __environment: EnviName;

  constructor(enviName: EnviName) {
    this.__environment = enviName;
  }

  /**
     * To fetch offline Kyes information for the specified environment.
     * * @returns {object} json object with list of key-value pairs.
     */
  __fetchOfflineConfig() {
    let envi: any = null;
    switch (this.__environment) {
      case 'dev':
        envi = Environment_Dev;
        break;
      case 'unt': envi = Environment_Unt;
        break;
      case 'uat': envi = Environment_Uat;
        break;
      case 'alpha':
      case 'prod': envi = Environment_Prod;
        break;
    }
    return envi.ENVIROMENT_KEYS();
  }

  /**
     * To fetch online Keys information for the specified environment.
     * * @returns {object} promise object with list of key-value pairs.
     */
  async __fetchOnlineConfig(subscriptionKey: string, token: string, endPoint: string) {
    return await (new HTTP()).__requestRestApi(getURLDetails("CONFIG", "APP_LEVEL", "default").get_Service_Url(), {"Content-Type": "application/json", "Ocp-Apim-Subscription-Key": subscriptionKey, "Authorization": `Bearer ${token}`},"get")
      .then((response: any) => {
        return response.data[0];
      }).catch(async function (err: any) {
        const _http: any = (new HTTP()).__instance();
        return await _http.get("./online-mock.json").then((response: any) => {
          return response.data[0];
        });
      });
  }

  /**
     * To get Headers information for the specified servcie API.
     * @param {*} __wrapper common service to maintain keys.
     * @param {*} __wrteKeys to set key-values pairs in store.
     * @param {*} __readKeys to get key-values pairs from store
     * @returns {object} promise object.
     */
  async getConfig(__wrapper: any, __store: any, subscriptionKey: string, token: string, endPoint: string): Promise<void> {

    if (!__store) {
      throw new Error(
        "Store service is not provided to share Keys accross the Applciation"
      );
      return
    }

    const storeKey = "config";
    const vault =  await __store.getVault();
    if (!await vault.getValue(storeKey)) {
      try {
        const [onlineConfig] = (await Promise.all([
          this.__fetchOnlineConfig(subscriptionKey, token, endPoint)
        ])) as [any];
        if (Object.keys(onlineConfig).length) {
          vault.storeValue(storeKey, btoa(JSON.stringify({ ...onlineConfig })));
          __wrapper.getKeys(JSON.parse(atob(await vault.getValue(storeKey))));
        }
      } catch (err) {
        throw new Error(err);
      }
    } else {
      __wrapper.getKeys(JSON.parse(atob(await vault.getValue(storeKey))));
    }
  }
}
