import { IFavoriteDataRequest, IFavoriteUpdateRequest } from "../../schemas/Favorite/Favorite.Schema";

export class Favorite {

    constructor() {
    }

    private _favorite_state: IFavoriteDataRequest = {page:0, sortColumn:"pickupdate", favourite:true};

    get page(){
        return this._favorite_state.page;
    }

    set page(value){
        if(value) {
            this._favorite_state.page = value;
        }
    }
    get size(){
        return this._favorite_state.size;
    }

    set size(value){
        if(value) {
            this._favorite_state.size = value;
        }
    }
    get sortColumn(){
        return this._favorite_state.sortColumn;
    }

    set sortColumn(value){
        if(value) {
            this._favorite_state.sortColumn = value;
        }
    }
    get userID(){
        return this._favorite_state.userID;
    }

    set userID(value){
        if(value) {
            this._favorite_state.userID = value;
        }
    }
    get carrierId(){
        return this._favorite_state.carrierId;
    }

    set carrierId(value){
        if(value) {
            this._favorite_state.carrierId = value;
        }
    }
    get favourite(){
        return this._favorite_state.favourite;
    }

    set favourite(value){
        if(value) {
            this._favorite_state.favourite = value;
        }
    }

    getFavoritePayload() {
        return {
            query: {
                findUserFavouriteSearchHistory: {
                    __args: {
                        input: this._favorite_state
                    },
                    id: true,
                    userid: true,
                    originState: true,
                    originCity: true,
                    originPostalCode: true,
                    originCountry: true,
                    originradius: true,
                    pickupdate: true,
                    destinationCity: true,
                    destinationState: true,
                    destinationPostalCode: true,
                    destinationCountry: true,
                    destinationradius: true,
                    dropdate: true,
                    trailertype: true,
                    searchdate : true,
                    favouritesearchindicator: true,
                    carrierid: true,
                    durationDays: true
                }
                }
            }
        }

        updateFavoriteLoad(data: IFavoriteUpdateRequest) {
            return {
              mutation: {
                updateUserSearchHistory: {
                  __args: {
                    input: data
                  },
                  id: true
                }
              }
            };
        }

        updateFavoriteLoadCheckDuplicate(data: IFavoriteUpdateRequest){
            return {
                mutation: {
                    updateUserSearchHistoryAsFavorite: {
                        __args: {
                            input: data
                        },
                        errorMessage: true,
                        status: true,
                        userSearchHistory: {
                            id: true
                        }
                    }
                }
            }
        }
}