import * as Constants from "../../utils/constants";

export const SEARCH_DATA = (data: any) => ({
  type: Constants.SEARCH_DATA,
  payload: data
});

export const loadSearchData = (data: any, isPagination: boolean) => ({
  type: "loadSearchData",
  payload: {
    data,
    isPagination
  }
});

export const saveAllSearchData = (data: any, isPagination: boolean) => ({
  type: Constants.SAVE_ALL_SEARCH_DATA,
  payload: {
    data,
    isPagination
  }
});

export const clearLoads = () => ({
    type: Constants.CLEAR_SEARCH_RESULST,
    payload: []
})

export const fetchResults = (searchParams: any, isPagination: boolean, isHideLoader = false) =>({
        type: Constants.FETCH_SEARCH_RESULTS,
        payload: {
            searchParams,
            isPagination,
            isHideLoader
        }
    })

export const removeSearchResults = (payload:any) =>({
      type: Constants.REMOVE_SEARCH_RESULTS,
      payload
    })

export const setSearchParams = (payload: any) =>({
    type: Constants.SEARCH_DATA,
    payload
});

export const showBuyLoadError = (payload: any) => ({
  type: Constants.SHOW_BUYLOAD_API_ERROR,
  payload
});
