import React from 'react';
import { IonPage, IonContent, IonList, IonItem, IonLabel, IonInput, IonGrid, IonRow, IonCol, IonButton, IonFooter, IonCard, IonIcon, IonCardHeader, IonCardContent, IonText, IonToggle } from '@ionic/react';
import AppHeader from '../../../components/app/Bars/Bar-header';
import { NewDropDown } from '../../../components/shared/NewUIComponents/DropDown';
import PickupData from '../../../components/app/Search/PickupData';
import { Anywhere } from '../../../utils/constants';
import { PickUpTimeComponent } from '../../../components/shared/pickupTimeComponent';
import { PickupDaysComponent } from '../../../components/shared/PickupDays';
import { SelectComponent } from '../../../components/shared/SelectComponent';
import { states } from '../../../utils/states';
import DeliverypData from '../../../components/app/Search/DeliverypData';

const ManageCapacityFormDesktop = (props:any) => {
    return (
        <IonPage className="desktop-page my-load-desktop-page manage-card-content manage-capacity-view-desktop">
            <AppHeader title={'Manage'} imageCapature={props} />
            <IonContent className="ion-padding custom-padding add-capacity-form-content">
                <h1>{props.title || 'Add Lane/Truck'}</h1>
                <IonGrid class="capacity-grid">
                    <IonRow>
                        <IonCol size="6">
                            <IonRow class="capacity-label-row">
                                <IonLabel class="capacity-label">Capacity Type</IonLabel>
                            </IonRow>
                            <IonRow
                                class="capacity-type"
                                data-test="capacity-type"
                                onClick={() => props.setShowCapacityOptions(!props.showCapacityOptions)}
                            >
                                <IonCol class="capacity-name">{props.capacityType.name}</IonCol>
                                {
                                    !props.capacityId ? (
                                        <IonCol class="dropDown-icon">
                                            <IonIcon src="/assets/icon/grid-down-arrow.svg"></IonIcon>
                                        </IonCol>
                                    ) : null
                                }
                            </IonRow>
                            <IonRow class="capacity-desc">{props.capacityType.desc}</IonRow>

                            {!props.capacityId && props.capacityOptions && props.showCapacityOptions ? (
                            <IonGrid class="suggetion-grid"  data-test="capacity-type-list">
                                {props.capacityOptions.map((capacityType: any, index: number) => (
                                <IonRow
                                    key={index}
                                    data-test="capacity-type-list-item"
                                    onClick={() => {
                                        props.setCapacityType(capacityType);
                                        props.setStatesShow(false);
                                        props.setShowCapacityOptions(false);
                                    }}
                                >
                                    {capacityType.name}
                                </IonRow>
                                ))}
                            </IonGrid>
                            ) : null}
                        </IonCol>
                    </IonRow>
                </IonGrid>
                <IonGrid class="capacity-grid-form">
                    <IonRow>
                        <IonCol size="6">
                            <IonRow class="pickup-row">
                                <IonCol>
                                    Pick-up</IonCol>
                            </IonRow>
                            <IonRow class="pickup-content">
                                <IonCol>
                                    <PickupData
                                        origin={props.originDetails.address}
                                        originError={props.originError}
                                        originRadius={props.originRadius}
                                        handleChange={props.handleChange}
                                        rangeDisabled={true}
                                        isDestination={false}
                                        defaultValue={Anywhere}
                                        useHandler={false}
                                        setErrorValue={props.setErrorValue}
                                        updateLocalCurrentTime={props.updateLocalCurrentTime}
                                        showDate={false}
                                        onFocus={() => {
                                            if(!props.isOriginEdit) {
                                                props.setOriginEdit(true);
                                            }
                                        }}
                                    />
                                    {
                                        props.capacityType.name === 'Truck' ? (
                                        <PickUpTimeComponent
                                            pickUpLocalTime={props.pickUpLocalTime}
                                            handleChange={props.handleChange}
                                            fromDate={props.pickupFromDate}
                                            toDate={props.pickupToDate}
                                            error={props.pickupTimeError}
                                            handleError={props.setPickuptimeError}
                                        />
                                        ) : (
                                            <PickupDaysComponent
                                            setPickupDays={props.setPreferredDays}
                                            showError={props.pickupDaysError}
                                            handleError={props.setPickupDaysError}
                                            />
                                        )
                                    }
                                </IonCol>
                            </IonRow>
                        </IonCol>
                        <IonCol size="6">
                            <IonRow class="pickup-row">
                                <IonCol size="6">
                                    Delivery
                                </IonCol>
                                <IonCol size="6">
                                    <IonCard class="trailer-type-card">
                                        <NewDropDown
                                            labelName="Trailer type"
                                            TrailerType={props.trailerType}
                                            handleChange={props.handleChange}
                                        />
                                    </IonCard>
                                </IonCol>
                            </IonRow>
                            <IonRow class="pickup-content">
                                <IonCol>
                                    { 
                                        props.capacityType.name === 'Truck' ? (
                                            <IonRow class="states-wrapper">
                                                <IonCol size="6" class="state-destination-label">
                                                    { props.isStatesShow ? <span>Destination</span>: null }
                                                </IonCol>
                                                <IonCol size="6" class="state-toggle-wrapper">
                                                    <IonText class="state-toggle-label">Select States</IonText>
                                                    <IonToggle mode="ios" class="state-toggle" checked={props.isStatesShow} name="stateToggle" onIonChange={props.handleChange}></IonToggle>
                                                </IonCol>
                                            </IonRow>
                                        ): null
                                    }
                                    {
                                        props.isStatesShow && props.capacityType.name === 'Truck'?
                                        (
                                        <div data-test="state-list-wrapper" className={`states-list-wrapper ${props.stateError ? 'error': ''}`}>
                                        {
                                            props.statesList && props.statesList.map((state:any, index:number) => (
                                                <SelectComponent key={`state${index}`} options={states} placeholder="Choose the state" label={state.label} name={state.name} handleChange={props.handleStateChange} />
                                                )
                                            )
                                        }
                                        </div>
                                        ) : (
                                        <DeliverypData
                                            destination={(props.destinationDetails.address !== Anywhere  && props.destinationDetails.address !== '') ? props.destinationDetails.address: (props.capacityType.name === 'Truck' ? Anywhere: '')}
                                            destinationError={props.destinationError}
                                            Destination={'DestinationDetails'}
                                            handleChange={props.handleChange}
                                            rangeDisable={props.destinationDetails && ((props.capacityType.name === 'Lane' && props.destinationDetails.address === Anywhere) || props.destinationDetails.address === '' || props.destinationDetails.address === null)}
                                            platform="mobile"
                                            keyboardshowHandler={() => {}}
                                            keyboardhideHandler={() => {}}
                                            defaultValue={Anywhere}
                                            isDestination={true}
                                            useHandler={true}
                                            setErrorValue={props.setErrorValue}
                                            destinationRadius={props.destinationRadius}
                                            updateLocalCurrentTime={props.updateLocalCurrentTime}
                                            showDate={false}
                                            onFocus={() => {
                                                if(!props.isDestinationEdit) {
                                                    props.setDestinationEdit(true);
                                                }
                                            }}
                                        />
                                        )
                                    }
                                </IonCol>
                            </IonRow>
                        </IonCol>
                    </IonRow>
                </IonGrid>
                <div className="manage-capacity-buttons">
                    <IonButton class="load-btn" data-test="back-btn" onClick={() => props.handleBackButton()}>
                        Back
                    </IonButton>
                    <IonButton class="load-btn save-btn"  data-test="save-btn"  onClick={() =>  props.handleSubmit()}>
                        Save
                    </IonButton>
                </div>
            </IonContent>
        </IonPage>
    );
}

export default ManageCapacityFormDesktop;