import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import useInjectReducer from '../../../utils/injectReducer';
import useInjectSaga from '../../../utils/injectSaga';
import { HideTabBar } from '../../../actions/Login/action';
import reducer from '../../../reducers/driverAssign/reducer';
import saga from '../../../saga/CarrierAssign/saga';
import { getLoading } from '../../../selectors/selector';
import DriverAssignList from '../../../pages/Execute/AssignDriverList/AssignDriverList';
import { upadateShowToaster } from '../../../actions/carrierAssign/action';
import { getDriverList } from '../../../selectors/carrierAssign/selector';

const key = 'carrierAssigned';
export class AssignDriverContainer extends PureComponent<any, any> {
  componentDidMount(): void {
    if (!this.props.makeHideTabBar) {
      this.props.HideTabBar();
    }
  }

  getShowToasterValue = (): void => {
    this.props.showToasterValue();
  };

  getloadDetails = (): object => {
    return (
      (this.props &&
        this.props.history &&
        this.props.history.location &&
        this.props.history.location.state &&
        this.props.history.location.state.loadDetails &&
        JSON.parse(this.props.history.location.state.loadDetails)) ||
      {}
    );
  };

  getPathname = (): string => {
    return (
      this.props.history &&
      this.props.history.location &&
      this.props.history.location.state &&
      this.props.history.location.state.pathname
    );
  };

  render() {
    return (
      <DriverAssignList
        {...this.props}
        getShowToasterValue={this.getShowToasterValue}
        driverList={this.props.getDriverList}
        module={this.getPathname()}
        loadDetails={this.getloadDetails()}
      />
    );
  }
}

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  getDriverList: getDriverList()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  showToasterValue: () => dispatch(upadateShowToaster()),
  HideTabBar: () => dispatch(HideTabBar())
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(AssignDriverContainer);
