import React from 'react';
import ReactDOM from 'react-dom';
import { defineCustomElements } from '@ionic/pwa-elements/loader';
import { Provider } from "react-redux";
import { IonApp, isPlatform } from "@ionic/react";


/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

/* Theme variables */
import "theme/variables.css";
import { store } from "./app/store";
import { PrivateRoute } from './app/containers/PrivateRoute';
import { Bugsnag } from "./app/utils/Bugsnag";
import { App } from 'app';
import SplashHOC from './app/components/app/hoc/splash/SplashHOC';
import { GPSWrapper } from './app/utils/common';

const AppContainer: React.FC = () => {

  const { ErrorBoundary } = Bugsnag();
  
  if (isPlatform("capacitor")) {
    GPSWrapper.details.initiateGPSconfig();
  }
  
  return (
    <ErrorBoundary>
      <SplashHOC WrappedComponent={
        <Provider store={store}>
            <IonApp>
              <PrivateRoute component={App} />
            </IonApp>
         </Provider>} />
    </ErrorBoundary>
  );
};

ReactDOM.render(<AppContainer />, document.getElementById('root'));
defineCustomElements(window);
