import React, { useState, useEffect } from "react";
import DataTable from "react-data-table-component";
import { IonImg, IonGrid } from "@ionic/react";
import "./searchResultsDesktop";
import "./searchResultsDesktop.scss";
import "../../../../theme/variables.scss";
import "../../../../theme/responsive-styles.scss";
import { RecommendedError } from "../../../pages/Home/RecommendedError";
import {
  searchResultsErrorText,
  searchResultsErrorButton,
  EVENT,
  SCREEN, PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE, NO_NEWORK
} from "../../../utils/constants";
import { Search } from "../../../models/home/Search.model";
import { logEvent } from "../../../utils/Analytics";

const customSort = (rows: any, field: any, direction: any) => {
  let sortedList = [];
  if (direction === "asc") {
    sortedList = [...rows].sort((a: any, b: any) =>
      a[field] > b[field] ? 1 : -1
    );
  } else {
    sortedList = [...rows].sort((a: any, b: any) =>
      a[field] > b[field] ? -1 : 1
    );
  }
  return sortedList;
};

const SearchResultDesktop = (props: any) => {
  const { searchInput, pageName, totalRecords } = props;
  const [sortable, setSortable] = useState(true);
  const [sortColumn, changeSortColumn] = useState<string>(
    searchInput && searchInput.sortColumn
  );
  const [pageSize, changePageSize] = useState<number>(
    (searchInput && searchInput.size) || 5
  );
  const [selectedPage, changePage] = useState<number>(
    (searchInput && searchInput.page) || 1
  );
  const [totalPages, updateTotalPages] = useState<number[]>([5, 10]);

  useEffect(() => {
    if (Object.keys(searchInput).length) {
      const userRole =
        props.userDetails &&
        props.userDetails.userApplicationDetails &&
        props.userDetails.userApplicationDetails[0].roles.map(
          (carrierName: any, index: number) => {
            return carrierName.roleName;
          }
        );
      const modelSearch: Search = new Search();
      const searchModel: Search = searchInput;
      modelSearch.originCity = searchModel.originCity;
      modelSearch.originState = searchModel.originState;
      modelSearch.originRadius = searchModel.originRadius;
      modelSearch.pickUpdate = searchModel.pickUpdate;
      modelSearch.dropDate = searchModel.dropDate;
      modelSearch.destinationCity = searchModel.destinationCity;
      modelSearch.destinationState = searchModel.destinationState;
      modelSearch.destinationRadius = searchModel.destinationRadius;
      modelSearch.favorite = searchModel.favorite;
      modelSearch.trailerType = searchModel.trailerType;
      modelSearch.userID =
        (props.userDetails && props.userDetails.userPrincipalName) || "";
      modelSearch.carrierId =
        (props.userDetails && props.userDetails.carrierId) || "";
      modelSearch.page = selectedPage - 1;
      modelSearch.size = pageSize;
      modelSearch.sortColumn = sortColumn;
      modelSearch.originCountry = searchModel.originCountry;
      modelSearch.destinationCountry = searchModel.destinationCountry;
      modelSearch.carrierScac =
        (props.userDetails && props.userDetails.carrierScac) || "";
      modelSearch.carrierTier =
        (props.userDetails && props.userDetails.carrierTier) || "";
      modelSearch.userRole = userRole && userRole[0];
      props.handleSortingPagination(modelSearch, false, false);
    }
  }, [JSON.stringify(searchInput), pageSize, selectedPage]);

  const showLoadDetails = (row: any) => {
    if (!props.isNetworkAvailable) {
      props.showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK,
      });
    } else if (props.pageName === "Recommended" || props.pageName === "Watched") {
      const url = `/app/home/recommended/${row.loadId}`;
      props.push(url);
    } else if (props.pageName !== "loadDetails") {
      const url = `/app/search/results/${row.loadId}`;
      props.push(url);
    }
  };
  useEffect(() => {
    if (props.pageName === "loadDetails") {
      setSortable(false);
    }
  }, [props.pageName]);

  useEffect(() => {
    // Analytics implementation
    logEvent(EVENT.SV_SEARCH_RESULT, {
      screen: SCREEN.SEARCH_RESULT,
      lead: props.lead,
    });
  }, []);

  const [starVisible, setStarVisible] = useState(false);
  const [src, setSrc] = useState("assets/icon/star.svg");
  const favorite = (row: any) => {
    setStarVisible(!starVisible);
    if (!starVisible) {
      setSrc("assets/icon/star-color.svg");
    } else {
      setSrc("assets/icon/star.svg");
    }
  };
  const columns = [
    {
      name: "Origin",
      selector: "origin_city",
      sortable,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000",
      },
    },
    {
      name: "Origin Date",
      selector: "origin_from_date",
      sortable,
      minWidth: "123px",
    },
    {
      name: "DH-O",
      selector: "origin_deadhead",
      sortable,
    },
    {
      name: "Origin Load Type",
      selector: "origin_load_type",
      sortable,
    },
    {
      name: "Destination",
      selector: "destination_city",
      sortable,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000",
      },
    },
    {
      name: "Destination Date",
      selector: "destination_from_date",
      minWidth: "123px",
      sortable,
    },
    {
      name: "DH-D",
      selector: "destination_deadhead",
      sortable,
    },
    {
      name: "Destination Load Type",
      selector: "destination_load_type",
      sortable,
    },
    {
      name: "Trailer Type",
      selector: "trailer",
      sortable,
    },
    {
      name: "Rate ($)",
      selector: "price",
      sortable,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000",
      },
    },
    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) =>
        props.segmentName === "Recommended" ? null : (
          <IonImg
            data-test="favorite-click"
            onClick={() => favorite(row)}
            src={src}
          />
        ),
    },
    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) =>
        props.pageName === "loadDetails" ? (
          ""
        ) : (
          <IonGrid class="datatable-badge">
            <i></i>
          </IonGrid>
        ),
    },
  ];
  const getTime = (data: any) => {
    if (data) {
      const _src = formatDate(data.appointmentFrom);
      const _dst = formatDate(data.appointmentTo);
      const _to =
        _dst.year === new Date().getFullYear()
          ? _dst.time
          : `${_dst.month} ${_dst.day} ${_dst.time}`;
      return `${_src.month} ${_src.day} ${_src.time}-${_to}`;
    }
  };
  const formatDate = (from: any) => {
    from = from.replace(/ /, "T");
    const _date = new Date(from);
    const _year = _date.getFullYear();
    const _month = _date.toLocaleString("default", { month: "long" });
    const _day = _date.getDate();
    let _hours = _date.getHours();
    const _AmOrPm = _hours >= 12 ? "pm" : "am";
    _hours = _hours % 12 || 12;
    const _time = `${_hours}:00${_AmOrPm}`;
    return { year: _year, month: _month, day: _day, time: _time };
  };
  const data =
    props.data &&
    props.data.map((load: any, index: number, array: any) => {
      const stopDetails: any = {};
      const origin = load.loadStop[0];
      const destination = load.loadStop.slice(-1)[0];
      stopDetails.loadId = load.loadId;
      stopDetails.origin_city = `${origin.city}, ${origin.state}`;
      stopDetails.origin_from_date = getTime(origin);
      stopDetails.origin_deadhead = origin.deadHead;
      stopDetails.origin_load_type = "Live load";
      stopDetails.destination_city =
        `${destination.city}, ${destination.state}`;
      stopDetails.destination_from_date = getTime(destination);
      stopDetails.destination_deadhead = destination.deadHead;
      stopDetails.destination_load_type = "Live unload";
      stopDetails.trailer = load.transportMode;
      stopDetails.price =
        load.rate === null ? "Contact for Price" : `$${load.rate.totalCharge}`;
      return stopDetails;
    });

  return (
    <>
      {data ? (
        <>
          {props.pageName === "Recommended" ||
          props.pageName === "Watched" ? null : (
            <div className="searchResults_div" data-test="search-results">
                Search Results
              </div>
            )}
          {data.length ? (
            <>
              <DataTable
                title={data && `${data.length} Matches`}
                columns={columns}
                data={data}
                highlightOnHover
                sortFunction={customSort}
                onRowClicked={showLoadDetails}
                pagination
                paginationServer
                paginationServerOptions={{
                  persistSelectedOnPageChange: false,
                  persistSelectedOnSort: false,
                }}
                paginationDefaultPage={selectedPage}
                paginationResetDefaultPage
                paginationTotalRows={totalRecords}
                paginationPerPage={pageSize}
                paginationRowsPerPageOptions={totalPages}
                onChangePage={(page: number, totalRows: number) => {
                  if (pageName === "searchResults") {
                    changePage(page);
                  }
                }}
                onChangeRowsPerPage={(
                  currentRowsPerPage: number,
                  currentPage: number
                ) => {
                  if (pageName === "searchResults") {
                    changePage(currentPage);
                    changePageSize(currentRowsPerPage);
                  }
                }}
                sortServer
                onSort={(column: any, sortDirection: "asc" | "desc") => {
                  changePage(1);
                  changeSortColumn("total_distance");
                }}
              />
              {props.segmentName === "Recommended" ||
              props.segmentName === "Watched" ? null : (
                  <div className="end-of-list">End of list</div>
                )}{" "}
            </>
          ) : (
            <div className="searchResultsError">
              <div className="searchInnerDiv"></div>
              <RecommendedError
                data-test="rec-error"
                constantText={searchResultsErrorText}
                constantButton={searchResultsErrorButton}
              />
            </div>
          )}
        </>
      ) : null}
    </>
  );
};
export default SearchResultDesktop;
