import { all, put, call, takeLatest, takeEvery } from "redux-saga/effects";
import {
  showLoading,
  hideLoading,
  showPullToRefresh,
  hidePullToRefresh
} from "../../actions/Login/action";
import * as constants from "../../utils/constants";
import request from "../../utils/request";
import { loadSearchData } from "../../actions/Search/action";
// import { GetRecent } from '../../actions/Recent/action';

import { getFavorite } from "../../actions/Favorite/action";
import { DomainConverter } from "../../utils/common";

export function* searchData(data: any) {
  try {
    yield put(showLoading());
    const repos = yield call(request, ["SEARCH", "SEARCH_LOADS"]);
    yield put(loadSearchData(repos.data, false));

    yield put(hideLoading());
  } catch (e) {
    // yield __model.getLoads() && put(loadData(__model.getLoads()));
    yield put(hideLoading());
  }
}
