export function* favoriteData() {
  try {
    yield put(showLoading());
    const favouriteData = yield call(request, ["FAVORITE", "FAVORITE_LOADS"]);
    yield put(getFavorite(favouriteData));
    yield put(hideLoading());
  } catch (e) {
    yield put(hideLoading());
  }
}
export function* fetchSearchResults(data: any) {
  const { searchParams, isPagination, isHideLoader } = data.payload;
  try {
    if (!(data && data.payload && isHideLoader)) {
      yield put(showLoading());
    }
    yield put(showPullToRefresh());
    const searchReqestQuery = DomainConverter.domainToData<any>(searchParams);
    const fetchSearchResults = yield request<any>([
      "SEARCH",
      "SEARCH",
      null,
      "",
      searchReqestQuery,
      "application/json"
    ]);
    let responseData: any = {};
    if (
      fetchSearchResults &&
      fetchSearchResults.data &&
      fetchSearchResults.data.searchLoadPostings
    ) {
      responseData = {
        searchLoads:
          fetchSearchResults.data.searchLoadPostings.loadPostings || [],
        totalRecords:
          fetchSearchResults.data.searchLoadPostings.totalRecords || 0
      };
    }

    yield put(loadSearchData(responseData, isPagination));
    if (!(data && data.payload && isHideLoader)) {
      yield put(hideLoading());
    }
    yield put(hidePullToRefresh());
  } catch (e) {
    if (!(data && data.payload && isHideLoader)) {
      yield put(hideLoading());
    }
    yield put(hidePullToRefresh());
  }
}

export default function* rootSaga() {
  yield all([
    takeLatest(constants.SEARCH_DATA, searchData),
    takeLatest(constants.FETCH_SEARCH_RESULTS, fetchSearchResults)
  ]);
}
