import React from 'react';
import {
  IonLabel,
  IonCard,
  IonItem,
  IonInput,
  IonCardHeader,
  IonCardContent,
  IonIcon,
  IonList,
  isPlatform
} from '@ionic/react';

interface AccordianProps {
    showAccordian: any,
    accordianClick: any,
    carrierHeader: any,
    data: any
}
export const CarrierInfoAccordian: React.FC<AccordianProps> = ({
  showAccordian,
  accordianClick,
  carrierHeader,
  data
}) => {
  return (
    <IonCard  class={` ${
      carrierHeader === 'Carrier Info' ? 'carrier-card2' : 'carrier-card'
    }`}
    >
      <IonCardHeader
        class={
          showAccordian
            ? 'card-header-pad carrier-accordion-header'
            : 'card-header-pad2 carrier-accordion-header'
        }
        data-test="carrier-acc-title"
        onClick={accordianClick}>
        <IonLabel className="carrier-card-title">{carrierHeader}</IonLabel>
        {showAccordian ? (
          <IonIcon src="assets/icon/add.svg" className="addAccordian" data-test="add-icon" />
        ) : (
          <IonIcon
            src="assets/icon/minus-icon.svg"
            className="addAccordian"
            data-test="remove-icon"
          />
        )}
      </IonCardHeader>
      <IonCardContent class={showAccordian ? 'fadeout' : 'fadein' + ' accordianCardContent'}>
        {data && data.length > 0 ? (
          <IonList className="carrier-profile-list">
            {data.map((carrierData: any, index: number) => (
              <IonItem
                mode="ios"
                key={index}
                className="carrier-profile-ionitem"
                lines={index === data.length - 1 && !isPlatform('desktop') ? 'none' : 'inset'}>
                <IonLabel mode="ios" position="floating" className="carrier-profile-label">
                  {carrierData.label}
                </IonLabel>
                <IonInput
                  type="text"
                  className="profile-form-control"
                  name="origin"
                  value={carrierData.value}
                  readonly
                />
              </IonItem>
            ))}
          </IonList>
        ) : null}
      </IonCardContent>
    </IonCard>
  );
};
