import React, { useEffect, useState } from "react";
import { Route, Redirect } from "react-router-dom";
import {
  IonRouterOutlet,
  IonTabs,
  IonTabBar,
  IonTabButton,
  IonLabel,
  IonIcon,
  IonImg,
  IonRow,
  IonCol,
  IonFooter
} from "@ionic/react";
import { SearchContainer } from "../../../containers/SearchContainer";
