import React, { useState } from "react";
import {
  IonCardHeader,
  IonImg,
  IonCardContent,
  IonRow,
  IonCol
} from "@ionic/react";
import CardContent from "./cardContent";
import { FormatText } from "../FormatTextComponent/formatText";
import getType from "../GetDataType";
import FeedbackComponent from "../SubmitFeedback/Feedback";

interface FAQAccordionProps {
  contextData: any;
  dataType: any;
}
export const Accordion: React.FC<FAQAccordionProps> = ({
  contextData,
  dataType
}) => {
  const [isShowContent, setIsShowContent] = useState<Boolean>(true);
  let valueDataType: string = "text";
  const onCardHeaderClick = () => {
    setIsShowContent(!isShowContent);
  };
  const getDataType = (fieldName: string) => {
    if (dataType && dataType.length) {
      valueDataType = getType(dataType, fieldName);
    } else {
      valueDataType = "text";
    }
    return valueDataType;
  };
  return (
    <>
      <IonCardHeader
        data-test="IonCardHeader-Menu"
        class="accordion_header"
        onClick={onCardHeaderClick}
      >
        <IonRow>
          <IonCol className="title-col" size="10">
            <FormatText
              text={contextData.title}
              dataType={getDataType("title")}
            />
          </IonCol>
          <IonCol size="2">
            {" "}
            {isShowContent ? (
              <IonImg src="assets/icon/plus.svg" className="plus-icon"></IonImg>
            ) : (
              <IonImg
                src="assets/icon/minus-icon.svg"
                className="plus-icon"
              ></IonImg>
            )}
          </IonCol>
        </IonRow>
      </IonCardHeader>
      <IonCardContent class={isShowContent ? "fadeout" : "fadein"}>
        {contextData && contextData.richTextDescription ? (
          <div className="description-div">
            <FormatText
              text={contextData.richTextDescription}
              dataType={getDataType("richTextDescription")}
            />
            {contextData.title.includes("Feedback for the App") ? (
              <FeedbackComponent notNowClick={onCardHeaderClick} />
            ) : null}{" "}
          </div>
        ) : (
          contextData &&
          contextData.group &&
          contextData.group.map((groupData: any, index: number) => (
            <CardContent
              data-test="card-content"
              key={index}
              groupData={groupData}
              dataType={getDataType("group")}
            />
          ))
        )}
      </IonCardContent>
    </>
  );
};
