import { IonRow, IonCol, IonGrid } from "@ionic/react";
import React from "react";
import { origin_Radius, PickUpdate, Origin } from "../../../utils/constants";
import { NewInput } from "../../../components/shared/NewUIComponents/OriginInput";
import { RadiusRange } from "../../../components/shared/NewUIComponents/RadiusRange";
import { DatePicker } from "../../../components/shared/NewUIComponents/DatePicker";

const PickupData = (props: any) => {
  const {
    originError,
    origin,
    originRadius,
    pickUpdateError,
    pickUpdate,
    isDestination,
    defaultValue,
    useHandler,
    setErrorValue,
    updateLocalCurrentTime,
    pickUpLocalTime,
  } = props;
  const upDateLocalTime = (time: string) => {
    if (props.showDate) {
      updateLocalCurrentTime(PickUpdate, time);
    }
  };
  return (
    <IonGrid className="pickup-data">
      <IonRow>
        <IonCol>
          <NewInput
            Error={originError}
            labelValue="Origin"
            InputValue={origin}
            InputName={Origin}
            handleChange={(e: any) => props.handleChange(e)}
            isDestination={isDestination}
            deFaultValue={defaultValue}
            locationValue={false}
            useHandler={useHandler}
            setErrorValue={setErrorValue}
            getLocalTime={upDateLocalTime}
            onFocus={props.onFocus || (() => {})}
          />
        </IonCol>
      </IonRow>
      <IonRow>
        <IonCol>
          <RadiusRange
            labelName="Origin Radius"
            rangeDisabled={props.rangeOriginDisable}
            InputValue={originRadius}
            divClassName="pickRadius"
            name={origin_Radius}
            handleChange={props.handleChange}
          />
        </IonCol>
      </IonRow>
      {props.showDate ? (
        <IonRow>
          <IonCol>
            <DatePicker
              Error={pickUpdateError}
              rangeDisabled={props.rangeOriginDisable}
              display-timezone="utc"
              labelName="Pickup Date"
              name={PickUpdate}
              InputValue={pickUpdate}
              handleChange={props.handleChange}
              placeholder="Choose Pickup Date"
              localTime={pickUpLocalTime}
            />
          </IonCol>
        </IonRow>
      ) : null}
    </IonGrid>
  );
};
export default PickupData;
