import React, { useState, useEffect } from 'react';
import { withRouter, RouteComponentProps } from 'react-router';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { isPlatform } from '@ionic/react';
import NewPage from '../../../pages/Search/New/NewPage.page';
import { fetchResults, SEARCH_DATA, clearLoads } from '../../../actions/Search/action';
import useInjectSaga from '../../../utils/injectSaga';
import saga from '../../../saga/Search/Search.saga';
import {
  getLoading,
  getUserData,
  getNetworkStatus,
  getUserDetails
} from '../../../selectors/selector';
import { makeSearch } from '../../../selectors/Search/selector';

import useInjectReducer from '../../../utils/injectReducer';
import reducer from '../../../reducers/Search/reducer';
import { Search } from '../../../models/home/Search.model';
import { Isearch, ILoadSearchResults } from '../../../schemas/Search/Search.schema';
import { DomainConverter } from '../../../utils/common';
import { SearchResults } from '../../../models/home/SearchResults.model';

import { showAlert } from '../../../actions/App/action';

const key = 'search';
let srchParams: any = null;
export interface newProps extends RouteComponentProps {
  history: any;
  searchData: any;
  data: any;
  loading: any;
  location: any;
  fectchData: any;
  clearLoads: any;
  userData: any;
  isRecentSearch: boolean;
  userDetails: any;
  isNetworkAvailable: boolean;
  showAlert: any;
  searchParams: any;
}
export const NewContainer: React.FC<newProps> = ({
  history,
  location,
  fectchData,
  searchData,
  data,
  loading,
  clearLoads,
  userData,
  isRecentSearch,
  userDetails,
  isNetworkAvailable,
  showAlert,
  searchParams
}) => {
  const [searchInput, setSearchInput] = useState<any>({});
  const submitForm = async (searchdata: Isearch, platform: any) => {
    const userRole =
      userDetails &&
      userDetails.userApplicationDetails &&
      userDetails.userApplicationDetails[0].roles.map((carrierName: any, index: number) => {
        return carrierName.roleName;
      });
    clearLoads();
    const modelSearch: Search = new Search();
    const destinationArray =
      searchdata.destination.indexOf(',') !== -1
        ? searchdata.destination.split(',')
        : [searchdata.destination, searchdata.destination];
    const originArray =
      searchdata.origin.indexOf(',') !== -1
        ? searchdata.origin.split(',')
        : [searchdata.origin, searchdata.origin];

    srchParams = searchdata;
    modelSearch.originCity = originArray[0].trim();
    modelSearch.originState = originArray[1].trim();
    modelSearch.originRadius = srchParams.originRadius;
    modelSearch.pickUpdate = srchParams.pickUpdate;
    modelSearch.dropDate = srchParams.dropdate;
    if (searchdata.destination !== 'Anywhere') {
      modelSearch.destinationCity = destinationArray[0].trim();
      modelSearch.destinationState = destinationArray[1].trim();
    }
    modelSearch.destinationRadius = srchParams.destinationRadius;
    modelSearch.favorite = srchParams.favorite;
    modelSearch.trailerType = srchParams.TrailerType;
    modelSearch.userID = (userDetails && userDetails.userPrincipalName) || '';
    modelSearch.carrierId = (userDetails && userDetails.carrierId) || '';
    modelSearch.page = 0;
    modelSearch.size = 5;
    modelSearch.sortColumn = 'pickUpdate';
    modelSearch.originCountry = srchParams.originCountry;
    modelSearch.destinationCountry = srchParams.destinationCountry;
    modelSearch.sortDirection = 'asc';
    modelSearch.carrierScac = (userDetails && userDetails.carrierScac) || '';
    modelSearch.carrierTier = (userDetails && userDetails.carrierTier) || '';
    modelSearch.userRole = userRole && userRole[0];
    setSearchInput(modelSearch);
    if (isPlatform('mobile')) {
      fectchData(modelSearch.getRequestPayload(), false, false);
    }
  };

  useEffect(() => {
    if (isPlatform('desktop')) {
      setSearchInput(searchParams);
    }
  }, [searchParams]);

  return (
    <NewPage
      isRecentSearch={isRecentSearch}
      data={location.state ? location.state.params : {}}
      submitForm={submitForm}
      results={DomainConverter.dataToDomain<ILoadSearchResults>(
        SearchResults,
        data
      )}
      userDetails={userDetails}
      totalRecords={data.totalRecords}
      searchInput={searchInput}
      isNetworkAvailable={isNetworkAvailable}
      showAlert={showAlert}
      handleSortingPagination={(inputs: Search, isPagination: boolean, isHideLoader = false) => {
        fectchData(inputs.getRequestPayload(), isPagination, isHideLoader);
      }}
    />
  );
};

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  data: makeSearch(),
  userData: getUserData(),
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus()
});
export const mapDispatchToProps = (dispatch: any) => ({
  fectchData: (searchdata: any, isPagination: boolean, isHideLoader = false) =>
    dispatch(fetchResults(searchdata, isPagination, isHideLoader)),
  searchData: (data: any) => dispatch(SEARCH_DATA(data)),
  clearLoads: () => dispatch(clearLoads()),
  showAlert: (data: any) => dispatch(showAlert(data))
});
const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });
export default compose<any>(
  withSaga,
  withReducer,
  withConnect
)(withRouter(React.memo(NewContainer)));
