import React, { useState, useEffect } from 'react';
import './Manage.scss';
import { IonContent, IonPage, IonList, IonItem, isPlatform } from '@ionic/react';
import { EnumType } from 'json-to-graphql-query';
import { ToastMessage } from '../../utils/ToastMessage';
import ImageCapture from '../../components/ImageCapture/ImageCapture';
import ManageDesktopPage from './ManageDesktop.page';
import { isEntitled, R2Configuration, IONICWrapper } from '../../utils/common';
import { CustomModal } from '../../components/CustomModal';
import {
  MANAGE_USER_CONFIRM_TRANSFER_TITLE,
  MANAGE_USER_CONFIRM_TRANSFER_MESSAGE,
  KEYS,
  ENV,
  EVENT,
  SCREEN,
  TAB,
  Roles,
  NO_ACCESS_MSG
} from '../../utils/constants';
import { getApplicationInfo, logEvent } from '../../utils/Analytics';

export interface ManageCardSubList {
  cradImg: string;
  profileHeading: string;
  profileRouting: string;
}
export interface ImanageCard {
  manageCard: ManageCardSubList[];
  history?: any;
  userDetails?: any;
  isTransferRequest?: any;
  transferDetails?: any;
  transferRequestReset?: any;
  handleTransfer?: any;
}
const ManagePage: React.FC<ImanageCard> = (props: ImanageCard) => {
  const {
    history: { push },
    userDetails,
    isTransferRequest,
    transferDetails,
    transferRequestReset
  } = props;
  const [manageCards, SetManageCards] = useState<any>([]);
  const [alertProps, setAlertProps] = useState<any>(null);

  useEffect(() => {
    // Analytics implementation
    logEvent(EVENT.SV_MANAGE_PROFILE,
      {
        screen: SCREEN.MANAGE_PROFILE,
        carrierId: props && props.userDetails && props.userDetails.carrierId,
      });
  }, []);

  useEffect(() => {
    const cards = props.manageCard.filter((card: any) => {
      return !(
        IONICWrapper.details.auth &&
        IONICWrapper.details.auth.user.roles.includes(Roles.carrierGuest)
      )
        ? isEntitled('manage', convertToCamel(card.profileHeading))
        : true;
    });
    SetManageCards(cards);
  }, [userDetails, props.manageCard]);

  const navigateToPage = (profile: ManageCardSubList) => {
    const { profileHeading, profileRouting } = profile;

    if (!isEntitled('manage', convertToCamel(profileHeading))) {
      ToastMessage.show(NO_ACCESS_MSG);
      return;
    }

    switch (profileHeading) {
      case 'My Profile':
        push(profileRouting);
        break;
      case 'Manage Users':
      case 'Manage My Capacity':
      case 'Carrier Profile':
        R2Configuration.details.getR2FeatureEnable() // (KEYS.RELEASE === ENV.UAT || KEYS.RELEASE === ENV.PROD)
          ? ToastMessage.show()
          : push(profileRouting);
        break;
    }
  };

  const handleTransfer = (confirm: boolean) => {
    const roles = (transferDetails.roles && [new EnumType(transferDetails.roles)]) || [];
    const payload = {
      userId: userDetails.userPrincipalName,
      carrierId: transferDetails.carrierId,
      confirm,
      roles
    };
    props.handleTransfer(payload);
  };

  const defaultProps = {
    isOpen: false,
    title: MANAGE_USER_CONFIRM_TRANSFER_TITLE,
    message: MANAGE_USER_CONFIRM_TRANSFER_MESSAGE,
    buttons: [
      {
        label: 'Not Now',
        callback: () => {
          setAlertProps(null);
          transferRequestReset();
          handleTransfer(false);
        }
      },
      {
        label: 'Agree',
        callback: () => {
          setAlertProps(null);
          handleTransfer(true);
        }
      }
    ]
  };

  useEffect(() => {
    if (isTransferRequest) {
      setAlertProps({
        ...defaultProps,
        isOpen: true,
        message: defaultProps.message.replace('{{COMPANY}}', transferDetails.carrierName)
      });
    } else {
      setAlertProps(null);
    }
  }, [isTransferRequest]);

  function convertToCamel(feature: any) {
    const words = feature.split(' ');
    let featureName = '';
    for (let i = 0, len = words.length; i < len; i++) {
      const currentStr = words[i];
      let tempStr = currentStr.toLowerCase();
      if (i !== 0) {
        tempStr = tempStr.substr(0, 1).toUpperCase() + tempStr.substr(1);
      }
      featureName += tempStr;
    }
    return featureName;
  }

  return isPlatform('desktop') ? (
    <ManageDesktopPage {...props} manageCards={manageCards} />
  ) : (
    <IonPage className="manage-header ">
      <IonContent className="manage-card-content">
        <ImageCapture {...props} />
        <div className="manage_options">
          <IonList mode="ios" className="manage-list">
            {manageCards &&
              manageCards.map((profileData: any, key: any) => (
                <IonItem
                  key={key}
                  data-test="manage-link"
                  onClick={() => {
                    navigateToPage(profileData);
                  }}
                  class="manage_list_item"
                  lines={key === props.manageCard.length - 1 ? 'none' : 'inset'}>
                  {profileData.profileHeading}
                  <img
                    slot="end"
                    alt="logo"
                    src="/assets/icon/more-arrow.svg"
                    className="moresideimg"
                  />
                </IonItem>
              ))}
          </IonList>
        </div>
      </IonContent>
      {alertProps ? <CustomModal {...alertProps} /> : null}
    </IonPage>
  );
};
export default ManagePage;
