import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import {
  IonModal,
  IonButton,
  IonCard,
  IonLabel,
  IonImg,
  IonCardContent,
  IonGrid,
  IonRow,
  IonCol,
  isPlatform,
  IonToast
} from '@ionic/react';
import './CarrierAssignedModal.scss';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { IAssignShipmentToDriverInputProps } from '../../../schemas/carrierAssign/CarrierAssign.schema';
import { LoadTileFooter } from '../../../components/app/home/Load-Tile-Footer';
import {
  assignDriverToShipment,
  clearAssignDriverToShipmentResponse,
  loadAssignedrData,
  loadCarrierData,
  carrierAssignedLoads
} from '../../../actions/carrierAssign/action';
import useInjectSaga from '../../../utils/injectSaga';
import useInjectReducer from '../../../utils/injectReducer';
import saga from '../../../saga/CarrierAssign/saga';
import reducer from '../../../reducers/carrierAssign/reducer';
import { CarrierLoads } from '../../../models/home/CarrierAssignLoad.model';
import { IONICWrapper } from '../../../utils/common';
import { getAssignShipmentDriverResponse } from '../../../selectors/carrierAssign/selector';
import { getUserDetails } from '../../../selectors/selector';

import { getLoaddata } from '../../../selectors/Myload/selector';
import { logEvent } from '../../../utils/Analytics';
import { ASSIGN_DRIVER_POPUP_TITLE, EVENT, LOAD_ASSIGN_FAIL, LOAD_ASSIGN_SUCCESS, SCREEN } from '../../../utils/constants';
import { getAppointmentTime } from '../../../utils/formatData';

export interface ICarrirAssignModalOwnProps {
  isOpen: boolean;
  onDismiss: () => void;
}

export const StopDetailsOnDialog = (props: any) => {
  const stopDetail = props.stopDetails;
  return (
    <IonRow data-test="stop-details-dialog" class="stop-details-row">
      <IonCol class="stop-icon-col">
        {props.index < props.length - 1 ? (
          <>
            <IonImg className="ellipse_icon" src="assets/icon/origin-pointer.svg" />
            <div className="stops_border"></div>
          </>
        ) : (
          <IonImg className="path_icon" src="assets/icon/dst-pointer.svg" />
        )}
      </IonCol>
      <IonCol class="stop-details-col">
        <p className="origin_city">
          {stopDetail.city}, {stopDetail.state}
        </p>
        <p className="origin_dateTime load_header_elements">{props.appointment}</p>
      </IonCol>
    </IonRow>
  );
};

export const CarrierAssignedModalBase: React.FC<any> = (props: any) => {
  const {
    isOpen,
    onDismiss,
    loadDetails,
    driverDetails,
    showDriverDialog,
    assignShipmentDriverResponse,
    clearAssignShipmentDriverResponse,
    userDetails,
    loadData,
    loadCarrierData,
    loadAssignedrData
  } = props;
  const history = useHistory();

  const driverName =
    (driverDetails && `${driverDetails.firstName || ''}`) || '';
  const shipmentId = (loadDetails && loadDetails.shipmentId) || '';

  const dispatcherDetails = IONICWrapper.details.auth.user;

  const onConfirm = () => {
    const obj: CarrierLoads = new CarrierLoads();

    const _assignDriver: IAssignShipmentToDriverInputProps = {
      carrierId: driverDetails && driverDetails.carrierId,
      dispatcherEmail: dispatcherDetails['signInNames.emailAddress'],
      dispatcherId: dispatcherDetails.upn,
      dispatcherName: `${userDetails.firstName || ''} ${userDetails.lastName || ''}`,
      dispatcherPhone: userDetails && userDetails.mobileNumber,
      driverEmail: driverDetails && driverDetails.email,
      driverId: driverDetails && driverDetails.userPrincipalName,
      driverName,
      driverPhone: driverDetails && driverDetails.mobileNumber,
      shipmentId: loadDetails && loadDetails.shipmentId,
      sourceSystem: loadDetails && loadDetails.sourceTMS,
      tractorNumber: driverDetails && driverDetails.tractorNumber
    };
    props.assignDriverToShipment(obj.assignShipmentToDriver(_assignDriver));
    toggleDialog(false);
    dismissDialog();
    if (isPlatform('desktop') && showDriverDialog) {
      showDriverDialog(false);
    }

    // Analytics implementation
    logEvent(EVENT.UE_ASSIGN_DRIVER_SUCCESS, {
      screen: SCREEN.MYLOADS_DRIVER_ASSIGNMENT,
      carrierId: driverDetails && driverDetails.carrierId
    });
  };

  const dismissDialog = () => {
    if (onDismiss) {
      onDismiss();
    }
  };

  const onCancel = () => {
    dismissDialog();
    if (isPlatform('desktop') && showDriverDialog) {
      showDriverDialog(true);
    }
  };

  let loadStops: any[] = (loadDetails && loadDetails.loadStop) || [];
  let loadStopsOriginDestination: any[] = [];
  loadStops = loadStops.slice().sort((load1: any, load2: any) => {
    if (load1.sequenceNo && load2.sequenceNo) {
      return load1.sequenceNo - load2.sequenceNo;
    }
    if (load1.stopSequence && load2.stopSequence) {
      return load1.stopSequence - load2.stopSequence;
    }
    return 0;
  });

  if (loadStops.length > 2) {
    const totalStops = loadStops.length;
    loadStopsOriginDestination = [{ ...loadStops[0] }, { ...loadStops[totalStops - 1] }];
  } else {
    loadStopsOriginDestination = loadStops;
  }

  const [showAlert, toggleAlert] = useState<boolean>(false);
  const [showDialog, toggleDialog] = useState<boolean>(false);
  const [showError, toggleErrorToast] = useState<boolean>(false);

  useEffect(() => {
    toggleDialog(isOpen);
  }, [isOpen]);

  useEffect(() => {
    if (assignShipmentDriverResponse && assignShipmentDriverResponse.assignDriverToShipment) {
      toggleAlert(true);
      let driverLoad: any[] = [];
      const { shipment } = assignShipmentDriverResponse.assignDriverToShipment;
      if (shipment) {
        driverLoad = loadData.driverLoad.concat({ ...shipment });
        loadAssignedrData(driverLoad);
      }
      if (clearAssignShipmentDriverResponse) {
        clearAssignShipmentDriverResponse();
      }
    } else if (assignShipmentDriverResponse && assignShipmentDriverResponse.errors) {
      toggleErrorToast(true);
    }
  }, [assignShipmentDriverResponse]);

  const handleAlert = () => {
    toggleAlert(false);
    dismissDialog();
    if (loadData && loadData.carrierLoad) {
      const carrierLoad = loadData.carrierLoad.filter(
        (item: any) => item.shipmentId !== loadDetails.shipmentId
      );
      loadCarrierData(carrierLoad);
    }

    history.push('/app/MyLoad/CarrierAssigned');
  };

  const handleError = () => {
    toggleErrorToast(false);
    if (clearAssignShipmentDriverResponse) {
      clearAssignShipmentDriverResponse();
    }
  };
  // console.log("CARRIER", props)
  return (
    <>
      <IonToast
        isOpen={showError}
        onDidDismiss={handleError}
        color="danger"
        duration={5000}
        data-test="ion-toast"
        message={LOAD_ASSIGN_FAIL}
      />
      <IonToast
        isOpen={showAlert}
        onDidDismiss={handleAlert}
        color="dark"
        duration={2000}
        data-test="success-ion-toast"
        message={ LOAD_ASSIGN_SUCCESS.replace("{{LOAD_ID}}", shipmentId).replace('{{DRIVER_NAME}}', driverName)}
      />
      <IonModal isOpen={showDialog} cssClass="assignmodal" backdropDismiss={false}>
        <header>
          <IonLabel color="dark">
            {ASSIGN_DRIVER_POPUP_TITLE.replace("{{LOAD_ID}}", loadDetails && loadDetails.shipmentId).replace('{{DRIVER_NAME}}', driverName)}
          </IonLabel>
        </header>
        <section>
          <IonCard className="ion-card-updated">
            <IonCardContent className="card-content">
              <IonGrid class="load-grid">
                <IonRow className="ion-card-row">
                  <IonCol className="card-price-col" size="4">
                    <LoadTileFooter {...loadDetails} />
                  </IonCol>
                  <IonCol className="card-city-col">
                    {loadStopsOriginDestination &&
                      loadStopsOriginDestination.map((stop: any, index: any) => (
                        <StopDetailsOnDialog
                          key={index}
                          stopDetails={stop}
                          appointment={getAppointmentTime(stop)}
                          length={loadStopsOriginDestination.length}
                          index={index}
                        />
                      ))}
                  </IonCol>
                </IonRow>
              </IonGrid>
            </IonCardContent>
          </IonCard>
        </section>
        <footer>
          <IonCol>
            <IonButton data-test="ion-button-cancel" class="btn load-btn" onClick={onCancel}>
              No
            </IonButton>
          </IonCol>
          <IonCol>
            <IonButton data-test="ion-button-carrier" class="btn load-btn" onClick={onConfirm}>
              Yes
            </IonButton>
          </IonCol>
        </footer>
      </IonModal>
    </>
  );
};
const key = 'carrierAssigned';

const mapStateToProps = createStructuredSelector({
  assignShipmentDriverResponse: getAssignShipmentDriverResponse(),
  userDetails: getUserDetails(),
  loadData: getLoaddata()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  assignDriverToShipment: (data: any) => dispatch(assignDriverToShipment(data)),
  clearAssignShipmentDriverResponse: () => dispatch(clearAssignDriverToShipmentResponse()),
  fetchCarrierAssigned: (data: any) => dispatch(carrierAssignedLoads(data)),
  loadAssignedrData: (data: any[]) => {
    dispatch(loadAssignedrData(data, data.length));
  },
  loadCarrierData: (data: any[]) => {
    dispatch(loadCarrierData(data, data.length));
  }
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export const CarrierAssignedModal = compose<any>(
  withSaga,
  withReducer,
  withConnect
)(CarrierAssignedModalBase);
