import React, { PureComponent } from 'react';
import {
  IonContent,
  IonPage,
  IonIcon,
  IonGrid,
  IonRow,
  IonLabel,
  IonInput,
  IonRadioGroup,
  IonRadio,
  IonItem,
  IonList,
  IonButton,
  IonCol,
  isPlatform
} from '@ionic/react';
import { RegisterFrom, EVENT, SCREEN } from '../../utils/constants';
import './Registration.page.scss';
import {
  getSelectedRoles,
  roles,
  handlePhoneNumber,
  formatPhoneNumber
} from '../Manage/ManageUser/ManageUserUtil';
import NetworkErrorToaster from '../../components/NetworkErrorToaster/NetworkErrorToaster';
import { logEvent } from '../../utils/Analytics';
import { LogOutContainer } from 'app/containers/LogOutContainer';

const textpattern = /^[a-zA-Z0-9 ]*$/;
const numericPattern = /^[0-9]*$/;

class RegisterComponent extends PureComponent<any, any> {
  state = {
    showOptions: false,
    slectedNo: 'carrierUsDotNo',
    firstName: '',
    lastName: '',
    mobileNumber: '',
    companyName: '',
    corporateZipCode: '',
    role: roles,
    allowToSave: false,
    roleError: '',
    usDotError: '',
    mobileNumberError: '',
    zipcodeEror: '',
    firstNameError: '',
    lastNameError: '',
    companyNameError: '',
    USDOTNO: ''
  };

  componentDidMount() {
    // Analytics implementation
    logEvent(EVENT.SV_REGISTRATION, {
      screen: SCREEN.REGISTRATION
    });
  }

  handleValueChange = (event: any) => {
    let { value } = event.target;
    if (
      !textpattern.test(event.target.value) &&
      (event.target.name === 'firstName' || event.target.name === 'lastName')
    ) {
      event.target.value = event.target.value.replace(/[^a-zA-Z0-9 ]*/g, '');
    }

    this.setState({
      [event.target.name]: value
    });
    if (
      this.state.firstName.length > 0 &&
      this.state.lastName.length > 0 &&
      this.state.companyName.length > 0 &&
      this.state.mobileNumber.length > 0 &&
      this.state.corporateZipCode.length > 0 &&
      this.state.USDOTNO.length > 0
    ) {
      this.setState({ allowToSave: true });
    } else {
      this.setState({ allowToSave: false });
    }
  };

  submitForm = () => {
    // Analytics implementation
    logEvent(EVENT.UE_REGISTER_NOW, {
      screen: SCREEN.REGISTRATION
    });

    const userInput = {
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      mobileNumber: this.state.mobileNumber,
      companyName: this.state.companyName,
      corporateZipCode: this.state.corporateZipCode,
      carrierUsDotNo: this.state.slectedNo === 'carrierUsDotNo' ? this.state.USDOTNO : null,
      carrierMcNo: this.state.slectedNo === 'carrierMcNo' ? this.state.USDOTNO : null,
      role: getSelectedRoles(this.state.role)
    };
    if (
      this.state.firstName.length > 0 &&
      this.state.lastName.length > 0 &&
      this.state.companyName.length > 0 &&
      this.state.mobileNumber.length > 0 &&
      this.state.corporateZipCode.length > 0 &&
      this.state.USDOTNO.length > 0 &&
      this.state.roleError === '' &&
      this.state.mobileNumberError === '' &&
      this.state.zipcodeEror === '' &&
      this.state.usDotError === ''
    ) {
      this.props.registerUser(userInput);
    }
  };

  optionClicked = (selectedRole: any) => {
    const roles = this.state.role;
    roles.forEach((role: any) => {
      if (role.label === selectedRole.label) role.selected = !role.selected;
    });

    const selectedValues: any = [];
    this.state.role.forEach((role: any) => {
      if (role.selected === true) selectedValues.push(role.value);
    });
    const selected = getSelectedRoles(this.state.role);
    this.setState({ showOptions: false });
    const event = { target: { name: 'role', value: selected } };
    this.validateFeilds(event);
  };

  validateFeilds = (event: any) => {
    const { name, value } = event.target;
    switch (name) {
      case RegisterFrom.mobileNumber:
        let enteredValue = value.replace(/\s/g, '');
        enteredValue = enteredValue.replace(/\./g, '');
        if (handlePhoneNumber(enteredValue)) {
          const mobileNumber = handlePhoneNumber(value) ? formatPhoneNumber(value, true) : value;
          this.setState({ mobileNumberError: '', mobileNumber });
        } else {
          this.setState({
            mobileNumberError: 'Please Enter a valid cell phone number'
          });
        }
        break;
      case RegisterFrom.corporateZipCode:
        if (textpattern.test(value) && value.length <= 10 && value.length > 0) {
          this.setState({ zipcodeEror: '' });
        } else {
          this.setState({
            zipcodeEror: 'Please Enter a valid company corporate zip code'
          });
        }
        break;
      case 'USDOTNO':
        if (numericPattern.test(value) && value.length >= 6 && value.length <= 8) {
          this.setState({ usDotError: '' });
        } else {
          this.setState({
            usDotError: 'Please Enter a valid company USDOT or MC#'
          });
        }
        break;
      case 'role':
        if (value.length > 0) {
          this.setState({ roleError: '' });
        } else {
          this.setState({ roleError: 'Please select a role' });
        }
        break;
      case RegisterFrom.firstName:
        if (value.length > 0) {
          this.setState({ firstNameError: '' });
        } else {
          this.setState({ firstNameError: 'Please Enter a valid first name' });
        }
        break;
      case RegisterFrom.lastName:
        if (value.length > 0) {
          this.setState({ lastNameError: '' });
        } else {
          this.setState({ lastNameError: 'Please Enter a valid last name' });
        }
        break;
      case RegisterFrom.companyName:
        if (value.length > 0) {
          this.setState({ companyNameError: '' });
        } else {
          this.setState({
            companyNameError: 'Please Enter a valid company name'
          });
        }
        break;
    }
  };

  render() {
    return (
      <IonPage>
        <NetworkErrorToaster />
        {isPlatform('mobile') ? (
          <>
            <IonContent className="registration-page">
              <IonIcon src="assets/icon/Logo.svg" class="logo" />
              <LogOutContainer {...this.props} isRegistrationPage={true}/>
              <IonGrid class="titel-grid"> Register Now</IonGrid>
              <IonList class="registration-form">
                <IonGrid class="select-role-grid">
                  <IonRow class="select-role-label">Type of User</IonRow>

                  <IonRow
                    onClick={() => this.setState({ showOptions: true })}
                    data-test="roleDropdown">
                    <IonCol class="role-name">
                      {this.state.role.map((roles: any, index: any) => (
                        <span key={index}>{roles.selected ? `${roles.label} ` : ''}</span>
                      ))}
                    </IonCol>
                    <IonCol class="dropDown-icon">
                      <IonIcon src="/assets/icon/grid-down-arrow.svg" />
                    </IonCol>
                  </IonRow>
                  {this.state.showOptions ? (
                    <IonGrid class="role-suggetion-grid">
                      {this.state.role.map((roles: any, index: number) => (
                        <IonRow
                          key={index}
                          class={`opt-row ${this.state.role[index].selected ? 'selected' : ''}`}
                          onClick={() => {
                            this.optionClicked(roles);
                          }}
                          data-test="roleOption">
                          <IonCol size="1">
                            <i className="check"></i>
                          </IonCol>
                          <IonCol>
                            <IonRow> {roles.label}</IonRow>
                            <IonRow class="role-desc">{roles.desc}</IonRow>
                          </IonCol>
                        </IonRow>
                      ))}
                    </IonGrid>
                  ) : null}
                </IonGrid>
                <IonGrid class={`errorGrid ${this.state.roleError ? 'showErrorGrid' : ''}`}>
                  {this.state.roleError}
                </IonGrid>
                <IonItem mode="ios">
                  <IonLabel mode="ios" position="floating">
                    First Name
                  </IonLabel>
                  <IonInput
                    type="text"
                    name={RegisterFrom.firstName}
                    pattern="[A-Za-z]"
                    placeholder="Enter Your First Name"
                    value={this.state.firstName}
                    onIonChange={event => this.handleValueChange(event)}
                    onBlur={event => this.validateFeilds(event)}
                    data-test="firstName"
                  />
                </IonItem>
                <IonGrid class={`errorGrid ${this.state.firstNameError ? 'showErrorGrid' : ''}`}>
                  {this.state.firstNameError}
                </IonGrid>
                <IonItem mode="ios">
                  <IonLabel mode="ios" position="floating">
                    Last Name
                  </IonLabel>
                  <IonInput
                    type="text"
                    name={RegisterFrom.lastName}
                    placeholder="Enter Your Last Name"
                    onIonChange={event => this.handleValueChange(event)}
                    pattern="[A-Za-z]"
                    value={this.state.lastName}
                    onBlur={event => this.validateFeilds(event)}
                    data-test="lastName"
                  />
                </IonItem>
                <IonGrid class={`errorGrid ${this.state.lastNameError ? 'showErrorGrid' : ''}`}>
                  {this.state.lastNameError}
                </IonGrid>
                <IonItem mode="ios">
                  <IonLabel mode="ios" position="floating">
                    Cell Phone Number
                  </IonLabel>
                  <IonInput
                    type="text"
                    name={RegisterFrom.mobileNumber}
                    placeholder="Enter Your Cell Phone Number"
                    onIonChange={event => this.handleValueChange(event)}
                    value={this.state.mobileNumber}
                    onBlur={event => this.validateFeilds(event)}
                    data-test="mobileNumber"
                  />
                </IonItem>
                <IonGrid class={`errorGrid ${this.state.mobileNumberError ? 'showErrorGrid' : ''}`}>
                  {this.state.mobileNumberError}
                </IonGrid>
                <IonItem mode="ios">
                  <IonLabel mode="ios" position="floating">
                    Company Name
                  </IonLabel>
                  <IonInput
                    type="text"
                    name={RegisterFrom.companyName}
                    placeholder="Enter Your Company Name"
                    onIonChange={event => this.handleValueChange(event)}
                    value={this.state.companyName}
                    onBlur={event => this.validateFeilds(event)}
                    data-test="companyName"
                  />
                </IonItem>
                <IonGrid class={`errorGrid ${this.state.companyNameError ? 'showErrorGrid' : ''}`}>
                  {this.state.companyNameError}
                </IonGrid>
                <IonItem mode="ios">
                  <IonLabel mode="ios" position="floating">
                    Company Corporate Zip Code
                  </IonLabel>
                  <IonInput
                    type="text"
                    name={RegisterFrom.corporateZipCode}
                    placeholder="Enter Your Company Corporate Zip Code"
                    onIonChange={event => this.handleValueChange(event)}
                    pattern="[A-Za-z]"
                    value={this.state.corporateZipCode}
                    onBlur={event => this.validateFeilds(event)}
                    data-test="corporateZipCode"
                  />
                </IonItem>
                <IonGrid class={`errorGrid ${this.state.zipcodeEror ? 'showErrorGrid' : ''}`}>
                  {this.state.zipcodeEror}
                </IonGrid>
                <IonItem mode="ios">
                  <IonLabel mode="ios" position="floating">
                    Company USDOT or MC#
                  </IonLabel>
                  <IonRadioGroup
                    onIonChange={event => this.handleValueChange(event)}
                    name="slectedNo"
                    value={this.state.slectedNo}>
                    <IonRow>
                      <IonCol>
                        <IonRadio value="carrierUsDotNo" class="radiobutton" mode="md" />
                      </IonCol>
                      <IonCol class="radiobutton-label"> USDOT </IonCol>
                      <IonCol>
                        <IonRadio value="carrierMcNo" class="radiobutton" mode="md" />
                      </IonCol>
                      <IonCol class="radiobutton-label">MC#</IonCol>
                    </IonRow>
                  </IonRadioGroup>
                  <IonInput
                    type="number"
                    name="USDOTNO"
                    placeholder="Enter Your Company USDOT or MC#"
                    onIonChange={event => this.handleValueChange(event)}
                    maxlength={8}
                    value={this.state.USDOTNO}
                    onBlur={event => this.validateFeilds(event)}
                    data-test="USDOTNO"
                  />
                </IonItem>
                <IonGrid class={`errorGrid ${this.state.usDotError ? 'showErrorGrid' : ''}`}>
                  {this.state.usDotError}
                </IonGrid>
              </IonList>
            </IonContent>
            <div className="bottom-grid">
              <IonButton
                expand="full"
                disabled={!this.state.allowToSave}
                className={`${this.state.allowToSave ? 'active-btn' : 'register-btn'}`}
                onClick={this.submitForm}
                data-test="registerButton">
                Register Now
              </IonButton>
            </div>
          </>
        ) : (
          <IonContent className="registration-page">
            <IonIcon src="assets/icon/Logo.svg" class="logo" />
            <IonGrid class="titel-grid"> Register Now</IonGrid>
            <IonList class="registration-form">
              <IonRow>
                <IonCol className="registration-form-col">
                  <IonGrid class="select-role-grid">
                    <IonRow class="select-role-label">Type of User</IonRow>

                    <IonRow onClick={() => this.setState({ showOptions: true })}>
                      <IonCol class="role-name">
                        {this.state.role.map((roles: any, index: any) => (
                          <span key={index}>{roles.selected ? `${roles.label} ` : ''}</span>
                        ))}
                      </IonCol>
                      <IonCol class="dropDown-icon">
                        <IonIcon src="/assets/icon/grid-down-arrow.svg" />
                      </IonCol>
                    </IonRow>

                    {this.state.showOptions ? (
                      <IonGrid class="role-suggetion-grid" data-test="capacity-type-list">
                        {this.state.role.map((roles: any, index: number) => (
                          <IonRow
                            key={index}
                            class={`opt-row ${roles.selected ? 'selected' : ''}`}
                            onClick={() => {
                              this.optionClicked(roles);
                            }}>
                            <IonCol size="1" className="opt-col-i">
                              <i className="check"></i>
                            </IonCol>
                            <IonCol>
                              <IonRow> {roles.label}</IonRow>
                              <IonRow class="role-desc">{roles.desc}</IonRow>
                            </IonCol>
                          </IonRow>
                        ))}
                      </IonGrid>
                    ) : null}
                  </IonGrid>
                  <IonGrid class="errorGrid">{this.state.roleError}</IonGrid>
                  <IonItem mode="ios">
                    <IonLabel mode="ios" position="floating">
                      Last Name
                    </IonLabel>
                    <IonInput
                      type="text"
                      name="lastName"
                      placeholder="Enter Your Last Name"
                      onIonChange={event => this.handleValueChange(event)}
                      pattern="[A-Za-z]"
                      value={this.state.lastName}
                      onBlur={event => this.validateFeilds(event)}
                    />
                  </IonItem>
                  <IonGrid class="errorGrid">{this.state.lastNameError}</IonGrid>
                  <IonItem mode="ios">
                    <IonLabel mode="ios" position="floating">
                      Company Name
                    </IonLabel>
                    <IonInput
                      type="text"
                      name="companyName"
                      placeholder="Enter Your Company Name"
                      onIonChange={event => this.handleValueChange(event)}
                      value={this.state.companyName}
                      onBlur={event => this.validateFeilds(event)}
                    />
                  </IonItem>
                  <IonGrid class="errorGrid">{this.state.companyNameError}</IonGrid>
                  <IonItem mode="ios">
                    <IonLabel mode="ios" position="floating">
                      Company USDOT or MC#
                    </IonLabel>
                    <IonRadioGroup
                      onIonChange={event => this.handleValueChange(event)}
                      name="slectedNo"
                      value={this.state.slectedNo}>
                      <IonRow>
                        <IonCol>
                          <IonRadio value="carrierUsDotNo" class="radiobutton" mode="md" />
                        </IonCol>
                        <IonCol class="radiobutton-label"> USDOT </IonCol>
                        <IonCol>
                          <IonRadio value="carrierMcNo" class="radiobutton" mode="md" />
                        </IonCol>
                        <IonCol class="radiobutton-label">MC#</IonCol>
                      </IonRow>
                    </IonRadioGroup>
                    <IonInput
                      type="number"
                      name="USDOTNO"
                      placeholder="Enter Your Company USDOT or MC#"
                      onIonChange={event => this.handleValueChange(event)}
                      maxlength={8}
                      value={this.state.USDOTNO}
                      onBlur={event => this.validateFeilds(event)}
                    />
                  </IonItem>
                  <IonGrid class="errorGrid">{this.state.usDotError}</IonGrid>
                </IonCol>
                <IonCol className="registration-col">
                  <IonItem mode="ios">
                    <IonLabel mode="ios" position="floating">
                      First Name
                    </IonLabel>
                    <IonInput
                      type="text"
                      name="firstName"
                      pattern="[A-Za-z]"
                      placeholder="Enter Your First Name"
                      value={this.state.firstName}
                      onIonChange={event => this.handleValueChange(event)}
                      onBlur={event => this.validateFeilds(event)}
                    />
                  </IonItem>
                  <IonGrid class="errorGrid">{this.state.firstNameError}</IonGrid>
                  <IonItem mode="ios">
                    <IonLabel mode="ios" position="floating">
                      Cell Phone Number
                    </IonLabel>
                    <IonInput
                      type="text"
                      name="mobileNumber"
                      placeholder="Enter Your Cell Phone Number"
                      onIonChange={event => this.handleValueChange(event)}
                      value={this.state.mobileNumber}
                      onBlur={event => this.validateFeilds(event)}
                    />
                  </IonItem>
                  <IonGrid class="errorGrid">{this.state.mobileNumberError}</IonGrid>
                  <IonItem mode="ios">
                    <IonLabel mode="ios" position="floating">
                      Company Corporate Zip Code
                    </IonLabel>
                    <IonInput
                      type="text"
                      name="corporateZipCode"
                      placeholder="Enter Your Company Corporate Zip Code"
                      onIonChange={event => this.handleValueChange(event)}
                      pattern="[A-Za-z]"
                      value={this.state.corporateZipCode}
                      onBlur={event => this.validateFeilds(event)}
                    />
                  </IonItem>
                  <IonGrid class="errorGrid">{this.state.zipcodeEror}</IonGrid>
                </IonCol>
              </IonRow>
              <div className="bottom-grid">
                <IonRow class="bottom-grid-row">
                  <IonCol>
                  <LogOutContainer {...this.props} isRegistrationPage={true}/>
                  </IonCol>
                  <IonCol>
                    <IonButton
                      expand="full"
                      disabled={!this.state.allowToSave}
                      className={`${this.state.allowToSave ? 'active-btn' : 'register-btn'}`}
                      onClick={this.submitForm}>
                      Register Now
                    </IonButton>
                  </IonCol>
                </IonRow>
              </div>
            </IonList>
          </IonContent>
        )}
      </IonPage>
    );
  }
}
export default RegisterComponent;
