import { Roles } from '../../../utils/constants';

/**
 * Get Address List from Azure
 * @param value
 * @param callback
 */
export const fetchAddressList = async (value: any, callback: Function) => {
  if (value.length > 2) {
    await AzureMapsWrapper.details.searchAddress(value, (results: any) => {
      const list = results.map((item: any) => ({
        addressLine1: item.address.streetName || '',
        addressLine2: '',
        addressType: '',
        city: item.address.municipality || '',
        state: item.address.countrySubdivision || '',
        postalCode: item.address.postalCode || ''
      }));
      callback(list);
    });
  }
};

/**
 * Should Allow Alpha numberic text only
 * @param event
 */
export const handleAlphaNumeric = (value: string): string => {
  return (value && value.replace(/[^a-zA-Z0-9 ]*/g, '')) || '';
};

/**
 * Validate Phone Number
 * @param value
 */
export const handlePhoneNumber = (value: string): boolean => {
  return /^([0-9]{10})$/.test(value);
};

export const isValidString = (value: string): boolean => {
  return !/[~`!#@$%\^&*+=\-\[\]\\';.,/(){}|\\"\_:<>\?]/g.test(value);
};
/**
 * Should allow only Numbers
 * @param event
 */
export const numberOnlyValidation = (value: string): string => {
  return (value && value.replace(/[^0-9+]*/g, '')) || '';
};

/**
 * Format Phone Numbers
 * @param phone
 */
export const formatPhoneNumber = (phone: string, isCountryCode = false) => {
  const cleaned = `${phone}`.replace(/\D/g, '');
  const match = cleaned.match(/^(1|91|52)?(\d{3})(\d{3})(\d{4})$/);
  if (match) {
    const intlCode = match[1] ? `+${match[1]}` : '';
    if (isCountryCode) {
      return [intlCode, ' ', match[2], '.', match[3], '.', match[4]].join('');
    }
    return [match[2], '.', match[3], '.', match[4]].join('');
  }
  return null;
};

/**
 * Remove Special Charters
 *
 * @param phone
 */
export const removeSpecial = (phone: string) => {
  return phone && phone.replace(/[ ,.]/g, '');
};

