/**
 * Get Selecte Roles with enum type
 * @param roles
 */
export const getSelectedRoles = (roles: Array<IRole>) => {
  return roles.filter(role => role.selected).map(role => new EnumType(role.value));
};

/**
 * Get Formatted Address
 * @param value
 */
export const getAddressValue = (value: IAddress) => {
	return `${value.addressLine1}${value.addressLine2 && (', ' + value.addressLine2)}${value.city && (', ' + value.city)}${value.state && (', ' + value.state)}${value.postalCode && (', ' + value.postalCode)}`.replace(/(^,)|(,$)/g, "");
};
