/**
     *  This method provides the current location latitude and longitude
     * * @param {*} get CurrentGPS callback to process the current location lat, long.
     * @returns null.
     */
export async function getCurrentPosition(getCurrentGPS: any) {
    BackgroundGeolocation.getCurrentPosition({ persist: false }, (location: any) => {
        getCurrentGPS(location.coords.latitude,
            location.coords.longitude);
    }, (error) => {
        // Error
    });
};

/**
     *  This will start the GPS tracking as well as geofence tracking.
     * * @param shipmentDetails to strat the GPS tracking as well as geofence
     *  i.g. activity,shipmentId, carrierScac, userId, sourceSystem
     * @returns null.
     */
export function startGPStracking(shipmentDetails: any) {

    GPS_Tracking_ShipmentID = shipmentDetails.shipmentID;
    validateLoadDetails(shipmentDetails.loadStops);
    

    if (loadDetailsTrackedShipment) {
            cogfigureActiveGeofence();
        }
    const session: Session = new Session();
    session.getFreshToken().then(function (_token) {
        __vault = _token;
        BackgroundGeolocation.setConfig({
            distanceFilter: distanceFilter, //Approximately 800 meters
            disableElasticity: true,
            url: locationURL,
            headers: {
                'Ocp-Apim-Subscription-Key': KEYS.SUBSCRIPTION_KEY,
                "Authorization": "Bearer " + __vault
            },
            extras: {
                messageType: MESSAGE_TYPE.GEO_POSITION,
                activity: shipmentDetails.activity,
                shipmentId: shipmentDetails.shipmentID,
                carrierScac: shipmentDetails.carrierScac,
                userId: shipmentDetails.userId,
                sourceSystem: shipmentDetails.sourceTMS,
                carrierId: shipmentDetails.carrierId,
                stopId: stopId,
                sequenceNo: sequenceNo,
                locationId: locationId
            }
        }).then((state) => {
            BackgroundGeolocation.start().then(() => {
                console.log("BackgroundGeolocation: started ", state);
            });
        });
    });
}

function getGeofenceObjects(loadStops: any) {
    const getGeoFence = (loadStop: any, isPreArrivalFence: boolean) => ({
        identifier: (isPreArrivalFence ? (loadStop.stopId + "_" + GEOFENCE_IDENTIFIER.PRE_ARRIVAL) : (loadStop.stopId + "_" + GEOFENCE_IDENTIFIER.ARRIVAL)),
        radius: isPreArrivalFence ? KEYS.GEO_PRE_ARRIVAL_RADIUS : GEO_ARRIVAL_RADIUS,
        latitude: loadStop.latitude,
        longitude: loadStop.longitude,
        notifyOnEntry: true,
        notifyOnExit: true,
        extras: {
            messageType: isPreArrivalFence ? MESSAGE_TYPE.GEOFENCE_PRE_ARRIVAL : MESSAGE_TYPE.GEOFENCE_ARRIVAL,
            stopId: loadStop.stopId,
            sequenceNo: loadStop.sequenceNo,
            locationId: loadStop.locationId
        }
    })

    const geofences: Array<Geofence> = [];
    loadStops.map((loadStop: any, index: any) => {
        if (loadStop.sequenceNo == sequenceNo) {
            const preArrivalFrnce = getGeoFence(loadStop, true);
            geofences.push(preArrivalFrnce);
            const arrivalFrnce = getGeoFence(loadStop, false);
            geofences.push(arrivalFrnce);
        }

    });
    return geofences;
}

function updateArrivalTime(currentSequenceNo: any) {
    for (let i = 0; i < loadDetailsTrackedShipment.length; i++) {
        if (loadDetailsTrackedShipment[i].sequenceNo == currentSequenceNo) {
            loadDetailsTrackedShipment[i].actualArrivalDateTime = new Date().toISOString();
        }
    }
}

function updateDepartureTime(currentSequenceNo: any) {
    let isLastStop = false;
    for (let i = 0; i < loadDetailsTrackedShipment.length; i++) {
        if (loadDetailsTrackedShipment[i].sequenceNo == currentSequenceNo) {
            if (loadDetailsTrackedShipment[i].actualArrivalDateTime) {
                loadDetailsTrackedShipment[i].actualDepartureDateTime = new Date().toISOString();
                if (loadDetailsTrackedShipment[i].stopType == "D"
                    && loadDetailsTrackedShipment.length == currentSequenceNo) {

                    isLastStop = true;
                    BackgroundGeolocation.stop().then(() => {
                        console.log("- stop success");
                        removeItem(DB_GPS_ACTIVATED_SHIPMENT).then(() => {
                            console.log("- data removed from DB");
                        });
                    });
                    BackgroundGeolocation.removeGeofences();   //Destroy all [[Geofence]].
                }
            }
        }
    }
    if (!isLastStop) {
        cogfigureActiveGeofence();
    }
}

async function cogfigureActiveGeofence() {
    // Only 1 pair of geo-fences to be active at any point of time.
    // Once current Stop will be departed, next Stop geofence pair will be activated
    // Remove Geofences before add new Geofences
    let updatedsequenceNo = updateActiveGeofenceSequenceNo();

    let currentSequenceNo;
    let geofences = await BackgroundGeolocation.getGeofences();
    geofences.forEach((geofence: any, index: any) => {
        currentSequenceNo = geofence.extras.sequenceNo;
    });
    if (currentSequenceNo != updatedsequenceNo) {  // this check to avoid multiple execution for same load in componentDidUpdate

        // Before Configure the Geofence Clean the Arrival Geofence Stationary timer and data
        clearArrivalGeofenceStationary();
  
        sequenceNo = updatedsequenceNo;
        BackgroundGeolocation.removeGeofences();
        const activeGeofences = getGeofenceObjects(loadDetailsTrackedShipment);
        // Add a geofences.
        if (activeGeofences.length > 0) {  // Geofences will be added when activeGeofences has data
            BackgroundGeolocation.addGeofences(activeGeofences, (success: any) => {
                //console.log("[Geofences] - Added geofences" + JSON.stringify(activeGeofences));
                //alert("[Geofences] - Added geofences" + JSON.stringify(activeGeofences));
            }, (error: any) => {
                //console.log("[Geofences] - ", 'Add geofences error', error);
                //alert("[Geofences] - Add geofences error ::: " + error);

            });
        }
    } else {
        for(let index=0; index< loadDetailsTrackedShipment.length; index++) {
            if (loadDetailsTrackedShipment[index].sequenceNo == currentSequenceNo 
                && loadDetailsTrackedShipment[index].actualArrivalDateTime){
                     // Before Configure the Geofence Clean the Arrival Geofence Stationary timer and data
                    clearArrivalGeofenceStationary();
            }
        }
    }
}

/**
     *  This will update the active Geofence sequenceNo
     * * @param
     * @returns null.
     */
function updateActiveGeofenceSequenceNo() {
    let stopSequenceNo = 99;
    for (var i = 0; i < loadDetailsTrackedShipment.length; i++) {
        if ((!loadDetailsTrackedShipment[i].actualArrivalDateTime
            || !loadDetailsTrackedShipment[i].actualDepartureDateTime)) {
            if (stopSequenceNo > loadDetailsTrackedShipment[i].sequenceNo) {
                stopSequenceNo = loadDetailsTrackedShipment[i].sequenceNo;
                stopId = loadDetailsTrackedShipment[i].stopId;
                locationId = loadDetailsTrackedShipment[i].locationId;      

            }
        }
    }
    return stopSequenceNo;
}

/**
     *  This will send pings for Geo Start Trip Location
     * * @param shipmentDetails to send the Geo Start Trip Location
     *  i.g. activity,shipmentId, carrierScac, userId, sourceSystem
     * @returns null.
     */
export function sendGeoStartTripLocation(shipmentDetails: any) {
    BackgroundGeolocation.getCurrentPosition({
        timeout: 30,          // 30 second timeout to fetch location
        maximumAge: 5000,     // Accept the last-known-location if not older than 5000 ms.
        desiredAccuracy: 10,  // Try to fetch a location with an accuracy of `10` meters.
        samples: 1,           // How many location samples to attempt.});
        extras: {             // Custom meta-data.
            messageType: MESSAGE_TYPE.GEO_START_TRIP_LOCATION,
            activity: shipmentDetails.activity,
            shipmentId: shipmentDetails.shipmentID,
            carrierScac: shipmentDetails.carrierScac,
            userId: shipmentDetails.userId,
            sourceSystem: shipmentDetails.sourceTMS,
            carrierId: shipmentDetails.carrierId,
            stopId: stopId,
            sequenceNo: sequenceNo,
            locationId: locationId
        }
    });
}

