import { IonContent, IonPage, IonGrid, IonImg, IonModal } from "@ionic/react";
import React, { Component } from "react";
import AppHeader from "../../components/app/Bars/Bar-header";
import "./MyloadCommonPage.page.scss";
import { DriverModal } from "../../pages/Execute/CarrierAssigned/DriverModalDesktop";
import DataTableComponent from "../../components/shared/dataTableComponent/data-tabel.page";
import { formatedData } from "../../utils/formatData";
import DriverNameFilter from "../../components/shared/CarrierAssignFilter/DriverNameFilter";
import { DriverList } from "../../models/home/CarrierAssign.model";
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  MODULES,
  EVENT,
  SCREEN,
  OFFLINE_MODE_STATE
} from "../../utils/constants";
import { CarrierAssignedModal } from "../../containers/ExecuteContainer/CarrierAssignedModal/CarrierAssignedModal";
import driverNames from "../../utils/getDriverNameList";
import sortLoads from "../../utils/getSortedLoads";
import { getApplicationInfo, logEvent } from "../../utils/Analytics";
import { GetShipment } from "../../models/home/GetShipment.model";

class MyloadPage extends Component<any, any> {
  segmentName: any;

  _model = new DriverList();

  segments = this.props.segment;

  titleText: any;

  loads: any;

  actualLoad: any;

  state = {
    segment: { name: this.props.currentSegment },
    isDriverModal: false,
    popupHeader: false,
    data: [],
    openAssignDriverDialog: false,
    driverDetails: {},
    loadDetails: {},
    driverAFilterData: [],
    isDriverFiltered: false,
    inTranistFilterData: [],
    isTranistFiltered: false,
    deliveredFilterData: [],
    deliveredFiltered: false,
    filteredDriverNameD: "All Driver",
    filteredDriverNameI: "All Driver",
    filteredDriverName: "All Driver",
    module: ""
  };

  columns = [
    {
      name: "Origin",
      selector: "origin_city",
      sortable: false,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },
    {
      name: "Origin Date",
      selector: "origin_from_date",
      sortable: false,
      minWidth: "123px"
    },
    {
      name: "DH-O",
      selector: "origin_deadhead",
      sortable: false
    },
    {
      name: "Origin Load Type",
      selector: "origin_load_type",
      sortable: false
    },
    {
      name: "Destination",
      selector: "destination_city",
      sortable: false,
      style: {
        fontWeight: 600,
        fontSize: 14,
        colorName: "#000000"
      }
    },
    {
      name: "Destination Date",
      selector: "destination_from_date",
      minWidth: "123px",
      sortable: false
    },
    {
      name: "DH-D",
      selector: "destination_deadhead",
      sortable: false
    },
    {
      name: "Destination Load Type",
      selector: "destination_load_type",
      sortable: false
    },
    {
      name: "Trailer Type",
      selector: "trailer",
      sortable: false
    },
    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) =>
        this.state.segment.name ===
        "Carrier Assigned" ? null : row.driverName ? (
          <IonGrid className="driver-name-grid">{row.driverName}</IonGrid>
          ) : null
    },
    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) =>
        this.state.segment.name === "Carrier Assigned" ? (
          <IonImg
            onClick={() => this.selectDriver(row)}
            src="assets/icon/add-user-icon-desktop.svg"
            class="assign-icon"
          />
        ) : (
          <IonImg src="assets/icon/re-assign-desktop.svg" class="assign-icon" />
        )
    },
    {
      name: "",
      selector: "",
      sortable: false,
      width: "50px",
      cell: (row: any) =>
        this.props.pageName === "loadDetails" ? (
          ""
        ) : (
          <IonGrid class="datatable-badge">
            <i></i>
          </IonGrid>
        )
    }
  ];

  selectDriver = (row: any) => {
    this.props.AssignDriver(this._model.getRequestPayload());
    this.setState({ isDriverModal: true, popupHeader: true });
    const loadDetails = this.loads.filter((load: any) => {
      return load.shipmentId === row.loadId;
    });
    this.setState({ loadDetails: loadDetails && loadDetails[0] });
  };

  closeDriverModal = () => {
    this.setState({ isDriverModal: false });
  };

  redirect = (load: any) => {
    if (this.props.isNetworkAvailable || OFFLINE_MODE_STATE) {
      let pageName = this.state.segment.name.replace(/\s/g, "");
      if (pageName === "LoadsAssigned") {
        pageName = "DriverAssigned";
      }
      if (pageName === "In-Transit") {
        pageName = "InTransit";
      }
      if (pageName === "RecentlyDeliveredLoads") {
        pageName = "Delivered";
      }
      this.props.history.push(`${pageName}/${load.loadId}`);
      if (this.props.buyLoadShipmentRequest) {
        const __getShipmentmodel: GetShipment = new GetShipment();
        const { module } = this.state;
        __getShipmentmodel.shipmentIds = load.loadId;
        this.props.buyLoadShipmentRequest({
          query: __getShipmentmodel.getRequestShipmentPayload(),
          shipmentId: load.loadId,
          module,
          isPullToRefresh: false
        });
      }
    } else {
      this.props.showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK
      });
    }
  };

  filterByDriver = (filteredData: any, dispalyText: string) => {
    if (
      this.state.segment.name === "Driver Assigned" ||
      this.state.segment.name === "Loads Assigned"
    ) {
      this.setState(
        {
          driverAFilterData: filteredData,
          isDriverFiltered: true,
          filteredDriverNameD: dispalyText
        },
        this.getRelatedLoads
      );
    }
    if (this.state.segment.name === "In-Transit") {
      this.setState(
        {
          inTranistFilterData: filteredData,
          isTranistFiltered: true,
          filteredDriverNameI: dispalyText
        },
        this.getRelatedLoads
      );
    }
    if (this.state.segment.name === "Recently Delivered Loads") {
      this.setState(
        {
          deliveredFilterData: filteredData,
          deliveredFiltered: true,
          filteredDriverName: dispalyText
        },
        this.getRelatedLoads
      );
    }
  };

  componentDidMount = () => {
    this.getRelatedLoads();
    this.getModuleName();
  };

  componentDidUpdate = (prevProps: any, prevState: any) => {
    if (prevProps.currentSegment !== this.props.currentSegment) {
      this.setState({ segment: { name: this.props.currentSegment } });
    }
    if (
      JSON.stringify(prevState.segment) !== JSON.stringify(this.state.segment)
    ) {
      this.getModuleName();
    }
  };

  getModuleName = () => {
    const pageName = this.state.segment.name.replace(/\s/g, "");
    let module = "";
    if (pageName === "LoadsAssigned" || pageName === "DriverAssigned") {
      module = MODULES.DRIVER_ASSIGNED_MODULE;
    }
    if (pageName === "In-Transit") {
      module = MODULES.INTRANSIT_MODULE;
    }
    if (pageName === "RecentlyDeliveredLoads") {
      module = MODULES.RECENTLY_DELIVERED_MODULE;
    }
    this.setState({ module });
  };

  getRelatedLoads = () => {
    if (this.state.segment.name === "Carrier Assigned") {
      this.loads = this.props.loads;
      this.titleText = (
        <>
          {this.loads && this.loads.length > 99 ? 99 : this.loads.length} Loads
          Assigned
        </>
      );
    } else if (
      this.state.segment.name === "Driver Assigned" ||
      this.state.segment.name === "Loads Assigned"
    ) {
      this.loads = this.state.isDriverFiltered
        ? this.state.driverAFilterData
        : this.props.driverAssigned;
      this.actualLoad = this.props.driverAssigned;
      this.getSubHeader("Loads Assigned", this.state.segment.name);
      // Analytics implementation
      logEvent(EVENT.UE_BOOK_SUCCESS, {
        screen: SCREEN.LOAD_DETAILS_RATE,
        loadId: this.props.loadId,
        carrierId: this.props.userDetails.carrierId,
        lead: "searched" // TODO "<"recommended" or "watched" or "searched">"
      });
    } else if (this.state.segment.name === "In-Transit") {
      this.loads = this.state.isTranistFiltered
        ? this.state.inTranistFilterData
        : this.props.inTransitLoads;
      this.actualLoad = this.props.inTransitLoads;
      this.getSubHeader("Loads In-Transit", this.state.segment.name);
    } else if (this.state.segment.name === "Recently Delivered Loads") {
      this.loads = this.state.deliveredFiltered
        ? this.state.deliveredFilterData
        : this.props.deliveredLoads;
      this.actualLoad = this.props.deliveredLoads;
      this.getSubHeader("Loads Delivered", this.state.segment.name);
    } else {
      this.loads = [];
      this.titleText = "";
    }
    this.getFormatedLoads(this.loads);
  };

  getSubHeader = (countText: string, segmentName: string) => {
    const driverNameList: any = driverNames(this.actualLoad, this.state.module);
    this.titleText = (
      <DriverNameFilter
        data={this.actualLoad}
        driverNameList={driverNameList}
        onSelect={this.filterByDriver}
        countText={
          this.loads && this.loads.length > 99
            ? 99
            : `${this.loads.length} ${countText}`
        }
        segmentName={segmentName}
        filteredDriverNameI={this.state.filteredDriverNameI}
        filteredDriverNameD={this.state.filteredDriverNameD}
        filteredDriverName={this.state.filteredDriverName}
        module={this.state.module}
      />
    );
  };

  getFormatedLoads = (load: any) => {
    load = sortLoads(load);
    load = load && load.length > 99 ? load.slice(0, 99) : load;
    const data = load
      ? formatedData(
        load,
        "appointmentEarlyDateTime",
        "appointmentLateDateTime",
        "shipmentId",
        "equipmentType",
        true
      )
      : [];
    this.setState({ data });
  };

  render() {
    return (
      <IonPage className="desktop-page my-load-desktop-page">
        <AppHeader
          title="My Loads"
          segments={this.segments}
          getSegment={(e: any) =>
            this.setState(
              { segment: { name: e.detail.value as any } },
              this.getRelatedLoads
            )
          }
          activeSegment={this.state.segment && this.state.segment.name}
          isMyLoad
        />
        <IonContent className="my-load-desktop-content">
          <DataTableComponent
            columns={this.columns}
            data={this.state.data}
            showEndofList
            showHeader={false}
            titleText={this.titleText}
            pagination
            redirect={this.redirect}
          />
          <IonModal
            isOpen={this.state.isDriverModal}
            cssClass="Driver-list-modal"
          >
            <DriverModal
              popupHeader={this.state.popupHeader}
              closeModal={() => this.closeDriverModal()}
              DesktopDriverList={this.props.driverList}
              {...this.props}
              showAssignDriverDialog={(isShow: boolean, driverDetails: any) => {
                this.setState({
                  openAssignDriverDialog: isShow,
                  driverDetails,
                  isDriverModal: isShow ? false : this.state.isDriverModal
                });
              }}
            />
          </IonModal>
          <CarrierAssignedModal
            isOpen={this.state.openAssignDriverDialog}
            onDismiss={() => {
              this.setState({
                openAssignDriverDialog: false
              });
            }}
            loadDetails={this.state.loadDetails}
            driverDetails={this.state.driverDetails}
            showDriverDialog={(isOpen: boolean) => {
              this.setState({ isDriverModal: isOpen });
            }}
          />
        </IonContent>
      </IonPage>
    );
  }
}
export default MyloadPage;
