export class CarrierProfileModel {
  /**
   * Get Capacity List
   * @param commonCarrierId
   * @param page
   * @param size
   */
  getProfile(carrierId: string) {
    return {
      query: {
        getCarrierProfile: {
          __args: {
            input: {
              carrierId
            }
          },
          carrierName: true,
          carrierMCNo: true,
          carrierUsDOTNo: true,
          commonCarrierId: true,
          carrierScac: true,
          carrierTier: true,
          carrierStatusCode: true,
          addressLine1: true,
          addressLine2: true,
          city: true,
          state: true,
          country: true,
          postalCode: true,
          carrierModeTruckloadInd: true,
          carrierModeLTLInd: true,
          carrierModeIMInd: true,
          primaryContact: {
            contactFirstName: true,
            contactLastName: true,
            contactType: true,
            contactPhoneNumber: true,
            contactFaxNumber: true,
            contactEmail: true
          },
          otherContacts: {
            contactFirstName: true,
            contactLastName: true,
            contactType: true,
            contactPhoneNumber: true,
            contactFaxNumber: true,
            contactEmail: true
          },
          insurances: {
            insuranceId: true,
            policyNumber: true,
            coverageAmount: true,
            maxInsuranceLimit: true,
            insuranceTypeCode: true,
            insuranceTypeDesc: true,
            insuranceStatusCode: true,
            insuranceCertificateId: true,
            policyEffectiveDate: true,
            policyExpirationDate: true
          },
          certifications: {
            certificationCode: true,
            certificationDesc: true,
            certificateExpireDate: true,
            qualificationStatus: true
          }
        }
      }
    };
  }
}
