import React, { useState, useEffect } from "react";
import {
  IonGrid,
  IonImg,
  IonRow,
  IonCol,
  IonIcon,
  IonButton,
  isPlatform,
  IonModal,
} from "@ionic/react";
import { IShipmentStopDetailsProps } from "../../../schemas/StartTrip/StartTrip";
import { ETA } from "./eta";
import { Arrival } from "./arrival";
import { Departure } from "./departure";
import { TrailerIn } from "./trailerIn";
import { TrailerOut } from "./trailerOut";
import { ReferenceDialog } from "./referenceDialog";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import {
  UPDATE_STOP_CTA_TEXT,
  STOP_OFF_TEXT,
  EXECUTION_ERRORS,
  EVENT,
  SCREEN,
  TRAILER_PARTTEN,
  OTMASSET,
  CALL_US_NUMBER,
} from "../../../utils/constants";
import {
  StopInformation,
  EquipmentFlow,
} from "../../../schemas/Exicution/exicution.schema";
import { logEvent } from "../../../utils/Analytics";
import { getAppointmentTime } from "../../../utils/formatData";
import { CallNumber } from "@ionic-native/call-number";
import {
  StopActivityProps,
  defaultStopActivity,
  getStopActivity,
} from "../../../models/stopActivity.model";

export const ShipmentStopDetails: React.FunctionComponent<IShipmentStopDetailsProps> = (
  props: IShipmentStopDetailsProps
) => {
  const stopDetail = props.stopDetails;
  const exicutionProgress = props.exicutionProgress;
  const startTripModel = new StartTripModel();
  const [isExpanded, toggleLoadDetails] = useState(false);
  const [isEndExecution, setendExecution] = useState(false);
  const [ETA_VALUE, setETA] = useState<string>("");
  const [ARRIVAL_VALUE, setARRIVAL] = useState<string>("");
  const [DEPARTURE_VALUE, setDEPARTURE] = useState<string>("");
  const [TRAILER_IN, setTRAILERIN] = React.useState("");
  const [TRAILER_OUT, setTRAILEROUT] = React.useState("");
  const [
    TRAILER_IN_ACTIVITY,
    setTRAILERINACTIVITY,
  ] = React.useState<StopActivityProps | null>(null);
  const [
    TRAILER_OUT_ACTIVITY,
    setTRAILEROUTACTIVITY,
  ] = React.useState<StopActivityProps | null>(null);
  const [showProgressBar, setShowProgressBar] = useState(false);
  const [showDotteedLine, setShowDottedLine] = useState(false);
  const [progressBarHeight, setProgressBarHeight] = useState("0px");
  const [isErrorOnArrival, onArrivalErrorChange] = useState<boolean>(false);
  const [isErrorOnDeparture, onDepartureErrorChange] = useState<boolean>(false);
  const [isOpen, openDialog] = useState<boolean>(false);
  const [dialogMessage, onChangeDialogMessage] = useState<string>("");
  const [showTrailerFeild, setShowTrailerFeild] = useState<boolean>(true);

  useEffect(() => {
    toggleLoadDetails(props.index === props.expandedStop);
    if (
      stopDetail &&
      stopDetail.stopActivity &&
      stopDetail.stopActivity.length > 0
    ) {
      setShowTrailerFeild(true);
    } else {
      setShowTrailerFeild(false);
    }
  }, [props.index, props.expandedStop, JSON.stringify(stopDetail)]);

  useEffect(() => {
    const step =
      exicutionProgress &&
      exicutionProgress[props.index] &&
      exicutionProgress[props.index].progress;
    const totalStep = 3;
    if (step) {
      if (step === totalStep) {
        setShowDottedLine(true);
        setShowProgressBar(false);
      } else if (step === 0) {
        setShowDottedLine(false);
        setShowProgressBar(false);
      } else {
        setShowDottedLine(false);
        setShowProgressBar(true);
        setTimeout(() => calculateHeight(props.index, step), 0);
      }
    }
  }, [
    props.index,
    props.expandedStop,
    isExpanded,
    JSON.stringify(stopDetail),
    ARRIVAL_VALUE,
    exicutionProgress,
  ]);

  useEffect(() => {
    const currentDate = new Date(
      startTripModel.getTimeWithTimezone(stopDetail.timeZone)
    );
    currentDate.setMinutes(currentDate.getMinutes() + 60);

    if (ARRIVAL_VALUE && new Date(ARRIVAL_VALUE) > currentDate) {
      onArrivalErrorChange(true);
    } else {
      onArrivalErrorChange(false);
    }
  }, [ARRIVAL_VALUE]);

  useEffect(() => {
    const currentDate = new Date(
      startTripModel.getTimeWithTimezone(stopDetail.timeZone)
    );
    currentDate.setMinutes(currentDate.getMinutes() + 60);

    if (DEPARTURE_VALUE) {
      const _arrival = ARRIVAL_VALUE ? new Date(ARRIVAL_VALUE) : currentDate;
      const _departure = new Date(DEPARTURE_VALUE);
      let isError = false;
      if (_departure > currentDate) {
        isError = true;
      }
      _arrival.setMinutes(_arrival.getMinutes() + 1);
      if (!isError && _arrival > _departure) {
        isError = true;
      }
      if (isError) {
        onDepartureErrorChange(true);
      } else {
        onDepartureErrorChange(false);
      }
    } else {
      onDepartureErrorChange(false);
    }
  }, [DEPARTURE_VALUE]);

  const calculateHeight = (index: number, step: number) => {
    const totalStep = 3;
    const grid = document.getElementById(`"${index}"`);
    const gridHeight = grid && grid.offsetHeight;
    if (step) {
      const barHeight = gridHeight && gridHeight * (step / totalStep);
      setProgressBarHeight(barHeight + "px");
    }
  };

  const getTrailerInValue = (): StopActivityProps => {
    if (!!!TRAILER_IN_ACTIVITY) {
      return { ...defaultStopActivity, stopId: stopDetail.stopId };
    }

    return {
      equipmentFlow: EquipmentFlow.TRAILER_IN,
      equipmentId: TRAILER_IN,
      equipmentOwner: TRAILER_IN_ACTIVITY.equipmentOwner,
      equipmentType: TRAILER_IN_ACTIVITY.equipmentType,
      id: TRAILER_IN_ACTIVITY.id,
      name: TRAILER_IN_ACTIVITY.name,
      stopId: TRAILER_IN_ACTIVITY.stopId,
      execActivity: TRAILER_IN_ACTIVITY.execActivity,
      seqNo: TRAILER_IN_ACTIVITY.seqNo,
      tmsActivity: TRAILER_IN_ACTIVITY.tmsActivity,
    };
  };

  const getTrailerOutValue = (): StopActivityProps => {
    if (!!!TRAILER_OUT_ACTIVITY) {
      return { ...defaultStopActivity, stopId: stopDetail.stopId };
    }

    return {
      equipmentFlow: EquipmentFlow.TRAILER_OUT,
      equipmentId: TRAILER_OUT,
      equipmentOwner: TRAILER_OUT_ACTIVITY.equipmentType,
      id: TRAILER_OUT_ACTIVITY.id,
      name: TRAILER_OUT_ACTIVITY.name,
      stopId: TRAILER_OUT_ACTIVITY.stopId,
      equipmentType: TRAILER_OUT_ACTIVITY.equipmentType,
      execActivity: TRAILER_OUT_ACTIVITY.execActivity,
      seqNo: TRAILER_OUT_ACTIVITY.seqNo,
      tmsActivity: TRAILER_OUT_ACTIVITY.tmsActivity,
    };
  };

  const updateStopInformation = () => {
    const currentDate = new Date(
      startTripModel.getTimeWithTimezone(stopDetail.timeZone)
    );
    currentDate.setMinutes(currentDate.getMinutes() + 60);

    if (ARRIVAL_VALUE && new Date(ARRIVAL_VALUE) > currentDate) {
      props.onShowError(EXECUTION_ERRORS.ARRIVAL_ERROR);
      onArrivalErrorChange(true);
      // Analytics implementation
      logEvent(EVENT.UE_STOP_UPDATE_FAILURE, {
        carrierId: props.carrierId,
        shipmentId: stopDetail.shipmentId,
        stopID: stopDetail.stopId,
      });
      return;
    }

    if (DEPARTURE_VALUE) {
      const _arrival = ARRIVAL_VALUE ? new Date(ARRIVAL_VALUE) : currentDate;
      const _departure = new Date(DEPARTURE_VALUE);
      if (_departure > currentDate) {
        props.onShowError(EXECUTION_ERRORS.DEPARTURE_ERROR);
        onDepartureErrorChange(true);
        return;
      }
      _arrival.setMinutes(_arrival.getMinutes() + 1);
      if (_arrival >= _departure) {
        props.onShowError(EXECUTION_ERRORS.DEPARTURE_ARRIVAL_ERROR);
        onDepartureErrorChange(true);
        return;
      }
      if (
        startTripModel.isOTMTypeShipment(props.loadDetails) &&
        !startTripModel.isOTypeStop(props.stopDetails) &&
        !TRAILER_IN
      ) {
        props.onShowError(EXECUTION_ERRORS.TRAILER_IN_ERROR);
        return;
      }
      if (
        startTripModel.isOTMTypeShipment(props.loadDetails) &&
        !startTripModel.isOTypeStop(props.stopDetails) &&
        !TRAILER_OUT
      ) {
        props.onShowError(EXECUTION_ERRORS.TRAILER_OUT_ERROR);
        return;
      }
    }

    if (
      startTripModel.isOTMTypeShipment(props.loadDetails) &&
      TRAILER_OUT &&
      !TRAILER_PARTTEN.test(TRAILER_OUT)
    ) {
      props.onShowError(EXECUTION_ERRORS.VALID_TRAILER_OUT);
      return;
    }
    if (
      startTripModel.isOTMTypeShipment(props.loadDetails) &&
      TRAILER_IN &&
      !TRAILER_PARTTEN.test(TRAILER_IN)
    ) {
      props.onShowError(EXECUTION_ERRORS.VALID_TRAILER_IN);
      return;
    }

    let trailerIn: StopActivityProps;
    let trailerOut: StopActivityProps;
    const activity: StopActivityProps[] = [];
    trailerIn = getTrailerInValue();
    trailerOut = getTrailerOutValue();
    if (trailerIn.equipmentFlow === EquipmentFlow.TRAILER_IN) {
      activity.push(trailerIn);
    }

    if (trailerOut.equipmentFlow === EquipmentFlow.TRAILER_OUT) {
      activity.push(trailerOut);
    }

    const stopInfo: StopInformation = {
      carrierPro: "",
      carrierScac: "",
      stopId: stopDetail.stopId,
      sequenceNo: stopDetail.sequenceNo,
      stopNumber: stopDetail.sequenceNo,
      locationId: stopDetail.locationId,
      city: stopDetail.city,
      state: stopDetail.state,
      country: stopDetail.country,
      latitude: stopDetail.latitude && parseFloat(stopDetail.latitude),
      longitude: stopDetail.longitude && parseFloat(stopDetail.longitude),
      timeZone: stopDetail.timeZone,
      positionDateTime: new Date().toISOString(),
      actualArrivalDateTime: ARRIVAL_VALUE,
      actualETADateTime: ETA_VALUE,
      actualDepartureDateTime: DEPARTURE_VALUE,
      stopTrailerActivity: activity,
      carrierId: "",
      shipmentId: stopDetail.shipmentId,
      sourceSystem: stopDetail.sourceSystem,
      userId: "",
    };
    props.changeExpandStop(stopInfo);
    // Enable GPS tracking
    props.GPSEnabledEvent();
    // Analytics implementation
    logEvent(EVENT.UE_STOP_UPDATE_SUCCESS, {
      carrierId: props.carrierId,
      shipmentId: stopDetail.shipmentId,
      stopID: stopDetail.stopId,
    });
  };
  const expandLoadDetails = () => {
    toggleLoadDetails(false);
  };
  const collapseLoadDetails = () => {
    toggleLoadDetails(true);
  };

  const calculateTime = (stop: any) => {
    return getAppointmentTime(stop);
  };

  const expandLoadForcely = () => {
    toggleLoadDetails(true);
  };

  const onYesClick = () => {
    props.clearWarningDialogMessage();
  };

  const onNoClick = () => {
    props.clearWarningDialogMessage();
  };

  return (
    <>
      <IonModal
        isOpen={!!props.warningDialogMessage}
        cssClass="shipment-dialog"
        backdropDismiss={false}
      >
        <section>{props.warningDialogMessage}</section>
        <footer>
          <IonCol>
            <IonButton class="btn btn-Yes" onClick={onYesClick}>
              Yes
            </IonButton>
          </IonCol>
          <IonCol>
            <IonButton class="btn btn-No" onClick={onNoClick}>
              No
            </IonButton>
          </IonCol>
        </footer>
      </IonModal>
      <IonGrid
        className={`stop-data-grid ${showDotteedLine ? "dotted-border" : ""}`}
        id={`"${props.index}"`}
      >
        {props.index < props.totalStops - 1 ? (
          <>
            {props.index === 0 ? (
              <IonImg
                className={`stop-icon ${
                  showDotteedLine ? "change_origin_icon" : ""
                }`}
                src="assets/icon/origin-pointer.svg"
              />
            ) : (
              <IonImg
                className="stop-icon"
                src="assets/icon/stop-pointer.svg"
              />
            )}
          </>
        ) : (
          <IonImg
            className={`path_icon ${
              startTripModel.isAllStopCompleted(props.loadDetails)
                ? "change_dst_icon"
                : ""
            }`}
            src="assets/icon/dst-pointer.svg"
          />
        )}
        <IonGrid className="stop-address-grid">
          {showProgressBar && props.index < props.totalStops - 1 ? (
            <IonGrid
              className={` ${showProgressBar ? "progressDiv" : "hide-line"}`}
              style={{ height: `${progressBarHeight}` }}
            ></IonGrid>
          ) : null}
          <IonRow className="stop-address-gridspace">
            <IonCol
              size={isPlatform("mobile") ? "7" : "2.5"}
              className="address-details2"
            >
              <IonGrid className="origin_city_shipment_p">
                <p className="origin_city_shipment">
                  {stopDetail.city}, {stopDetail.state}
                </p>
                {isExpanded ? (
                  <IonIcon
                    src="assets/icon/minus-icon.svg"
                    class="plus-icon"
                    onClick={expandLoadDetails}
                  />
                ) : (
                  <IonIcon
                    src="assets/icon/plus.svg"
                    class="plus-icon"
                    onClick={collapseLoadDetails}
                  />
                )}
              </IonGrid>
              {isExpanded ? (
                <IonGrid
                  class={`complete-address2  ${
                    isExpanded ? "fadein" : "fadeout"
                  }`}
                >
                  <IonRow>
                    <IonCol>
                      {stopDetail.locationName && (
                        <p>{stopDetail.locationName}</p>
                      )}
                      {stopDetail.addressLine1 && (
                        <p> {stopDetail.addressLine1}</p>
                      )}
                      {stopDetail.addressLine2 && (
                        <p> {stopDetail.addressLine2}</p>
                      )}
                    </IonCol>
                    <IonCol size="4">
                      <span
                        onClick={() => {
                          props.openSatelliteMap(stopDetail.locationName, {
                            lat: stopDetail.latitude,
                            lng: stopDetail.longitude,
                          });
                        }}
                      >
                        <IonImg
                          src="/assets/icon/shipment-map-icon.svg"
                          className="shipment-map-icon"
                        />
                      </span>
                    </IonCol>
                  </IonRow>
                </IonGrid>
              ) : null}
              {isPlatform("desktop") ? (
                <>
                  {props.index === 0 ? (
                    <p className="origin_milehead load_header_elements">
                      {getStopActivity(
                        (stopDetail && stopDetail.stopActivity) || []
                      )}
                    </p>
                  ) : (
                    <>
                      {" "}
                      {props.index === props.totalStops - 1 ? (
                        <p className="origin_milehead load_header_elements">
                          {getStopActivity(
                            (stopDetail && stopDetail.stopActivity) || []
                          )}
                        </p>
                      ) : (
                        <p className="origin_milehead load_header_elements">
                          {STOP_OFF_TEXT} {props.index} :{" "}
                          {getStopActivity(
                            (stopDetail && stopDetail.stopActivity) || []
                          )}{" "}
                        </p>
                      )}
                    </>
                  )}
                  <p
                    className={`origin_dateTime origin-deadheadShipment2 ${
                      props.expandedStop ? "" : "origin-deadheadShipment2 "
                    }`}
                  >
                    {stopDetail && calculateTime(stopDetail)}
                  </p>
                </>
              ) : null}
            </IonCol>
            {((isExpanded && props.index > props.expandedStop) ||
              props.index <= props.expandedStop) &&
            isPlatform("desktop") ? (
              <IonCol size="9.5">
                <IonRow>
                  <IonCol class="exe-col">
                    <ETA
                      timestamp={props.timestamp}
                      shipmentId={props.shipmentId}
                      loadStop={props.stopDetails}
                      onChange={(value: string) => {
                        setETA(value);
                      }}
                      value={ETA_VALUE}
                      isNextStop={props.index > props.expandedStop}
                      isExpanded={isExpanded}
                      timeZone={props.stopDetails.timeZone}
                    />
                  </IonCol>
                  <IonCol class="exe-col">
                    <Arrival
                      timestamp={props.timestamp}
                      isError={isErrorOnArrival}
                      loadStop={props.stopDetails}
                      etaValue={ETA_VALUE}
                      onChange={(value: string) => {
                        setARRIVAL(value);
                      }}
                      value={ARRIVAL_VALUE}
                      isNextStop={props.index > props.expandedStop}
                      valueEntered={() => {
                        props.arivalEntered(props.index);
                      }}
                      isExpanded={isExpanded}
                      timeZone={props.stopDetails.timeZone}
                      shipmentId={props.shipmentId}
                    />
                  </IonCol>
                  <IonCol class="trailer-col exe-col">
                    <TrailerIn
                      timestamp={props.timestamp}
                      shipment={props.loadDetails}
                      expandCurrentStopForcefully={expandLoadForcely}
                      loadStop={props.stopDetails}
                      onChange={(value: string) => {
                        setTRAILERIN(value);
                      }}
                      value={TRAILER_IN}
                      isNextStop={props.index > props.expandedStop}
                      isFirstStop={props.index === 0}
                      isExpanded={isExpanded}
                      shipmentId={props.shipmentId}
                      readonly={
                        startTripModel.isOTypeStop(props.stopDetails) ||
                        startTripModel.isShipmentTripCompleted(
                          props.loadDetails
                        ) ||
                        startTripModel.areStopActivitiesMissing(
                          props.stopDetails
                        )
                      }
                      changeActivity={(activity: StopActivityProps) => {
                        setTRAILERINACTIVITY(activity);
                      }}
                      showError={startTripModel.showTrailerInError(
                        TRAILER_IN,
                        props.loadDetails,
                        DEPARTURE_VALUE,
                        props.index > props.expandedStop,
                        props.stopDetails
                      )}
                      showTrailerFeild={showTrailerFeild}
                      isPreviousStop={props.index < props.expandedStop}
                      updateStopInfo={props.updateStopInfo}
                    />
                  </IonCol>
                  <IonCol class="exe-col">
                    <Departure
                      timestamp={props.timestamp}
                      isError={isErrorOnDeparture}
                      loadStop={props.stopDetails}
                      onChange={(value: string) => {
                        setDEPARTURE(value);
                      }}
                      value={DEPARTURE_VALUE}
                      isNextStop={props.index > props.expandedStop}
                      arrivalVal={ARRIVAL_VALUE}
                      isExpanded={isExpanded}
                      timeZone={props.stopDetails.timeZone}
                      shipmentId={props.shipmentId}
                    />
                  </IonCol>
                  <IonCol class="trailer-col exe-col">
                    <TrailerOut
                      shipment={props.loadDetails}
                      timestamp={props.timestamp}
                      expandCurrentStopForcefully={expandLoadForcely}
                      loadStop={props.stopDetails}
                      onChange={(value: string) => {
                        setTRAILEROUT(value);
                      }}
                      shipmentId={props.shipmentId}
                      readonly={
                        startTripModel.isShipmentTripCompleted(
                          props.loadDetails
                        ) ||
                        startTripModel.isOTypeStop(props.stopDetails) ||
                        startTripModel.areStopActivitiesMissing(
                          props.stopDetails
                        )
                      }
                      value={TRAILER_OUT}
                      isNextStop={props.index > props.expandedStop}
                      isExpanded={isExpanded}
                      isLastStop={props.index === props.totalStops - 1}
                      changeActivity={(activity: StopActivityProps) => {
                        setTRAILEROUTACTIVITY(activity);
                      }}
                      showError={startTripModel.showTrailerOutError(
                        TRAILER_OUT,
                        props.loadDetails,
                        DEPARTURE_VALUE,
                        props.index > props.expandedStop,
                        props.stopDetails
                      )}
                      showTrailerFeild={showTrailerFeild}
                      isPreviousStop={props.index < props.expandedStop}
                    />
                  </IonCol>
                  <IonCol class="cta-btn-col exe-col">
                    {!startTripModel.isShipmentTripCompleted(
                      props.loadDetails
                    ) && (
                      <IonButton
                        class="load-btn desktop-cta-btn"
                        onClick={updateStopInformation}
                        disabled={
                          props.index > props.expandedStop ||
                          !props.isNetworkAvailable
                        }
                      >
                        {UPDATE_STOP_CTA_TEXT}
                      </IonButton>
                    )}
                  </IonCol>
                  <IonCol>
                    <ReferenceDialog
                      stopDetail={stopDetail}
                      carrierId={props.carrierId}
                      shipment={props.loadDetails}
                    />
                  </IonCol>
                </IonRow>
              </IonCol>
            ) : null}
            {isExpanded && isPlatform("mobile") ? (
              <IonCol size="5" className="address-details">
                <ReferenceDialog
                  stopDetail={stopDetail}
                  carrierId={props.carrierId}
                  shipment={props.loadDetails}
                />
              </IonCol>
            ) : null}
          </IonRow>
          {isExpanded ||
          (!isEndExecution && props.index > props.expandedStop) ||
          (!isEndExecution && !isPlatform("mobile")) ? (
            <IonRow>
              {isPlatform("mobile") ? (
                <IonCol size="7" className="address-details2">
                  {props.index === 0 ? (
                    <p className="origin_milehead load_header_elements">
                      {getStopActivity(
                        (stopDetail && stopDetail.stopActivity) || []
                      )}
                    </p>
                  ) : (
                    <>
                      {props.index === props.totalStops - 1 ? (
                        <p className="origin_milehead load_header_elements">
                          {getStopActivity(
                            (stopDetail && stopDetail.stopActivity) || []
                          )}
                        </p>
                      ) : (
                        <p className="origin_milehead load_header_elements">
                          {STOP_OFF_TEXT} {props.index} :{" "}
                          {getStopActivity(
                            (stopDetail && stopDetail.stopActivity) || []
                          )}
                        </p>
                      )}
                    </>
                  )}
                  <p
                    className={`origin_dateTime origin-deadheadShipment2 ${
                      props.expandedStop ? "" : "origin-deadheadShipment2 "
                    }`}
                  >
                    {stopDetail && calculateTime(stopDetail)}
                  </p>
                </IonCol>
              ) : null}
              {isExpanded && isPlatform("mobile") ? (
                <IonCol size="5" className="address-details">
                  <ETA
                    timestamp={props.timestamp}
                    loadStop={props.stopDetails}
                    onChange={(value: string) => {
                      setETA(value);
                    }}
                    value={ETA_VALUE}
                    isNextStop={props.index > props.expandedStop}
                    isExpanded={isExpanded}
                    timeZone={props.stopDetails.timeZone}
                    shipmentId={props.shipmentId}
                  />
                </IonCol>
              ) : null}
            </IonRow>
          ) : null}

          {isPlatform("mobile") &&
          ((isExpanded && props.index > props.expandedStop) ||
            props.index <= props.expandedStop) ? (
            <IonGrid className="address-details2">
              <IonRow class={`${isEndExecution ? "" : "arrival-row"}`}>
                <IonCol size="7" className="address-details">
                  <Arrival
                    timestamp={props.timestamp}
                    isError={isErrorOnArrival}
                    loadStop={props.stopDetails}
                    etaValue={ETA_VALUE}
                    onChange={(value: string) => {
                      setARRIVAL(value);
                    }}
                    value={ARRIVAL_VALUE}
                    isNextStop={props.index > props.expandedStop}
                    valueEntered={() => {
                      props.arivalEntered(props.index);
                    }}
                    isExpanded={isExpanded}
                    timeZone={props.stopDetails.timeZone}
                    shipmentId={props.shipmentId}
                  />
                </IonCol>

                <IonCol size="5" className="address-details">
                  <TrailerIn
                    shipment={props.loadDetails}
                    timestamp={props.timestamp}
                    expandCurrentStopForcefully={expandLoadForcely}
                    loadStop={props.stopDetails}
                    onChange={(value: string) => {
                      setTRAILERIN(value);
                    }}
                    value={TRAILER_IN}
                    isNextStop={props.index > props.expandedStop}
                    isFirstStop={props.index === 0}
                    isExpanded={isExpanded}
                    shipmentId={props.shipmentId}
                    readonly={
                      startTripModel.isOTypeStop(props.stopDetails) ||
                      startTripModel.isShipmentTripCompleted(
                        props.loadDetails
                      ) ||
                      startTripModel.areStopActivitiesMissing(props.stopDetails)
                    }
                    changeActivity={(activity: StopActivityProps) => {
                      setTRAILERINACTIVITY(activity);
                    }}
                    showError={startTripModel.showTrailerInError(
                      TRAILER_IN,
                      props.loadDetails,
                      DEPARTURE_VALUE,
                      props.index > props.expandedStop,
                      props.stopDetails
                    )}
                    showTrailerFeild={showTrailerFeild}
                    isPreviousStop={props.index < props.expandedStop}
                    updateStopInfo={props.updateStopInfo}
                  />
                </IonCol>
              </IonRow>
              <IonRow class={`${isEndExecution ? "" : "arrival-row"}`}>
                <IonCol size="7" className="address-details">
                  <Departure
                    timestamp={props.timestamp}
                    isError={isErrorOnDeparture}
                    loadStop={props.stopDetails}
                    onChange={(value: string) => {
                      setDEPARTURE(value);
                    }}
                    value={DEPARTURE_VALUE}
                    isNextStop={props.index > props.expandedStop}
                    arrivalVal={ARRIVAL_VALUE}
                    isExpanded={isExpanded}
                    shipmentId={props.shipmentId}
                    timeZone={props.stopDetails.timeZone}
                  />
                </IonCol>

                <IonCol size="5" className="address-details">
                  <TrailerOut
                    shipment={props.loadDetails}
                    timestamp={props.timestamp}
                    expandCurrentStopForcefully={expandLoadForcely}
                    loadStop={props.stopDetails}
                    onChange={(value: string) => {
                      setTRAILEROUT(value);
                    }}
                    value={TRAILER_OUT}
                    isNextStop={props.index > props.expandedStop}
                    isExpanded={isExpanded}
                    isLastStop={props.index === props.totalStops - 1}
                    readonly={
                      startTripModel.isShipmentTripCompleted(
                        props.loadDetails
                      ) ||
                      startTripModel.isOTypeStop(props.stopDetails) ||
                      startTripModel.areStopActivitiesMissing(props.stopDetails)
                    }
                    shipmentId={props.shipmentId}
                    changeActivity={(activity: StopActivityProps) => {
                      setTRAILEROUTACTIVITY(activity);
                    }}
                    showError={startTripModel.showTrailerOutError(
                      TRAILER_OUT,
                      props.loadDetails,
                      DEPARTURE_VALUE,
                      props.index > props.expandedStop,
                      props.stopDetails
                    )}
                    showTrailerFeild={showTrailerFeild}
                    isPreviousStop={props.index < props.expandedStop}
                  />
                </IonCol>
              </IonRow>
              {isExpanded &&
              !startTripModel.isShipmentTripCompleted(props.loadDetails) ? (
                <IonRow>
                  <IonButton
                    class="load-btn load-btn3"
                    id="btn-update"
                    onClick={updateStopInformation}
                    disabled={
                      props.index > props.expandedStop ||
                      !props.isNetworkAvailable
                    }
                  >
                    {UPDATE_STOP_CTA_TEXT}
                  </IonButton>
                </IonRow>
              ) : null}
            </IonGrid>
          ) : null}
        </IonGrid>
      </IonGrid>
    </>
  );
};
