import React, { memo } from 'react';
import { IonCardContent, IonGrid, IonRow, IonCol, IonImg, IonBadge, IonCard } from '@ionic/react';
import { getMonth } from '../../../pages/Search/Recent/RecentSearchDesktop';
import "./CapacityCard.scss";
import { Anywhere } from '../../../utils/constants';
import { formatStatesList } from 'app/pages/Manage/ManageCapacity/ManagaCapacityUtil';

export const CardDetails: React.FC<any> = memo((props: any) => {
    const { states, isDestination } = props;
    return (
        <IonRow class="stop-details-row">
            <IonCol class="stop-icon-col">
                <IonImg className="ellipse_icon" src={`assets/icon/${props.icon}`} />
                { props.isOrigin ? <div className="stops_border"></div> : null }
            </IonCol>
            <IonCol class="stop-details-col capacity">
                <p className={`origin_city ${(isDestination && states && states.length > 0) || !props.radius ? 'states-active': ''}`}>
                {isDestination && states && states.length > 0 ? states.join(', '): props.city === Anywhere  ||  
                props.city === null ? Anywhere : `${props.city}, ${props.state}`}</p>
                {
                    !states || (isDestination && states && states.length === 0 && props.radius) || props.isOrigin ? (
                        <IonBadge className="originRadius">Radius : {props.radius} mi</IonBadge>
                    ) : null
                }
                { props.isTruck ? (
                    <IonBadge className='pickupDate'>
                        {props.fromDate && props.toDate ? `${getMonth(new Date(props.fromDate).getMonth())}
                        ${("0" + new Date(props.fromDate).getDate()).slice(-2)} to ${getMonth(new Date(props.toDate).getMonth())} 
                        ${("0" + new Date(props.toDate).getDate()).slice(-2)}` : null}
                    </IonBadge>
                ) : null }
            </IonCol>
        </IonRow>
    )
});

const CapacityCard: React.FC<any> = ({ data, handleClick, handleDelete }) => {

    const details = [
        {
            icon: 'origin-pointer.svg',
            ...data.origin,
            fromDate: data.pickUpFromDate,
            toDate: data.pickUpToDate,
            isOrigin: true,
            isTruck: data.capacityType.toLowerCase() === 'truck'
        },
        {
            icon: 'dst-pointer.svg',
            ...data.destination,
            isDestination: true,
            states: formatStatesList(data.destinationStates)
        }
    ];

    const handleDeleteClick = (event:any) => {
        event.stopPropagation();
        handleDelete(data);
    }

    return (
        <IonCard data-test="capacity-card" className="ion-card-updated" onClick={() => handleClick(data)}>
            <IonCardContent className="card-content recent-card">
                <IonGrid className="recent_grid">
                    <IonRow>
                        <IonCol className="card-price-col" size="4">
                            <p className="capacity-type">{data.capacityType.toLowerCase()}</p>
                            <p className="trailer-type">{data.trailerType}</p>
                        </IonCol>
                        <IonCol className="card-city-col">
                            <IonImg className="star_icon"  data-test="capacity-card-delete"  src="assets/icon/Delete_bin.svg"  onClick={handleDeleteClick}/>
                            {details.map((detail: any, index: number) => <CardDetails {...detail} key={`details_${data.capacityId}_${index}`}/>)}
                        </IonCol>
                        {/* <IonCol size="1" className="star_icon_col">
                           
                        </IonCol> */}
                    </IonRow>
                </IonGrid>
            </IonCardContent>
        </IonCard>
    );
}

export default CapacityCard;
