import React, { useState } from "react";
import {
  IonPage,
} from "@ionic/react";
import "./Manage.scss";
import AppHeader from '../../components/app/Bars/Bar-header'
import {SectionContainer } from "../../containers/ManageContainer/sectionContainer";

export const  ManageDesktopPage:React.FC<any> = (props:any) => {

  const manageCards = props.manageCards.map((card:any) => card.profileHeading);

  const [segment, setSegment] = useState<any>({ name: "Profile", data: {} });
  const onChangeSegment = (segmentName: string) => {
    setSegment({ name: segmentName, data: {} });
  };

    return (
      <IonPage className="desktop-page my-load-desktop-page manage-card-content">
          <AppHeader
            title="Manage"
            getSegment={(e: any) => {
              setSegment({ name: e.detail.value as any, data: {} });
            }}
            segments={manageCards}
            activeSegment={segment.name}
            imageCapature={props}
          />
          <SectionContainer
              onRedirectToTab={onChangeSegment}
              section={segment.name}
            />
      </IonPage>
    );
}
export default ManageDesktopPage;
