import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import ExecutePage from '../../pages/Execute/ExecutePage.page';
import useInjectSaga from '../../utils/injectSaga';
import useInjectReducer from '../../utils/injectReducer';
import { makeMyLoadCardData, getCount } from '../../selectors/Myload/selector';
import reducer from '../../reducers/myload/reducer';
import saga from '../../saga/MyLoad/saga';
import { MyLoadCard, profileImageSet } from '../../actions/myload/action';
import { showPermissionAlert } from '../../actions/Login/action';
import {
  getLoading,
  getRefreshStatus,
  getNetworkStatus,
  getUserDetails
} from '../../selectors/selector';
import { fetchShipments } from '../../actions/carrierAssign/action';
import { isEntitled } from '../../utils/common';
import { ExicutionLoads } from '../../models/ExicutionLoads.model';
import { showAlert } from '../../actions/App/action';

const key = 'myload';
class ExecuteContainer extends PureComponent<any, any> {
  componentDidMount() {
    this.props.myLoadData();
    if (this.props && this.props.userDetails && this.props.userDetails.carrierId) {
      this.getUserLoads(false);
    }
  }

  componentDidUpdate(prevProps: any) {
    const prevUserDetails = (prevProps && prevProps.userDetails) || {};
    const currentUserDetails = (this.props && this.props.userDetails) || {};
    if (
      currentUserDetails &&
      currentUserDetails.carrierId !== prevUserDetails.carrierId &&
      !this.props.loading
    ) {
      this.getUserLoads(false);
    }
  }

  getUserLoads = (isHideLoader = false) => {
    if (!this.props.isNetworkAvailable) {
      return;
    }
    try {
      const carrierId =
        (this.props && this.props.userDetails && this.props.userDetails.carrierId) || '';

      const userPrincipalName =
        (this.props && this.props.userDetails && this.props.userDetails.userPrincipalName) || '';
      const modelLoad: ExicutionLoads = new ExicutionLoads();
      const isDriver = !isEntitled('myLoad', 'carrierAssigned');
      modelLoad.carrierId = carrierId;
      modelLoad.filter = {
        carrierAssigned: !isDriver,
        driverAssigned: true,
        inTransit: true,
        driverAssignmentPending: true,
        delivered: true
      };
      if (isDriver) {
        modelLoad.driverId = userPrincipalName;
      }
      this.props.fetchShipments(modelLoad.getRequestPayload(), isHideLoader);
    } catch (error) {
      console.log('ERROR:', error);
    }
  };

  render() {
    return <ExecutePage {...this.props} getUserLoads={this.getUserLoads} />;
  }
}

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  myLoadCount: getCount(),
  myLoadCard: makeMyLoadCardData(),
  userDetails: getUserDetails(),
  isInPullToRefresh: getRefreshStatus(),
  isNetworkAvailable: getNetworkStatus()
});

const mapDispatchToProps = (dispatch: Function) => ({
  myLoadData: () => dispatch(MyLoadCard()),
  showPermissionAlert: (data: any) => dispatch(showPermissionAlert(data)),
  profileImg: (data: any) => dispatch(profileImageSet(data)),
  fetchShipments: (details: any, hideLoader: boolean) =>
    dispatch(fetchShipments(details, hideLoader)),
  showAlert: (data: any) => dispatch(showAlert(data))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });
export default compose < any > (withSaga, withReducer, withConnect)(React.memo(ExecuteContainer));
