import React, { useState } from 'react';
import { IonGrid,IonCol, IonRow } from '@ionic/react';
import { ROLES } from '../../utils/constants';
import { IonImage } from '../app/Manage/IonImage';

const ImageCapture:React.FC<any> = (props:any) => {

    const { userDetails } = props;
    const roles = userDetails && userDetails.userApplicationDetails && userDetails.userApplicationDetails.reduce((current:any, item:any) => {
        return [...current, ...item.roles];
    }, []).map(((role:any) => ROLES[role.roleName])) || [];
    
    const [isRolesShow, setRolesShow] = useState(false);

    const toggleRoles = () => {
        setRolesShow(!isRolesShow);
    }

    return (
        <>
            <IonGrid>
                {
                    userDetails ? (
                        <IonRow>
                            <IonCol className="profileImg-col" size="4">
                                <IonImage {...userDetails} xlarge />
                            </IonCol>
                            <IonCol className="profile-Col" size="8">
                                <div className="profileName" data-test="profile-name">{`${userDetails.firstName || ''}  ${userDetails.lastName || ''}`}</div>
                                <div className="profile-role" data-test="admin">
                                    { roles && roles.length > 0 && roles[0] || '' }
                                    { roles.length > 1 ?<span className="admincount" onClick={toggleRoles}> { isRolesShow ? '-' : `+ ${roles.length - 1}`}</span>: null }
                                    {
                                        isRolesShow ? (
                                            <ul className="profile-add-roles">
                                                { roles.slice(1, roles.length).map((role:any) => (
                                                    <li>
                                                        {role}
                                                    </li>
                                                ))}
                                            </ul>
                                        ) : null
                                    }
                                </div>
                                <div className="profileEmail" data-test="profile-email">{userDetails.email}</div>
                            </IonCol>
                        </IonRow>
                    ) : null
                }
            </IonGrid>
        </>
    );
    
}

export default ImageCapture;


