import { WatchedLoadModel } from 'app/models/home/WatchedLoads.model';
import { logEvent } from 'app/utils/Analytics';
import { ToastMessage } from 'app/utils/ToastMessage';
import * as Constants from '../../utils/constants';

export const onAnalyticsLoadPosting = (segmentName: any) => {
    logEvent(
      segmentName === Constants.WATCHED_TAB
        ? Constants.EVENT.UE_VIEW_WATCHED_TAB
        : Constants.EVENT.UE_SCROLL_RECOMMENDATIONS,
      {
        screen: Constants.SCREEN.HOME,
        tab: segmentName === Constants.WATCHED_TAB ? Constants.TAB.WATCHED : Constants.TAB.RECOMMENDED,
      }
    );
};

export const getRecSortingValues = (recommendedfilter: any) => {
    const selected = recommendedfilter.selected;
    const isAsc = recommendedfilter.direction === Constants.SORTING_ORDER_ASC;
    let filterBy = Constants.recommendedSortByOptions[0].value;
    if (selected.length > 0) {
        filterBy = selected
            .filter((item: any) => item.selected)
            .map((item: any) => item.value)
            .join("");
    }
    return { filterBy, isAsc };
};

export const doSorting = (getRecommendedLoadsData: any, isAsc: boolean, filterBy: string) => {
    if (filterBy === Constants.ORIGIN_FROM_DATE) {
        return (
            getRecommendedLoadsData &&
            [...getRecommendedLoadsData].sort((a: any, b: any) => {
                const firstpickupLoad = a[Constants.LOAD_STOP].filter(
                    (stop: any, index: any) => {
                        return stop.stopType === "P";
                    }
                );
                const secondpickupLoad = b[Constants.LOAD_STOP].filter(
                    (stop: any, index: any) => {
                        return stop.stopType === "P";
                    }
                );
                if (
                    a &&
                    a[Constants.LOAD_STOP] &&
                    a[Constants.LOAD_STOP].length > 0 &&
                    firstpickupLoad &&
                    firstpickupLoad.length > 0 &&
                    firstpickupLoad[0][Constants.APPOINTMENT_FROM] &&
                    b &&
                    b[Constants.LOAD_STOP] &&
                    b[Constants.LOAD_STOP].length > 0 &&
                    secondpickupLoad &&
                    secondpickupLoad.length > 0 &&
                    secondpickupLoad[0][Constants.APPOINTMENT_FROM]
                ) {
                    return new Date(firstpickupLoad[0][Constants.APPOINTMENT_FROM]).getTime() >
                        new Date(secondpickupLoad[0][Constants.APPOINTMENT_FROM]).getTime()
                        ? isAsc
                            ? 1
                            : -1
                        : isAsc
                            ? -1
                            : 1;
                } else if (
                    a[Constants.LOAD_STOP].length > 0 &&
                    firstpickupLoad &&
                    firstpickupLoad.length > 0 &&
                    firstpickupLoad[0][Constants.APPOINTMENT_FROM] &&
                    !(
                        b &&
                        b[Constants.LOAD_STOP] &&
                        b[Constants.LOAD_STOP].length > 0 &&
                        secondpickupLoad &&
                        secondpickupLoad.length > 0 &&
                        secondpickupLoad[0][Constants.APPOINTMENT_FROM]
                    )
                ) {
                    return isAsc ? -1 : 1;
                } else if (
                    !(
                        a &&
                        a[Constants.LOAD_STOP].length > 0 &&
                        firstpickupLoad &&
                        firstpickupLoad.length > 0 &&
                        firstpickupLoad[0][Constants.APPOINTMENT_FROM]
                    ) &&
                    b &&
                    b[Constants.LOAD_STOP] &&
                    b[Constants.LOAD_STOP].length > 0 &&
                    secondpickupLoad &&
                    secondpickupLoad.length > 0 &&
                    secondpickupLoad[0][Constants.APPOINTMENT_FROM]
                ) {
                    return isAsc ? 1 : -1;
                } else {
                    return isAsc ? 1 : -1;
                }
            })
        );
    } else if (filterBy === Constants.TOTAL_DISTANCE) {
        return (
            getRecommendedLoadsData &&
            [...getRecommendedLoadsData].sort((a: any, b: any) => {
                if (a && a[Constants.TOTAL_DISTANCE] && b && b[Constants.TOTAL_DISTANCE]) {
                    return a[Constants.TOTAL_DISTANCE] > b[Constants.TOTAL_DISTANCE]
                        ? isAsc
                            ? 1
                            : -1
                        : isAsc
                            ? -1
                            : 1;
                // } else if (a && a[Constants.TOTAL_DISTANCE] && b && b[Constants.TOTAL_DISTANCE]) {
                //     return isAsc ? -1 : 1;
                } else if (!(a && a[Constants.TOTAL_DISTANCE]) && b && b[Constants.TOTAL_DISTANCE]) {
                    return isAsc ? 1 : -1;
                } else {
                    return isAsc ? 1 : -1;
                }
            })
        );
    } else {
        const priceLoads =
            getRecommendedLoadsData &&
            getRecommendedLoadsData.filter((loads: any, index: any) => {
                if (
                    loads &&
                    loads.rate &&
                    (loads.rate !== null) &&
                    (loads.rate.totalCharge && loads.rate.totalCharge !== 0) &&
                    (loads.rate.totalCharge && loads.rate.totalCharge !== null)
                ) {
                    return loads;
                }
            });
        const noPriceLoads =
            getRecommendedLoadsData &&
            getRecommendedLoadsData.filter((loads: any, index: any) => {
                if (loads && loads.rate && (loads.rate === null) ||
                    (loads.rate && loads.rate.totalCharge === 0) ||
                    (loads.rate.totalCharge && loads.rate.totalCharge === null)
                ) {
                    return loads;
                }
            });
        const sortLoads =
            priceLoads &&
            [...priceLoads].sort((a: any, b: any) => {
                if (
                    a &&
                    a[Constants.RATE] &&
                    a[Constants.RATE][Constants.TOTAL_CHARGE] &&
                    b &&
                    b[Constants.RATE] &&
                    b[Constants.RATE][Constants.TOTAL_CHARGE]
                ) {
                    return a[Constants.RATE][Constants.TOTAL_CHARGE] > b[Constants.RATE][Constants.TOTAL_CHARGE]
                        ? isAsc
                            ? 1
                            : -1
                        : isAsc
                            ? -1
                            : 1;
                } else if (
                    a &&
                    a[Constants.RATE] &&
                    a[Constants.RATE][Constants.TOTAL_CHARGE] &&
                    !(b && b[Constants.RATE] && b[Constants.RATE][Constants.TOTAL_CHARGE])
                ) {
                    return isAsc ? -1 : 1;
                } else if (
                    !(a && a[Constants.RATE] && a[Constants.RATE][Constants.TOTAL_CHARGE]) &&
                    b &&
                    b[Constants.RATE] &&
                    b[Constants.RATE][Constants.TOTAL_CHARGE]
                ) {
                    return isAsc ? 1 : -1;
                } else {
                    return isAsc ? 1 : -1;
                }
            });
        return sortLoads.concat(noPriceLoads);
    }
}

export const toggleWatchLoad = (props:any) => (loadId: string) => {
    const { carrierId, userPrincipalName: userId } = props.userDetails;

    if (!props.isNetworkAvailable) {
      props.showAlert({
        isShowAlert: true,
        alertMessage: Constants.PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: Constants.NO_NEWORK,
      });
      return true;
    }
    const watchDate = new Date().toISOString();
    const payload = {
      carrierId,
      userId,
      origin: "",
      loadId,
      watchDate,
    };
    const loadDetails = props.watchedLoad.find(
      (watched: any) => watched.loadId === loadId
    );
    const watchLoadModel = new WatchedLoadModel();
    const watchLoadCallback = () =>
      props.addWatchLoad({
        query: watchLoadModel.addWatchLoad(payload),
        loadDetails,
      });
    const callback = (error: boolean = false) => {
      if (error) {
        ToastMessage.show(Constants.watchedErrorText);
        return true;
      }
      ToastMessage.open({
        message: Constants.WATCHED_LOAD_REMOVE,
        showUndo: true,
        callback: watchLoadCallback,
      });
    };
    props.removeWatchLoad({
      query: watchLoadModel.removeWatchLoad(payload),
      loadId,
      callback,
    });
  };