import React from 'react';
import DataTable from 'react-data-table-component';
import '../../../../theme/variables.scss';
import '../../../../theme/responsive-styles.scss';

const customSort = (rows: any, field: any, direction: any) => {
  let sortedList = [];
  if (direction === 'asc') {
    sortedList = [...rows].sort((a: any, b: any) => (a[field] > b[field] ? 1 : -1));
  } else {
    sortedList = [...rows].sort((a: any, b: any) => (a[field] > b[field] ? -1 : 1));
  }
  return sortedList;
};
interface datatableProps {
  columns: any;
  data: any;
  redirect?: any;
  showEndofList: boolean;
  titleText: any;
  showHeader: boolean;
  headerText?: string;
  pagination: boolean;
  onButtonClick?: any;
}

const DataTableComponent: React.FC<datatableProps> = ({
  columns,
  data,
  redirect,
  showEndofList,
  showHeader,
  titleText,
  headerText,
  pagination,
  onButtonClick
}) => {
  const onRowClick = (row: any) => {
    redirect && redirect(row);
  };
  return (
    <>
      {data ? (
        <>
          {showHeader ? null : <div className="searchResults_div">{headerText}</div>}
          <DataTable
            title={titleText}
            columns={columns}
            data={data && data.reverse()}
            highlightOnHover
            sortFunction={customSort}
            onRowClicked={onRowClick}
            pagination={pagination}
            // customStyles={customStyle}
          />
          {showEndofList ? null : <div className="end-of-list">End of list</div>}
        </>
      ) : null}
    </>
  );
};
export default DataTableComponent;
