import React from "react";
import { IonContent, IonRow, IonCol, IonLabel, IonImg } from "@ionic/react";
import { DriverCard } from "../../../pages/Execute/AssignDriverList/DriverCard";

export const DriverModal = (props: any) => {
  const addDriver = () => {
    props.closeModal();
    props.history.push("/app/MyLoad/addDriver");
  };
  return (
    <IonContent className="driverModal-content">
      <div className="Background-popup">
        {props.popupHeader ? (
          <IonRow className="popup_row">
            <IonCol size="11.5" className="select-driver-main-Col">
              <IonRow>
                <IonCol size="8">
                  <IonLabel className="select-driver-header">
                    Select A Driver
                  </IonLabel>
                </IonCol>
                <IonCol size="4" className="add-right" onClick={addDriver}>
                  <IonImg className="add_img" src="assets/icon/Add.svg" />
                  <IonLabel className="popup-add-driver">Add Driver</IonLabel>
                </IonCol>
              </IonRow>
            </IonCol>
            <IonCol size="0.5" className="cancel_col">
              <div
                className="cancel_text"
                onClick={() => {
                  props.closeModal();
                }}
              >
                x
              </div>
            </IonCol>
          </IonRow>
        ) : null}
        <DriverCard
          DriverList={props.DesktopDriverList}
          loadDetails={props.loadDetails}
          showAssignDriverDialog={(isShow: boolean, driverDetails: any) => {
            props.showAssignDriverDialog(isShow, driverDetails);
          }}
        />
      </div>
    </IonContent>
  );
};
