import { all, put, call, takeLatest } from 'redux-saga/effects';
import { showLoading, hideLoading } from '../../actions/Login/action';
import * as constants from '../../utils/constants';
import request from '../../utils/request';
import { GetManageCardData } from '../../actions/manage/action';
import { ManageCardData } from '../../utils/mock_Data';
import { getManageCapaityList, addManageCapacity, removeManageCapacity, updateManageCapacity } from './ManageCapacitySaga';
import { getManageUsersList, validateUser, addUser, transferUser, updateUser, confirmTransfer, removeUser } from './ManageUserSaga';
import { loadCarrierData } from './ManageCarrierSaga';

export function* profileImg(data: any) {
  try {
    yield put(showLoading());
    const body = {
      "image-content": "data.payload"
    }
    const response = yield call(request, ["MANAGE", "PROFILE_IMG", null, "Content-type", body, null]);
    // yield put(PROFILE_IMAGE_SET(repos));
    yield put(hideLoading());
  } catch (e) {
    yield put(hideLoading());
  }
}

export function* ManageCardDataData() {
  try {
    yield put(showLoading());
    const repos = ManageCardData;
    yield put(GetManageCardData(repos));
    yield put(hideLoading());
  } catch (e) {
    yield put(hideLoading());
  }
}




export default function* rootSaga() {
  yield all([
    takeLatest(constants.PROFILE_IMAGE_SET, profileImg),
    takeLatest(constants.MANAGE_CARD_DATA, ManageCardDataData),
    takeLatest(constants.GET_MANAGE_USERS_LIST, getManageUsersList),
    takeLatest(constants.MANAGE_USER_VALIDATE, validateUser),
    takeLatest(constants.MANAGE_USER_TRANSFER, transferUser),
    takeLatest(constants.MANAGE_USER_TRANSFER_COFIRM_REQUEST, confirmTransfer),
    takeLatest(constants.MANAGE_USER_ADD, addUser),
    takeLatest(constants.MANAGE_USER_EDIT, updateUser),
    takeLatest(constants.MANAGE_USER_REMOVE, removeUser),
    takeLatest(constants.GET_MANAGE_CAPACITY_LIST, getManageCapaityList),
    takeLatest(constants.MANAGE_CAPACITY_ADD, addManageCapacity),
    takeLatest(constants.MANAGE_CAPACITY_REMOVE, removeManageCapacity),
    takeLatest(constants.MANAGE_CAPACITY_EDIT, updateManageCapacity),
    takeLatest(constants.CARRIER_MANAGE_DATA_REQUEST, loadCarrierData),
  ]);
}