import "@capacitor-community/firebase-analytics";
import { Plugins } from '@capacitor/core';
import moment from 'moment';
import { isPlatform } from '@ionic/react';
import { IONICWrapper } from './common';
import { store } from '../store';

const { FirebaseAnalytics, Device } = Plugins;

/**
 *
 * LogEvent Method used to log events
 *
 * @param name
 * @param parameters
 * @param sessionFields
 */
export const logEvent = (name: string, parameters: object, sessionFields: string[] = []) => {
    if(isPlatform('desktop')) {
        return true;
    }
    if(name) {
        const storeData = (store.getState());
        const session = (storeData && storeData.App && storeData.App.session);
        let params:any = parameters;
        if(session) {
            params = {...params, sessionId: getSessionId(session.uuid)};
            /**
             *  Session Fields Array includes required fields  from session
             */
            sessionFields.forEach(field => {
                params[field] = session[field] || '';
            });
        }
        FirebaseAnalytics.logEvent({ name,  params});
    }
};

/**
 *  SetUserId Method used to set the unique userid
 */
export const setUserId = (userId: string) => {
    if(isPlatform('desktop')) {
        return true;
    }
    if(userId) {
        FirebaseAnalytics.setUserId({ userId });
    }
}

/**
 *  SetUserProperty Method used to set the user properties
 */
export const setUserProperty = (name: string, value: string) => {
    if(isPlatform('desktop')) {
        return true;
    }
    if(name && value) {
        FirebaseAnalytics.setUserProperty({name, value});
    }
}

/**
 *  SetScreenName Method used to log screen navigationss
 */
export const setScreenName = (screenName: string) => {
    if(isPlatform('desktop')) {
        return true;
    }
    if(screenName) {
        FirebaseAnalytics.setScreenName({screenName});
    }
}

export const getSessionId = (id: string): string => {
  return id + moment(new Date()).format('MMDDYYYYHHmmss');
};

const fetchCurrentTime = (): string => {
  const month = new Date().getMonth() + 1;
  const date = new Date().getDate();
  const year = new Date().getFullYear();
  const hour = new Date().getHours();
  const minute = new Date().getMinutes();
  const seconds = new Date().getSeconds();
  const monthStr = month / 10 < 1 ? `0${month.toString()}` : month.toString();
  const dateStr = date / 10 < 1 ? `0${date.toString()}` : date.toString();
  const yearStr = (year % 100).toString();
  const hourStr = hour / 10 < 1 ? `0${hour.toString()}` : hour.toString();
  const minuteStr = minute / 10 < 1 ? `0${minute.toString()}` : minute.toString();
  const secondsStr = seconds / 10 < 1 ? `0${seconds.toString()}` : seconds.toString();
  return monthStr + dateStr + yearStr + hourStr + minuteStr + secondsStr;
};

export const getApplicationInfo = async () => {
  const info = await Device.getInfo();
  let channel = null;
  if (info.platform === 'web') {
    channel = 'web-app';
  } else if (info.platform === 'android' || info.platform === 'ios') {
    channel = 'mobile-app';
  }
  const timeString = fetchCurrentTime();
  const uuidLastFour = info.uuid;
  const infoObj = {
    os: info.operatingSystem,
    device_name: info.model,
    model: info.model,
    channel,
    uuid: uuidLastFour.substring(uuidLastFour.length - 4),
    sessionId: uuidLastFour.substring(uuidLastFour.length - 4) + timeString,
    userId: IONICWrapper.details.auth.user.upn,
    roles: IONICWrapper.details.auth.user.roles.join(',')
  };
  return infoObj;
};

export const getApplicationInfoBeforeLogin = async () => {
  const info = await Device.getInfo();
  let channel = null;
  if (info.platform === 'web') {
    channel = 'web-app';
  } else if (info.platform === 'android' || info.platform === 'ios') {
    channel = 'mobile-app';
  }
  const infoObj = {
    os: info.operatingSystem,
    device_name: info.model,
    model: info.model,
    channel
  };
  return infoObj;
};
