

import React, { Component, useState } from 'react';
import AppHeader from '../../../components/app/Bars/Bar-header';
import { IonContent, IonPage, IonCard } from '@ionic/react';
import '../Menu.page.scss';
import { Accordion } from './Accordion';
import { HtmlParseComponent } from '../htmlParsingComponent';
import getType from '../GetDataType';

export class CommonMenuPage extends Component<any, any> {
    constructor(props: any) {
        super(props);
        this.state = {
            isRichText: true
        }
      }
    
    render() {
        let contextData;
        let type:any;
        const entries = this.props &&
            this.props.history &&
            this.props.history.location &&
            this.props.history.location.state &&
            this.props.history.location.state.contextData &&
            this.props.history.location.state.contextData;
        if (entries && entries.sections) {
            contextData = entries && entries.sections;
            type = getType(entries.dataTypes, "sections");
        } else if (entries && entries.group) {
            contextData = entries && entries.group;
            type = getType(entries.dataTypes, "group");
        }
        const url = this.props.match.url;
        const module = this.props.match.params[0];
        const menuHeading = this.props && this.props.history && this.props.history.location && this.props.history.location.state && this.props.history.location.state.heading;
        return (
            <IonPage data-test="IonPage-Menu" className={url === "/app/more/FrequentlyAskedQuestions" ? 'desktop-page-faq' : 'desktop-page-menu'}>
                <AppHeader title={menuHeading} backUrl={"/app/" + module} isSearch={true} tab={module} History={this.props.history} />
                <IonContent class="ion-padding menu_list_page_content">
                    {entries && entries.richText ? <HtmlParseComponent richText={entries.richText} isRichText={this.state.isRichText}/> : contextData && contextData.map((detailList: any, index: number) =>
                        <IonCard key={index} class="ion-card" >
                            <Accordion contextData={detailList} dataType={type}/>
                        </IonCard>
                    )}
                </IonContent>
            </IonPage>
        );
    }
}
export default CommonMenuPage;