export function* Registeruser(data: any) {
  try {
    clearRegisterUserStatusMessage();
    yield put(showLoading());
    const { token, body } = data.payload;
    let registerQuery = DomainConverter.domainToData<any>(body);
    const fetchhResults = yield call(request, [
      "FETCH_USER_DETAILS",
      "USER_DETAILS",
      null,
      token,
      registerQuery,
      "application/json"
    ]);
    const regiserUser =
      (fetchhResults &&
        fetchhResults.data &&
        fetchhResults.data.registerMember) ||
      {};

    const errors = fetchhResults && fetchhResults.errors;
    if (!errors) {
      yield put(fetchUserDetailsSuccess(regiserUser.member));
      yield put(
        registerUserStatusMessage({
          status: regiserUser.status,
          message: regiserUser.message
        })
      );
    } else if (errors && errors.length > 0 && errors[0].message) {
      yield put(
        registerUserStatusMessage({
          status: false,
          message: errors[0].message
        })
      );
    }
    yield put(hideLoading());
  } catch (e) {
    yield put(hideLoading());
  }
}

