export default function* rootSaga() {
  yield all([
    takeLatest(constants.UPDATE_GPS_COORDS, updateGpsData),
    takeLatest(constants.OFFLINE_MODE_START, fetchConfigJSON),
    takeLatest(constants.FIND_SEARCH_DATA, findSearchDataAPI),
    takeLatest(constants.FIND_SHIPMENT_INFO, findShipmentInfoAPI),
    takeLatest(constants.FETCH_USER_DETAILS, fetchUserDetailsSaga),
    takeLatest(constants.REGISTER_DEVICE_ACTION, registerDeviceSaga),
    takeLatest(
      constants.FETCH_DRIVER_ASSIGNED_LOADS_FROM_PUSH,
      fetchDriverrAssignedLoadsFromPush
    ),
    takeLatest(constants.GET_MORE_TAB_CONTENT, fetchMoreTabContent),
    takeLatest(constants.MYLOAD_CARD_DATA, MyLoadCardDataData),
    takeLatest(constants.RECENT_LOADS, fetchRecentResults),
    takeLatest(constants.REGISTER_USER, Registeruser),
    takeLatest(constants.FAVORITE_LOADS_DATA, fetchFavoriteData)
  ]);
}
