import { EquipmentFlow } from "app/schemas/Exicution/exicution.schema";
import { BOBTAIL } from "app/utils/constants";

export type StopActivityProps = {
  id: string;
  stopId: string;
  seqNo: string;
  execActivity: string;
  tmsActivity: string;
  equipmentId: string;
  equipmentType: string;
  equipmentOwner: string;
  equipmentFlow: EquipmentFlow;
  name: string;
};

export const defaultStopActivity: StopActivityProps = {
  equipmentFlow: EquipmentFlow.NA,
  equipmentId: "",
  equipmentOwner: "",
  equipmentType: "",
  execActivity: "",
  id: "",
  name: "",
  seqNo: "",
  stopId: "",
  tmsActivity: "",
};

export const stopActivityProps = {
  id: true,
  stopId: true,
  seqNo: true,
  execActivity: true,
  tmsActivity: true,
  equipmentId: true,
  equipmentType: true,
  equipmentOwner: true,
  equipmentFlow: true,
  name: true,
};

export const getStopActivity = (
  stopActivity: StopActivityProps[] = []
): string => {
  let activity: StopActivityProps[] = stopActivity || [];
  activity = activity.filter(
    (item: StopActivityProps) => (item.name || "").toUpperCase() != BOBTAIL
  );
  activity = activity
    .slice()
    .sort((activity1: StopActivityProps, activity2: StopActivityProps) => {
      return Number(activity1.seqNo) - Number(activity2.seqNo);
    });

  const activityNames: string[] = [];

  activity.forEach((item: StopActivityProps) => {
    if (!activityNames.includes((item.name || "").trim())) {
      activityNames.push(item.name);
    }
  });

  return activityNames.map((item: string) => item).join(", ") || "";
};
