import React, { useState, useEffect } from 'react';
import { IonGrid, IonModal, IonIcon, IonRow, IonCol, isPlatform } from '@ionic/react';
import './carrierAssignFilter.scss';
import { MODULES } from '../../../utils/constants';

export const DriverNameFilter = (props: any) => {
  const allDriver = 'All Driver';
  const allDrivers = 'All Drivers';
  const [showList, setShowList] = useState(false);
  const [dispalyText, setDisplayText] = useState(allDrivers);
  const [selected, setSelected] = useState(allDriver);
  const [chosed, setChosed] = useState(allDriver);
  const [count, setCount] = useState(props.data ? props.data.length : 0);
  const [filterCriteria, setFilterCriteria] = useState([
    { moduleName: MODULES.DRIVER_ASSIGNED_MODULE, option: allDriver },
    { moduleName: MODULES.INTRANSIT_MODULE, option: allDriver },
    { moduleName: MODULES.RECENTLY_DELIVERED_MODULE, option: allDriver }
  ]);
  let filteredByDriver = props.data;
  // to display the selected driver name and filter data accordingly
  const onNameselect = (driver: any): void => {
    let displayName = '';
    setShowList(false);
    if (driver === allDriver) {
      displayName = allDrivers;
      setDisplayText(allDrivers);
    } else if (driver === 'Unknown') {
      displayName = 'Unknown';
      if (isPlatform('mobile')) {
        setDisplayText('Assigned to Unknown');
      } else {
        setDisplayText('Unknown');
      }
      filteredByDriver = props.data.filter((record: any) => {
        if (
          props.module !== MODULES.DRIVER_ASSIGNED_MODULE ||
          (props.module === MODULES.DRIVER_ASSIGNED_MODULE &&
            record.driverAssignmentStatus === true)
        ) {
          if (record.shipmentInvolvedParties.length > 0) {
            const involvedParties = record.shipmentInvolvedParties.find((party: any) => {
              return party.partyType === 'PRIMARY DRIVER';
            });
            if (involvedParties) {
              if (involvedParties.partyName === null) {
                return true;
              }
            } else {
              return true;
            }
          } else {
            return true;
          }
        }
        if (
          props.module === MODULES.DRIVER_ASSIGNED_MODULE &&
          record.driverAssignmentStatus === false
        ) {
          const driverName =
            record.assignmentTransaction &&
            record.assignmentTransaction[0] &&
            record.assignmentTransaction[0].driverName;

          return driverName === null;
        }
      });
    } else {
      displayName = driver.slice(0, driver.indexOf(' '));

      if (isPlatform('mobile')) {
        setDisplayText(`Assigned to ${displayName}`);
      } else {
        setDisplayText(displayName);
      }
      filteredByDriver = props.data.filter((record: any) => {
        if (
          props.module !== MODULES.DRIVER_ASSIGNED_MODULE ||
          (props.module === MODULES.DRIVER_ASSIGNED_MODULE &&
            record.driverAssignmentStatus === true)
        ) {
          return record.shipmentInvolvedParties.find((party: any) => {
            return party.partyName === driver && party.partyType === 'PRIMARY DRIVER';
          });
        }
        if (
          props.module === MODULES.DRIVER_ASSIGNED_MODULE &&
          record.driverAssignmentStatus === false
        ) {
          const driverName =
            record.assignmentTransaction &&
            record.assignmentTransaction[0] &&
            record.assignmentTransaction[0].driverName;

          return driverName === driver;
        }
      });
    }
    props.onSelect(filteredByDriver, displayName);
    setCount(filteredByDriver.length);
  };
  useEffect(() => {
    if (props.data) {
      const curentModule = filterCriteria.find((obj: any) => {
        return obj.moduleName === props.module;
      });
      const option = curentModule ? curentModule.option : allDriver;
      onNameselect(option);
    }
    // setCount(props.data ? props.data.length : 0)
  }, [JSON.stringify(props.data), props.module]);
  useEffect(() => {
    if (isPlatform('desktop')) {
      if (props.segmentName === 'Driver Assigned' || props.segmentName === 'Loads Assigned') {
        setDisplayText(props.filteredDriverNameD);
      }
      if (props.segmentName === 'In-Transit') {
        setDisplayText(props.filteredDriverNameI);
      }
    }
  }, [props.segmentName]);

  const func = (): void => {
    setSelected(chosed);
    onNameselect(chosed);
    const newFilterCriteria = [...filterCriteria];
    newFilterCriteria.forEach((obj: any) => {
      if (obj.moduleName === props.module) obj.option = chosed;
    });
    setFilterCriteria(newFilterCriteria);
  };
  return (
    <>
      {isPlatform('mobile') ? (
        <IonGrid class="display-text-grid">
          {dispalyText} ({count})
          <IonIcon src="assets/icon/filter.svg" onClick={() => setShowList(true)} />
        </IonGrid>
      ) : (
        <IonRow class="display-text-grid-desktop">
          <IonCol class="count-col" size="6">
            {props.countText}
          </IonCol>
          <IonCol class="filter-col" size="6">
            {dispalyText}
            <IonIcon src="assets/icon/filter.svg" onClick={() => setShowList(true)} />
          </IonCol>
        </IonRow>
      )}
      <IonModal isOpen={showList} cssClass="dropdown-modal" onDidDismiss={() => setShowList(false)}>
        {/* <div  className="modal-div" onClick={() => (setShowList(false))}> */}
        <div className="search_options">
          <IonRow class="dropdown-heading">
            <IonCol>
              <p className="sortBy_text">Filter by</p>
            </IonCol>
          </IonRow>
          {props.driverNameList.map((option: any, k: any) => (
            <IonRow className="dropdown-row" key={k} onClick={() => setChosed(option)}>
              <IonCol class={option === chosed ? 'checked' : ''}>
                {option} <i className="check"></i>
              </IonCol>
            </IonRow>
          ))}
          <IonRow class="dropdown-handler">
            <IonCol size="1" />
            <IonCol
              size="5"
              className="dropdown_cancel"
              onClick={() => {
                setShowList(false);
                setChosed(selected);
              }}>
              <span>Cancel</span>
            </IonCol>
            <IonCol size="5" className="dropdown_cancel" onClick={func}>
              <span>OK</span>
            </IonCol>
            <IonCol size="1" />
          </IonRow>
        </div>
        {/* </div> */}
      </IonModal>
    </>
  );
};
export default DriverNameFilter;
