import React, { useState, useEffect } from 'react';
import { IonCol, IonModal, IonRow, IonIcon, IonText } from '@ionic/react';
import './SortOnly.scss';
import { ASC_DIRECTION, DESC_DIRECTION } from '../../../utils/constants';

const SortOnly: React.FC<any> = (props: any) => {
  const {
    dropdown: { options, setFilter },
    direction = ASC_DIRECTION,
    storeOptions = []
  } = props;
  const selectedOptions =
    storeOptions.length > 0
      ? storeOptions
      : options.map((option: any, index: number) => ({ ...option, selected: index === 0 }));
  const [isModalOpen, setModalOpen] = useState(false);
  const [selectedOption, setSelectedOptions] = useState<any>(selectedOptions);
  const [filterOption, setFilterOptions] = useState<any>(selectedOptions);
  const [sortOrder, setSortOrder] = useState(direction);
  const selectItems = selectedOption.filter((option: any) => option.selected);
  const btnTitle = selectItems.map((option: any) => option.name).join(' + ');
  const openModel = () => {
    setFilterOptions(selectedOption);
    setModalOpen(true);
  };

  useEffect(() => {
    const selectedOptions =
      storeOptions.length > 0
        ? storeOptions
        : options.map((option: any, index: number) => ({ ...option, selected: index === 0 }));
    setSelectedOptions(selectedOptions);
    setFilterOptions(selectedOptions);
  }, [props.dropdown, storeOptions]);

  useEffect(() => {
    setSortOrder(direction);
  }, [direction]);

  const setFilterOption = (opion: any) => {
    const options = filterOption.map((item: any) => {
      return {
        ...item,
        selected: item.name === opion.name ? !item.selected : false
      };
    });
    const selected = options.find((option: any) => option.selected);
    if (selected) {
      setFilterOptions(options);
    }
  };

  const sortData = (direction = sortOrder) => {
    setFilter({
      filter: {
        selected: filterOption,
        direction
      }
    });
  };

  const confirmSelection = () => {
    sortData();
    setSelectedOptions(filterOption);
    setModalOpen(false);
  };

  const handleSorting = () => {
    const direction = sortOrder === ASC_DIRECTION ? DESC_DIRECTION : ASC_DIRECTION;
    setSortOrder(direction);
    sortData(direction);
  };
  return (
    <IonRow class="list-items-header sort-only-wrapper">
      <IonCol>
        <div className="list-items-filter">
          <IonText item-left class="filter-dropdown" onClick={openModel}>
            {btnTitle}
            <i className="down"></i>
          </IonText>
          <IonText class="filter-sort" onClick={handleSorting}>
            <IonIcon
              src={`assets/icon/sort icon${sortOrder !== DESC_DIRECTION ? ' downward' : ''}.svg`}
            />
          </IonText>
        </div>
        <IonModal
          show-backdrop
          backdrop-dismiss={false}
          isOpen={isModalOpen}
          cssClass="dropdown-modal"
          onDidDismiss={() => {
            setModalOpen(false);
          }}>
          <div className="search_options">
            <IonRow class="dropdown-row dropdown-heading">
              <IonCol>
                <p className="sortBy_text">Sort By</p>
              </IonCol>
            </IonRow>
            {filterOption.map((option: any, k: any) => (
              <IonRow
                className="dropdown-row filter-option"
                key={k}
                onClick={() => setFilterOption(option)}>
                <IonCol className={option.selected ? 'checked' : ''}>
                  {option.name} <i className="check"></i>
                </IonCol>
              </IonRow>
            ))}
            <IonRow class="dropdown-handler">
              <IonCol size="1" />
              <IonCol size="5" className="dropdown_cancel" onClick={() => setModalOpen(false)}>
                <span>Cancel</span>
              </IonCol>
              <IonCol size="5" className="dropdown_cancel dropdown_ok" onClick={confirmSelection}>
                <span>OK</span>
              </IonCol>
              <IonCol size="1" />
            </IonRow>
          </div>
        </IonModal>
      </IonCol>
    </IonRow>
  );
};

export default SortOnly;
