import { APPLE_DEVICES } from '../utils/constants';
import { Device } from '@ionic-enterprise/device';
import { isPlatform } from '@ionic/react';

export const capitalize = (str: any, lower = false) =>
    (lower ? str.toLowerCase() : str).replace(/(?:^|\s|["'([{])+\S/g, (match: any) => match.toUpperCase());
;

export const deviceModel = () => {
    const model = APPLE_DEVICES.find((device) => device.identifier === Device.model);
    return model ? model.generation : Device.model;
}

export const manufacturer = () => Device.manufacturer;
export const manufacturerForAndroid = () => isPlatform('android') ? Device.manufacturer : "";
export const operationSystem = () => Device.platform;
export const osVersion = () => Device.version;
export const osAndVersion = () => `${operationSystem} ${osVersion}`;

