import { IRecentDataRequest } from "../../schemas/Recent/Recent.Schema";
import { IFavoriteUpdateRequest } from "../../schemas/Favorite/Favorite.Schema";

export class Recent {

    constructor() {
    }

    private __recent_state: IRecentDataRequest = {page:0, sortColumn:"pickupdate", favourite:null};

    get page(){
        return this.__recent_state.page;
    }

    set page(value){
        if(value) {
            this.__recent_state.page = value;
        }
    }
    get size(){
        return this.__recent_state.size;
    }

    set size(value){
        if(value) {
            this.__recent_state.size = value;
        }
    }
    get sortColumn(){
        return this.__recent_state.sortColumn;
    }

    set sortColumn(value){
        if(value) {
            this.__recent_state.sortColumn = value;
        }
    }
    get userID(){
        return this.__recent_state.userID;
    }

    set userID(value){
        if(value) {
            this.__recent_state.userID = value;
        }
    }
    get carrierId(){
        return this.__recent_state.carrierId;
    }

    set carrierId(value){
        if(value) {
            this.__recent_state.carrierId = value;
        }
    }
    get favourite(){
        return this.__recent_state.favourite;
    }

    set favourite(value){
        if(value) {
            this.__recent_state.favourite = value;
        }
    }

    getRequestPayload() {
        return {
            query: {
                findUserRecentSearchHistory: {
                    __args: {
                        input: this.__recent_state
                    },
                    id: true,
                    userid: true,
                    originCity: true,
                    originState: true,
                    originCountry: true,
                    originradius: true,
                    destinationCity: true,
                    destinationState: true,
                    destinationCountry: true,
                    destinationradius: true,
                    pickupdate: true,
                    favouritesearchindicator: true,
                    trailertype: true,
                    dropdate: true,
                    searchdate: true,
                    destinationPostalCode: true,
                    originPostalCode: true,
                    carrierid: true,
                    durationDays: true
                }

                }
            }
        }
}