export const manageCarrierdata = (props:any) => [
    {
        label: 'Carrier Name',
        value: props.carrierName || '-'
    },
    {
        label: 'Carrier ID',
        value: props.commonCarrierId || '-'
    },
    {
        label: 'Carrier MC#',
        value: props.carrierMCNo || '-'
    },
    {
        label: 'USDOT#',
        value: props.carrierUsDOTNo || '-'
    },
];

export const manageContactdata = (props:any) => [
    {
        label: 'Carrier Address',
        value: props && Object.keys({
            "addressLine1": props.addressLine1 || null,
            "addressLine2": props.addressLine2 || null,
            "city": props.city || null,
            "state": props.state || null,
            "country": props.country || null,
            "postalCode": props.postalCode || null,
        }).filter(key => props[key]).map(key => props[key]).join(', ') || '-'
    },
    {
        label: 'Carrier Contact',
        value: props.primaryContact && (`${props.primaryContact.contactFirstName}, ${props.primaryContact.contactLastName}`)  || '-' 
    },
    {
        label: 'Carrier Phone',
        value: props.primaryContact && props.primaryContact.contactPhoneNumber || '-'
    },
    {
        label: 'Email',
        value:  props.primaryContact && props.primaryContact.contactEmail  || '-'
    },
];
