import React, { useState, useEffect } from "react";
import { IonLabel } from "@ionic/react";
import { ITrailerInProps } from "../../../schemas/StartTrip/StartTrip";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import { ComboBox } from "./trailerComboBox";

export const TrailerIn: React.FunctionComponent<ITrailerInProps> = (
  props: ITrailerInProps
) => {
  const model: StartTripModel = new StartTripModel();
  const isOtmType = model.isOTMTypeShipment(props.shipment);
  const trailerIn = model.getTrailerIn(
    props.loadStop,
    props.isNextStop,
    props.readonly,
    props.shipment,
    props.updateStopInfo
  );
  useEffect(() => {
    props.onChange(trailerIn.trailerIn);
    if (trailerIn && trailerIn.activity) {
      props.changeActivity(trailerIn.activity);
    }
  }, [
    trailerIn.trailerIn,
    JSON.stringify(trailerIn.activity),
    props.shipmentId,
    props.timestamp,
  ]);

  return (
    <>
      <IonLabel className="ETA-label ">Trailer In</IonLabel>
      <ComboBox
        value={props.value}
        onChange={(value: string) => props.onChange(value)}
        showError={props.showError}
        disabled={props.readonly || props.isNextStop}
        onFocus={() => {
          props.expandCurrentStopForcefully();
        }}
      />
    </>
  );
};
