import React from 'react';
import {
  isPlatform,
  IonModal,
  IonContent,
  IonToolbar,
  IonButtons,
  IonTitle,
  IonIcon
} from '@ionic/react';
import { LoadMap } from './LoadMap';

import './LoadMap.scss';

export interface ILoadMapSatelliteProps {
  locationName: string;
  showSatelliteMap: boolean;
  loadStop: any;
  onDismissSatelliteMap: () => void;
  shipmentId: string;
}

export const LoadMapStartTrip: React.FunctionComponent<ILoadMapSatelliteProps> = (
  props: ILoadMapSatelliteProps
) => {
  const isDesktop = isPlatform('desktop');
  const { showSatelliteMap, onDismissSatelliteMap, loadStop, shipmentId } = props;

  return (
    <>
      <IonModal
        isOpen={showSatelliteMap}
        onDidDismiss={() => onDismissSatelliteMap()}
        animated={false}
        cssClass={`${isDesktop ? 'satellite-view-desktop' : 'satellite-view-mobile'}`}>

        <IonContent fullscreen={isDesktop}>
          <IonToolbar className="satellite-toolbar">
            <IonButtons
              slot="end"
              onClick={() => {
                onDismissSatelliteMap();
              }}>
              <IonIcon className="satellite-close" icon="close" />
            </IonButtons>
            <IonTitle className="satellite-title">{`${
              isDesktop ? 'Satellite view' : shipmentId
            }`}</IonTitle>
          </IonToolbar>
          <div className="startTrip-map">
            <LoadMap loadStop={loadStop} />
          </div>
        </IonContent>
      </IonModal>
    </>
  );
};
export default LoadMapStartTrip;
