import {
  IonContent,
  IonRow,
  IonCol,
  IonGrid,
  IonIcon,
  IonButton,
  IonLabel,
  IonCard,
  IonCardHeader,
  IonCardContent,
  IonFooter,
  isPlatform
} from "@ionic/react";
import React, { PureComponent } from "react";
import "./NewPage.scss";
import { withRouter, RouteComponentProps } from "react-router";
import {
  Anywhere,
  PickUpdateError,
  dropUpdateError,
  CityError,
  DryVan,
  PickUpdate,
  Dropdate,
  Destination,
  Origin,
  DestinationRadius,
  EVENT,
  SCREEN,
  TAB,
  OFFLINE_MODE_STATE, PickupError, DropoffError
} from "../../../utils/constants";
import { NewDropDown } from "../../../components/shared/NewUIComponents/DropDown";
import SearchResultDesktop from "../../../components/shared/NewUIComponents/searchResultsDesktop";
import PickupData from "../../../components/app/Search/PickupData";
import DeliverypData from "../../../components/app/Search/DeliverypData";
import { Isearch } from "../../../schemas/Search/Search.schema";
import { Search } from "../../../models/home/Search.model";
import { ToastMessage } from "../../../utils/ToastMessage";
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK
} from "../../../utils/constants";
import {
  logEvent,
  setScreenName,
  getApplicationInfo
} from "../../../utils/Analytics";

export interface newProps extends RouteComponentProps {
  history: any;
  data: any;
  submitForm: any;
  results: any;
  isRecentSearch: boolean;
  totalRecords: number;
  searchInput: any;
  handleSortingPagination: (searchInput: any, isPagination: boolean) => void;
  userDetails: any;
  isNetworkAvailable: boolean;
  showAlert: any;
}
const currentDate = new Date().toISOString();
class NewPage extends PureComponent<newProps, any> {
  state = {
    showDelivaryfields: false,
    origin: "",
    originCountry: "",
    destinationCountry: "",
    destination: Anywhere,
    originRadius: 100,
    pickUpdate: currentDate,
    dropdate: "",
    TrailerType: DryVan,
    destinationRadius: 100,
    favorite: false,
    searchResultPage: false,
    pickUpdateError: "",
    dropUpdateError: "",
    destinationError: "",
    originError: "",
    isCityError: false,
    newSearch: {
      origin: "",
      destination: Anywhere,
      originRadius: 100,
      pickUpdate: currentDate,
      dropdate: "",
      TrailerType: DryVan,
      destinationRadius: 100,
      favorite: false
    },
    rangeDisable: true,
    rangeOriginDisable: false,
    showResults: this.props.isRecentSearch,
    pickUpLocalTime: currentDate,
    dropLocalTime: currentDate
  };
  placeSearch = {};
  SelectedOriginVal = "";
  SelectedDestinationVal = "";
  dateUpdated: boolean = false;
  dropDateUpdated: boolean = false;
  componentDidUpdate() {
    if (
      this.state.destination.length > 0 &&
      this.state.destination !== Anywhere &&
      this.state.TrailerType.length > 0 &&
      (this.state.destinationRadius === 100 ||
        this.state.destinationRadius > 100)
    ) {
      this.setState({ rangeDisable: false });
    }
    if (
      this.state.destination.length > 0 &&
      this.state.destination === Anywhere
    ) {
      this.setState({
        rangeDisable: true,
        dropdate: "",
        destinationRadius: 100,
        dropUpdateError: ""
      });
    }
  }

  keyboardshowHandler = () => {
    const headerDiv = document.getElementsByClassName("search-form-mobile")[0];
    headerDiv.classList.add("scroll-up");
  };
  keyboardhideHandler = () => {
    const headerDiv = document.getElementsByClassName("search-form-mobile")[0];
    headerDiv.classList.remove("scroll-up");
  };
  handleChange = (event: any) => {
    const { value } = event.target;
    const { backClicked } = event.target;
    let pickUpLocalTime =
      this.state.pickUpLocalTime && this.state.pickUpLocalTime.split("T")[0];
    let pickUpdateValue = "";
    if (event.target.name === PickUpdate) {
      pickUpdateValue = event.target.value;
    }
    this.setState({
      [event.target.name]: value
    });
    // if (
    //   event.target.name === Origin &&
    //   event.target.value &&
    //   (event.target.value.includes("&") ||
    //     event.target.value.includes("!") ||
    //     event.target.value.includes("=") ||
    //     event.target.value.includes("@") ||
    //     event.target.value.includes("#") ||
    //     event.target.value.includes("$") ||
    //     event.target.value.includes("^") ||
    //     event.target.value.includes("%") ||
    //     event.target.value.includes("*") ||
    //     event.target.value.includes("+") ||
    //     event.target.value.includes("-"))
    // ) {
    //   this.setState(() => ({ originError: CityError }));
    //   return;
    // }
    if (event.target.name === PickUpdate && pickUpdateValue < pickUpLocalTime) {
      this.setState({ pickUpdateError: PickUpdateError });
    } else if (
      event.target.name === PickUpdate &&
      pickUpdateValue > pickUpLocalTime
    ) {
      this.setState({ pickUpdateError: "" });
    } else if (
      event.target.name === PickUpdate &&
      pickUpdateValue === pickUpLocalTime
    ) {
      this.setState({ pickUpdateError: "" });
    } else if (
      event.target.name === Dropdate &&
      event.target.value >= this.state.pickUpdate
    ) {
      this.setState({ dropUpdateError: "" });
    } else if (
      event.target.name === Dropdate &&
      event.target.value < this.state.pickUpdate &&
      event.target.name === Dropdate &&
      event.target.value !== ""
    ) {
      this.setState({ dropUpdateError: dropUpdateError });
    } else if (
      event.target.name === Origin &&
      event.target.value.length === 0 &&
      !backClicked
    ) {
      this.setState({ originError: PickupError });
    } else if (
      event.target.name === Origin &&
      event.target.country.length > 0
    ) {
      this.setState({ originCountry: event.target.country });
    } else if (
      event.target.name === Destination &&
      event.target.country.length > 0
    ) {
      this.setState({ destinationCountry: event.target.country });
    } else if (event.target.name === Origin && event.target.value.length > 0) {
      this.setState({ originError: "" });
    } else if (
      event.target.name === Destination &&
      event.target.value.length === 0 &&
      !backClicked
    ) {
      this.setState({ destinationError: DropoffError, rangeDisable: true });
    } else if (
      event.target.name === Destination &&
      event.target.value.length > 0
    ) {
      this.setState({ destinationError: "", rangeDisable: false });
    } else if (
      event.target.name === DestinationRadius &&
      event.target.value > 100
    ) {
      this.setState({ rangeDisable: false });
    }
    if (
      event.target.name === PickUpdate &&
      this.state.dropdate < event.target.value &&
      this.state.dropdate !== ""
    ) {
      this.setState({ dropUpdateError: dropUpdateError });
    } else if (
      event.target.name === PickUpdate &&
      this.state.dropdate >= event.target.value
    ) {
      this.setState({ dropUpdateError: "" });
    }
  };

  // componentWillMount() {
  //   this.setState(this.props.data);
  // }

  componentDidMount() {
    setScreenName("screen_search_page");
    this.setState(this.props.data);

    // Analytics implementation
    logEvent(EVENT.SV_SEARCH_FILTER, {
      screen: SCREEN.SEARCH_FILTER,
      tab: TAB.NEW
    });
  }

  dropDownChange = (event: any) => {
    this.setState(() => ({
      [event.name]: event.value
    }));
  };

  setErrorValue = (value: boolean, name: string) => {
    if (name === Origin && value === true) {
      this.setState({ originError: PickupError });
    } else if (name === Destination && value === true) {
      this.setState({ destinationError: DropoffError });
    } else if (name === Origin && value === false) {
      this.setState({ originError: "" });
    } else if (name === Destination && value === false) {
      this.setState({ destinationError: "" });
    }
  };
  Apply = (event: any) => {
    logEvent("ue_search_request", { page: "Search Page" });
    logEvent(EVENT.UE_SEARCH_SEE_RESULTS, {
      screen: SCREEN.SEARCH_FILTER,
      tab: TAB.NEW
    });
    event.preventDefault();
    if (this.state.destination !== Anywhere && this.state.dropdate === "") {
      this.setState({ dropUpdateError: dropUpdateError });
    } else {
      this.setState({ dropUpdateError: "" });
      /* NETWORK CHECK CONDITION*/
      if (this.props.isNetworkAvailable) {
        this.newSearch();
      } else {
        this.props.showAlert({
          isShowAlert: true,
          alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
          alertTitle: NO_NEWORK
        });
      }
    }
  };
  newSearch = () => {
    const {
      origin,
      originRadius,
      pickUpdate,
      TrailerType,
      dropdate,
      destinationRadius,
      favorite,
      destination,
      originCountry,
      destinationCountry
    } = this.state;
    // Generate Query For request
    let pickupLocal = "";
    if (this.state.pickUpLocalTime) {
      pickupLocal = this.state.pickUpLocalTime.split("T")[1];
    }
    let dropLocal = "";
    if (this.state.dropLocalTime) {
      dropLocal = this.state.dropLocalTime.split("T")[1];
    }

    const __searchParams: Isearch = {
      origin: origin,
      originCountry: originCountry,
      destinationCountry: destinationCountry,
      originRadius: originRadius,
      destination: destination,
      pickUpdate:
        (pickUpdate && `${pickUpdate.split("T")[0]}T${pickupLocal}`) || "",
      TrailerType: TrailerType,
      dropdate: (dropdate && `${dropdate.split("T")[0]}T${dropLocal}`) || "",
      destinationRadius: destinationRadius,
      favorite: favorite
    };

    if (__searchParams.origin) {
      this.setState({
        newSearch: Object.assign(__searchParams),
        searchResultPage: true
      });
      if (
        this.state.destinationError === "" &&
        this.state.originError === "" &&
        this.state.dropUpdateError === "" &&
        this.state.pickUpdateError === "" &&
        !this.state.isCityError
      ) {
        this.props.submitForm(__searchParams);
        if (isPlatform("capacitor") || !isPlatform("desktop")) {
          this.props.history.push("/app/search/results", {
            params: __searchParams,
            searchInput: this.getSearchInputes(__searchParams),
            totalRecords: this.props.totalRecords
          });
        } else if (isPlatform("desktop")) {
          this.setState({ showResults: true });
        }
      }
    } else {
      this.setState(() => ({ originError: PickupError }));
    }
  };

  getSearchInputes = (searchdata: any): Search => {
    let __model: Search = new Search();
    if (searchdata) {
      const destinationArray =
        searchdata.destination.indexOf(",") !== -1
          ? searchdata.destination.split(",")
          : [searchdata.destination, searchdata.destination];
      const originArray =
        searchdata.origin.indexOf(",") !== -1
          ? searchdata.origin.split(",")
          : [searchdata.origin, searchdata.origin];
      const userRole =
        this.props.userDetails &&
        this.props.userDetails.userApplicationDetails &&
        this.props.userDetails.userApplicationDetails[0].roles.map(
          (carrierName: any, index: number) => {
            return carrierName.roleName;
          }
        );
      const srchParams = searchdata;
      __model.originCity = originArray[0].trim();
      __model.originState = originArray[1].trim();
      __model.originRadius = srchParams.originRadius;
      __model.pickUpdate = srchParams.pickUpdate;
      __model.dropDate = srchParams.dropdate;
      if (searchdata.destination !== "Anywhere") {
        __model.destinationCity = destinationArray[0].trim();
        __model.destinationState = destinationArray[1].trim();
      }
      __model.destinationRadius = srchParams.destinationRadius;
      __model.favorite = srchParams.favorite;
      __model.trailerType = srchParams.TrailerType;
      __model.userID =
        this.props.userDetails && this.props.userDetails.userPrincipalName;
      __model.carrierId =
        this.props.userDetails && this.props.userDetails.carrierId;
      __model.page = 0;
      __model.size = 5;
      __model.sortColumn = "pickUpdate";
      __model.originCountry = srchParams.originCountry;
      __model.destinationCountry = srchParams.destinationCountry;
      __model.carrierScac =
        (this.props.userDetails && this.props.userDetails.carrierScac) || "";
      __model.carrierTier =
        (this.props.userDetails && this.props.userDetails.carrierTier) || "";
      __model.userRole = userRole && userRole[0];
    }

    return __model;
  };

  updateLocalCurrentTime = (feildName: string, localTime: string) => {
    if (feildName === PickUpdate) {
      let pickupdate = this.state.pickUpdate;
      let pickUpLocalTime = localTime;
      if (!this.dateUpdated && localTime) {
        pickupdate = pickUpLocalTime && pickUpLocalTime.split("T")[0];
        this.dateUpdated = true;
      }
      this.setState({ pickUpdate: pickupdate, pickUpLocalTime: localTime });
    }
    if (feildName === Dropdate) {
      this.setState({ dropLocalTime: localTime });
    }
  };
  render() {
    return (
      <>
        <IonContent
          className="ion-padding search_form_container"
          class="background"
        >
          <form className="search-form">
            <IonGrid class="new_page_list search-mobile">
              <IonCard class="trailer-type-card">
                <NewDropDown
                  labelName="Trailer type"
                  TrailerType={this.state.TrailerType}
                  handleChange={this.handleChange}
                />
              </IonCard>
              <IonGrid class="search-form-mobile">
                <IonRow class="pickup-row">Pick-up</IonRow>
                <PickupData
                  data-test="pick-upData"
                  {...this.state}
                  showDate={true}
                  handleChange={this.handleChange}
                  rangeDisabled={this.state.rangeOriginDisable}
                  isDestination={false}
                  defaultValue={Anywhere}
                  useHandler={false}
                  setErrorValue={this.setErrorValue}
                  updateLocalCurrentTime={this.updateLocalCurrentTime}
                />
                <IonCard class="delivery-type-card">
                  <IonCardHeader
                    class={`${
                      this.state.showDelivaryfields ? "" : "changeColor"
                    }`}
                    onClick={() => {
                      this.setState({
                        showDelivaryfields: !this.state.showDelivaryfields
                      });
                    }}
                  >
                    <span> Delivery</span>
                    {this.state.showDelivaryfields ? (
                      <IonIcon src="assets/icon/minus-icon.svg" />
                    ) : (
                      <div className="desitnation-city">
                        {this.state.destination}{" "}
                        <IonIcon src="assets/icon/plus.svg" />
                      </div>
                    )}
                  </IonCardHeader>
                  <IonCardContent
                    class={`${
                      this.state.showDelivaryfields ? "fadein" : "fadeout"
                    }`}
                  >
                    <DeliverypData
                      {...this.state}
                      showDate={true}
                      handleChange={this.handleChange}
                      rangeDisabled={this.state.rangeDisable}
                      platform="mobile"
                      keyboardshowHandler={this.keyboardshowHandler}
                      keyboardhideHandler={this.keyboardhideHandler}
                      defaultValue={Anywhere}
                      isDestination={true}
                      useHandler={true}
                      setErrorValue={this.setErrorValue}
                      DestinationRadius="DestinationRadius"
                      updateLocalCurrentTime={this.updateLocalCurrentTime}
                    />
                  </IonCardContent>
                </IonCard>
              </IonGrid>
              <IonCard class="fav-card">
                <IonLabel>Add as Favorite</IonLabel>{" "}
                <IonIcon
                  data-test="favorite-click"
                  src="assets/icon/star.svg"
                  class={this.state.favorite ? "fav-star" : ""}
                  onClick={() => {
                    // ToastMessage.show(); // Functionality coming soon
                    this.setState({ favorite: !this.state.favorite });
                  }}
                />
              </IonCard>
            </IonGrid>
            <IonGrid class="new_page_list search-desktop">
              <IonRow class="data-type">
                <IonCol>Pickup</IonCol>
                <IonCol>
                  Delivery{" "}
                  <NewDropDown
                    labelName="Trailer Type"
                    TrailerType={this.state.TrailerType}
                    handleChange={this.handleChange}
                  />
                </IonCol>
              </IonRow>
              <IonRow>
                <IonCol class="pickup-data-feilds">
                  <PickupData
                    {...this.state}
                    showDate={true}
                    handleChange={this.handleChange}
                    rangeDisabled={this.state.rangeOriginDisable}
                    isDestination={false}
                    defaultValue={Anywhere}
                    useHandler={false}
                    setErrorValue={this.setErrorValue}
                    updateLocalCurrentTime={this.updateLocalCurrentTime}
                  />
                </IonCol>
                <IonCol class="delivery-data-feilds">
                  <DeliverypData
                    {...this.state}
                    showDate={true}
                    handleChange={this.handleChange}
                    rangeDisabled={this.state.rangeDisable}
                    platform="desktop"
                    defaultValue={Anywhere}
                    isDestination={true}
                    useHandler={false}
                    setErrorValue={this.setErrorValue}
                    updateLocalCurrentTime={this.updateLocalCurrentTime}
                  />
                </IonCol>
              </IonRow>
              <IonRow class="footer-row">
                <IonCol>
                  <IonIcon
                    data-test="fav-click2"
                    src="assets/icon/star.svg"
                    class={this.state.favorite ? "fav-star" : ""}
                    onClick={() => {
                      // ToastMessage.show(); // Functionality coming soon
                      this.setState({ favorite: !this.state.favorite });
                    }}
                  />{" "}
                  <IonLabel class="add-to-fav">Add as Favorite</IonLabel>
                </IonCol>
                <IonCol>
                  <IonButton
                    onClick={event => this.Apply(event)}
                    data-test="apply-mobile"
                  >
                    See Results
                  </IonButton>
                </IonCol>
              </IonRow>
            </IonGrid>
          </form>
          {this.state.showResults ? (
            <SearchResultDesktop
              searchInput={this.props.searchInput}
              handleSortingPagination={this.props.handleSortingPagination}
              data={this.props.results.__carrier_state.searchLoads}
              {...this.props.history}
              pageName="searchResults"
              totalRecords={this.props.totalRecords}
              userDetails={this.props.userDetails}
              isNetworkAvailable={this.props.isNetworkAvailable}
              showAlert={this.props.showAlert}
              lead={TAB.SEARCHED}
            />
          ) : null}
        </IonContent>
        <IonFooter class="searchPageFooter">
          <IonButton
            expand="full"
            data-test="apply-desktop"
            onClick={event => this.Apply(event)}
          >
            See Results
          </IonButton>
        </IonFooter>
      </>
    );
  }
}
export { NewPage };
export default withRouter(NewPage);
