import React, { Component } from "react";
import { MapState, MapProps, MapType } from "./map.type";

export class GoogleMap extends Component<MapProps, MapState> {
  private readonly mapContainer = React.createRef<HTMLDivElement>();

  constructor(props: MapProps) {
    super(props);
    this.state = {
      isMapReady: false,
      map: null,
      google: null,
      directionsService: null,
      directionsRenderer: null,
    };
  }

  initMap = () => {
    const {
      defaultCenter,
      zoom,
      mapTypeId,
      disableDefaultUI,
      mapTypeControl,
      scaleControl,
      streetViewControl,
      zoomControl,
      fullscreenControl,
    } = this.props;
    const center: any = defaultCenter;

    const map = new google.maps.Map(this.mapContainer.current!, {
      center,
      zoom,
      disableDefaultUI,
      mapTypeControl,
      scaleControl,
      streetViewControl,
      mapTypeId: mapTypeId || MapType.roadmap,
      zoomControl,
      fullscreenControl,
    });
    const directionsService = new google.maps.DirectionsService();
    const directionsRenderer = new google.maps.DirectionsRenderer({
      map,
      suppressMarkers: true,
      // markerOptions: {
      //   icon: "http://maps.google.com/mapfiles/ms/icons/orange-dot.png"
      // }
    });
    directionsRenderer.setMap(map);

    this.setState({
      map,
      google,
      isMapReady: true,
      directionsService,
      directionsRenderer,
    });
  };

  renderChildren = () => {
    const { children, defaultCenter } = this.props;

    if (!children) return;
    const { map, google, directionsService, directionsRenderer } = this.state;
    return React.Children.map(children, (child: any) => {
      if (!child) return;
      return React.cloneElement(child, {
        map,
        google,
        mapCenter: defaultCenter,
        directionsService,
        directionsRenderer,
      });
    });
  };

  render() {
    const { className } = this.props;
    return (
      <React.Fragment>
        <div
          style={{
            height: "100%",
            width: "100%",
            translate: "translate3d(0)",
            opacity: 1,
            position: 'relative'
          }}
          ref={this.mapContainer}
          className={className}
        />
        {this.renderChildren()}
      </React.Fragment>
    );
  }

  componentDidMount() {
    this.initMap();
  }
}
