import { all, put, call, takeLatest } from "redux-saga/effects";
import { showLoading, hideLoading } from "../../actions/Login/action";
import * as constants from "../../utils/constants";
import request from "../../utils/request";
import {
  SelectDriverList,
  assignDriverToShipmentResponse,
} from "../../actions/carrierAssign/action";
import { DomainConverter } from "../../utils/common";

export function* fetchDriverListResults(data: any) {
  try {
    yield put(showLoading());
    let driverlistReqestQuery = DomainConverter.domainToData<any>(data.payload);
    const response = yield request<any>([
      "DRIVERS_LIST",
      "DRIVERS_LIST",
      null,
      "",
      driverlistReqestQuery,
      "application/json",
    ]);
    const driverList = (response && response.data && response.data.getMembers) || []
    yield put(SelectDriverList(driverList));
    yield put(hideLoading());
  } catch (e) {
    yield put(hideLoading());
  }
}

export function* assignDriverToShipmentSaga(data: any) {
  try {
    yield put(showLoading());
    let mutationQuery = DomainConverter.domainToData<any>(data.payload);
    const response = yield request<any>([
      "MYLOAD",
      "CARRIER",
      null,
      "",
      mutationQuery,
      "application/json",
    ]);
    yield put(hideLoading());
    yield put(
      assignDriverToShipmentResponse({
        assignDriverToShipment:
          (response.data && response.data.assignDriverToShipment) || false,
        errors: response.errors,
      })
    );
  } catch (e) {
    yield put(hideLoading());
  }
}

export default function* rootSaga() {
  yield all([
    takeLatest(constants.ASSIGN_DRIVER, fetchDriverListResults),
    takeLatest(
      constants.CarrierActionTypes.ASSIGN_DRIVER_TO_SHIPMENT,
      assignDriverToShipmentSaga
    ),
  ]);
}
