
import React, { useState, useEffect } from 'react';
import './search.page.scss'
import { IonContent, IonPage } from '@ionic/react';
import { SectionContainer } from "../../containers/SearchContainer/sectionContainer";
import AppHeader from '../../components/app/Bars/Bar-header';
import { isPlatform } from '@ionic/react';
import { ToastMessage } from '../../utils/ToastMessage';
import { Isearch } from '../../schemas/Search/Search.schema';

interface SearchProps { };
const Search: React.FC<SearchProps> = () => {
	const [segment, setSegment] = useState<any>({ name: 'New', data: {} });
	const [isRecentSearch, setRecentSearch] = useState<boolean>(false);
	const [searchParams, setSearchParms]= useState({});
	const onChangeSegment = (segmentName: string, searchParams: Isearch) => {
		setSegment({ name: segmentName, data: {} });
		if (isPlatform("desktop")) {
			setRecentSearch(true);
			setSearchParms(searchParams);
		}
	}
	useEffect(() => {
		if (segment !== 'New' && isPlatform("desktop")) {
			setRecentSearch(false);
		}
	}, [segment])

	// function is to make 'Favourite' Dummy.
	const makeFavDummy = () => {
		ToastMessage.show();
		setSegment({ name: segment.name as any, data: {} })
	}
	return (
		<IonPage className="desktop-page search-page">
			<AppHeader title="Search" getSegment={(e: any) => {
					setSegment({ name: e.detail.value as any, data: {} })
			}} segments={['New', 'Recent', 'Favorite']} activeSegment={segment.name} />
			<IonContent className="ion-padding load-page-content search-page-content">
				<SectionContainer onRedirectToTab={onChangeSegment} section={segment.name} isRecentSearch={isRecentSearch} searchParams={searchParams} />
			</IonContent>
		</IonPage>
	);
}
export default Search;