import { createSelector } from "reselect";
import { carrierAssignInitialState } from "./../../reducers/carrierAssign/reducer";
import { myLoadInitialState } from "./../../reducers/myload/reducer";

const selectData = (state: any) => state.carrierAssigned;
const selctCarrierData = (state: any) =>
  (state && state.myload && state.myload.carrierAssigned) ||
  myLoadInitialState.carrierAssigned;
const selctAssignedData = (state: any) =>
  (state && state.myload && state.myload.driverAssigned) ||
  myLoadInitialState.driverAssigned;
const selctIntransitData = (state: any) =>
  (state && state.myload && state.myload.inTransit) ||
  myLoadInitialState.inTransit;
const selctDeliveredData = (state: any) =>
  (state && state.myload && state.myload.delivered) ||
  myLoadInitialState.delivered;

export const getShowToasterValue = () =>
  createSelector(
    [selectData],
    (ShowToaster) =>
      ShowToaster.showToaster || carrierAssignInitialState.showToaster
  );

export const getCarrierLoads = () =>
  createSelector(
    selctCarrierData,
    (carrierAssigned) => carrierAssigned || myLoadInitialState.carrierAssigned
  );

export const getDriverList = () =>
  createSelector(
    [selectData],
    (DriverList) =>
      (DriverList && DriverList.DriverList) ||
      carrierAssignInitialState.DriverList
  );

export const getAssignedLoads = () =>
  createSelector(selctAssignedData, (driverAssigned) => driverAssigned);
export const getAssignShipmentDriverResponse = () =>
  createSelector(
    [selectData],
    (carrierAssigned) =>
      carrierAssigned.assignShipmentDriverResponse ||
      carrierAssignInitialState.assignShipmentDriverResponse
  );

export const getInTransitLoad = () =>
  createSelector(
    selctIntransitData,
    (inTransit) => inTransit || myLoadInitialState.inTransit
  );

export const getStartTripShipment = () =>
  createSelector(
    selectData,
    (shipmentData) => shipmentData && shipmentData.shipmentForStartTrip
  );

  export const getStartTripShipmentTimeStamp = () =>
  createSelector(
    selectData,
    (shipmentData) => shipmentData && shipmentData.timestamp
  );

  export const getDeliveredLoad = () =>
  createSelector(
    [selctDeliveredData],
    (delivered) => delivered || myLoadInitialState.delivered
  );
