import * as React from 'react';
import './Load-Tile.scss';

class LoadTileFooter extends React.Component<any, any> {
  public render() {
    const stopOffs = this.props.numberOfStops - 2;
    return (
      <div>
        <p className="card_p-elements loadno_card">
          {this.props.showLoadNo ? this.props.shipmentId : ""}
        </p>
        {this.props.rate === null ||
        (this.props.rate && this.props.rate.totalCharge === null) ||
        (this.props.rate && this.props.rate.totalCharge === 0) ? (
          <p
            className="card_p-elements card-contact_for_price"
            data-test="contact-price"
          >
            Contact for price
          </p>
        ) : (
          <>
            {this.props.rate && this.props.rate.totalCharge ? (
              <p className="card-price" dat-test="card-price">
                ${Math.trunc(this.props.rate.totalCharge)}
              </p>
            ) : null}
          </>
        )}
        {this.props.totalDistance ? (
          <p
            className={`card-distance ${
              this.props.showLoadNo ? "no-margin" : ""
            }`}
            data-test="card-distance"
          >
          {Math.round(this.props.totalDistance)}{" "}
            miles
          </p>
        ) : null}
        {stopOffs > 0 && (
          <p className="card-stopOffs" data-test="stop-offs">
            {stopOffs > 1 ? `${stopOffs} Stop offs` : `${stopOffs} Stop off`}
          </p>
        )}
        <p className="card_p-elements card-trailerType">
          {this.props.isCarrierLoad
            ? this.props.equipmentType
            : this.props.transportMode}
        </p>
        <p className="card_p-elements card-lbs">
          {this.props.totalWeight && this.props.totalWeight.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} {this.props.weightUOM}
        </p>
        {this.props.isHazardous && (
          <p className="card_p-elements card-hazmat" data-test="is-Hazardous">
            Hazmat
          </p>
        )}
        {this.props.isHighValue && (
          <p className="card_p-elements card-value" data-test="is-HighValue">
            High Value
          </p>
        )}
        {(this.props.specialServices &&
          this.props.specialServices.length &&
          this.props.specialServices.map((service: any, index: any) => (
            <p className="card_p-elements card-value" key={index}>
              {service.specialServiceDesc}
            </p>
          ))) ||
          null}
        {(this.props.shipmentServices &&
          this.props.shipmentServices.length &&
          this.props.shipmentServices.map((service: any, index: any) => (
            <p className="card_p-elements card-value" key={index}>
              {service.serviceDescription}
            </p>
          ))) ||
          null}
        {this.props.isTemperatureControlled && (
          <p className="card_p-elements card-value" data-test="is-temperature">
            Temp Control
          </p>
        )}
      </div>
    );
  }
}

export { LoadTileFooter };
