import React, { Component } from 'react';
import { NewContainer } from '../../../containers/SearchContainer/NewContainer';
import { RecentContainer } from '../../../containers/SearchContainer/RecentContainer';
import { FavouriteContainer } from '../../../containers/SearchContainer/FavouriteContainer';
import { SearchResultContainer } from '../../../containers/SearchContainer/SearchResultContainer';

class SectionContainer extends Component<any, any> {
  renderSwitch(section: string) {
    switch (section) {
      case 'New':
        return (
          <NewContainer
            isRecentSearch={this.props.isRecentSearch}
            searchParams={this.props.searchParams}
          />
        );
      case 'Recent':
        return <RecentContainer onRedirectToTab={this.props.onRedirectToTab} />;
      case 'Favorite':
        return <FavouriteContainer onRedirectToTab={this.props.onRedirectToTab} />;
      case 'results':
        return <SearchResultContainer />;
      default:
        return <NewContainer />;
    }
  }

  render() {
    return this.renderSwitch(this.props.section);
  }
}

export default SectionContainer;
