import React, { Component, useEffect } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { compose } from 'redux';
import { withRouter } from 'react-router';
import ManageUserPage from '../../../pages/Manage/ManageUser/ManageUser.page';
import { IUserListPayload } from '../../../schemas/manage/manage.Schema';
import {
  getMembersList,
  setSelectedUser,
  setFilter,
  resetLoadedStatus
} from '../../../actions/manage/action';
import { getUserDetails, getRefreshStatus, getNetworkStatus } from '../../../selectors/selector';
import { UserDetails } from '../../../models/userPforile/userProfile.modal';
import {
  getManageUsers,
  getSelectedFilter,
  getManageUsersLoaded
} from '../../../selectors/Manage/selector';
import { IONICWrapper } from '../../../utils/common';
import { showNetworkAlert } from '../../../actions/App/action';
import useInjectSaga from '../../../utils/injectSaga';
import useInjectReducer from '../../../utils/injectReducer';
import reducer from '../../../reducers/manage/reducer';
import saga from '../../../saga/Manage/saga';
import { logEvent } from '../../../utils/Analytics';
import { EVENT, SCREEN } from '../../../utils/constants';

const key = 'manage';

export const ManageUserContainer: React.FC<any> = (props: any) => {
  const { userDetails, getUsersListFn, usersList, isUsersListLoaded, history } = props;

  const users = usersList.map((user: any) => {
    const roles = user.userApplicationDetails.reduce((roles: Array<any>, item: any) => {
      const itemRoles = (item.roles && item.roles.map((role: any) => role.roleName)) || [];
      return [...roles, ...itemRoles];
    }, []);
    return { ...user, roles };
  });

  const getUsersList = (isPullToRefresh = false) => {
    const { carrierId } = userDetails;
    const userDetailsModel = new UserDetails();
    const fetchAllUsersQuery = userDetailsModel.getUsers(carrierId, '');
    getUsersListFn({
      fetchAllUsersQuery,
      isPullToRefresh
    });
  };

  useEffect(() => {
    const status =
      (history &&
        history.location &&
        history.location.state &&
        history.location.state.resetLoaded) ||
      false;
    if (status) {
      props.resetUsersLoaded();
    }
  }, [history]);

  useEffect(() => {
    if (userDetails && !isUsersListLoaded) {
      getUsersList();
    }
  }, [userDetails, isUsersListLoaded]);

  // Analytics implementation
  logEvent(EVENT.SV_MANAGE_USERS, {
    screen: SCREEN.MANAGE_USERS,
    carrierId: props.userDetails.carrierId
  });
  return <ManageUserPage {...props} module="manage" users={users} getUsersList={getUsersList} />;
};

const mapStateToProps = createStructuredSelector({
  userDetails: getUserDetails(),
  isPullToRefresh: getRefreshStatus(),
  usersList: getManageUsers(),
  isNetworkAvailable: getNetworkStatus(),
  filters: getSelectedFilter(),
  isUsersListLoaded: getManageUsersLoaded()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  getUsersListFn: (payload: IUserListPayload) => dispatch(getMembersList(payload)),
  selectedUser: (payload: IUserListPayload) => dispatch(setSelectedUser(payload)),
  showNetworkAlert: () => dispatch(showNetworkAlert()),
  setFilter: (payload: any) => dispatch(setFilter(payload)),
  resetUsersLoaded: () => dispatch(resetLoadedStatus())
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = useInjectReducer({ key, reducer });

const withSaga = useInjectSaga({ key, saga });

export default compose(withSaga, withReducer, withConnect)(withRouter(ManageUserContainer));
