import React, { useState, useEffect } from "react";
import {
  IonContent,
  IonPage,
  IonGrid,
  isPlatform,
  IonButton,
  IonIcon
} from "@ionic/react";
import AppHeader from "../../../components/app/Bars/Bar-header";
import { CapacityCard } from "../../../components/app/Manage";
import PullToRefresh from "../../../components/PullToRefresh/PullToRefresh";
import { CapacityModel } from "../../../models/manage/capacity.model";
import { ToastMessage } from "../../../utils/ToastMessage";
import { EmptyListComponent } from "../../../components/EmptyListComponent";
import {
  NO_TRUCKS_MESSAGE,
  EVENT,
  SCREEN,
  Anywhere, CAPACITY_REMOVED
} from "../../../utils/constants";
import { capitalize } from "../../../utils/helper";
import { FilterOnly } from "../../../components/core/FilterOnly";
import { ICapacity } from "../../../schemas/manage/manage.Schema";
import { getApplicationInfo, logEvent } from "../../../utils/Analytics";
import DataTableComponent from "../../../components/shared/dataTableComponent/data-tabel.page";
import { columns } from "../../../pages/Manage/ManageCapacity/ManagaCapacityUtil";
import moment from "moment";
import { R2Configuration } from "../../../utils/common";
import { states } from "app/utils/states";

const ManageCapacity: React.FC<any> = (props: any) => {
  const {
    isPullToRefresh,
    getCapacityList,
    capacityList,
    userDetails,
    isNetWorkAvailable,
    showNetworkAlert,
    history,
    removeCapacityAction,
    setCapacityFilter: setFilter,
    filter,
    onPageScroll
  } = props;
  const [capacityListItems, setCapacityListItems] = useState(props.data);
  const [capacityListDesktop, setCapacityListDesktop] = useState([]);
  const capacityModel = new CapacityModel();

  const routePath = `/app/${props.module}/ManageMyCapacity`;

  useEffect(() => {
    // Analytics implementation
    logEvent(EVENT.SV_MANAGE_CAPACITY, {
      screen: SCREEN.MANAGE_CAPACITY,
      carrierId: userDetails.carrierId
    });
  }, []);

  const options = [
    { value: "", name: "All Capacity" },
    { value: "TRUCK", name: "Trucks" },
    { value: "LANE", name: "Lanes" }
  ];

  const dropdown = {
    options,
    data: capacityListItems,
    setFilter
  };

  const addPage = () => {
    if (R2Configuration.details.getR2FeatureEnable()) {
      //KEYS.RELEASE === ENV.UAT || KEYS.RELEASE === ENV.PROD) {
      ToastMessage.show();
      return;
    }
    if (!isNetWorkAvailable) {
      showNetworkAlert();
      return;
    }
    history.push(`${routePath}/Add`);
  };

  const filterCapacityItems = () => {
    const filterValue = filter.selected
      .filter((item: any) => item.selected)
      .map((item: any) => item.value)
      .join("");
    if (!filterValue) {
      return capacityList;
    }
    return capacityList.filter(
      (item: any) => item.capacityType === filterValue
    );
  };

  useEffect(() => {
    const capacityList = filterCapacityItems();
    const desktopList = capacityList.map((item: any) => ({
      ...item,
      originDetails: item.origin
        ? `${item.origin.city}, ${item.origin.state}`
        : "",
      originDate:
        item.capacityType === "TRUCK"
          ? `${moment(item.pickUpFromDate).format("MMMM DD")} to ${moment(
              item.pickUpToDate
            ).format("MMMM DD")}`
          : "",
      originRadius: item.origin ? item.origin.radius || "" : "",
      destinationDetails:
        item && item.destinationStates && item.destinationStates > 0
          ? item.destinationStates.join(", ")
          : item.destination.city === null
          ? Anywhere
          : `${item.destination.city}, ${item.destination.state && item.destination.state.toUpperCase()}`,
      destinationRadius: !(
        item &&
        item.destinationStates &&
        item.destinationStates > 0
      )
        ? item.destination.radius
        : ""
    }));
    setCapacityListItems(capacityList);
    setCapacityListDesktop(desktopList);
  }, [capacityList, filter]);

  const undoCapacity = (capacity: ICapacity) => {
    const destination = capacity.destination
      ? {
          destinationCity: capacity.destination.city,
          destinationState: capacity.destination.state,
          destinationCountry: capacity.destination.country,
          destinationLatitude: capacity.destination.latitude,
          destinationLongitude: capacity.destination.longitude,
          destinationRadius: capacity.destination.radius
        }
      : {};

    const origin = capacity.origin
      ? {
          originCity: capacity.origin.city,
          originState: capacity.origin.state,
          originRadius: capacity.origin.radius,
          originCountry: capacity.origin.country,
          originLongitude: capacity.origin.latitude,
          originLatitude: capacity.origin.longitude
        }
      : {};
    const input = {
      capacityId: null,
      userId: capacity.lastUpdatedBy,
      userEmail: capacity.lastUpdatedByEmail,
      userPhone: capacity.lastUpdatedByPhoneNumber,
      capacityType: capacity.capacityType,
      commonCarrierId: capacity.commonCarrierId,
      trailerType: capacity.trailerType,
      ...origin,
      ...destination,
      preferredDays: capacity.preferredDays,
      pickUpToDate: capacity.pickUpToDate,
      pickUpFromDate: capacity.pickUpFromDate,
      destinationStates: capacity.destinationStates
    };
    const query = capacityModel.addCapacity();
    props.addCapacityItem({ params: { query, input }, callback: () => {} });
  };

  const handleDelete = (capacity: ICapacity) => {
    const { capacityId, capacityType } = capacity;
    const type: any = capacityType;
    if (!isNetWorkAvailable) {
      showNetworkAlert();
      return;
    }
    const query = capacityModel.deleteCapacity(capacityId);
    const callback = () => {
      ToastMessage.open({
        showUndo: true,
        message: CAPACITY_REMOVED.replace("{{CAPACITY}}", capitalize(type, true)),
        callback: () => {
          undoCapacity(capacity);
        }
      });
    };
    removeCapacityAction({ query, capacityId, callback });
  };

  const handleClick = (capacityItem: any) => {
    if (!isNetWorkAvailable) {
      showNetworkAlert();
      return;
    }
    props.setSelectedCapacityAction({ capacityItem });
    history.push(`${routePath}/View`);
  };

  return isPlatform("desktop") ? (
    <IonContent
      className="manage-capacity-desktop"
      onIonScrollEnd={onPageScroll}
    >
      <DataTableComponent
        columns={columns({ handleDelete })}
        data={capacityListDesktop}
        showEndofList={true}
        showHeader={false}
        titleText={<FilterOnly dropdown={dropdown} filter={filter.selected} />}
        pagination={true}
        redirect={handleClick}
      />
      <div className="manage-capacity-buttons">
        <IonButton
          class="load-btn"
          onClick={() => history.push("/app/manage/ManageMyCapacity/Add")}
        >
          <IonIcon src="assets/icon/Add_Black.svg"></IonIcon>Add
        </IonButton>
      </div>
    </IonContent>
  ) : (
    <IonPage className="desktop-page" data-test="IonPage-Manage">
      <AppHeader
        title="Manage My Capacity"
        backUrl={"/app/" + props.module}
        History={props.history}
        addPage={addPage}
      />
      <IonContent
        className="ion-padding recent_container"
        onIonScrollEnd={onPageScroll}
      >
        <PullToRefresh
          status={isPullToRefresh}
          onPullToRefresh={getCapacityList}
        />
        <FilterOnly dropdown={dropdown} filter={filter.selected} />
        <IonGrid class="recent-list">
          {capacityListItems.length > 0 ? (
            <>
              {capacityListItems.map((listItem: any, index: number) => (
                <CapacityCard
                  data={listItem}
                  key={`lane${index}`}
                  handleClick={handleClick}
                  handleDelete={handleDelete}
                />
              ))}
              <div className="end-of-list">End of list</div>
            </>
          ) : (
            <EmptyListComponent
              icon="no-search-icon"
              message={NO_TRUCKS_MESSAGE}
              callback={addPage}
              constantButton={"Add"}
            />
          )}
        </IonGrid>
      </IonContent>
    </IonPage>
  );
};
export default ManageCapacity;
