import React from 'react';
import "./IonUser.scss";

export const IonImage: React.FC<any> = (props:any) => {
    const {firstName, lastName, big = false, xlarge = false, normal = false} = props;
    const className = `ion-user-custom-image ${normal ? 'normal': ''} ${big ? 'big': ''} ${xlarge ? 'xlarge': ''}`;
    return (
        <div className="ion-image-wrapper">
            <div className={className}>
            { 
                [firstName, lastName].map((name:string, index: number) => name ? (
                    <span key={name + index} className="ion-user-child-img">{
                        !!name
                        ? name[0].toUpperCase()
                        : ""
                    }</span>
                ): null)
            }
            </div>
        </div>
    )
}

