import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { Redirect, withRouter } from 'react-router';
import { isPlatform } from '@ionic/react';
import _ from 'lodash';
import { RecentSearch } from '../../../pages/Search/Recent/RecentPage.page';
import useInjectSaga from '../../../utils/injectSaga';
import useInjectReducer from '../../../utils/injectReducer';
import {
  makeRecent,
  favoriteError,
} from '../../../selectors/RecentSearch/selector';
import reducer from '../../../reducers/Recent/reducer';
import saga from '../../../saga/Recent/saga';
import {
  GetrecentData,
  clearFavoriteError,
} from '../../../actions/Recent/action';
import {
  getLoading,
  getUserData,
  getRefreshStatus,
  getUserDetails,
  getNetworkStatus,
} from '../../../selectors/selector';
import { Recent } from '../../../models/home/recent.model';
import { DomainConverter } from '../../../utils/common';
import {
  Isearch,
  ILoadSearchResults,
} from '../../../schemas/Search/Search.schema';
import {
  fetchResults
} from '../../../actions/Search/action';
import { makeSearch } from '../../../selectors/Search/selector';
import { SearchResults } from '../../../models/home/SearchResults.model';
import { showAlert } from '../../../actions/App/action';
import { ToastMessage } from '../../../utils/ToastMessage';
import {
  updateFavoriteData,
} from '../../../actions/Favorite/action';
import {
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
  FAVORITE_SEARCH_REMOVED,
  FAVORITE_ALREADY_EXIST_TITLE,
  FAVORITE_ALREADY_EXIST_MESSAGE,
} from '../../../utils/constants';
import './DuplicatePopup.scss';
import { Favorite } from '../../../models/home/favorite.model';
import { CustomModal } from 'app/components/CustomModal';
import { getSearchInputes, onSearchResult } from '../searchUtility';

const key = 'recent';
let onSearch = false;
let srchParams: any = null;
export class RecentContainer extends PureComponent<any, any> {
  __model: Recent;

  __modelF: Favorite;

  constructor(props: any) {
    super(props);
    this.state = {
      id: [],
      data: {},
      undo: false,
      showErrorPopup: false,
      flag: null,
    };
    this.__model = new Recent();
    this.__modelF = new Favorite();
  }

  componentDidMount() {
    this.getRecentSearchList(false);
  }

  updateFavorite = (fav: boolean, data: any) => {
    if (!this.props.isNetworkAvailable) {
      showAlert({
        isShowAlert: true,
        alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
        alertTitle: NO_NEWORK,
      });
      return;
    }
    const ISOpickupdate = new Date(data.pickupdate).toISOString();
    const ISOsearchdate = new Date(data.searchdate).toISOString();
    const ISOdropdate = new Date(data.dropdate).toISOString();
    const newData = {
      ...data,
      pickupdate: ISOpickupdate,
      searchdate: ISOsearchdate,
      dropdate: data.destinationCity === null ? null : ISOdropdate,
      favouritesearchindicator: fav,
    };
    const arr = [];
    arr.push(data.id);
    this.setState({ id: arr, data: newData, undo: false, flag: null });
    if (!fav) {
      ToastMessage.open({
        message: FAVORITE_SEARCH_REMOVED,
        showUndo: true,
        callback: this.undoClicked,
      });
      this.props.updateFavoriteLoads(
        this.__modelF.updateFavoriteLoad(newData),
        false
      );
    } else {
      this.props.updateFavoriteLoads(
        this.__modelF.updateFavoriteLoadCheckDuplicate(newData),
        false
      );
    }
  };

  undoClicked = () => {
    const data = {
      ...this.state.data,
      favouritesearchindicator: !this.state.data.favouritesearchindicator,
    };
    this.props.updateFavoriteLoads(
      this.__modelF.updateFavoriteLoadCheckDuplicate(data),
      false
    );
    this.setState({ undo: true });
  };

  getRecentSearchList = (isHideLoader: boolean) => {
    this.__model.userID =
      (this.props.userDetails && this.props.userDetails.userPrincipalName) ||
      '';
    this.__model.carrierId =
      (this.props.userDetails && this.props.userDetails.carrierId) || '';
    this.__model.page = 0;
    this.__model.size = 10;
    this.props.recentLoads(this.__model.getRequestPayload(), isHideLoader);
  };

  onSearchResult = (searchData: any, _srchParams: Isearch) => {
    srchParams = _srchParams;
    onSearch = true;
    const modelSearch = onSearchResult(
      searchData,
      _srchParams,
      this.props.userDetails
    );
    if (isPlatform('mobile')) {
      this.props.fetchData(modelSearch.getRequestPayload());
    } else {
      this.props.onRedirectToTab(
        'New',
        getSearchInputes(srchParams, this.props.userDetails)
      );
      onSearch = false;
    }
  };

  render() {
    return this.props &&
      this.props.data &&
      this.props.data.searchLoads &&
      onSearch &&
      !isPlatform('desktop') ? (
      ((onSearch = false),
      (
        <Redirect
          to={{
            pathname: '/app/search/results',
            state: {
              data: DomainConverter.dataToDomain<ILoadSearchResults>(
                SearchResults,
                this.props.data
              ),
              params: srchParams,
              searchInput: getSearchInputes(srchParams, this.props.userDetails),
            },
          }}
        />
      ))
    ) : (
      <>
        <RecentSearch
          data-test="recent-search"
          getRecentSearchList={this.getRecentSearchList}
          getLoads={
            this.props &&
            this.props.recentSearchLoads &&
            this.props.recentSearchLoads.data &&
            this.props.recentSearchLoads.data.findUserRecentSearchHistory
          }
          isInPullToRefresh={this.props.isInPullToRefresh}
          isNetworkAvailable={this.props.isNetworkAvailable}
          showAlert={this.props.showAlert}
          onSearchResult={this.onSearchResult}
          updateFavorite={this.updateFavorite}
          favoriteFlag={false}
          id={this.state.id}
          undo={this.state.undo}
          flag={this.state.flag}
        />
        <div
          data-test="error-state"
          onClick={() => {
            this.setState({ showErrorPopup: false, flag: true });
            this.props.clearErrorState();
          }}
        >
          <CustomModal
            isOpen={this.state.showErrorPopup}
            title={FAVORITE_ALREADY_EXIST_TITLE}
            message={FAVORITE_ALREADY_EXIST_MESSAGE}
            buttons={[{ label: 'Ok', callback: () => {} }]}
          />
        </div>
      </>
    );
  }

  componentDidUpdate() {
    if (this.props.errorData.updateUserSearchHistoryAsFavorite) {
      this.setState({ showErrorPopup: true });
    }
  }
}

const mapStateToProps = createStructuredSelector({
  loading: getLoading(),
  errorData: favoriteError(),
  recentSearchLoads: makeRecent(),
  userData: getUserData(),
  data: makeSearch(),
  isInPullToRefresh: getRefreshStatus(),
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus()
});

export const mapDispatchToProps = (dispatch: Function) => ({
  recentLoads: (recentSearch: any, isHideLoader: boolean) =>
    dispatch(GetrecentData(recentSearch, isHideLoader)),
  clearErrorState: () => dispatch(clearFavoriteError()),
  fetchData: (searchData: any) =>
    dispatch(fetchResults(searchData, false, false)),
  showAlert: (data: any) => dispatch(showAlert(data)),
  updateFavoriteLoads: (favoriteSearch: any, isHideLoader: boolean) =>
    dispatch(updateFavoriteData(favoriteSearch, isHideLoader))
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key, reducer });
const withSaga = useInjectSaga({ key, saga });

export default compose<any>(
  withSaga,
  withReducer,
  withConnect
)(withRouter(RecentContainer));
