import React, { Component } from "react";

import { IonCard, IonImg, IonGrid, IonButton, isPlatform } from "@ionic/react";
import "./ShipmentUpdateScreen.scss";
import { compose } from "redux";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import * as moment from "moment-timezone";
import {
  IShipmentUpdateCardProps,
  IShipmentUpdateCardState,
  ICardProps,
} from "../../../schemas/StartTrip/StartTrip";
import { ShipmentStopDetails } from "./ShipmentStopDetails";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import { StartTrip } from "./startTrip";
import useInjectSaga from "../../../utils/injectSaga";
import { getUserDetails, getNetworkStatus } from "../../../selectors/selector";
import useInjectReducer from "../../../utils/injectReducer";
import saga from "../../../saga/MyLoad/saga";
import reducer from "../../../reducers/carrierAssign/reducer";

import {
  updateStopInformation,
  confirmStartInformation,
  confirmStartInformationStatusMessage,
  clearWarningDialog,
  updateStopInformationResponse,
  clearUpdateStopInformationResponse,
} from "../../../actions/myload/action";
import { StopInformation } from "../../../schemas/Exicution/exicution.schema";
import { UpdateStopModel } from "../../../models/home/updateStop.model";
import { InTransitModel } from "../../../models/home/InTransit.model";
import {
  COMPLETE_TRIP_CTA_TEXT,
  PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
  NO_NEWORK,
} from "../../../utils/constants";
import { showAlert } from "../../../actions/App/action";
import { getWarningDialogMessage, updateStopInfo } from "app/selectors/Myload/selector";

const cityError = "please provide correct CityName";

class ShipmentUpdateCard extends Component<
  ICardProps,
  IShipmentUpdateCardState
> {
  private readonly startTripModel: StartTripModel;

  private startTrip: any;

  constructor(props: ICardProps) {
    super(props);
    this.startTripModel = new StartTripModel();
    this.startTrip = null;
    this.state = {
      showExpandView: false,
      isConfirmStartButton: true,
      expandedStop: this.startTripModel.getCurrentOpenStop(props.loadDetails),
      locationName: "",
      originError: "",
      isLocationExpand: props.locationValue,
      ActualStart: new Date().toISOString(),
      isLoadRefreshed: false,
      actualStartDateTime: new Date(),
      currentLocationName: "",
      lat: "",
      lng: "",
      timezone: "",
      progressBarHeight: "20px",
      exicutionProgress: [],
      showDottedBorder: false,
    };
  }

  confirmStartInformation = (
    locationName: string,
    startDate: string,
    lat: string,
    lng: string,
    country: string,
    timezone: string
  ) => {
    const { userDetails = {} } = this.props;
    const { loadDetails = {} } = this.props;
    const _model: InTransitModel = new InTransitModel();
    const location = locationName ? locationName.split(",") : [];
    if (location.length === 1) {
      location.push("");
    }
    const callBack = (shipment: any) => {
      this.props.refreshShipmentInDriverLoads(shipment);
    };
    const _startDate: string = startDate
      ? moment.tz(startDate.substring(0, 19), timezone).format()
      : startDate;

    this.props.confirmStartLoadInformation(
      _model.confirmStart({
        carrierId: userDetails.carrierId,
        carrierPro: "",
        carrierScac: "",
        city: location[0],
        country,
        latitude: lat ? lat.toString() : "",
        longitude: lng ? lng.toString() : "",
        positionDateTime: _startDate,
        shipmentId: loadDetails.shipmentId,
        sourceSystem: loadDetails.sourceTMS,
        startDateTime: _startDate,
        state: location[1],
        timeZone: timezone,
        userId: userDetails.userPrincipalName,
      }),
      callBack
    );
    this.setState({
      showExpandView: true,
      expandedStop: this.startTripModel.getCurrentOpenStop(
        this.props.loadDetails
      ),
      isConfirmStartButton: false,
      isLoadRefreshed: true,
      progressBarHeight: "70px",
    });
  };

  private readonly getActualStartTime = (startingDate: string): string => {
    const actualStartDateTime =
      (startingDate && new Date(startingDate)) ||
      this.state.actualStartDateTime;
    const monthName: string = actualStartDateTime.toLocaleString("default", {
      month: "short",
    });
    const _date: number = actualStartDateTime.getDate();
    const time: string = actualStartDateTime.toLocaleString("default", {
      hour: "numeric",
      minute: "numeric",
      hour12: true,
    });

    return `${monthName} ${_date} ${time}`;
  };

  componentDidMount() {
    this.setState({
      isLoadRefreshed: false,
      showExpandView: false,
      isConfirmStartButton: true,
      expandedStop: this.startTripModel.getCurrentOpenStop(
        this.props.loadDetails
      ),
      locationName: this.state.currentLocationName,
      originError: "",
      isLocationExpand: this.props.locationValue,
      progressBarHeight: "0px",
      exicutionProgress: [],
    });
    setTimeout(() => this.updateExicutionProgress(), 0);
  }

  updateExicutionProgress = (stopIndex?: number) => {
    const exicutionProgress: any = {};
    const tripCompleted = this.startTripModel.isAllStopCompleted(
      this.props.loadDetails
    );
    const shipmentloadStop = this.sortShipmentsStop();

    const startLocation = this.startTripModel.getStartingLocation(
      this.props.loadDetails
    );
    this.getActualStartTime(
      this.startTripModel.getStartingDateTime(this.props.loadDetails)
    );
    let confrimStartProgres = 0;
    const hideConfirmStart: boolean = this.startTripModel.isConfirmStartButtonDisabled(
      this.props.loadDetails
    );
    if (
      tripCompleted ||
      stopIndex === 0 ||
      (shipmentloadStop[0] && shipmentloadStop[0].actualArrivalDateTime) ||
      this.state.expandedStop > 0
    ) {
      confrimStartProgres = 3;
    } else {
      if (startLocation) {
        confrimStartProgres = 1;
      }
      if (hideConfirmStart) {
        confrimStartProgres = 2;
      }
    }
    exicutionProgress.startTrip = { progress: confrimStartProgres };
    shipmentloadStop &&
      shipmentloadStop.map((stop: any, index: number) => {
        let stopProgress = 0;
        if (tripCompleted || index < this.state.expandedStop - 1) {
          stopProgress = 3;
        } else if (
          (index === this.state.expandedStop ||
            index === this.state.expandedStop - 1) &&
          index !== shipmentloadStop.length - 1
        ) {
          if (stopIndex === index) {
            stopProgress = 0.5;
          }
          if (stop.actualArrivalDateTime) {
            stopProgress += 1;
          }
          if (stop.actualArrivalDateTime && stop.actualDepartureDateTime) {
            stopProgress += 1;
          }
          if (
            (stopIndex && stopIndex - 1 === index) ||
            (shipmentloadStop[index + 1] &&
              shipmentloadStop[index + 1].actualArrivalDateTime)
          ) {
            stopProgress = 3;
          }
        }
        exicutionProgress[index] = { progress: stopProgress };
      });
    this.setState({ exicutionProgress }, () => {
      setTimeout(() => this.getProgress(), 0);
    });
  };

  componentDidUpdate(prevProps: IShipmentUpdateCardProps) {
    if (
      JSON.stringify(prevProps.loadDetails) !==
      JSON.stringify(this.props.loadDetails)
    ) {
      this.setState({
        isLoadRefreshed: false,
        showExpandView: false,
        isConfirmStartButton: true,
        expandedStop: this.startTripModel.getCurrentOpenStop(
          this.props.loadDetails
        ),
        locationName: this.state.currentLocationName,
        originError: "",
        isLocationExpand: this.props.locationValue,
        exicutionProgress: [],
      });
      setTimeout(() => this.updateExicutionProgress(), 0);
    }
  }

  getTime = (time: string, timeZone: string): string => {
    return time ? moment.tz(time.substring(0, 19), timeZone).format() : time;
  };

  updateShipmentStopInformation = (stopInfo: StopInformation) => {
    stopInfo.shipmentId = this.props.shipmentId;
    stopInfo.sourceSystem = this.props.loadDetails.sourceTMS;
    const _model: UpdateStopModel = new UpdateStopModel();
    const { userDetails = {} } = this.props;
    const callBack = (shipment: any) => {
      this.props.refreshShipmentsLoads(shipment);
    };
    const currentOpenStopSNo = this.state.expandedStop + 1;
    if (currentOpenStopSNo === stopInfo.sequenceNo) {
      this.props.updateStopInformationResponse({
        shipmentId: stopInfo.shipmentId,
        sequenceNo: stopInfo.sequenceNo,
      });
    } else {
      this.props.clearUpdateStopInformationResponse();
    }

    this.props.updateStopInformation(
      _model.updateStop({
        sourceSystem: stopInfo.sourceSystem,
        carrierPro: stopInfo.carrierPro,
        carrierScac: stopInfo.carrierScac,
        shipmentId: stopInfo.shipmentId,
        stopId: stopInfo.stopId,
        sequenceNo: stopInfo.sequenceNo,
        stopNumber: stopInfo.stopNumber,
        locationId: stopInfo.locationId,
        carrierId: userDetails.carrierId,
        userId: userDetails.userPrincipalName,
        city: stopInfo.city,
        state: stopInfo.state,
        country: stopInfo.country,
        latitude: stopInfo.latitude,
        longitude: stopInfo.longitude,
        timeZone: stopInfo.timeZone,
        positionDateTime: stopInfo.positionDateTime,
        actualArrivalDateTime: this.getTime(
          stopInfo.actualArrivalDateTime,
          stopInfo.timeZone
        ),
        actualETADateTime: this.getTime(
          stopInfo.actualETADateTime,
          stopInfo.timeZone
        ),
        actualDepartureDateTime: this.getTime(
          stopInfo.actualDepartureDateTime,
          stopInfo.timeZone
        ),
        stopTrailerActivity: stopInfo.stopTrailerActivity,
      }),
      callBack
    );
  };

  sortShipmentsStop = () => {
    let { shipmentloadStop = [] } = this.props;

    shipmentloadStop = shipmentloadStop
      .slice()
      .sort((load1: any, load2: any) => {
        if (load1.sequenceNo && load2.sequenceNo) {
          return load1.sequenceNo - load2.sequenceNo;
        }
        if (load1.stopSequence && load2.stopSequence) {
          return load1.stopSequence - load2.stopSequence;
        }
        return 0;
      });
    return shipmentloadStop;
  };

  getProgress = () => {
    const totalStep = 3;
    if (
      this.state.exicutionProgress &&
      this.state.exicutionProgress.startTrip &&
      this.state.exicutionProgress.startTrip.progress
    ) {
      const { progress } = this.state.exicutionProgress.startTrip;
      if (progress === totalStep) {
        this.setState({ showDottedBorder: true });
      } else {
        this.setState({ showDottedBorder: false });
        const grid = this.startTrip;
        const gridHeight = grid && grid.offsetHeight;
        const barHeight = gridHeight && gridHeight * (progress / totalStep);
        this.setState({ progressBarHeight: `${barHeight}px` });
      }
    }
  };

  render() {
    const shipmentloadStop = this.sortShipmentsStop();
    const hideConfirmStart: boolean = this.startTripModel.isConfirmStartButtonDisabled(
      this.props.loadDetails
    );
    return (
      <>
        <IonCard className="shipment-card">
          <IonGrid
            className={`stop-grid  ${
              this.state.showDottedBorder ? "dotted-border" : ""
            }`}
            style={{ paddingBottom: "0px" }}
            ref={(ref) => {
              this.startTrip = ref;
              return true;
            }}
            id="start-trip"
          >
            <IonGrid
              className={`${
                this.state.showDottedBorder ? "hide-line" : "progressDiv"
              }`}
              style={{ height: `${this.state.progressBarHeight}` }}
            />
            <IonImg
              src="assets/icon/start-trip-location.svg"
              className={`stop-icon location-start ${
                hideConfirmStart ? "change_icon" : ""
              }`}
            />
            <StartTrip
              confirmStartInformation={this.confirmStartInformation}
              locationValue={this.props.locationValue}
              networkStatus={this.props.networkStatus}
              isConfirmStartButton={this.state.isConfirmStartButton}
              ActualStart={this.startTripModel.getStartingDateTime(
                this.props.loadDetails
              )}
              shipmentId={this.props.shipmentId}
              hideConfirmStart={hideConfirmStart}
              locationOriginValue={this.startTripModel.getStartingLocation(
                this.props.loadDetails
              )}
              isNetworkAvailable={this.props.isNetworkAvailable}
              GPSEnabledEvent={this.props.GPSEnabledEvent}
            />
          </IonGrid>
          <IonGrid className="stop-details-grid">
            {shipmentloadStop
              ? shipmentloadStop.map((stop: any, index: any) => (
                  <ShipmentStopDetails
                    clearWarningDialogMessage={
                      this.props.clearWarningDialogMessage
                    }
                    warningDialogMessage={this.props.warningDialogMessage}
                    timestamp={this.props.timestamp}
                    key={index}
                    stopDetails={stop}
                    totalStops={shipmentloadStop.length}
                    index={index}
                    showExpandView={this.state.showExpandView}
                    expandedStop={this.state.expandedStop}
                    changeExpandStop={this.updateShipmentStopInformation}
                    shipmentId={this.props.shipmentId}
                    carrierId={this.props.userDetails.carrierId}
                    isConfirmStartButton={this.state.isConfirmStartButton}
                    loadDetails={this.props.loadDetails}
                    isNetworkAvailable={this.props.isNetworkAvailable}
                    exicutionProgress={this.state.exicutionProgress}
                    arivalEntered={(stopIndex: number) => {
                      this.updateExicutionProgress(stopIndex);
                    }}
                    onShowError={(message: string) => {
                      this.props.showErrorMessage(message);
                    }}
                    GPSEnabledEvent={this.props.GPSEnabledEvent}
                    openSatelliteMap={this.props.openSatelliteMap}
                    updateStopInfo={this.props.updateStopInfo}
                  />
                ))
              : null}
          </IonGrid>
        </IonCard>
        {(this.startTripModel.isAllStopCompleted(this.props.loadDetails) &&
          !this.startTripModel.isTripCompleted(this.props.loadDetails) &&
          isPlatform("desktop") && (
            <IonButton
              expand="full"
              class="complete-load-btn"
              onClick={() => {
                const {
                  loadDetails = {},
                  isNetworkAvailable,
                  showAlert,
                } = this.props;
                if (!isNetworkAvailable) {
                  showAlert({
                    isShowAlert: true,
                    alertMessage: PLEASE_CHECK_NETOWRK_CONNCTIVITY_MESSAGE,
                    alertTitle: NO_NEWORK,
                  });
                  return;
                }
                this.props.completeTripRequest(
                  loadDetails.sourceTMS,
                  loadDetails.shipmentId
                );
              }}
            >
              <img
                src="/assets/icon/complete-trip.svg"
                className="complete-icon"
              />
              {COMPLETE_TRIP_CTA_TEXT}
            </IonButton>
          )) ||
          null}
      </>
    );
  }
}
const mapStateToProps = createStructuredSelector({
  userDetails: getUserDetails(),
  isNetworkAvailable: getNetworkStatus(),
  warningDialogMessage: getWarningDialogMessage(),
  updateStopInfo: updateStopInfo(),
});

export const mapDispatchToProps = (dispatch: Function) => ({
  updateStopInformation: (data: any, callBack: Function) => {
    dispatch(updateStopInformation(data, callBack));
  },
  confirmStartLoadInformation: (
    data: any,
    callback: (shipment: any) => void
  ) => {
    dispatch(confirmStartInformation(data, callback));
  },
  clearWarningDialogMessage: () => dispatch(clearWarningDialog()),
  showAlert: (data: any) => dispatch(showAlert(data)),
  showErrorMessage: (message: string) => {
    dispatch(
      confirmStartInformationStatusMessage({
        status: false,
        message,
      })
    );
  },
  updateStopInformationResponse: (data: any) => {
    dispatch(
      updateStopInformationResponse({
        data,
      })
    );
  },
  clearUpdateStopInformationResponse: () =>
    dispatch(clearUpdateStopInformationResponse()),
});

const withConnect = connect(mapStateToProps, mapDispatchToProps);
const withReducer = useInjectReducer({ key: "carrierAssigned", reducer });
const withSaga = useInjectSaga({ key: "myload", saga });

export default compose<any>(
  withSaga,
  withReducer,
  withConnect
)(ShipmentUpdateCard);
export { ShipmentUpdateCard };
