import {
  IonRow,
  IonCol,
  IonList,
  IonItem,
  IonLabel,
  IonInput,
  IonCard,
  IonCardHeader,
  IonIcon,
  IonCardContent,
  IonGrid,
  IonText,
  isPlatform
} from '@ionic/react';
import * as React from 'react';
import moment from 'moment';
import { CARRIER_PROFILE_INSURENCE } from '../../../utils/constants';

const formatDate = (date: string) => moment(date).format('MM/DD/YYYY');

export const CarrierInsuranceCard = (props: any) => {
  const insurances = props.data.filter(
    (insurance: any) =>
      new Date(moment(insurance.policyExpirationDate).format('YYYY-MM-DD')).getTime() >=
      new Date().getTime()
  );

  return (
    <IonCard class="carrier-card">
      <IonCardHeader
        class={
          props.showAccordian
            ? 'card-header-pad carrier-accordion-header'
            : 'card-header-pad2 carrier-accordion-header'
        }
        onClick={props.accordianClick}>
        <IonLabel className="carrier-card-title">{props.carrierHeader}</IonLabel>
        {props.showAccordian ? (
          <IonIcon src="/assets/icon/add.svg" className="addAccordian" />
        ) : (
          <IonIcon src="/assets/icon/minus-icon.svg" className="addAccordian" />
        )}
      </IonCardHeader>
      <IonCardContent class={props.showAccordian ? 'fadeout' : 'fadein' + ' accordianCardContent'}>
        {isPlatform('desktop') ? (
          <IonText className="carrier-text insurence">{CARRIER_PROFILE_INSURENCE}</IonText>
        ) : null}
        {insurances.map((v: any, index: number) => (
          <>
            <IonGrid className="insurance-card-header">
              <IonRow>
                <IonCol size={isPlatform('desktop') ? '1' : '2'}>
                  <IonIcon src="/assets/icon/insurence-icon.svg" className="insurence-icon" />
                </IonCol>
                <IonCol>
                  <IonText class="insurence-title">
                    {!isPlatform('desktop') ? v.insuranceTypeCode : 'Insurance'}
                  </IonText>
                </IonCol>
              </IonRow>
            </IonGrid>
            {isPlatform('desktop') ? (
              <IonList className="insurance-card-list">
                <IonItem mode="ios" key={index} className="carrier-profile-ionitem">
                  <IonLabel mode="ios" position="floating">
                    Effective Date
                  </IonLabel>
                  <IonInput
                    type="text"
                    className="profile-form-control"
                    name="origin"
                    value={formatDate(v.policyEffectiveDate)}
                    readonly
                  />
                </IonItem>
                <IonItem mode="ios" key={index} className="carrier-profile-ionitem">
                  <IonLabel mode="ios" position="floating">
                    Type
                  </IonLabel>
                  <IonInput
                    type="text"
                    className="profile-form-control"
                    name="origin"
                    value={v.insuranceTypeCode}
                    readonly
                  />
                </IonItem>
                <IonItem mode="ios" key={index} className="carrier-profile-ionitem">
                  <IonLabel mode="ios" position="floating">
                    Coverage Amount
                  </IonLabel>
                  <IonInput
                    type="text"
                    className="profile-form-control"
                    name="origin"
                    value={`$${v.coverageAmount}`}
                    readonly
                  />
                </IonItem>
                <IonItem mode="ios" key={index} className="carrier-profile-ionitem">
                  <IonLabel mode="ios" position="floating">
                    Expiration Date
                  </IonLabel>
                  <IonInput
                    type="text"
                    className="profile-form-control"
                    name="origin"
                    value={formatDate(v.policyExpirationDate)}
                    readonly
                  />
                </IonItem>
              </IonList>
            ) : (
              <IonList className="insurance-card-list">
                <IonItem mode="ios" key={index} className="carrier-profile-ionitem">
                  <IonLabel mode="ios" position="floating">
                    Effective Date
                  </IonLabel>
                  <IonInput
                    type="text"
                    className="profile-form-control"
                    name="origin"
                    value={formatDate(v.policyEffectiveDate)}
                    readonly
                  />
                </IonItem>
                <IonItem mode="ios" key={index} className="carrier-profile-ionitem">
                  <IonLabel mode="ios" position="floating">
                    Expiration Date
                  </IonLabel>
                  <IonInput
                    type="text"
                    className="profile-form-control"
                    name="origin"
                    value={formatDate(v.policyExpirationDate)}
                    readonly
                  />
                </IonItem>
                <IonItem mode="ios" key={index} className="carrier-profile-ionitem" lines="none">
                  <IonLabel mode="ios" position="floating">
                    Coverage Amount
                  </IonLabel>
                  <IonInput
                    type="text"
                    className="profile-form-control"
                    name="origin"
                    value={`$${v.coverageAmount}`}
                    readonly
                  />
                </IonItem>
              </IonList>
            )}
          </>
        ))}
      </IonCardContent>
    </IonCard>
  );
};
