import React, { useState, useEffect } from "react";
import { IonDatetime, IonLabel, isPlatform } from "@ionic/react";
import { IEtaProps } from "../../../schemas/StartTrip/StartTrip";
import { StartTripModel } from "../../../models/home/StartTrip.model";
import * as moment from "moment-timezone";
import { getYearValues } from "../../../utils/formatData";

const model: StartTripModel = new StartTripModel();
export const ETA: React.FunctionComponent<IEtaProps> = (props: IEtaProps) => {

  const eta = model.getETA(props.loadStop);
  const yearValues = getYearValues() || new Date().getFullYear();

  useEffect(() => {
    props.onChange(eta);
  }, [eta, props.shipmentId, props.timestamp]);
  console.log("props.timeZone=", props.timeZone);
  return (
    <React.Fragment>
      <IonLabel className="ETA-label">ETA</IonLabel>
      <IonDatetime
        displayFormat="MMM DD hh:mm A"
        pickerFormat="YYYY MMM DD hh:mm A"
        className={`ETA-picker ${
          model.isETAReadonly(props.loadStop) ? "hide-border" : ""
        } execution-field`}
        value={props.value}
        onIonChange={(e) => {
          if (e.detail.value) {
            props.onChange(e.detail.value);
          }
        }}
        yearValues={yearValues}
        minuteValues="00,05,10,15,20,25,30,35,40,45,50,55"
        mode="ios"
        readonly={model.isETAReadonly(props.loadStop) || props.isNextStop}
        disabled={model.isETAReadonly(props.loadStop) || props.isNextStop}
      ></IonDatetime>
    </React.Fragment>
  );
};
