const responseParams = {
  userPrincipalName: true,
  firstName: true,
  lastName: true,
  companyName: true,
  email: true,
  liablePartyId: true,
  liablePartyName: true,
  lastSelectedCustomer: true,
  lastSelectedCarrier: true,
  carrier: true,
  carrierId: true,
  tractorNumber: true,
  internalUser: true,
  customer: true,
  carrierTier: true,
  carrierScac: true,
  registered: true,
  image: true,
  address: {
    addressLine1: true,
    addressLine2: true,
    addressType: true,
    city: true,
    state: true,
    postalCode: true
  },
  phone: {
    number: true,
    type: true
  },
  mobileNumber: true,
  userApplicationDetails: {
    roles: {
      roleId: true,
      roleName: true
    }
  },
  updatedTimeStamp: true
};

export class UserDetails {
  /**
   *
   * @param carrierId
   * @param role
   */
  getUsers(carrierId: string, role: string) {
    return {
      query: {
        getMembers: {
          __args: {
            input: {
              carrierId,
              role
            }
          },
          ...responseParams
        }
      }
    };
  }

  /**
   *
   * @param userPrincipalName
   */
  getRequestPayload(userPrincipalName: string) {
    return {
      query: {
        getUserDetails: {
          __args: {
            userPrincipalName
          },
          ...responseParams
        }
      }
    };
  }

  /**
   * Validate User Funcion return query format
   * @param userPrincipalName
   */
  validateUser(userPrincipalName: string) {
    return {
      query: {
        validateUser: {
          __args: {
            userPrincipalName
          },
          ...responseParams
        }
      }
    };
  }

  /**
   * Transfer User Funcion return query format
   * @param userPrincipalName
   */
  transferMember(input: any) {
    return {
      mutation: {
        transferMember: {
          __args: {
            input
          },
          status: true,
          message: true
        }
      }
    };
  }

  /**
   * Confirm Transfer User Funcion return query format
   * @param userPrincipalName
   */
  confirmTransfer(input: any) {
    return {
      mutation: {
        confirmTransfer: {
          __args: {
            input
          },
          status: true,
          message: true
        }
      }
    };
  }

  /**
   * Add User Funcion return query format
   * @param userPrincipalName
   */
  addUser(input: any) {
    return {
      mutation: {
        addMember: {
          __args: {
            input
          },
          message: true,
          status: true,
          member: responseParams
        }
      }
    };
  }

  /**
   * Add User Funcion return query format
   * @param userPrincipalName
   */
  updateMember(input: any) {
    return {
      mutation: {
        updateMember: {
          __args: {
            input
          },
          message: true,
          status: true,
          member: responseParams
        }
      }
    };
  }

  /**
   * regiter User Funcion return query format
   * @param userDetils
   */
  registerMember(input:any) {
    return {
      mutation: {
        registerMember: {
          __args: {
            input
          },
          status: true,
          message: true,
          member: responseParams
        }
      }
    };
  }
}
